import React from 'react';

const text = ({
  value, element, title, dispatch, param = 'text',
}) => (
  <div className="elementBox text">
    <p>{title}</p>
    <textarea
      onChange={e => dispatch({
        type: 'text',
        name: element,
        param,
        value: e.target.value,
      })
      }
      value={value}
    />
  </div>
);

export default text;
