import React from 'react';
import {
  SomeLineColumn, ReportLine, LineDescription, LineValue, ColumnButton,
  StarRatings, StarRatingsTop, StarRatingsButton, StarRaitingContainer,
  TicketStatus, DivWithoutStyles, CardReportBox, CardReportTitle,
  CardReportBody, CardReportElement, CardReportDiscription, CardReportItem, Qq, ColumnButtonNotActive,
} from '../../../../styles/ReportTableColumns';

export function withButtonsTamplate(row, moreInfo, tableName,
  firstItem, secontItem, thirdItem) {
  if (firstItem === null || firstItem === '0') {
    firstItem = 0;
  }
  if (secontItem === null || secontItem === '0') {
    secontItem = 0;
  }
  if (thirdItem === null || thirdItem === '0') {
    thirdItem = 0;
  }

  return (
    <SomeLineColumn>
      <ReportLine key={11}>
        <LineDescription>Всего</LineDescription>
        {firstItem === 0
          ? (
            <ColumnButtonNotActive
              disabled={firstItem === 0}
            >
              {firstItem}
            </ColumnButtonNotActive>
          )
          : (
            <ColumnButton
              key={21}
              disabled={firstItem === 0}
              notActive={firstItem === 0}
              // onClick={() => moreInfo(row, 'all', tableName)}
            >
              {firstItem}
            </ColumnButton>
          )}
      </ReportLine>
      <ReportLine key={12}>
        <LineDescription>Закрыто</LineDescription>
        {secontItem === 0
          ? (
            <ColumnButtonNotActive
              disabled={secontItem === 0}
            >
              {secontItem}
            </ColumnButtonNotActive>
          )
          : (
            <ColumnButton
              key={22}
              disabled={secontItem === 0}
              notActive={secontItem === 0}
              // onClick={() => moreInfo(row, 'closed', tableName)}
            >
              {secontItem}
            </ColumnButton>
          )}
      </ReportLine>
      <ReportLine key={13}>
        <LineDescription>Закрыто по неявке</LineDescription>
        {thirdItem === 0
          ? (
            <ColumnButtonNotActive
              disabled={thirdItem === 0}
            >
              {thirdItem}
            </ColumnButtonNotActive>
          )
          : (
            <ColumnButton
              key={23}
              disabled={thirdItem === 0}
              notActive={thirdItem === 0}
              // onClick={() => moreInfo(row, 'nonAppearance', tableName)}
            >
              {thirdItem}
            </ColumnButton>
          )}
      </ReportLine>
    </SomeLineColumn>
  );
}

export function DubleLineTemplate(firstItemName, firstItemValue, secondItemName, secondItemValue) {
  if (firstItemValue === null) {
    if ((firstItemName.toLowerCase().indexOf('время') !== -1) || (firstItemName.indexOf('Средняя') !== -1) || (firstItemName.indexOf('Общая') !== -1)) {
      firstItemValue = '00:00:00';
    } else {
      firstItemValue = 0;
    }
  }
  if (secondItemValue === null) {
    if ((secondItemName.toLowerCase().indexOf('время') !== -1) || (secondItemName.indexOf('Средняя') !== -1) || (secondItemName.indexOf('Общая') !== -1)) {
      secondItemValue = '00:00:00';
    } else {
      secondItemValue = 0;
    }
  }


  return (
    <SomeLineColumn>
      <ReportLine key={firstItemName}>
        <LineDescription>{firstItemName}</LineDescription>
        <LineValue>{firstItemValue}</LineValue>
      </ReportLine>
      <ReportLine key={secondItemName}>
        <LineDescription>{secondItemName}</LineDescription>
        <LineValue>{secondItemValue}</LineValue>
      </ReportLine>
    </SomeLineColumn>
  );
}


