export function DubleLineTemplate2(value) {
  const style1 = {
    display: 'flex', justifycontent: 'space-between', height: '41px', alignItems: 'center',
  };
  const style2 = {
    display: 'flex', justifycontent: 'space-between', height: '41px', alignItems: 'center', marginTop: '5px',
  };
  const renderRows = () => (
    value.map((el, i) => (
      <div key={i} style={i === 0 ? (style1) : (style2)}>
        <div style={{ display: 'flex', width: '50%' }}>
          <div style={{ flex: '1' }}>{el.name}</div>
          <div style={{ width: '50px', fontWeight: '600' }}>{el.value}</div>
        </div>
        <div style={{ display: 'flex', width: '50%' }}>
          <div style={{ flex: '1' }}>{el.name2}</div>
          <div style={{ width: '50px', fontWeight: '600' }}>{el.value2}%</div>
        </div>
      </div>
    ))
  );

  return (<div>{renderRows()}</div>);
}


export function markTemplate(markValue) {
  return (
    <StarRaitingContainer title="Оценка качества обслуживания">
      <StarRatings>
        <StarRatingsTop
          width={markValue}
        />
        <StarRatingsButton />
      </StarRatings>
    </StarRaitingContainer>
  );
}

export function TiketReoportStatusTemplate(markValue, status) {
  let statusColor = '';
  switch (status) {
    default:
      return (null);
    case 'Закрыт':
      statusColor = 'green';
      break;
    case 'Закрыт по неявке':
      statusColor = '#7F7F7F';
      break;
    case 'Удален системой':
      statusColor = 'maroon';
      break;
  }

  return (
    <TicketStatus
      color={statusColor}
    >
      <DivWithoutStyles>{status}</DivWithoutStyles>
      { markValue !== '0%'
        ? (
          <StarRaitingContainer title="Оценка качества обслуживания">
            <StarRatings>
              <StarRatingsTop
                width={markValue}
              />
              <StarRatingsButton />
            </StarRatings>
          </StarRaitingContainer>
        )
        : null
    }
    </TicketStatus>
  );
}

export function PriorityTamplate(value) {
  let priorityText = '';
  if (value <= 100) {
    priorityText = 'Обычный';
  } else {
    priorityText = 'Высокий';
  }
  return (
    <DivWithoutStyles>{priorityText}</DivWithoutStyles>
  );
}

export function GeneralReportMarkTemplate(markValue, markCnt) {
  return (
    <React.Fragment>
      { markValue !== '0%'
        ? (
          <SomeLineColumn>
            <ReportLine>
              <LineDescription>Количество оценок:</LineDescription>
              <LineValue>{markCnt}</LineValue>
            </ReportLine>

            <ReportLine>
              <LineDescription>Средняя оценка</LineDescription>
              <LineValue>
                <StarRaitingContainer title="Средняя оценка качества обслуживания">
                  <StarRatings>
                    <StarRatingsTop
                      width={markValue}
                    />
                    <StarRatingsButton />
                  </StarRatings>
                </StarRaitingContainer>
              </LineValue>
            </ReportLine>
          </SomeLineColumn>
        )
        : (<DivWithoutStyles>Оценок нет</DivWithoutStyles>)
    }
    </React.Fragment>
  );
}
export function CardReportTemplate(data) {
  return (
    <Qq m="0 0 50px 0">
      <CardReportBox>
        <CardReportTitle>Обслужено талонов</CardReportTitle>
        <CardReportBody>
          <CardReportElement>
            <CardReportDiscription>Закрыто</CardReportDiscription>
            <CardReportItem>{data ? (data[2] || 0) : (<div>загрузка</div>)}</CardReportItem>
          </CardReportElement>
          <CardReportElement>
            <CardReportDiscription>Неявка</CardReportDiscription>
            <CardReportItem>{data ? (data[3] || 0) : (<div>загрузка</div>)}</CardReportItem>
          </CardReportElement>
          <CardReportElement>
            <CardReportDiscription>Всего</CardReportDiscription>
            <CardReportItem>{data ? (data[1] || 0) : (<div>загрузка</div>)}</CardReportItem>
          </CardReportElement>
        </CardReportBody>
      </CardReportBox>

      <CardReportBox>
        <CardReportTitle>Среднее время</CardReportTitle>
        <CardReportBody>
          <CardReportElement>
            <CardReportDiscription>обслуживания</CardReportDiscription>
            <CardReportItem>{data ? (data[5] !== null ? data[5] : '00:00:00') : (<div>загрузка</div>)}</CardReportItem>
          </CardReportElement>
          <CardReportElement>
            <CardReportDiscription>ожидания</CardReportDiscription>
            <CardReportItem>{data ? (data[6] !== null ? data[6] : '00:00:00') : (<div>загрузка</div>)}</CardReportItem>
          </CardReportElement>
        </CardReportBody>
      </CardReportBox>

      <CardReportBox>
        <CardReportTitle>Удалено талонов</CardReportTitle>
        <CardReportBody>
          <CardReportElement>
            <CardReportDiscription>Системой</CardReportDiscription>
            <CardReportItem>{data ? (data[4] || 0) : (<div>загрузка</div>)}</CardReportItem>
          </CardReportElement>
        </CardReportBody>
      </CardReportBox>
    </Qq>
  );
}


export function HistoryTableStatusTemplate(cell) {
  let status = '';
  if (cell === '0') {
    status = 'Талон создан';
  } else if (cell === '1') {
    status = 'Вызван на обработку';
  } else if (cell === '2') {
    status = 'Процесс обработки';
  } else if (cell === '3') {
    status = 'Закрыт';
  } else if (cell === '4') {
    status = 'Закрыт по неявке';
  } else if (cell === '5') {
    status = 'Переведен в другую очередь';
  } else if (cell === '6') {
    status = 'Решение отложено';
  } else if (cell === '7') {
    status = 'Возвращен в очередь';
  } else if (cell === '8') {
    status = 'Ручная обработка диспетчером';
  } else if (cell === '9') {
    status = 'Удален системой';
  } else if (cell === '10') {
    status = 'Смена очереди';
  }

  return (
    <DivWithoutStyles>
      {status}
    </DivWithoutStyles>
  );
}
