const animationHover = (color) => keyframes`
    0% {
      box-shadow: 0 0 0px ${color};
    }
    100% {
      box-shadow: 0 0px 0px 3px ${color};
    }
  `;
export const Btn = styled.button`
  cursor: ${(p) => (p.cancel ? 'pointer' : p.valid ? 'pointer' : 'default')};
  background: ${(p) => (p.cancel ? p.theme.accent : p.valid ? p.theme.green : p.theme.disable)};
  color: ${(p) => (p.valid ? p.theme.buttonHover : p.cancel ? p.theme.buttonHover : p.theme.disableText)};
  margin: ${(props) => (props.margin ? props.margin : 0)};
  border: 0;
  padding: 10px 16px;
  border-radius: 3px;
  &:focus {
    outline: none;
  }
  &:hover {  
    animation: ${(p) => animationHover(p.valid ? p.theme.greenl : p.cancel ? p.theme.accentl : 'transparent')} 0.15s ease-out forwards;
  }
`;
export const BtnRemove = styled(Btn)`
  background: ${(p) => (p.remove ? p.theme.green : p.theme.accent)};
  color: ${(p) => p.theme.buttonHover};
  margin: 0 15px;
  cursor: pointer;
`;

