import React, { useState, useCallback, useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import {
  Input, Button, Space,
} from 'antd';
import Highlighter from 'react-highlight-words';
import 'antd/dist/antd.css';
import { SearchOutlined } from '@ant-design/icons';
import defaultFilters from '../../../../../Elements/ReportFilters/utils/defaultFilters';
import TableConfig from './TableConfig';
import { getReportConfig } from '../../../../../../store/actions/reports';
import PrepareTable from '../../../../../Elements/Tablev2/PrepareTable';
import ReportFilters from '../../../../../Elements/ReportFilters/ReportFilters';

const OperatorTable = (props) => {
  const [queryParams, setQueryParams] = useState();
  const { dispatch, preloader } = props;
  const reportConfig = useSelector((s) => s);

  const moreInfo = useCallback((row, status, type) => {
    dispatch(getReportConfig(
      {
        ...reportConfig,
        reportType: 'ticketReport',
        rootReport: reportConfig.reportType,
        renderBackButton: true,
        rootTable: type,
        buttonType: status,
        activeRow: row,
      },
    ));
  }, [reportConfig, dispatch]);

  const doughnutClick = useCallback((markValue, operatorId, markText) => {
    dispatch(getReportConfig(
      {
        ...reportConfig,
        reportType: 'markReport',
        rootReport: reportConfig.reportType,
        renderBackButton: true,
        anyId: operatorId,
        buttonType: markValue,
        markText,
      },
    ));
  }, [reportConfig, dispatch]);


  const [searchText, setSearchText] = useState();
  const [searchedColumn, setSearchedColumn] = useState();
  const input = useRef(null);

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters) => {
    clearFilters();
    setSearchText('');
  };

  const getColumnSearchProps = (dataIndex) => ({
    filterDropdown: ({
      setSelectedKeys, selectedKeys, confirm, clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={input}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={(e) => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered) => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) => (record[dataIndex]
      ? record[dataIndex].toString().toLowerCase().includes(value.toLowerCase())
      : ''),
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        setTimeout(() => input.current.select(), 100);
      }
    },
    render: (text) => (searchedColumn === dataIndex ? (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[searchText]}
        autoEscape
        textToHighlight={text ? text.toString() : ''}
      />
    ) : (
      text
    )),
  });

  const preparQueryParams = useCallback(async (filters) => {
    console.log(filters);
    const columns = TableConfig(moreInfo, doughnutClick, getColumnSearchProps); // Колонки для таблиц
    const params = {
      columns: columns.columns,
      filters: filters || defaultFilters(),
      url: '/api/reports/build',
      type: 'operatorReport',
    };
    setQueryParams(params);
  }, [moreInfo, doughnutClick]);

  useEffect(() => {
    preparQueryParams();
  }, [props, preparQueryParams]);

  if (queryParams) { console.log('queryParams() === ', queryParams); }

  return (
    <>
      { queryParams ? (
        <>
          <ReportFilters
            showFilters={{
              main: ['reportRange', 'operators'],
              others: ['actionFilters', 'servedfilters', 'workTimeFilter', 'markFilters'],
              orders: ['user_fio', 'all_cnt', 'close_cnt', 'neyavka_cnt', 'total_work_time', 'avg_work_time', 'share_cnt', 'solved_questions',
                'mark1', 'mark2', 'mark3', 'mark4', 'mark5', 'markcnt'],
            }}
            paramsCallback={preparQueryParams}
            type="operatorReport"
          />
          <PrepareTable
            keys="operator"
            type="OperatorTable"
            {...queryParams}
            preloader={preloader}
          />
        </>
      )
        : (null)}
    </>
  );
};

export default OperatorTable;
