import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { FaPaperPlane, FaTimes } from 'react-icons/fa';
import {
  Box, CircleBox, QuestionBox, BoxRelative, InputContainer, Body, Item,
} from './style';
import { Input } from '../../styles/Common';
import { ReactComponent as SVG } from './icon.svg';


const Helper = () => {
  const [value, setValue] = useState('');
  const [open, setOpen] = useState(false);
  const [local, setLocal] = useState(false);
  const input = useRef(null);
  const bodyMessage = useRef(null);
  const initialMessages = [
    { idx: 0, fromUser: 0, value: 'Напишите команду или задайте вопрос' },
  ];

  const [messages, setMessages] = useState(initialMessages);

  const addMessage = (msg, fromUser = 1) => {
    setMessages((allMessages) => [...allMessages, { fromUser, value: msg }]);
  };

  const send = async () => {
    if (value === '') return;
    addMessage(value);
    setValue('');
    const res = await axios({
      method: 'post',
      url: '/api/chat',
      data: {
        token: window.localStorage.getItem('token'),
        msg: value,
      },
      json: true,
    });
    const { data } = res;
    const { success, message } = data;
    if (success) {
      addMessage(message, 0);
    } else {
      addMessage('Ошибка обработки', 0);
    }
    input.current.focus();
  };

  useEffect(() => {
    const l = window.location.hostname === 'localhost';
    setLocal(l);
    // if (local) input.current.focus();
  }, []);

  useEffect(() => {
    if (local) bodyMessage.current.scrollTo(0, bodyMessage.current.scrollHeight + 50);
  }, [messages, local]);

  return (
    <>
      {local && (
      <>
        <Box open={open}>
          <BoxRelative>
            <QuestionBox>
              <h4>Виртуальный помощник <FaTimes onClick={() => setOpen(false)} /></h4>
              <Body ref={bodyMessage}>
                {messages.map((el, index) => <Item server={!el.fromUser} key={index} dangerouslySetInnerHTML={{ __html: el.value }} />)}
              </Body>
              <InputContainer>
                <Input ref={input} onKeyDown={(e) => e.keyCode === 13 && send()} value={value} onChange={(e) => setValue(e.target.value)} />
                <FaPaperPlane onClick={() => send(value)} />
              </InputContainer>
            </QuestionBox>
          </BoxRelative>
        </Box>
        <CircleBox dop onClick={() => setOpen(!open)}>
          <SVG />
        </CircleBox>
      </>
      )}
    </>
  );
};

export default Helper;
