import React, { useState, useEffect } from 'react';
import {
  FilterBoxTitle, FilterItemBox, MsFiltersItem, FilterBox,
} from '../../../../../styles/ReportFilters';
import Dropdown from '../../../Dropdown/Dropdown';
import MultiSelectNew from '../../../MultiSelectNew/MultiSelectNew';

const WorkPlaceSelect = ({ changeFilter, filtersData }) => {
  const [dropdownConfig, setDropdownConfig] = useState({
    headerTitleText: 'По рабочим местам',
    options: {},
  });


  const [msData, setMsData] = useState([]);

  const getDefaultMsData = () => {
    const MsList = [];
    filtersData.map((el) => { if (el.bunchType === ('wp_id')) { MsList.push(el); } });
    setMsData(MsList);
  };

  useEffect(() => {
    const items = [
      { key: 'wp_id', name: 'По рабочим местам', text: 'По рабочим местам' },
      { key: 'q_id', name: 'По очередям', text: 'По очередям' },
      { key: 'modified_by_id', name: 'По операторам', text: 'По операторам' },
    ];
    setDropdownConfig({ ...dropdownConfig, items });
    getDefaultMsData();
  }, [filtersData]);

  const changeOperetorFilter = async (e, options, key) => {
    changeFilter('groupinType', key);
    const MsList = [];
    filtersData.map((el) => {
      if (el.bunchType === (key)) {
        MsList.push(el);
      }
    });
    setMsData(MsList);
  };


  const MultiSelectItemList = (list) => {
    const newAd = [];
    if (list) {
      list.forEach((el) => {
        newAd.push(el.id);
      });
      const ww = newAd.join(', ');
      if (ww === '') changeFilter('bunch_id', 'all');
      else changeFilter('bunch_id', ww);
    }
  };

  return (
    <>
      <FilterBox Select>
        <FilterBoxTitle>Тип групировки</FilterBoxTitle>
        <FilterItemBox>
          <MsFiltersItem>
            {dropdownConfig.items ? (
              <Dropdown
                config={dropdownConfig}
                clickHandler={changeOperetorFilter}
                replaceableTitle
              />
            ) : (null)}
          </MsFiltersItem>
        </FilterItemBox>
      </FilterBox>
      <FilterBox Select>
        <FilterBoxTitle>Группа</FilterBoxTitle>
        <FilterItemBox>
          <MsFiltersItem>
            <MultiSelectNew
              data={msData}
              selectedList={MultiSelectItemList}
              selectValuse="name"
              keys="id"
            />
          </MsFiltersItem>
        </FilterItemBox>
      </FilterBox>
    </>
  );
};

export default WorkPlaceSelect;
