import React, { useState } from 'react';
import Table from '../../../Elements/Table/Table';
import Config from './TableConfig';
import DeleteWarning from '../../../Elements/Modal/DeleteWarning';
import { useToggle } from '../../../../hooks/useToggle';
import DeleteItem from '../../../../utils/deleteItem';

const QuestionsTable = ({
  questList, setQuestData,
  setQuestUpdate, toggleOpen, preloader, fetchQuests, queueList, write, showError,
}) => {
  const [showDelWarn, setShowDelWarn] = useToggle(false);
  const [serviceId, setServiceId] = useState(false);
  const [serviceName, setServiceName] = useState(false);

  const delService = async () => {
    await DeleteItem({
      preloader, url: `/server/api/servedquestion/${serviceId}`, fetchData: fetchQuests, setShowWarn: setShowDelWarn, setId: setServiceId, showError,
    });
  };

  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const {
      id, description, name, displayname, connectedQueues,
    } = row;
    if (dataset.name === 'del') {
      setServiceId(id);
      setServiceName(name);
      setShowDelWarn();
    } else if (dataset.name === 'update') {
      setQuestData({
        id, description, name, displayname, connectedQueues,
      });
      setQuestUpdate(true);
      toggleOpen();
    }
  };

  const Setting = Config(queueList, write);
  const { columns } = Setting;
  return (
    <>
      <React.Suspense fallback={null}>
        { questList.length > 0 && <Table keys="id" data={questList} handleIcon={handleTableIcon} columns={columns} />}
        <DeleteWarning name={serviceName} confirmDel={delService} isOpen={showDelWarn} toggleOpen={setShowDelWarn} />
      </React.Suspense>
    </>
  );
};

export default React.memo(QuestionsTable);
