import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import Input from '../../../components/input/input';
import {
  Body, BodyContainer, InputBox, KeyboardContainer, NextButton,
} from '../../../style/style';
import Keyboard from '../../../components/input/keyboard';
import history from '../../../../../../../../../utils/customHistory';
import { resetTicket } from '../../../utils/actions';
import Store from '../../../utils/store';

const { dispatch } = Store;

const PinCode = ({ children }) => {
  const { style, config } = useSelector((state) => state);
  const { id, headUrl } = config;
  const [pin, setPin] = useState('');
  const [inputId, setId] = useState('input');
  const { title, button, input } = style;

  const item = {
    name: '',
    params: {
      fid: 'input',
      value: 'number',
    },
  };

  const clickInput = (value) => setId(value);


  const onNext = () => {
    const data = {
      zone_id: config.idZone,
      pinCode: +pin,
      satellite_id: config.id,
    };
    dispatch(resetTicket(data));
    history.push(`${headUrl}/${id}/print`);
  };

  return (
    <BodyContainer style={style.body}>
      <Body>
        <div style={title}>Введите пин-код</div>
        <KeyboardContainer height>
          <InputBox button={button} style={{ flex: 0, margin: '30px 0' }}>
            <Input
              item={item}
              height={70}
              style={input}
              width="50%"
              value={pin}
              id={inputId}
              clickInput={clickInput}
            />
          </InputBox>
          <Keyboard
            styleButton={button}
            setValue={(e) => setPin(e)}
            value={pin}
            id={inputId}
          />
        </KeyboardContainer>
        {pin.length >= 4 && <NextButton style={{ ...style.bottomButton }} onClick={onNext}> Готово </NextButton>}
      </Body>
      {children}
    </BodyContainer>
  );
};

export default PinCode;
