import React, { useState } from 'react';
import Table from '../../../../Elements/Table/Table';
import Config from './TableConfig';
import DeleteWarning from '../../../../Elements/Modal/DeleteWarning';
import { useToggle } from '../../../../../hooks/useToggle';
import DeleteItem from '../../../../../utils/deleteItem';

const WeekTable = ({
  weekList, setWeekUpdate, setWeekData, toggleOpen, preloader, fetchDay, dayList, write, showError,
}) => {
  const [weekId, setWeekId] = useState(false);
  const [weekName, setWeekName] = useState(false);
  const [showDelWarn, setShowDelWarn] = useToggle(false);

  const delWeek = async () => {
    await DeleteItem({
      preloader, url: `/server/api/dworkweek/${weekId}/`, fetchData: fetchDay, setShowWarn: setShowDelWarn, setId: setWeekId, showError,
    });
  };

  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const {
      id, name, description, friday_id, monday_id, saturday_id, sunday_id, thursday_id, tuesday_id, wednesday_id,
    } = row;
    if (dataset.name === 'del') {
      setWeekId(id);
      setWeekName(name);
      setShowDelWarn();
    } else if (dataset.name === 'update') {
      setWeekUpdate(true);
      setWeekData({
        id, name, description, friday_id, monday_id, saturday_id, sunday_id, thursday_id, tuesday_id, wednesday_id,
      });
      toggleOpen();
    }
  };

  const Setting = Config(dayList, write);
  const { columns } = Setting;
  return (
    <>
      {weekList.data.length > 0 && <Table keys="id" data={weekList.data} handleIcon={handleTableIcon} columns={columns} />}
      <DeleteWarning name={weekName} confirmDel={delWeek} isOpen={showDelWarn} toggleOpen={setShowDelWarn} />
    </>
  );
};

export default React.memo(WeekTable);
