import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { ReactComponent as Edit } from '../../../../images/edit.svg';
import iconFormatter from '../../../../utils/iconFormatter';

function priceFormatterDef(column, colIndex, { sortElement, filterElement }) {
  return (
    <div className="filterBoxTable deff">
      <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
        { column.text }
        { sortElement }
      </div>
      { filterElement }
    </div>
  );
}

function priceFormatter(column, colIndex, { filterElement }) {
  return (
    <div className="filterBoxTable">
      {column.dataField !== 'Settings' && <Edit style={{ margin: '2px 10px 0 0' }} /> }
      { filterElement }
    </div>
  );
}


