import React, { useState, useEffect } from 'react';
import { FaChevronDown } from 'react-icons/fa';
import { ReactComponent as Picture } from '../../../../../../../images/picture.svg';
import Close from '../../../../../../../images/close.png';


const UploadImages = ({ items, smart, dispatch = () => { } }) => {
  const [active, setActive] = useState(false);
  const [open, setOpen] = useState(smart);
  const [arrDiff, setDiff] = useState([true, true, true, true]);

  const diff = 120 / 409;

  const uploadLogo = (e, value, isDrop) => {
    let file;
    if (isDrop) {
      const dt = e.dataTransfer;
      const { files } = dt;
      file = files[0];
    } else {
      file = e.target.files[0];
    }

    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = (() => (evt) => {
      const image = evt.target;
      if (image.error === null) {
        dispatch({ type: 'uploadImage', payload: image.result, value });
      } else {
        console.log('error upload image');
      }
    })(file);
  };

  const handleActive = (e, el) => {
    e.preventDefault();
    if (active !== el) setActive(el);
  };
  const handleDrop = (e, value) => {
    e.preventDefault();
    e.stopPropagation();
    setActive(false);
    uploadLogo(e, value, 'drop');
  };

  useEffect(() => {
    const arrPromise = [];
    items.forEach((el) => {
      arrPromise.push(new Promise((resolve) => {
        if (el.img) {
          const img = new Image();
          img.onload = () => {
            resolve(diff < img.height / img.width);
          };
          img.src = el.img;
        } else {
          resolve(true);
        }
      }));
    });
    Promise.all(arrPromise).then((values) => {
      setDiff(values);
    });
  }, [items, diff]);

  const renderItems = () => items.map((item, index) => (
    <div key={item.value} className="upload-item-box">
      <p className="small-title">
        {item.name}
        {/* <Tooltip
          // link="/custom/videos/?goBack=1"
          type="icon"
          text={item.tooltipText}
        >
          <img src={Question} alt="" />
        </Tooltip> */}
      </p>
      <div className="upload-item_content">
        <div className="upload-label-box">
          <div>
            <label
              htmlFor={`input${item.value}`}
              onDragEnter={e => handleActive(e, item.value)}
              onDragOver={e => handleActive(e, item.value)}
              onDragLeave={e => handleActive(e, false)}
              onDrop={e => handleDrop(e, item.value)}
              className="upload-label"
              style={{ backgroundColor: active === item.value ? 'orange' : 'transparent' }}
            >
              <span>Перетащите изображение сюда</span>
              <span>Или нажмите здесь для загрузки изображения</span>
            </label>
            <input
              type="file"
              accept="image/*"
              style={{ display: 'none' }}
              id={`input${item.value}`}
              onChange={e => uploadLogo(e, item.value)}
            />
          </div>
        </div>
        <div className={`upload-label_preview-image ${arrDiff[index] && 'vertical'}`}>
          {item.img && (
            <div
              className="upload-label-box__del-btn"
              onClick={() => dispatch({ type: 'delImage', value: item.value, payload: 'none' })}
            >
              <img src={Close} alt="" />
            </div>
          )}
          {item.img ? <img src={item.img} alt="" /> : <p>изображение не загружено</p>}
        </div>
      </div>
    </div>
  ));

  return (
    <div className={`upload-images-box ${smart}`}>
      {!smart && (
      <button className={`head-title ${open ? 'open' : 'hide'}`} onClick={() => setOpen(!open)}>
        <Picture className="left" />
        <p>Загрузите изображения</p>
        <FaChevronDown className="right" />
      </button>
      )
      }
      {open && <div className="labels-box">{renderItems()}</div>}
    </div>
  );
};

export default UploadImages;
