import React, { useState } from 'react';
import axios from 'axios';
import Table from '../../Elements/Table/Table';
import { Button, EmptyList } from '../../../styles/Common';
import Config from './TableConfig';
import Preloader from '../../Elements/Preloader/Preloader';
import DeleteWarning from '../../Elements/Modal/DeleteWarning';
import { useToggle } from '../../../hooks/useToggle';
// import DeleteItem from '../../../utils/deleteItem';

const ReserveTable = ({
  reserveList, write, fetchData, preloader,
}) => {
  const [copyName, setCopyName] = useState(false);
  // const [copyPath, setCopyPath] = useState(false);
  const [showDelWarn, setShowDelWarn] = useToggle(false);

  const delCopy = async () => {
    // await DeleteItem({
    //   preloader, url: `/server/api/digitboard/${displayId}/`, fetchData: fetchReserve, setShowWarn: setShowDelWarn, setId: setDisplayId, showError,
    // });
  };

  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const {
      name, path,
    } = row;
    if (dataset.name === 'del') {
      setCopyName(name);
      // setCopyPath(path);
      setShowDelWarn();
    } else if (dataset.name === 'download') {
      const link = document.createElement('a');
      link.href = path;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  const createCopy = async () => {
    preloader();
    try {
      await axios({
        method: 'post',
        url: '/api/createbackup',
        data: {
          token: window.localStorage.getItem('token'),
        },
        json: true,
      });
      preloader();
      fetchData();
    } catch (e) {
      preloader();
    }
  };

  const Setting = Config(write);
  const { columns } = Setting;

  return (
    <>
      <Button onClick={createCopy}>Создать копию</Button>
      {reserveList === null
        ? <Preloader isOpen small /> : reserveList.length
          ? (
            <Table keys="name" data={reserveList} handleIcon={handleTableIcon} columns={columns} />
          ) : <EmptyList>Резервных копий нет</EmptyList>}
      <DeleteWarning confirmDel={delCopy} name={copyName} isOpen={showDelWarn} toggleOpen={setShowDelWarn} />
    </>
  );
};

export default React.memo(ReserveTable);
