import React from 'react';
import { Input } from '../../../../../../../../../styles/Common';
import { ColorBox } from '../../../../../Components/style';

const Position = ({ bottomButton, onChange }) => {
  const arr = [
    { title: 'Слева', value: bottomButton.left, param: 'left' },
    { title: 'Сверху', value: bottomButton.top, param: 'top' },
  ];
  return (
    <div className="elementBox">
      <p>Расположение кнопок</p>
      <div className="box">
        {arr.map(el => (
          <ColorBox width="auto">
            <span>{el.title}</span>
            <Input
              size="16px"
              disabled={bottomButton.position}
              width="100px"
              textAlign="center"
              type="number"
              onChange={e => onChange(e, el.param)}
              value={el.value}
            />
          </ColorBox>
        ))}
      </div>
    </div>
  );
};

export default Position;
