import React, { useMemo } from 'react';
import moment from 'moment';
import { FaChevronRight, FaChevronLeft } from 'react-icons/fa';
import {
  FilterMonth,
  MonthBox,
} from '../style';
import SelectInput from '../../../../Elements/Input/SelectInput/SelectInput';
import Day from './day';
import Record from './record';

const Calendar = ({
  optionsQueue, queue, setQueue, month, year, setMonth, setYear, calendar, onRecord,
  setIsEdit, isLoad, selectedDay, setSelectedDay, selectedQueue,
  onDelete, setClear, saveDay, setOpen, open, dayId, openInfo, setOpenInfo, onClear, copyDay, showError,
}) => {
  const day = moment().format('DD');
  const newDay = moment(`${year}.${month}.${day}`, 'YYYY:M:DD').isValid() ? day : 1;
  const mom = moment(`${year}.${month}.${newDay}`, 'YYYY:M:DD');
  const offset = moment(`${year}.${month}.01`, 'YYYY:MM:DD').isoWeekday();
  const offsetEnd = 7 - moment(`${year}.${month}.01`, 'YYYY:MM:DD').endOf('month').isoWeekday();
  const countDays = mom.daysInMonth();
  const monthName = mom.format('MMMM');
  const arrNameDays = ['Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб', 'Вс'];


  const getDays = useMemo(() => {
    const arrDays = [];
    if (calendar) {
      for (let i = 1; i < offset; i += 1) {
        arrDays.push(<li key={i + 100} />);
      }
      for (let b = 1; b <= countDays; b += 1) {
        const item = calendar[b];
        arrDays.push(<Day dayId={dayId} m={month} b={b} item={item} key={b} queue={queue} setSelectedDay={setSelectedDay} onRecord={onRecord} />);
      }
    }
    return arrDays;
  }, [month, calendar, dayId, offset, onRecord, queue, selectedDay, countDays]);

  const ofEnd = [];
  for (let of = 1; of <= offsetEnd; of += 1) {
    ofEnd.push(<li key={of}>{of}</li>);
  }

  const description = [
    { text: 'Запись не ведется', class: 'disable' },
    { text: 'Можно записаться', class: 'green' },
    { text: 'Все слоты для записи заняты', class: 'blue' },
    { text: 'Все записи обслужены', class: 'red' },
    { text: 'Выбранный день', class: 'white' },
  ];

  const onChangeQueue = (e) => {
    setQueue(e.target.value);
  };

  return (
    <div className="flex" style={{ marginBottom: 60, alignItems: 'flex-start' }}>
      <MonthBox>
        <FilterMonth>
          <div>
            <p>Выберите услугу</p>
            <SelectInput noDefault options={optionsQueue} value={queue} onChange={onChangeQueue} />
          </div>
        </FilterMonth>
        <FilterMonth>
          {queue && (
          <div className="monthFilter">
            { moment().format('YYYY.MM') !== `${year}.${month}`
              ? (month !== 1
                ? <span onClick={() => setMonth(+month - 1)}><FaChevronLeft /></span>
                : <span onClick={() => { setMonth(12); setYear(+year - 1); }}><FaChevronLeft /></span>)
              : <span className="shadow" />}
            <p>{monthName}, {year}</p>
            { month < 12
              ? <span onClick={() => setMonth(+month + 1)}><FaChevronRight /></span>
              : <span onClick={() => { setMonth(1); setYear(+year + 1); }}><FaChevronRight /></span>}
          </div>
          )}
        </FilterMonth>
        { isLoad && queue
          ? <div className="preloader"> Загрузка... </div>
          : queue && (
            <>
              <div className="month">
                <div className="titleBox">{arrNameDays.map((el) => <li key={el} className="title">{el}</li>)}</div>
                {getDays}
                {ofEnd}
              </div>
              <div className="description">
                {description.map((el, index) => (
                  <div key={index} className="item">
                    <span className={el.class} />
                    <p>{el.text}</p>
                  </div>
                ))}
              </div>
            </>
          )}
      </MonthBox>
      {selectedDay.data.length > 0 && (
      <Record
        showError={showError}
        open={open}
        isLoad={isLoad}
        onClear={onClear}
        setOpen={setOpen}
        copyDay={copyDay}
        setOpenInfo={setOpenInfo}
        openInfo={openInfo}
        setClear={setClear}
        saveDay={saveDay}
        onDelete={onDelete}
        setIsEdit={setIsEdit}
        selectedDay={selectedDay}
        selectedQueue={selectedQueue}
        queue={optionsQueue}
        onRecord={onRecord}
      />
      )}
      {/* {queue && selectedDay.data.length === 0 && ( */}
      {/* <Constructor */}
      {/*  isEdit={isEdit} */}
      {/*  setIsEdit={setIsEdit} */}
      {/*  onSave={onSave} */}
      {/*  data={templateData} */}
      {/*  setData={setTemplateData} */}
      {/* /> */}
      {/* )} */}
    </div>
  );
};

export default Calendar;
