import React from 'react';
import { Input } from '../../../../../../styles/Common';
import { ColorBox } from '../style';

const onRefresh = () => console.log('Функция сброса не назначена');

const Height = ({
  value, element, dispatch, title, nameParams, width, widthInput, refresh = onRefresh, clear = false,
}) => {
  const big = nameParams.match(/\./);

  const onChange = (e) => {
    if (!big) {
      dispatch({
        type: 'params',
        name: element,
        param: nameParams,
        value: e.target.value,
      });
    } else {
      const arr = nameParams.split('.');
      dispatch({
        type: 'bigParams',
        parentName: element,
        name: arr[0],
        param: arr[1],
        value: Number(e.target.value),
      });
    }
  };

  return (
    <ColorBox width={width}>
      <span dangerouslySetInnerHTML={{ __html: title }} />
      <Input
        size="16px"
        m="0"
        width={widthInput || '100px'}
        textAlign="center"
        onChange={onChange}
        value={value}
      />
      {clear && <div className="refresh" onClick={refresh}>Сбросить</div>}
    </ColorBox>
  );
};

export default Height;
