function animationHover(add, w, p) {
  return keyframes`
    0% {
      box-shadow: 0 0 0px ${p.green ? p.theme.greenl : p.theme.accentl};
    }
    100% {
      box-shadow: 0 0px 0 3px ${p.green ? p.theme.greenl : p.theme.accentl};
      max-width: ${add ? w ? `${w}px` : '120px' : 'auto'};
    }
  `;
}

