import React from 'react';
import { Draggable } from 'react-beautiful-dnd';
import { OrderElementBox } from '../../../../../styles/ReportFilters';
import Dropdown from '../../../Dropdown/Dropdown';
import getDropdownConfig from './DropdownConfig';

const OrderElement = ({ task, index, changeFilter }) => {
  const DropdownConfig = getDropdownConfig();

  const changeFilterMark = (e, options, key, name) => {
    changeFilter(`${task.fild}`, `${name}`);
  };

  return (
    <Draggable draggableId={`${task.fild}`} index={index} key={task.id}>
      {(provided, snapshot) => (
        <OrderElementBox
          key={index}
          {...provided.draggableProps}
          {...provided.dragHandleProps}
          ref={provided.innerRef}
          isDragging={snapshot.isDragging}
        >
          {task.FieldDescription}
          <Dropdown
            config={DropdownConfig}
            clickHandler={changeFilterMark}
            replaceableTitle
          />
        </OrderElementBox>
      )}

    </Draggable>
  );
};

export default OrderElement;
