import React, { useContext } from 'react';
import {
  TemplateBox, Title, LayoutBox,
} from '../style';
import { Input } from '../../../../../../styles/Common';

import Select from '../../../../../Elements/Input/SelectInput/SelectInput';
import terminalCtx from '../terminalCtx';
import Uppercase from '../../Components/modules/uppercase';

const Layouts = () => {
  const {
    data: { head: { fontFamily } }, data, dispatch, fonts,
  } = useContext(terminalCtx);

  const { head, general } = data;

  const onChange = (value, name, param) => dispatch({
    type: 'params', name, param, value: Number(value),
  });

  return (
    <LayoutBox>
      <TemplateBox width="320px">
        <Title>Шрифт</Title>
        <Select
          width="320px"
          m={0}
          noDefault
          options={fonts}
          value={fontFamily}
          onChange={e => dispatch({ type: 'fonts', value: e.target.value })}
        />
      </TemplateBox>
      <TemplateBox>
        <Title> Ширина терминала </Title>
        <Input
          textAlign="center"
          size="16px"
          onChange={e => onChange(e.target.value, 'head', 'width')}
          m="0px"
          value={head.width}
        />
      </TemplateBox>
      <TemplateBox>
        <Title> Высота терминала </Title>
        <Input
          textAlign="center"
          size="16px"
          onChange={e => onChange(e.target.value, 'head', 'height')}
          m="0px"
          value={head.height}
        />
      </TemplateBox>
      <TemplateBox width="240px">
        <Uppercase
          element="general"
          dispatch={dispatch}
          title="Разделение экрана на два блока"
          nameParams="twoScreen"
          value={general.twoScreen}
        />
      </TemplateBox>
      <TemplateBox width="240px">
        <Uppercase
          element="general"
          dispatch={dispatch}
          title="Поля ввода в Предварительной записи"
          nameParams="preEntryInput"
          value={general.preEntryInput}
        />
      </TemplateBox>
    </LayoutBox>
  );
};

export default Layouts;
