import React from 'react';
import { Input } from '../../../../../../styles/Common';

import { ColorBox } from '../style';

const fontSize = ({
  dispatch, element, value, paramName, rusName, width,
}) => (
  <ColorBox width={width}>
    <span dangerouslySetInnerHTML={{ __html: rusName }} />
    <Input
      textAlign="center"
      size="16px"
      width="auto"
      onChange={e => dispatch({
        type: 'params',
        name: element,
        param: paramName,
        value: Number(e.target.value),
      })}
      m="0px"
      value={value}
    />
  </ColorBox>
);

export default fontSize;
