import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Dropdown from '../../../Elements/Dropdown/Dropdown';

const QuestionSelection = ({ handleQuestionChange }) => {
  const [dropdownConfig, setDropdownConfig] = useState({});
  const fetchQuestionList = async () => {
    const items = [];
    const url = '/api/reports/build';
    const type = 'qualityQueueList';
    const token = window.localStorage.getItem('token');
    const res = await axios({
      method: 'post',
      url,
      data: {
        token,
        type,
        filters: { main: { } },
      },
    });

    res.data.answer.forEach((e) => {
      items.push({ key: e.id, name: e.name, text: e.questiontext });
    });

    setDropdownConfig({
      headerTitleText: items[0].text,
      items,
    });

    handleQuestionChange(items[0].text, items[0].name, items[0].key);
  };

  useEffect(() => {
    fetchQuestionList();
  }, []);

  return (
    <div style={{ marginLeft: '15px' }}>
      {dropdownConfig.items
        && (
        <Dropdown
          config={dropdownConfig}
          clickHandler={handleQuestionChange}
          replaceableTitle
        />
        )}
    </div>
  );
};

export default QuestionSelection;
