import React, { useState, useEffect } from 'react';
import Timer from './timerUtils';

const TimerComponent = ({ clock, styleTime, viewType }) => {
  const [time, setTime] = useState({});
  const incrementTime = () => {
    const t = Timer();
    setTime(t);
  };
  useEffect(() => {
    const Interval = setInterval(incrementTime, 1000);
    return () => clearInterval(Interval);
  }, []);
  let dateView = time.date;
  const typeClock = ['.', '-', '/'];
  if (dateView) {
    dateView = dateView.replace(/\.|-|\//g, typeClock[viewType]);
  }

  return (
    <>
      <div
        style={{
          order: clock.order,
          fontSize: clock.fontSizeСlock,
          fontWeight: clock.fontWeightClock,
          textAlign: clock.textAlignTime,
          ...styleTime,
        }}
        className="time"
      >{time.time}
      </div>
      <div
        className="date"
        style={{
          fontSize: clock.fontSize,
          order: Number(!clock.order),
          fontWeight: clock.fontWeight,
          textAlign: clock.textAlign,
        }}
      >{dateView}
      </div>
    </>
  );
};

export default TimerComponent;
