import React, { useState, useMemo } from 'react';
import InputMask from 'react-input-mask';
import { useSelector } from 'react-redux';
import {
  InputBox, InputGroup, KeyboardContainer, NextButton,
} from '../../../style/style';
import ModalInput from './Modal';

const InnerData = ({ createTicket, bottomStyle }) => {
  const { input, button } = useSelector((state) => state.config.config.style);
  const [id, setId] = useState(null);

  const [values, setValues] = useState(['', '', '']);

  const inputs = useMemo(() => [<InputGroup button={button} input={input}>
    <label className="phoneLabel">Ваш телефон</label>
    <InputMask
      className={`phonenumber ${id === 1 && 'activeInput'}`}
      value={values[0]}
      mask="+7 (999) 999 99 99"
      alwaysShowMask
      onClick={() => setId(1)}
    />
  </InputGroup>,
    <InputGroup button={button} input={input}>
      <label className="phoneLabel">Ваш e-mail</label>
      <input
        className={id === 2 && 'activeInput'}
        value={values[1]}
        onClick={() => setId(2)}
        onChange={(e) => setValues([values[0], e.target.value, values[2]])}
      />
    </InputGroup>,
    <InputGroup button={button} input={input}>
      <label className="phoneLabel">ФИО</label>
      <input
        className={id === 3 && 'activeInput'}
        value={values[2]}
        onClick={() => setId(3)}
      />
    </InputGroup>,
  ], [values, button, id, input]);

  return id ? (
    <ModalInput
      input={input}
      id={id}
      values={values}
      setValues={setValues}
      styleButton={button}
      inputs={inputs}
      setId={setId}
    />
  )
    : (
      <KeyboardContainer>
        <InputBox button={input}>
          {inputs}
          <div className="flex" style={{ marginTop: 50 }}>
            <NextButton
              style={bottomStyle}
              onClick={() => createTicket({
                clientPhoneNumber: values[0],
                clientEmail: values[1],
                clientFIO: values[2],
              })}
            > Записаться
            </NextButton>
          </div>
        </InputBox>
      </KeyboardContainer>
    );
};

export default InnerData;
