import { withButtonsTamplate, DubleLineTemplate } from '../../../utils/Formater';

export default function (getColumnSearchProps) {
  return {
    columns: [
      {
        dataIndex: 'name',
        title: 'Название группы',
        ...getColumnSearchProps('name'),
      },
      {
        dataIndex: 'all_cnt',
        title: 'Обслуженно',
        render: (cell, row) => withButtonsTamplate(row, 'WorkPlace', row.all_cnt, row.close_cnt, row.neyavka_cnt),
        sorter: (a, b) => a.all_cnt - b.all_cnt,
      },
      {
        dataIndex: '13',
        title: 'Длительность ожидания',
        render: (cell, row) => DubleLineTemplate('Общая:', row.total_wait_time, 'Средняя:', row.avg_wait_time),
      },
      {
        dataIndex: '12',
        title: 'Длительность обслуживания',
        render: (cell, row) => DubleLineTemplate('Общая:', row.total_proc_time, 'Средняя:', row.avg_proc_time),
      },
    ],
  };
}
