import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
// import { BtnTable } from '../../../styles/Common';
// import { ReactComponent as Trash } from '../../../images/del.svg';
// import { ReactComponent as Config } from '../../../images/config.svg';
import { ReactComponent as Edit } from '../../../images/edit.svg';
import c from '../../../styles/Colors';
import IconFormatter from '../../../utils/iconFormatter';

function priceFormatter(column, colIndex, { filterElement }) {
  return (
    <div className="filterBoxTable">
      {column.dataField !== 'Settings' && <Edit style={{margin: '2px 10px 0 0'}}  fill={c.main} />}
      {filterElement}
    </div>
  );
}

function typeFormatter(cell) {
  const obj = {
    wp_id: 'По рабочим местам',
    modified_by_id: 'По операторам',
    q_id: 'По услугам',
  };
  return obj[cell];
}

function objectsFormatter(cell, row) {
  const RenderObjectList = () => {
    if (row.bunchType === 'wp_id') {
      return row.bunchObjectsIds.map(el => (
        <div key={el.id}>{el.name}</div>
      ));
    } if (row.bunchType === 'q_id') {
      return row.bunchObjectsIds.map(el => (
        <div key={el.id}>{el.fullname}</div>
      ));
    } if (row.bunchType === 'modified_by_id') {
      return row.bunchObjectsIds.map(el => (
        <div key={el.id}>{el.fio}</div>
      ));
    }
    return false;
  };


  return (<div> {RenderObjectList()}</div>);
}


export default function Setting(write) {
  // }
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Название',
        filter: textFilter({
          placeholder: 'Имя',
        }),
        editable: false,
        headerFormatter: priceFormatter,
      },
      {
        dataField: 'description',
        text: 'Описание',
        filter: textFilter({
          placeholder: 'Описание',
        }),
        editable: false,
        headerFormatter: priceFormatter,
      },
      {
        dataField: 'bunchType',
        text: 'Тип',
        editable: false,
        formatter: typeFormatter,

        sort: true,
      },
      {
        dataField: 'bunchObjectsIds',
        text: 'Группируемые объекты',
        editable: false,
        formatter: (cell, row) => objectsFormatter(cell, row),
        sort: true,
      },
      {
        dataField: 'created_on',
        text: 'Дата создания',
        filter: textFilter({
          placeholder: 'Дата создания',
        }),
        headerFormatter: priceFormatter,
        editable: false,
      },
      {
        dataField: 'modified_by_username',
        text: 'Изменен',
        editable: false,
      },
      {
        dataField: 'modified_on',
        text: 'Когда изменен',
        editable: false,
      },
      {
        dataField: 'Settings',
        text: '',
        headerStyle: {
          width: 110,
        },
        formatter: () => IconFormatter({ filter: false, write, params: false }),
        formatExtraData: {},
        editable: false,
      },
    ],
  };
}
