import React from 'react';
import { TimePicker } from 'antd';
import 'antd/dist/antd.min.css';

const { RangePicker } = TimePicker;

const MultiTimePicker = ({ data, onChange }) => {
  const { startTime } = data;
  const change = (e) => {
    onChange((v) => ({ ...v, startTime: e[0].format('HH.mm'), endTime: e[1].format('HH.mm') }));
  };
  return (
    <div className="interval">
      <p>Интервал времени</p>
      <RangePicker defaultTime={startTime} minuteStep={5} bordered={false} format="HH:mm" inputReadOnly onChange={change} />
      {/* <SelectTime text="Начало" data={data} onChange={onChange} time={startTime} start currentDay={currentDay} /> */}
      {/* <SelectTime text="Конец" data={data} onChange={onChange} time={endTime} minTime={[startTime[0] + 1, startTime[1]]} currentDay={currentDay} /> */}
    </div>
  );
};

export default MultiTimePicker;
