import React, { useState } from 'react';
import Table from '../../Elements/Table/Table';
import { EmptyList } from '../../../styles/Common';
import Config from './TableConfig';
import Preloader from '../../Elements/Preloader/Preloader';
import DeleteWarning from '../../Elements/Modal/DeleteWarning';
import { useToggle } from '../../../hooks/useToggle';
import DeleteItem from '../../../utils/deleteItem';

const FieldsTable = ({
  fieldList, preloader, fetchFields, setFieldUpdate, setFieldData, toggleOpen,
  setBookUpdate, showError, write,
}) => {
  const [showDelWarn, setShowDelWarn] = useToggle(false);
  const [fieldId, setFieldId] = useState(false);
  const [fieldName, setFieldName] = useState(false);

  const delField = async () => {
    await DeleteItem({
      preloader, url: `/server/api/ticketsfields/${fieldId}/`, fetchData: fetchFields, setShowWarn: setShowDelWarn, setId: setFieldId, showError,
    });
  };

  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const {
      id, description, name, rus_name, type, dict_id,
    } = row;
    if (dataset.name === 'del') {
      setFieldId(id);
      setFieldName(name);
      setShowDelWarn();
    } else if (dataset.name === 'update') {
      setFieldUpdate(true);
      if (type !== 'text') {
        setBookUpdate(true);
        setFieldData({
          id, description, name, rus_name, type, dict_id,
        });
      } else {
        setFieldData({
          id, description, name, rus_name, type,
        });
      }
      toggleOpen();
    }
  };
  const Setting = Config(write);
  const { columns } = Setting;

  return (
    <React.Fragment>
      {fieldList === null
        ? <Preloader isOpen small /> : fieldList.length
          ? (
            <Table keys="id" data={fieldList} handleIcon={handleTableIcon} columns={columns} />
          ) : <EmptyList>Полей нет</EmptyList>
      }
      <DeleteWarning name={fieldName} confirmDel={delField} isOpen={showDelWarn} toggleOpen={setShowDelWarn} />
    </React.Fragment>
  );
};

export default React.memo(FieldsTable);
