import React from 'react';
import DatePickers from 'react-datepicker';
import moment from 'moment';
import { TimePicker } from 'antd';
import { Box } from '../style';
import SelectInput from '../../../../Elements/Input/SelectInput/SelectInput';
import { Checkbox } from '../../Poll/style';
import { ColorBox } from '../../../../Control/Devices/DeviceStyle/Components/style';

const Form = ({
  options, data, onData, setData,
}) => {
  const onChange = (value) => {
    setData({ ...data, time: value });
  };
  return (
    <>
      <Box>
        <p>Выберите услугу</p>
        <SelectInput width="100%" onChange={(e) => onData(e.target.value, 'q_id')} noDefault value={data.q_id} options={options} />
      </Box>
      <div className="createOneBox">
        <Box>
          <p>Выберите день</p>
          <DatePickers
            selected={moment(data.day || data.dayId.day, 'DD:MM:YYYY').toDate()}
            minDate={moment().toDate()}
            onChange={(e) => onData(e, 'calendarday_id')}
            dateFormat="dd.MM.yyyy"
            locale="ru-RU"
          />
        </Box>
        <Box>
          <p>Выберите время</p>
          <TimePicker defaultTime={data.startTime} minuteStep={5} bordered={false} format="HH:mm" inputReadOnly onChange={e => onChange(e.format('HH.mm'))} />
        </Box>
      </div>
      <ColorBox
        style={{ justifyContent: 'flex-start', marginTop: 15 }}
        onClick={() => setData({ ...data, approved: !data.approved })}
      >
        <Checkbox
          active={data.approved}
          style={{ marginRight: 5 }}
        />
        <span style={{ flex: 'none' }}>Запись открыта</span>
      </ColorBox>
    </>
  );
};

export default Form;
