import Request from '../../../../../../utils/request';

async function doQuest(reportType, setOperatorList) {
  const query = 'select id, username, fio from eq.tuser where username != \'Root\' and system = \'false\' order by fio desc';

  const res = await Request('/server/api/genreportfromquery/', 'get', {
    querySql: `${query}`,
  });

  const answer = res.data.data;
  const items = [{ key: 'all', name: 'Все', text: 'Все' }];

  answer.forEach((e) => {
    items.push({ key: e[0], name: e[1], text: e[2] });
  });

  const conf = {
    headerTitleText: 'Все',
    items,
  };

  setOperatorList(conf);

  // return { conf };
}

export default doQuest;
