import React, { useState, useEffect } from 'react';
import {
  FaSave, FaTrash, FaEye, FaPlus,
} from 'react-icons/fa';
import axios from 'axios';
import { toast } from 'react-toastify';
import Settings from './components/Settings';
import ImportExportConfig from '../../../../utils/importExportConfig';
import { BtnAdd, Input, AbsoluteBox } from '../../../../styles/Common';
import ModalSettings from './components/Modal';
import ConstrDragg from './ConstructorDraggable';
import Request from '../../../../utils/request';
import {
  SettingsBox, TicketConstructorHead, TicketConstructorName, TicketFieldBox,
  ImgContainerSettings, HeadArrLine, Item, TableRow, Name, LastIcon, EditButton,
} from '../style';
import c from '../../../../styles/Colors';

const ConstructorSettings = ({
  name, setName, ticket, data, setTicket, idSat, history,
}) => {
  const [open, setOpen] = useState(false);
  const [activeChange, setActiveChange] = useState('');
  const [acName, setAcName] = useState('');
  const [fonts, setFonts] = useState([]);

  let url;

  const changeImage = (value, type) => {
    const newData = { ...ticket };
    newData[type].url = value;
    setTicket(newData);
  };

  useEffect(() => {
    axios({
      method: 'get',
      url: '/api/fonts',
    }).then((res) => setFonts(res.data));
  }, []);

  const change = (type, value, paramName) => {
    const newData = { ...ticket };
    newData[type][paramName] = value;
    if (paramName === 'text') {
      newData[type].textInput = value;
    }
    setTicket(newData);
  };

  const addField = (e) => {
    const field = Object.keys(ticket);
    const obj = ticket[field[field.length - 1]].name;
    let nameField = 0;
    if (obj.indexOf('Дополнительный текст') >= 0) {
      nameField = obj.split(' ')[2];
      if (nameField) {
        nameField = `Дополнительный текст ${+nameField + 1}`;
      } else {
        nameField = 'Дополнительный текст 1';
      }
    } else {
      nameField = 'Дополнительный текст';
    }
    const item = {
      x: 0, y: 0, fontSize: 18, fontFamily: 'Roboto', align: '', text: 'Текст', name: nameField, show: true, textInput: 'Текст',
    };
    const newTicket = { ...ticket };
    newTicket[nameField] = item;
    setTicket(newTicket);
  };

  const removeField = (item) => {
    const newTicket = { ...ticket };
    delete newTicket[item];
    setTicket(newTicket);
  };

  const openModal = (name) => {
    setOpen(!open);
    setAcName(name);
  };

  const toggle = () => setOpen(!open);
  //
  const addInputField = (dvb, fvid, fvn) => {
    const newTicket = { ...ticket };
    const v = isNaN(fvid);
    const re = v ? ` $${fvid}$` : ` $FIELD${fvid}$`;
    const text = ticket[acName].textInput;
    if (text.indexOf(re) >= 0) {
      newTicket[acName].textInput = ticket[acName].textInput.replace(re, '');
    } else {
      newTicket[acName].textInput = `${ticket[acName].textInput}${re}`;
    }
    setTicket(newTicket);
  };
  //
  const changePosition = (val) => {
    let value = +val;
    if (+val > 500) value = 500;
    if (+val < 0) value = 0;
    return value;
  };
  //
  const scrollPosition = (obj) => {
    if (ticket[obj.name].align && obj.param === 'x') return;
    change(obj.name, changePosition(+obj.value), obj.param);
  };

  const changeTicket = (name) => setActiveChange(name);

  const sendServer = () => {
    if (!name) return;
    const dataSend = {};
    dataSend.config = ticket;
    dataSend.connected_queues = [];
    dataSend.name = name;
    const path = idSat ? `/server/api/tickettemplate/${idSat || ''}` : `/server/api/tickettemplate/${idSat}`;
    Request(path, idSat ? 'update' : 'add', dataSend)
      .then(() => {
        history.goBack();
        toast.success('Выполнено', 1000);
      });
  };

  if (ticket) {
    const blobStyle = new Blob([JSON.stringify(ticket)], { type: 'application/json' });
    url = URL.createObjectURL(blobStyle);
  }
  // const uploadFile = (event, name) => {
  //   const file = new FileReader();
  //   const { type } = event.target.files[0];
  //   file.onload = (e) => {
  //     const conf = JSON.parse(e.target.result);
  //     setTicket({ ...conf });
  //   };
  //
  //   if (type === 'application/json') {
  //     file.readAsText(event.target.files[0]);
  //   }
  // };
  const arrLine = {};
  const arrImg = {};
  Object.keys(ticket).map((item) => (ticket[item].name !== 'Штрихкод' && ticket[item].name !== 'Логотип' ? arrLine[item] = ticket[item] : arrImg[item] = ticket[item]));
  const newLine = Object.keys(arrLine).sort((a, b) => (arrLine[a].y > arrLine[b].y ? 1 : -1));

  return (
    <SettingsBox>
      <TicketConstructorHead>
        <TicketFieldBox width={300}>
          <TicketConstructorName>
            Наименование:
            <Input
              type="text"
              defaultValue={name}
              onChange={(e) => setName(e.target.value)}
            />
            <AbsoluteBox>
              <BtnAdd fix={140} top="34px" add hover show onClick={sendServer}>
                <FaSave />
                <span>Сохранить</span>
              </BtnAdd>
              <ImportExportConfig name="ticket-template.json" setData={setTicket} url={url} />
            </AbsoluteBox>
          </TicketConstructorName>

        </TicketFieldBox>
        <div className="line mt-4 mb-1" />
      </TicketConstructorHead>
      <TicketFieldBox>
        <ImgContainerSettings m="0 0 -15px" width="50vw">
          <HeadArrLine>
            <Item width="18vw">Наименование</Item>
            <Item width="5.2vw">Высота</Item>
            <Item width="14vw">Изображение</Item>
            <Item width="3.2vw">Выр.</Item>
            <Item width="3.2vw">X</Item>
            <Item width="3.2vw">Y</Item>
            <Item width="3.2vw" />
          </HeadArrLine>
          {Object.keys(arrImg).map((img, index) => (
            <TableRow key={img} active={activeChange === img}>
              <Name>
                {arrImg[img].name}
                :
              </Name>
              <Settings
                idSat={idSat}
                type={img}
                align={arrImg[img].align}
                height={arrImg[img].height}
                changeImage={changeImage}
                img={!arrImg[img].text}
                barcode={arrImg[img].name === 'Штрихкод'}
                data={arrImg[img]}
                title={arrImg[img].name}
                change={change}
                fonts={fonts}
              />
              <Input
                width="3.2vw"
                className="positionSettings"
                min="0"
                disabled={arrImg[img].align}
                onWheel={(e) => scrollPosition({ value: Number(arrImg[img].x) + e.deltaY / 100, name: img, param: 'x' })}
                max="500"
                plaicholer="x"
                value={arrImg[img].x}
                onChange={(e) => change(img, e.target.value, 'x')}
              />
              <Input
                width="3.2vw"
                className="positionSettings"
                min="0"
                max="500"
                onWheel={(e) => scrollPosition({ value: Number(arrImg[img].y) + e.deltaY / 100, name: img, param: 'y' })}
                plaicholer="x"
                value={arrImg[img].y}
                onChange={(e) => change(img, e.target.value, 'y')}
              />
              <LastIcon active={arrImg[img].show} onClick={() => change(img, !arrImg[img].show, 'show')}>
                <FaEye />
              </LastIcon>
            </TableRow>
          ))}
        </ImgContainerSettings>

        <BtnAdd width={150} onClick={addField} add>
          <span>Добавить поле</span>
        </BtnAdd>
        <ImgContainerSettings>
          <HeadArrLine>
            <Item width="35%">Наименование</Item>
            <Item width="30%">Текст</Item>
            <Item width="15%">Шрифт</Item>
            <Item width="100px">Размер</Item>
            <Item width="60px">Выр.</Item>
            <Item width="60px">X</Item>
            <Item width="60px">Y</Item>
            <Item width="0" />
          </HeadArrLine>
          {newLine.map((item, index) => (
            <TableRow key={item} active={activeChange === item}>
              <Name>
                {arrLine[item].name}:
              </Name>
              {arrLine[item].text !== null && (
                <div style={{ position: 'relative', minWidth: '30%' }}>
                  <Input
                    width="100%"
                    type="text"
                    className="inputTextSettings"
                    value={arrLine[item].textInput}
                    onChange={(e) => change(item, e.target.value, 'text')}
                  />
                  <EditButton accent={c.accent} onClick={() => openModal(item)}>
                    <FaPlus />
                  </EditButton>
                </div>
              )}
              <Settings
                idSat={idSat}
                type={item}
                align={arrLine[item].align}
                changeImage={changeImage}
                img={!arrLine[item].text === null}
                barcode={arrLine[item].name === 'Штрихкод'}
                data={arrLine[item]}
                title={arrLine[item].name}
                change={change}
                fonts={fonts}
              />
              <Input
                className="positionSettings"
                width="60px"
                min="0"
                disabled={arrLine[item].align}
                onWheel={(e) => scrollPosition({ value: Number(arrLine[item].x) + e.deltaY / 100, name: item, param: 'x' })}
                max="500"
                plaicholer="x"
                value={arrLine[item].x}
                onChange={(e) => change(item, e.target.value, 'x')}
              />
              <Input
                className="positionSettings"
                width="60px"
                min="0"
                max="500"
                onWheel={(e) => scrollPosition({ value: Number(arrLine[item].y) + e.deltaY / 100, name: item, param: 'y' })}
                plaicholer="y"
                value={arrLine[item].y}
                onChange={(e) => change(item, e.target.value, 'y')}
              />
              <LastIcon active onClick={() => removeField(item)}>
                <FaTrash />
              </LastIcon>
            </TableRow>

          ))}
        </ImgContainerSettings>
      </TicketFieldBox>
      {open && (
        <ModalSettings
          data={data}
          open={open}
          activeElem={acName}
          ticket={ticket}
          toggle={toggle}
          change={addInputField}
        />
      )}

      {data && <ConstrDragg onClick={changeTicket} ticket={ticket} data={data} />}
    </SettingsBox>
  );
};

export default ConstructorSettings;
