import React from 'react';
import moment from 'moment';
import { textFilter, selectFilter } from 'react-bootstrap-table2-filter';
import { ReactComponent as Download } from '../../../images/download.svg';
import { ReactComponent as Trash } from '../../../images/del.svg';
import { ReactComponent as Edit } from '../../../images/edit.svg';
import { BtnTable } from '../../../styles/Common';
import c from '../../../styles/Colors';


const priceFormatter = (column, colIndex, { filterElement }) => (

  <div className="filterBoxTable">
    {column.dataField !== 'type' && column.dataField !== 'status' ? <Edit style={{ margin: '2px 10px 0 0' }} fill={c.main} /> : null }
    { filterElement }
  </div>
);

function playButtonTempl(row) {
  const { fileUrl, status } = row;
  return (
    <div style={{ display: 'flex' }}>
      <a href={fileUrl} target="_blank" rel="noopener noreferrer">
        <BtnTable data-name="download" type="button" disabled={status !== 1}><Download /></BtnTable>
      </a>
      <div style={{ marginLeft: '10px' }}>
        <BtnTable data-name="del" active><Trash /></BtnTable>
      </div>
    </div>

  );
}

export default function () {
  const reportTypeList = {
    exactlyOperator: 'Отчет по оператору',
    exactlyQueue: 'Отчет по очереди',
    exactlyWorkplace: 'Отчет по рабочему месту',
    ticketReport: 'Отчет по талонам',
    queueReport: 'Отчет по очередям',
    operatorReport: 'Отчет по операторам',
    workPlaceQuery: 'Отчет по рабочим местам',
    ticketHistoryReport: 'Отчет история талона',
    markReport: 'Отчет по оценкам',
    clockwiseReport: 'Почасовой отчет',
    dynamicsServiceTimeReport: 'Отчет по динамике времени обслуживания по услугам',
    intervalReport: 'За интервал',
  };

  const statusText = {
    0: 'Подготовка',
    1: 'Готов для скачивания',
  };

  function dataTemplate(cell) {
    const dateFormat = moment(cell).format('DD.MM.YY HH:mm:ss');
    return (
      <div>{dateFormat}</div>
    );
  }

  function statusTemplate(cell) {
    return (
      <div>{statusText[cell]}</div>
    );
  }

  function typeTemplate(cell) {
    return (
      <div>{reportTypeList[cell]}</div>
    );
  }

  function extensionTemplate(cell, row) {
    const { fileName } = row;
    const str = fileName.substr(fileName.lastIndexOf('.') + 1);
    return (
      <div>{str}</div>
    );
  }
  return {
    columns: [
      {
        dataField: 'createdAt',
        text: 'Дата создания',
        sort: true,
        editable: false,
        headerStyle: {
          width: 180,
        },
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Дата создания',
        }),
        headerFormatter: priceFormatter,
        formatter: (cell) => dataTemplate(cell),
      }, {
        dataField: 'fileName',
        text: 'Название файла',
        sort: true,
        editable: false,
      },
      {
        dataField: 'type',
        text: 'Тип отчета',
        sort: true,
        editable: false,
        headerFormatter: priceFormatter,
        filter: selectFilter({
          options: reportTypeList,
          placeholder: 'Тип отчета',
        }),
        formatter: (cell) => typeTemplate(cell),
        headerStyle: {
          width: 300,
        },
      }, {
        dataField: 'status',
        text: 'Статус',
        sort: true,
        editable: false,
        headerFormatter: priceFormatter,
        filter: selectFilter({
          options: statusText,
          placeholder: 'Статус',
        }),
        formatter: (cell) => statusTemplate(cell),
        headerStyle: {
          width: 180,
        },
      },
      {
        dataField: '003',
        text: 'Расширение',
        isDummyField: true,
        sort: true,
        editable: false,
        formatter: (cell, row) => extensionTemplate(cell, row),
      },
      {
        dataField: 'Settings',
        text: '  ',
        isDummyField: true,
        sort: true,
        editable: false,
        formatter: (cell, row) => playButtonTempl(row),
      },
    ],
  };
}
