import React, { useState, useEffect, useCallback } from 'react';
import { toast } from 'react-toastify';
import useRouter from 'use-react-router';
import CardEdit from './cardEdit';
import CardList from './cardList';
import request from '../../../../utils/request';
import DeleteWarning from '../../../Elements/Modal/DeleteWarning';

const Poll = ({ showError, preloader }) => {
  const { history, match } = useRouter();
  const [item, setItem] = useState(false);
  const [data, setData] = useState([]);
  const [open, setOpen] = useState(false);
  const [id, setId] = useState(false);
  const [name, setName] = useState(false);
  const idPoll = match.params.ticketId;

  const onEdit = (editId) => {
    history.push(`/services/poll/${editId}`);
  };

  const getData = useCallback(async () => {
    preloader();
    try {
      const res = await request('/server/public/surveys/');
      setData([...res.data.data]);
      console.log(res.data.data[0].connectedQuestions[0].answers);
      if (item) {
        setItem(res.data.data.find((el) => el.id === item.id));
      }
      preloader();
    } catch (e) {
      showError(e.data.error);
      preloader();
    }
  }, [preloader, showError]);

  useEffect(() => { getData(); }, [getData]);

  const update = async (updateItem, newItem) => {
    const d = {
      questiontext: updateItem.questiontext,
      operator_mark: updateItem.operator_mark,
      correctAnswers: updateItem.answers.map((el) => {
        console.log(el);
        const res = {
          type: !isNaN(el.answerint) ? el.type : 3,
          answerint: !isNaN(el.answerint) ? +el.answerint : el.answerint,
          answertxt: el.answerint || el.answertxt,
          id: el.id,
          correct: el.correct,
        };
        return res;
      }),
    };
    try {
      await request(`/server/api/qcquestion/${updateItem.id}/`, 'update', d);
      toast.success('Выполнено');
      setItem(newItem);
    } catch (e) {
      showError(e.data.error);
    }
  };

  const deleteItem = (clickData) => {
    setOpen(true);
    setId(clickData.id);
    setName(clickData.name);
  };

  const onClick = async (clickData, action) => {
    try {
      if (action) {
        deleteItem(clickData);
      } else {
        await request('/server/api/qcsurvey/', 'add', clickData);
        getData();
        toast.success('Выполнено');
      }
    } catch (e) {
      showError(e.data.error);
    }
  };

  const sendDelete = async () => {
    try {
      await request(`/server/api/qcsurvey/${id}`, 'delete');
      toast.success('Выполнено');
      setOpen(false);
      setId(false);
      setName(false);
      getData();
    } catch (e) {
      setOpen(false);
      showError(e.data.error);
    }
  };

  const goBack = () => {
    setItem((val) => false);
    if (!item) getData();
  };

  return (
    <>
      {!idPoll
        ? <CardList data={data} onClick={onClick} onEdit={onEdit} />
        : <CardEdit goBack={goBack} getData={getData} update={update} showError={showError} />}
      <DeleteWarning name={name} confirmDel={sendDelete} isOpen={open} toggleOpen={() => setOpen(false)} />
    </>
  );
};

export default Poll;
