import React, { useState } from 'react';
import request from '../../../utils/request';
import { Btn, BtnsBox } from '../../../styles/Modal';
import Modal from '../../Elements/Modal/Modal';
import { useToggle } from '../../../hooks/useToggle';

const NewField = ({ preloader, showError }) => {
  const [licence, setLicence] = useState('');
  const [isOpen, toggleOpen] = useToggle(false);

  const resetModal = () => {
    toggleOpen();
    setLicence('');
  };
  const handleUpdate = async () => {
    preloader();
    try {
      await request('/server/api/licence/', 'update', { licBody: licence });
    } catch (err) {
      showError(err.data.error);
    }
    resetModal();
    preloader();
  };

  return (
    <React.Fragment>
      <div onClick={toggleOpen} style={{ textAlign: 'center', margin: '30px 0 60px' }}>
        <span style={{
          background: '#FC4C02', padding: 10, color: '#fff', cursor: 'pointer',
        }}
        >Обновить лицензию
        </span>
      </div>
      <Modal title="Обновление лицензии" isOpen={isOpen} toggleOpen={resetModal}>
        <div>
          <textarea name="" id="" cols="30" rows="10" onChange={e => setLicence(e.target.value)} style={{ resize: 'none', width: '100%', padding: 6 }} placeholde="Введите лицензию" />
        </div>
        <BtnsBox>
          <Btn
            margin="0 25px 0 0"
            valid={licence}
            onClick={handleUpdate}
          >
            Сохранить
          </Btn>
          <Btn onClick={resetModal} cancel>Отменить</Btn>
        </BtnsBox>
      </Modal>
    </React.Fragment>
  );
};

export default React.memo(NewField);
