import React, { useState, useContext } from 'react';
import { MdExitToApp } from 'react-icons/md';
import { toast } from 'react-toastify';
import {
  LogoutLink, UserName, LogoutBox, StyleBox, Item,
} from '../../../styles/LoginStyles';
import c from '../../../styles/Colors';
import cd from '../../../styles/ColorDark';
import request from '../../../utils/request';
import cleanLocalStorage from '../../../utils/cleanLocalStorage';
import themeCtx from '../../../utils/themeCtx';
import history from '../../../utils/customHistory';

const Logout = () => {
  const [open, setOpen] = useState(false);
  const { color, dispatch } = useContext(themeCtx);

  const handleLogout = async () => {
    try {
      await request('/server/api/logout/');
      cleanLocalStorage();
      history.push('/login');
    } catch (e) {
      toast.warn('Ошибка');
    }
  };

  const active = color === cd;
  const changeColor = (c) => {
    dispatch(c);
    window.localStorage.setItem('theme', active ? 'white' : 'dark');
  };

  const fio = window.localStorage.getItem('fio');

  return (
    <LogoutBox>
      <UserName>
        <span onClick={() => setOpen(!open)}>{fio}</span>
        <LogoutLink to="/login">
          <MdExitToApp size="20px" onClick={handleLogout} />
        </LogoutLink>
      </UserName>
      <StyleBox open={open}>
        <Item onClick={() => changeColor(c)} active={!active}>Светлая</Item>
        <Item onClick={() => changeColor(cd)} active={active}>Темная</Item>
      </StyleBox>
    </LogoutBox>
  );
};

export default Logout;
