import React, { useState } from 'react';
import {
  UpdateItemInfo, AboutItemTitle, UpdateButton, UpdateItem, InfoBox,
} from '../../../styles/About';
import request from '../../../utils/request';
import Preloader from '../../Elements/Preloader/Preloader';
import Loader from '../../../utils/loader';


const UpdateBlock = (props) => {
  const [inLoad, setInLoad] = useState(false);

  const {
    title, cVersion, lVersion, type, loading,
  } = props;

  const update = async (appType) => {
    const res = await request('/update', 'post', {
      type: appType,
    });
    const { success } = res.data;
    setInLoad(true);
    if (success) {
      setInLoad(false);
      window.location.reload();
    }
  };


  return (
    <>
      <Preloader isOpen={inLoad} small={false} />
      <UpdateItem>
        <AboutItemTitle mTop="10px" fSize="20px">{title}</AboutItemTitle>
        {/* <AboutItemTitle mTop="15px">Версии</AboutItemTitle> */}
        {loading
          ? (
            <InfoBox>
              <UpdateItemInfo>Текущая: <span>{(cVersion) && cVersion}</span></UpdateItemInfo>
              <UpdateItemInfo>Актуальная: <span>{(lVersion) && lVersion}</span></UpdateItemInfo>
              {(cVersion !== lVersion && lVersion) && <UpdateButton onClick={() => update(type)}>Обновить</UpdateButton>}
            </InfoBox>
          )
          : <Loader /> }
      </UpdateItem>
    </>
  );
};
export default UpdateBlock;
