import React, { useState, useEffect } from 'react';

import { AddFontBox } from '../../../../styles/Common';

const UploadForm = ({ onSave, lineText, setLineText }) => {
  const [empty, setEmpty] = useState(true);

  useEffect(() => {
    if (lineText.length > 0) setEmpty(false);
    else setEmpty(true);
  }, [lineText]);


  return (
    <AddFontBox enctype="multipart/form-data" arr>
      <label>
        <p>Текст бегущей строки</p>
        <textarea onChange={(e) => setLineText(e.target.value)} value={lineText}>
          {lineText}
        </textarea>
      </label>
      {!empty && <div className="save" onClick={onSave}>Сохранить</div>}
    </AddFontBox>
  );
};

export default UploadForm;
