import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { dateFormat, placeholder } from '../../../../../utils/tableFormatters';
import iconFormatter from '../../../../../utils/iconFormatter';

function IntervalFormatter(cell, intervals) {
  const { data } = intervals;
  return (
    <div className="intervalBox">
      {data.map(el => cell.workIntervals.some(int => int === el.id) && <div key={el.id} className="connectIntervals">{el.name}</div>)}
    </div>
  );
}

export default function (intervals, write) {
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Наименование',
        headerFormatter: placeholder,
        editable: false,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Наименование',
        }),
      },
      {
        dataField: 'description',
        text: 'Описание',
        headerFormatter: placeholder,
        editable: false,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Описание',
        }),
      },
      {
        dataField: 'modified_by_username',
        text: 'Изменен',
        headerFormatter: placeholder,
        sort: true,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Изменен',
        }),
        editable: false,
      },
      {
        dataField: 'modified_on',
        text: 'Когда изменен',
        editable: false,
        formatter: dateFormat,
      },
      {
        dataField: 'connectIntervals',
        text: 'Подключенные интервалы',
        editable: false,
        headerStyle: {
          width: 450,
        },
        formatter: (cell, column) => IntervalFormatter(column, intervals),
      },
      {
        dataField: 'Settings',
        isDummyField: true,
        text: '',
        headerStyle: {
          width: 100,
        },
        formatter: () => iconFormatter({ filter: false, write, params: false }),
        editable: false,
      },
    ],
  };
}
