import React from 'react';

const Schedule = ({
  text, styles, handleFz, elWidth, setActiveEl, direction,
}) => {
  const {
    fontSize3, color2, fontFamily2, textAlign,
  } = styles;
  const order = styles.order === 1 ? 1 : 0;
  const styleBorderColumn = {
    top: !order ? 'auto' : '0',
    bottom: order ? 'auto' : '0',
  };
  const styleBorderRow = {
    top: '50%',
    bottom: 'auto',
    right: order ? 'auto' : '0',
    left: !order ? 'auto' : '0',
    width: 1,
    height: '70%',
    transform: 'translateY(-50%)',
  };
  return (
    <div className="boardScheduleBox" style={{ order, alignItems: textAlign === 'left' ? 'flex-start' : textAlign === 'right' ? 'flex-end' : 'center' }} onClick={() => setActiveEl('time')}>
      <div className="boardScheduleBorder" style={direction ? styleBorderColumn : styleBorderRow} />
      <p style={{
        fontSize: handleFz(fontSize3, elWidth), color: color2, fontFamily: fontFamily2, margin: 0,
      }}
      >{text}
      </p>
    </div>
  );
};

export default Schedule;
