import React, { useEffect, useState } from 'react';
import WebSocketClient from '../../../utils/Board/WebSocketClient';

const location = window.location.href;

const debug = (location.indexOf('localhost') !== -1);
const wsHost = !debug ? window.location.hostname : '192.168.3.121';
const wsPort = !debug ? window.location.port : '48088';

const BoardScreen = ({
  setCurrentScreen, currentBoardStatus, styleConfig, checkTicketStatus,
}) => {
  const position = localStorage.getItem('position');
  const MyTicketNumber = localStorage.getItem('currentTicketNumber');
  const [ticketList, setTicketList] = useState(currentBoardStatus);
  const { button } = styleConfig;
  //   const webLogin = async (ws) => {
  //     try {
  //       const reqClientType = await ws.sendMessage({
  //         command: 'cWSSetClientType',
  //         clientType: 'vcweb',
  //       });
  //       console.log('reqClientType === ', reqClientType);
  //       if (reqClientType.status === 'ok') {
  //         //   console.debug('Set client type "vcweb"');
  //         const req = await ws.sendMessage({
  //           command: 'vcWebLogin',
  //           mediaSession: `webuser-${randomId(5)}`,
  //         });
  //         const { mediaSession } = req;

  //         console.log('mediaSession === ', mediaSession);
  //       } else throw new Error(reqClientType.error);
  //     } catch (e) {
  //       console.log(' Насосалити === ', e);
  //       return { success: false, e };
  //     }
  //   };

  const initWebSocket = () => {};

  const errorWebSocket = () => {};

  const changeBoardList = (ticket) => {
    const { number, wp_fname } = ticket;
    setTicketList((oldTicketList) => {
      const newState = [...oldTicketList];
      newState.unshift({ tiketNumber: number, wplace: wp_fname });
      if (newState.length > 6)newState.pop();
      return newState;
    });
  };

  const messageInit = async (e) => {
    const currentTicketId = window.localStorage.getItem('currentTicketId');
    const { id, event } = e;
    if (event === 'wStartProcTicket') {
      // if (id === +currentTicketId) setCurrentScreen('quality');
    } else if (event === 'wСallProcTicket') {
      await checkTicketStatus(true);
      if (id === +currentTicketId) {
        setCurrentScreen('ticketProcess');
      } else {
        changeBoardList(e);
      }
    }
  };


  useEffect(() => {
    // eslint-disable-next-line no-new
    const boardWs = new WebSocketClient(wsHost, wsPort, !debug, messageInit, initWebSocket, errorWebSocket);
    return () => {
      if (boardWs) boardWs.disconnect();
    };
  }, []);

  const renderBoard = () => (ticketList.map((el, index) => (
    <div
      key={index}
      className="boardScreen__tabloRow"
      style={{
        background: index === 0 ? '#0076FF' : button.background,
      }}
    >
      <span style={{
        color: index === 0 ? '#ffffff' : button.color,
      }}
      >{el.tiketNumber}
      </span>
      <span style={{
        color: index === 0 ? '#ffffff' : button.color,
      }}
      >{el.wplace.split(',')[0]}
      </span>
    </div>
  )));

  const declOfNum = (number, words) => words[(number % 100 > 4 && number % 100 < 20) ? 2 : [2, 0, 1, 1, 1, 2][(number % 10 < 5) ? number % 10 : 5]];

  return (
    <div className="boardScreen">
      <p className="boardScreen__title">Ваш талон: {MyTicketNumber}<br /> {+position === 0 ? 'Вы следующий' : `Перед вами: ${position} ${declOfNum(position, ['человек', 'человека', 'человек'])}`}</p>
      <div className="boardScreen__container">
        <div className="boardScreen__tablo">
          <div className="boardScreen__tabloHeader">
            <span>Талон</span>
            <span>Окно</span>
          </div>
          <div className="boardScreen__tabloBody">
            {renderBoard()}
          </div>
        </div>
      </div>
      {/* <div>Тут на всякий место для бегузей строки или чет впилим</div> */}
    </div>
  );
};

export default BoardScreen;
