import React from 'react';
import moment from 'moment';

const Day = ({
  b, item, setSelectedDay, dayId, m
}) => {
  let isOpen = false;
  let isRegistered = 0;
  let takeTicket = 0;
  if (item) {
    isOpen = item.some((el) => !el.registered);
    isRegistered = item.some((el) => el.registered);
    takeTicket = item.every((el) => el.ticket_id);
  }

  const classLi = `${isOpen ? 'open' : isRegistered ? 'registered' : takeTicket ? 'take' : false}`;
  const isActive = item && dayId.id === item[0].calendarday_id;
  const title = `${isOpen ? 'Можно записаться' : isRegistered ? 'Уже зарегистрировались' : takeTicket ? 'Записан' : 'Нет свободных слотов'}`;
  return (
    <li
      key={b}
      className={`item ${classLi} ${isActive ? 'active' : ''}`}
      title={title}
      onClick={() => (item ? setSelectedDay({
        data: item,
        id: item.length > 0 ? item[0].calendarday_id : null,
        day: moment(`${b}.${m}`, 'DD.MM').format('DD.MM.YYYY'),
      }, 'fetchCalendar') : {})}
    >
      {b}
    </li>
  );
};

export default Day;
