import React, { useState, useEffect, useCallback } from 'react';
import NewQuestion from './NewQuestion/NewQuestion';
import request from '../../../../utils/request';
import QuestionsTable from './QuestionsTable';
import { PreloaderBox } from '../../../../styles/Common';
import { useToggle } from '../../../../hooks/useToggle';

const Questions = (props) => {
  const { preloader, write, showError } = props;
  const initialData = {
    name: '', displayname: '', description: '', connectedQueues: [],
  };
  const [questList, setQuestList] = useState([]);
  const [queueList, setQueueList] = useState([]);
  const [isOpen, toggleOpen] = useToggle(false);
  const [questData, setQuestData] = useState({ ...initialData });
  const [questUpdate, setQuestUpdate] = useState(false);

  const fetchQuests = useCallback(async () => {
    preloader();
    try {
      await request('/server/api/servedquestion/', 'get')
        .then((res) => {
          setQuestList(res.data.data);
        });
      await ('/server/api/queue/', 'get')
        .then((res) => { setQueueList(res.data.data); });
      preloader();
    } catch (e) {
      preloader();
    }
  }, [preloader]);

  useEffect(() => {
    fetchQuests();
  }, [fetchQuests]);

  return (
    <div style={{ width: '100%' }}>
      <NewQuestion
        initialData={initialData}
        questData={questData}
        setQuestData={setQuestData}
        fetchQuests={fetchQuests}
        setQuestUpdate={setQuestUpdate}
        questUpdate={questUpdate}
        toggleOpen={toggleOpen}
        isOpen={isOpen}
        write={write}
        preloader={preloader}
      />
      <PreloaderBox>
        <QuestionsTable
          questList={questList}
          queueList={queueList}
          setQuestData={setQuestData}
          showError={showError}
          setQuestUpdate={setQuestUpdate}
          toggleOpen={toggleOpen}
          write={write}
          preloader={preloader}
          fetchQuests={fetchQuests}
        />
      </PreloaderBox>
    </div>
  );
};

export default Questions;
