import React, { useState } from 'react';
import { Btn, BtnsBox } from '../../../../../../styles/Modal';
import { BtnAdd } from '../../../../../../styles/Common';
import Modal from '../../../../../Elements/Modal/Modal';
import request from '../../../../../../utils/request';
import RenderInputs from './RenderInputs';

const NewInterval = ({
  isOpen, toggleOpen, fetchIntervals, intervalUpdate, setIntervalData,
  intervalData, initialData, setIntervalUpdate, preloader, write
}) => {
  const [isValid, setIsValid] = useState(false);

  const resetModal = () => {
    setIntervalData({ ...initialData });
    toggleOpen();
    setIsValid('');
    setIntervalUpdate(false);
  };

  const addInterval = async () => {
    if (isValid) {
      preloader();
      await request('/server/api/dworkinterval/', 'add', intervalData);
      fetchIntervals();
      resetModal();
      preloader();
    }
  };

  const updateInterval = async () => {
    if (isValid) {
      const itemId = intervalData.id;
      delete intervalData.id;
      preloader();
      await request(`/server/api/dworkinterval/${itemId}/`, 'update', { ...intervalData });
      fetchIntervals();
      resetModal();
      preloader();
    }
  };

  return (
    <React.Fragment>
      {write && <BtnAdd onClick={toggleOpen} add>
        <span>Добавить</span>
        </BtnAdd>
      }
      <Modal title={intervalUpdate ? 'Редактирование интервала' : 'Добавление интервала'} isOpen={isOpen} toggleOpen={resetModal}>
        <RenderInputs
          intervalUpdate={intervalUpdate}
          intervalData={intervalData}
          write={write}
          setIntervalData={setIntervalData}
          setIsValid={setIsValid}
        />
        {write && <BtnsBox>
          <Btn
            margin="0 25px 0 0"
            valid={isValid}
            onClick={intervalUpdate ? updateInterval : addInterval}
          >
            Сохранить
          </Btn>
          <Btn onClick={resetModal} cancel>Отменить</Btn>
        </BtnsBox>
        }
      </Modal>
    </React.Fragment>
  );
};

export default React.memo(NewInterval);
