import React from 'react';
import { textFilter, selectFilter } from 'react-bootstrap-table2-filter';
import moment from 'moment';
import { ReactComponent as Edit } from '../../../../images/edit.svg';
import c from '../../../../styles/Colors';

export default function (getColumnSearchProps) {
  function date(cell) {
    return moment(cell, 'YYYY.MM.DD HH:mm:ss').format('DD.MM.YYYY HH:mm:ss');
  }

  function status(cell) {
    let text;
    switch (cell) {
      case 0: text = 'Создан и находится в изначальной очереди';
        break;
      case 1: text = 'Вызван на обработку';
        break;
      case 2: text = 'В процессе обработки';
        break;
      case 3: text = 'Закрыт (талон был обработан)';
        break;
      case 4: text = 'Удалён';
        break;
      case 5: text = 'Переведён в новую очередь';
        break;
      case 6: text = 'Ожидание решения';
        break;
      case 7: text = 'Возвращён в очередь';
        break;
      case 8: text = 'Ручная обработка диспетчером';
        break;
      case 9: text = 'Удален из-за наступления нового дня (талон по какой-то причине просуществовал до ночи в системе)';
        break;
      default: break;
    }
    return text;
  }

  // const statusType = {
  //   0: 'Создан и находится в изначальной очереди',
  //   1: 'Вызван на обработку',
  //   2: 'В процессе обработки',
  //   3: 'Закрыт (талон был обработан)',
  //   4: 'Удалён',
  //   5: 'Переведён в новую очередь',
  //   6: 'Ожидание решения',
  //   7: 'Возвращён в очередь',
  //   8: 'Ручная обработка диспетчером',
  //   9: 'Удален из-за наступления нового дня (талон по какой-то причине просуществовал до ночи в системе)',
  // };

  return {
    columns: [
      {
        dataIndex: 'number',
        title: 'Номер',
        ...getColumnSearchProps('number'),
      },
      {
        dataIndex: 'queueName',
        title: 'Наименование очереди',
        ...getColumnSearchProps('queueName'),
      },
      {
        dataIndex: 'status',
        title: 'Статус',
        render: status,
        filters: [
          {
            text: 'Создан и находится в изначальной очереди',
            value: 0,
          },
          {
            text: 'Вызван на обработку',
            value: 1,
          },
          {
            text: 'В процессе обработки',
            value: 2,
          },
          {
            text: 'Закрыт (талон был обработан)',
            value: 3,
          },
          {
            text: 'Удалён',
            value: 4,
          },
          {
            text: 'Переведён в новую очередь',
            value: 5,
          },
          {
            text: 'Ожидание решения',
            value: 6,
          },
          {
            text: 'Возвращён в очередь',
            value: 7,
          },
          {
            text: 'Ручная обработка диспетчером',
            value: 8,
          },
          {
            text: 'Удален из-за наступления нового дня (талон по какой-то причине просуществовал до ночи в системе)',
            value: 9,
          },
        ],
        onFilter: (value, record) => record.status === value,
      },
      {
        dataIndex: 'createdOn',
        title: 'Время создания',
        render: date,
        ...getColumnSearchProps('createdOn'),
      },
      {
        dataIndex: 'modifiedOn',
        title: 'Время изменения',
        render: date,
        ...getColumnSearchProps('modifiedOn'),
      },
    ],
  };
}
