import React from 'react';
import { Doughnut } from 'react-chartjs-2';
import { withButtonsTamplate, DubleLineTemplate } from '../../../utils/Formater';
import { DoughnutCenterLabel, DoughnutContainer } from '../../../../../../styles/ReportTableColumns';

function doughnutOnClick(evn, item, row, doughnutClick) {
  if (item[0] !== undefined) {
    const { _model } = item[0];
    const { label } = _model;
    if (item.length > 0) {
      const operatorId = row[0];
      const currentMark = label;
      let markValue;
      if (currentMark === 'Очень плохо') {
        markValue = 1;
      } else if (currentMark === 'Плохо') {
        markValue = 2;
      } else if (currentMark === 'Нормально') {
        markValue = 3;
      } else if (currentMark === 'Хорошо') {
        markValue = 4;
      } else if (currentMark === 'Отлично') {
        markValue = 5;
      }
      doughnutClick(markValue, operatorId, currentMark);
    }
  }
}

function serviceTmpl(cell, row, doughnutClick) {
  const doughnutData = {
    labels: ['Отлично', 'Хорошо', 'Нормально', 'Плохо', 'Очень плохо'],
    datasets: [{
      data: [row.mark5, row.mark4, row.mark3, row.mark2, row.mark1],
      backgroundColor: [
        '#00FF00',
        '#ADFF2F',
        '#FFFF00',
        '#FF0000',
        '#B22222',
      ],
    },
    ],
  };
  const doughnutOptions = {
    maintainAspectRatio: false,
    responsive: false,
    legend: {
      position: 'right',
      labels: {
        boxWidth: 10,
      },
    },
    // onClick: (evn, items) => {
    //   doughnutOnClick(evn, items, row, doughnutClick);
    // },
  };
  return (
    <DoughnutContainer>
      <DoughnutCenterLabel>{row.markcnt}</DoughnutCenterLabel>
      <Doughnut
        data={doughnutData}
        options={doughnutOptions}
      />
    </DoughnutContainer>
  );
}


export default function (moreInfo, doughnutClick, getColumnSearchProps) {
  return {
    columns: [
      {
        dataIndex: 'user_fio',
        title: 'Оператор',
        ...getColumnSearchProps('user_fio'),
      }, {
        dataIndex: 'share_cnt',
        title: 'Талонов обработано',
        sorter: (a, b) => a.share_cnt - b.share_cnt,
      }, {
        dataIndex: 'solved_questions',
        title: 'Кол-во решенных вопросов',
      }, {
        dataIndex: '6',
        isDummyField: true,
        title: 'Обслуженно',
        render: (cell, row) => withButtonsTamplate(row, moreInfo, 'Operator', row.all_cnt, row.close_cnt, row.neyavka_cnt),
      }, {
        dataIndex: '12',
        isDummyField: true,
        title: 'Время обслуживания',
        render: (cell, row) => DubleLineTemplate('Всего:', row.total_work_time, 'Среднее:', row.avg_work_time),
      }, {
        dataIndex: '14',
        isDummyField: true,
        title: 'Качество обслуживания',
        render: (cell, row) => serviceTmpl(cell, row, doughnutClick),
      },
    ],
  };
}
