import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment';
import 'moment/locale/ru';
import ruDate from 'date-fns/locale/ru';
import { isAfter } from 'date-fns';
import {
  ReportParametrsBox, ReportParametrsTitle, DataPicersBox, DatePicersItem,
} from '../../../../styles/ReportMenu';
import QuestionSelection from './QuestionSelection';

const Params = ({ params, activeSection }) => {
  const { settings, setSettings } = params;
  const dpConfig = {
    selectsStart: true,
    showTimeSelect: true,
    timeFormat: 'HH:mm',
    timeIntervals: 15,
    locale: ruDate,
    timeCaption: 'Время',
    maxDate: new Date(),
    dateFormat: 'dd.MM.yyyy HH:mm',
  };

  const [startDate, setStartDate] = useState(moment().startOf('month'));
  const [endDate, setEndDate] = useState(moment().endOf('day'));

  const handleDataChange = ({ sDate, eDate }) => {
    const defaultStartDate = startDate;
    const defaultEndDate = endDate;
    const newStartDate = sDate || defaultStartDate.toDate();
    let newEndDate = eDate || defaultEndDate.toDate();

    newEndDate = isAfter(newStartDate, newEndDate) ? newStartDate : newEndDate;
    const dateFormat = 'DD.MM.YYYY HH:mm';
    const time = moment(newStartDate);
    const endTime = moment(newEndDate);

    setStartDate(time);
    setEndDate(endTime);
    setSettings({
      ...settings,
      startDate: time.format(dateFormat),
      endDate: endTime.format(dateFormat),
    });
  };

  const handleQuestionChange = (q, w, e) => {
    setSettings({
      ...settings,
      questId: e,
    });
  };

  return (
    <ReportParametrsBox>
      <ReportParametrsTitle>Параметры</ReportParametrsTitle>
      <div style={{ display: 'flex' }}>
        <DataPicersBox>
          <DatePicersItem>
            <div style={{ marginRight: '8px' }}>С</div>
            <DatePicker
              {...dpConfig}
              selectsStart
              selected={startDate.toDate()}
              onChange={(date) => handleDataChange({ sDate: date })}
            />
          </DatePicersItem>
          <DatePicersItem left>
            <div style={{ marginRight: '8px' }}>По</div>
            <DatePicker
              {...dpConfig}
              selectsEnd
              selected={endDate.toDate()}
              minDate={startDate.toDate()}
              onChange={(date) => handleDataChange({ eDate: date })}
            />
          </DatePicersItem>
        </DataPicersBox>
        {activeSection === 4 ? (<QuestionSelection handleQuestionChange={handleQuestionChange} />) : null}
      </div>
    </ReportParametrsBox>
  );
};

export default Params;
