import React from 'react';
import { useSelector } from 'react-redux';
import { changeDictionary } from '../../utils/actions';
import { Checkbox, CheckboxBox, Dot } from '../../style/style';
import Store from '../../utils/store';

const { dispatch } = Store;

const CheckboxComponent = ({ scene }) => {
  const {
    dictionary, checkbox,
  } = useSelector((state) => ({
    ticket: state.ticket, checkbox: state.style.checkbox, headUrl: state.config.headUrl, id: state.config.id, dictionary: state.ticket.dictionary,
  }));
  const { buttons } = scene;
  const f = dictionary.find((el) => el.fid === scene.currentTicketField);
  const arr = f ? f.fv : [];

  const onClick = (value) => {
    const find = arr.some((el) => el === value);
    const fv = [...arr];
    if (find) {
      fv.splice(fv.indexOf(value), 1);
    } else {
      fv.push(value);
    }
    dispatch(changeDictionary({ fid: scene.currentTicketField, fv, to: scene.screenId }));
  };

  return buttons.map((item) => {
    const active = arr.some((el) => el === item.params.value);
    const activeDotStyle = {
      ...checkbox,
      width: checkbox.height,
      marginRight: 30,
      background: active
        ? checkbox.background
        : 'white',
    };
    return (
      <CheckboxBox onClick={() => onClick(item.params.value)}>
        <Dot style={activeDotStyle} />
        <Checkbox
          arr={arr}
          key={item.id}
          st={checkbox}
          style={{
            ...checkbox, width: checkbox.width - checkbox.height - 30, marginLeft: 0, marginRight: 0,
          }}
          item={item}
        >
          {item.name}

        </Checkbox>
      </CheckboxBox>
    );
  });
};

export default React.memo(CheckboxComponent);
