import React from 'react';
import { FaTrash } from 'react-icons/fa';
import { BtnTable, Input } from '../../../../../styles/Common';
import { checkFormatter } from '../../../../../utils/tableFormatters';
import SelectInput from '../../../../Elements/Input/SelectInput/SelectInput';

export default function Setting(write) {
  function iconFormatter() {
    return (
      <div>
        {write && (
        <BtnTable active data-name="del">
          <FaTrash />
        </BtnTable>
        )}
      </div>
    );
  }

  const handjobData = [
    {
      label: 'Да',
      value: true,
    },
    {
      label: 'Нет',
      value: false,
    },
  ];

  return {
    columns: [
      {
        dataField: 'id',
        text: 'ID',
        sort: true,
        editable: false,
      },
      {
        dataField: 'name',
        text: 'Наименование',
        sort: true,
        editable: write,
        editorRenderer: (editorProps, value) => (<Input defaultValue={value} onBlur={e => editorProps.onUpdate(e.target.value)} />),
      },
      {
        dataField: 'valprefix',
        text: 'Префикс',
        sort: true,
        editable: write,
        editorRenderer: (editorProps, value) => (<Input defaultValue={value} onBlur={e => editorProps.onUpdate(e.target.value)} />),
      },
      {
        dataField: 'prior',
        text: 'Приоритет',
        sort: true,
        editable: write,
        editorRenderer: (editorProps, value) => (<Input defaultValue={value} onBlur={e => editorProps.onUpdate(e.target.value)} />),
      },
      {
        dataField: 'handjob',
        text: 'Ручная обработка',
        sort: false,
        editable: write,
        formatter: checkFormatter,
        editorRenderer: (editorProps, value) => {
          const options = handjobData.map(el => ({ id: el.value, name: el.label }));
          return <SelectInput value={value} onChange={e => editorProps.onUpdate(e.target.value)} options={options} noDefault />;
        },
      },
      {
        dataField: 'Settings',
        text: '',
        isDummyField: true,
        formatter: iconFormatter,
        formatExtraData: {},
        editable: false,
      },
    ],
  };
}
