import React, { useContext } from 'react';
import Columns from './columns/Columns';
import Big from './big/Big';
import VideoNewsSchedule from './videoNewsSchedule/VideoNewsSchedule';
import handleFz from '../../../../../../../../utils/handleFz';
import BoardCtx from '../../../BoardCtx';

const Body = ({
  layout, text, ticketsList, edit, inSoundAction,
}) => {
  const {
    elWidth, setActiveEl,
  } = useContext(BoardCtx);
  const {
    vertical, footer, window, tidings, date, vid,
  } = layout;
  console.log('layout === ', layout);
  return (
    <div className="boardBodyBox" style={{ flexDirection: vertical ? 'column' : 'row', order: !footer.order ? 2 : 1 }}>
      <Columns
        styles={layout}
        text={text}
        handleFz={handleFz}
        elWidth={elWidth}
        setActiveEl={setActiveEl}
        ticketsList={ticketsList}
        edit={edit}
      />
      {window && (
      <Big
        styles={layout}
        text={text}
        handleFz={handleFz}
        elWidth={elWidth}
        setActiveEl={setActiveEl}
        ticketsList={ticketsList}
        edit={edit}
      />
      )}
      {(tidings || date || vid) && (
      <VideoNewsSchedule
        styles={layout}
        inSoundAction={inSoundAction}
        text={text}
        handleFz={handleFz}
        elWidth={elWidth}
        setActiveEl={setActiveEl}
      />
      )}
    </div>
  );
};

export default Body;
