import React from 'react';
import SelectInput from '../../../../../Elements/Input/SelectInput/SelectInput';
import { ColorBox } from '../style';

const PositionContent = ({
  element, value, dispatch, title, icon, paramName,
}) => {
  const option1 = [
    { id: 1, name: 'Вертикальное' },
    { id: 0, name: 'Горизонтальное' },
  ];
  const option2 = [
    { id: 1, name: 'До текста' },
    { id: 0, name: 'После текста' },
  ];
  const options = icon ? option2 : option1;
  return (
    <ColorBox>
      <span dangerouslySetInnerHTML={{ __html: title }} />
      <SelectInput
        noDefault
        width="180px"
        value={+value}
        onChange={e => dispatch({
          type: 'params',
          name: element,
          param: paramName,
          value: e.target.value,
        })
        }
        options={options}
      />
    </ColorBox>
  );
};

export default PositionContent;
