import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { startOfWeekWithOptions } from 'date-fns/fp';
import { PolicyContainer, PoliceUl } from '../../../../style/style';
import changeStyle from '../../../../utils/changeStyle';
import PoliceButton from './policyButton';
import PolicyBB from './policyBB';
import { setTicket } from '../../../../utils/actions';
import history from '../../../../../../../../../../utils/customHistory';
import scanPolicyStore, { updateScanPolicyStore } from '../../../../../../../../../../store/scanPolicy';


const PolicyList = ({
  children, startScreen, button, title,
}) => {
  const records = scanPolicyStore.useState((s) => s.records);
  const zxc = scanPolicyStore.useState((s) => s);
  console.log(zxc);
  console.log('records === ', records);

  const { headUrl, id } = useSelector((state) => state.config);
  const styleButton = changeStyle({ style: button, item: { size: 'md' } });
  const dispatch = useDispatch();
  const style = {
    fontSize: title.fontSize * 0.4,
    fontWeight: 'normal',
    textTransform: 'capitalize',
    marginBottom: title.marginBottom * 0.2,
  };

  const styleBottom = {
    ...style,
    background: button.background,
  };

  const createTicket = (q_id, startTime) => {
    dispatch(setTicket({ name: 'q_id', value: q_id }));
    dispatch(setTicket({ name: 'prec_dt_str', value: startTime }));
    history.push(`${headUrl}/${id}/print`);
  };

  const policeItem = (el) => {
    const doctors = el.comments.split(',');
    const h3 = doctors[0];
    console.log(el);
    return (
      <li key={el.comments} style={{ padding: '10px 25px' }}>
        <div className="policeItemText">
          <h6 style={style}>{h3.toLowerCase()}</h6>
          <p style={styleBottom}>{doctors[1]}</p>
          <div className="policeListBottom">
            <p className="time" style={styleBottom}>начало приема: {el.begin_time}</p>
          </div>
        </div>
        <PoliceButton
          style={{ ...styleButton, alineSelf: 'center' }}
          el={{ text: 'Получить талон', link: startScreen }}
          handeleClick={() => { createTicket(el.to_q_id, el.begin_time); }}
        />
      </li>
    );
  };

  return (
    <PolicyContainer>
      <div className="policyBox">
        {records !== null && records.length > 0
          ? (
            <PoliceUl maxWidth={button.widthContent}>
              {records.map((el) => policeItem(el))}
            </PoliceUl>
          )
          : <p style={{ ...title, fontSize: title.fontSize * 0.8 }}>Записей нет</p>}
      </div>
      <PolicyBB button={button} startScreen={startScreen} />
    </PolicyContainer>
  );
};

export default PolicyList;
