import React, { useState, useEffect, useCallback } from 'react';
import { Bar } from 'react-chartjs-2';
import { useSelector } from 'react-redux';
import SqlQuery from './SqlQuery';
// import SqlQueryCloseCnt from './SqlQueryCloseCnt';
// import SqlQueryNoneCnt from './SqlQueryNoneCnt';
import BasicBarReportSettings from './BasicBarReportSettings';
import ReportTitle from '../../../../ReportTitle';
import { ReportBox } from '../../../../../../../../../styles/ReportTableColumns';
import Preloader from '../../../../../../../../Elements/Preloader/Preloader';
import { PreloaderBox } from '../../../../../../../../../styles/Common';
import appStore from '../../../../../../../../../store/appStore';

const ReportBar = () => {
  const [ReportParams, setReportParams] = useState();

  const startDateMoment = appStore.useState((s) => s.reportConfig.startDate);
  const endDateMoment = appStore.useState((s) => s.reportConfig.endDate);
  const dateFormat = 'YYYY-MM-DD HH:mm:ss';
  const startDate = startDateMoment.format(dateFormat);
  const endDate = endDateMoment.format(dateFormat);

  const answerFnc = useCallback(async () => {
    const barData = await SqlQuery(startDate, endDate);
    const BasicSettings = BasicBarReportSettings();


    const allData = {
      ...BasicSettings,
      label: 'Всего создано',
      data: barData.count_crate,
      pointHoverBackgroundColor: '#7486fc',
      pointBorderColor: '#7486fc',
      borderColor: '#7486fc',
      backgroundColor: 'rgba(3, 82, 252, 0.4)',
    };

    const noneData = {
      ...BasicSettings,
      label: 'Закрыто по неявке',
      data: barData.count_neyavka,
      pointHoverBackgroundColor: '#f75454',
      pointBorderColor: '#f75454',
      borderColor: '#f75454',
      backgroundColor: 'rgba(252, 5, 5, 0.4)',
    };

    const createData = {
      ...BasicSettings,
      label: 'Удалено системой',
      data: barData.count_del,
      pointHoverBackgroundColor: '#019421',
      pointBorderColor: '#019421',
      borderColor: '#019421',
      backgroundColor: 'rgba(2, 179, 40, 0.4)',
    };

    const closeData = {
      ...BasicSettings,
      label: 'Закрытых',
      data: barData.count_close,
      pointHoverBackgroundColor: '#20a8d8',
      pointBorderColor: '#20a8d8',
      borderColor: '#20a8d8',
      backgroundColor: 'rgba(75, 192, 192, 0.4)',
    };
    // createDatanoneData
    const ReportSettings = {
      labels: ['Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота', 'Воскресенье'],
      datasets: [allData, closeData, createData, noneData],
    };
    setReportParams(ReportSettings);
  }, [endDate, startDate]);

  useEffect(() => {
    answerFnc();
  }, [startDate, endDate, answerFnc]);

  return (
    <ReportBox>
      <ReportTitle
        name="Распределение талонов по дням недели"
      />
      { ReportParams ? (
        <div style={{ height: '300px' }}>
          <Bar
            data={ReportParams}
            height={50}
            options={{
              maintainAspectRatio: false,
              intersect: true,
            }}
          />
        </div>
      )
        : (
          <PreloaderBox>
            <Preloader isOpen small />
          </PreloaderBox>
        ) }
    </ReportBox>

  );
};

export default ReportBar;
