import React, { useState, useEffect, useCallback } from 'react';
import { PreloaderBox } from '../../../../../styles/Common';
import Table from '../../../../Elements/Table/Table';
import SqlQuery from './SqlQuery';
import Preloader from '../../../../Elements/Preloader/Preloader';
import { useToggle } from '../../../../../hooks/useToggle';
import TableConfig from './TableConfig';
import Expand from './ExpandTable/Expander';
import Modal from '../../../../Elements/Modal/Modal';
import { Btn, BtnsBox } from '../../../../../styles/Modal';
import TableTitle from '../../utils/TableTitle';
import ShowText from './ShowText';


const QualityTable = (props) => {
  const { preloader, pagination } = props;
  const [ReportParams, setReportParams] = useState();
  const [modalData, setModalData] = useState({});
  const [isOpen, toggleOpen] = useToggle(false);
  const [modalWndtitle, setModalWndtitle] = useState('');
  const [downloadLink, setDownloadLink] = useState();
  const [downloadPhotoName, setDownloadPhotoName] = useState();

  const resetModal = () => {
    toggleOpen();
  };

  const handleTableIcon = useCallback((e, row) => {
    const { dataset } = e.target;

    if (dataset.name === 'photo') {
      setModalData(
        <img style={{ width: '100%' }} src={row[6]} alt="" />,
      );
      setModalWndtitle('Фотография посетителя');
      setDownloadLink(row[6]);
      setDownloadPhotoName(`Photo-session${row[0]}`);
      toggleOpen();
    } else if (dataset.name === 'micro') {
      setModalData(
        <audio style={{ width: '100%' }} controls>
          <source src={row[4]} />
        </audio>,
      );
      setModalWndtitle('Аудио запись');
      setDownloadLink(row[4]);
      toggleOpen();
    } else if (dataset.name === 'video') {
      setModalData(
        <video controls style={{ width: '100%' }}>
          <source src={row[5]} />
        </video>,
      );
      setModalWndtitle('Видео запись');
      setDownloadLink(row[5]);
      toggleOpen();
    } else if (dataset.name === 'comment') {
      setModalData(
          <ShowText item={row[8]} />
      );
      setModalWndtitle('Комментарий посетителя');
      toggleOpen();
    }
  }, []);


  useEffect(() => {
    SqlQuery(pagination.page,
      pagination.sizePerPage,
      pagination.sortOrder,
      pagination.filters,
      pagination.startDate,
      pagination.endDate).then((resp) => {
      const { dataObjects, queryExport } = resp;
      const newPagination = {
        ...pagination,
        totalSize: dataObjects[0] ? dataObjects[0][12] : 0,
      };
      const params = {
        items: dataObjects,
        query: queryExport,
        TableConfig: TableConfig(handleTableIcon),
        pagination: newPagination,
        TebleType: 'RequestTable',
      };
      setReportParams(params);
    });
  }, [pagination, handleTableIcon, preloader]);


  return (
    <div>
      <Modal title={modalWndtitle} isOpen={isOpen} toggleOpen={resetModal}>
        {modalData}
        <BtnsBox>
          {modalWndtitle === 'Комментарий посетителя' ? null : (
            <>
              {modalWndtitle === 'Фотография посетителя'
                ? (
                  <a href={downloadLink} download={downloadPhotoName}>
                    <Btn valid margin="0 25px 0 0" bg="green">Сохранить</Btn>
                  </a>
                )
                : (
                  <a href={downloadLink} download>
                    <Btn valid margin="0 25px 0 0" bg="green">Сохранить</Btn>
                  </a>
                )}
            </>
          )}

          <Btn cancel onClick={() => toggleOpen()} bg="accent">Закрыть</Btn>
        </BtnsBox>
      </Modal>

      { ReportParams ? (
        <>
          <TableTitle
            ReportParams={ReportParams}
            name="Оценка качества"
            type="RequestTable"
            pagination={pagination}
          />
          <Table
            keys="0"
            data={ReportParams.items}
            columns={ReportParams.TableConfig.columns}
            handleIcon={handleTableIcon}
            onTableChange={pagination.onTableChange}
            pagination={ReportParams.pagination}
            remote={{ pagination: true, filter: true, sort: true }}
            expandRow={Expand(ReportParams.items.filter((it) => it[2] === null).map((it) => it[0]))}
          />
        </>
      )
        : (
          <PreloaderBox>
            <Preloader isOpen small />
          </PreloaderBox>
        )}
    </div>
  );
};

export default React.memo(QualityTable);
