import React, { useState } from 'react';
import Config from './config';
import Table from '../../../Elements/Table/Table';
import DeleteWarning from '../../../Elements/Modal/DeleteWarning';
import DeleteItem from '../../../../utils/deleteItem';

const TableSession = ({
  data, users, write, fetchData, preloader, showError,
}) => {
  const [id, setId] = useState([]);
  const [name, setName] = useState(false);
  const [open, setOpen] = useState(false);

  const deleteItem = async () => {
    await DeleteItem({
      preloader, url: `/server/api/usersession/${id}/`, fetchData, setShowWarn: setOpen, setId, showError,
    });
  };

  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const {
      user_id, id,
    } = row;
    if (dataset.name === 'del') {
      setId(id);
      setName(users.find((el) => el.id === user_id).username);
      setOpen(true);
    }
  };

  const columns = Config(users, write);
  return (
    <>
      {users.length > 0 ? (
        <Table
          keys="id"
          data={data}
          columns={columns}
          handleIcon={handleTableIcon}
          cellEdit
        />
      ) : null}
      <DeleteWarning name={name} confirmDel={deleteItem} isOpen={open} toggleOpen={() => setOpen(false)} />
    </>
  );
};

export default TableSession;
