import React from 'react';
import Input from '../../../Elements/Input/Input';
import BookValues from './BookValues/BookValues';

const RenderInputs = ({
  fieldData, setFieldData, setIsValid, fieldUpdate, bookUpdate, preloader, showError, write,
}) => {
  const handleInput = (e) => {
    const { name, value } = e.target;
    const validData = { ...fieldData, [name]: value };
    const { name: iV, rus_name: pV, type } = validData;
    setFieldData({ ...validData });
    if (iV && pV && type) {
      setIsValid(true);
    } else {
      setIsValid(false);
    }
  };

  const options = [
    { name: 'Справочник с одиночным выбором', id: 'select' },
    { name: 'Справочник с множественным выбором', id: 'multiselect' },
    { name: 'Текстовое поле', id: 'text' },
  ];

  const typeOptions = () => {
    const { type } = fieldData;
    return fieldUpdate && type !== 'text'
      ? [options[0], options[1]]
      : fieldUpdate ? [options[2]]
        : options;
  };


  const {
    name, rus_name, description, type, dict_id,
  } = fieldData;
  const items = [
    {
      label: 'Имя:', name: 'name', value: name || '', required: true,
    },
    {
      label: 'Наименование:', name: 'rus_name', value: rus_name || '', required: true,
    },
    { label: 'Описание:', name: 'description', value: description || '' },
    {
      label: 'Тип:', name: 'type', type: 'select', noDefault: true, value: type || '', options: typeOptions(), required: true,
    },
  ];
  return (
    <>
      {items.map((item) => (
        <Input
          key={item.name}
          item={item}
          onChange={handleInput}
        />
      ))}
      {bookUpdate && (<BookValues write={write} bookId={dict_id} preloader={preloader} showError={showError} />)}
    </>

  );
};
export default RenderInputs;
