import React, { useMemo } from 'react';
import { FaSave, FaQuestion } from 'react-icons/fa';
import { connect } from 'react-redux';
import Input from '../../../../Elements/Input/Input';
import { RenderBox } from '../../../../../styles/HandlePoints';
import { BtnAdd } from '../../../../../styles/Common';
import { HandleInputBox } from './PreviewTerminal/style';
import CheckBoxComp from '../../../../Elements/Input/CheckBox';
import { CheckboxBox } from '../../../Devices/DeviceStyle/TerminalStyle/Terminal/style/style';

const RenderInputs = ({
  screenList, screenTypes, setIsValid,
  setScreen, nowScreen, dictOptions, updateScreen, isValid, addScreen, create, setScreenList, setInfoScreen,
}) => {
  const handleInput = (e) => {
    const { name, value } = e.target;
    const validData = { ...nowScreen, [name]: value };
    if (name === 'type' && value === 'infoScreen') {
      setInfoScreen(1);
    } else {
      setInfoScreen((curr) => (curr ? !curr : curr));
    }
    setScreen(validData);
    if (validData.headTitle && validData.type) {
      setIsValid(true);
    }
  };

  const inputOptions = useMemo(() => {
    const printOption = { id: 'print', name: 'На печать' };
    const screenOptions = screenList.map((item) => ({
      id: item.screenId,
      name: item.headTitle,
    }));
    screenOptions.push(printOption);
    return screenOptions;
  }, [screenList]);

  const items = [];

  if (nowScreen) {
    const {
      help, title, description, type, currentTicketField, screenPath, headTitle,
    } = nowScreen;
    items.push(
      {
        label: 'Наименование:', name: 'headTitle', value: headTitle || '', required: true, help: 'Наименование - предназначенно для удобного выбора экрана',
      },
      {
        label: 'Заголовок экрана:', name: 'title', value: title || '', help: 'Текст на экране',
      },
      {
        label: 'Подзаголовок:', name: 'description', value: description || '', help: 'Дополнительный текст на экране',
      },
      {
        label: 'Тип экрана:', name: 'type', width: '200px', value: type || '', type: 'select', noDefault: true, options: screenTypes, readOnly: !create, required: true, help: 'Разные типы экранов',
      },
    );
    const descriptionScreen = {
      label: 'Информационный текст:', name: 'help', value: help || '', help: 'Текст выводимый на экран при нажатии на кнопку информации',
    };
    const dictScreenOption = {
      label: 'Выберите доп. поле:', name: 'currentTicketField', value: currentTicketField || '', noDefault: true, type: 'select', options: dictOptions, maxContent: true,
    };
    const inputScreenOption = {
      label: 'Действие при нажатии:', name: 'screenPath', value: screenPath || '', type: 'select', options: inputOptions, help: 'Переход на следующий экран',
    };

    if (type === 'dictScreen') {
      items.push(dictScreenOption);
    }

    if (type === 'inputScreen') {
      items.push(inputScreenOption);
    }

    if (type === 'multiScreen') {
      items.push(dictScreenOption, inputScreenOption);
    }

    if (type !== 'infoScreen') {
      items.push(descriptionScreen);
    }
  }

  const renderInputs = () => items.map((item) => (
    <HandleInputBox>
      <FaQuestion />
      <p>{item.help}</p>
      <Input
        noDefault={item.noDefault}
        key={item.name}
        item={item}
        onChange={handleInput}
      />
    </HandleInputBox>
  ));

  // const options = terminals.map((el) => ({ name: el.name, id: el.id }));

  const ChangeCheck = () => {
    setScreenList((l) => l.map((el) => ({ ...el, isStart: false })));
    setScreen((v) => ({ ...v, isStart: !v.isStart }));
    setIsValid(true);
  };
  console.log({ nowScreen, screenList });

  return (
    <div>
      <RenderBox>
        {items.length && renderInputs()}
        <div>
          <CheckboxBox style={{ margin: '30px 0', minWidth: '300px' }}>
            <CheckBoxComp value={nowScreen.isStart} onChange={ChangeCheck} checked={nowScreen.isStart} name="Чек бокс" />
            <p>Назначить стартовым экраном</p>
          </CheckboxBox>
        </div>
        {/*  <HeadTitle>Просмотр</HeadTitle> */}
        {/*  <div style={{ display: 'flex', flexDirection: 'column' }}> */}
        {/*    <p>Выберите терминал: </p> */}
        {/*    <Select */}
        {/*      maxHeight="300px" */}
        {/*      noDefault */}
        {/*      width="250px" */}
        {/*      m="10px 0 0 0" */}
        {/*      value={terminal.id} */}
        {/*      options={options} */}
        {/*      onChange={e => setTerminal(terminals.find(el => el.id === e.target.value))} */}
        {/*    /> */}
        {/*  </div> */}
        <BtnAdd
          color={isValid ? 'green' : 'disable'}
          fix={140}
          top="34px"
          green
          add
          hover
          show
          onClick={() => (isValid ? create ? addScreen() : updateScreen() : null)}
        >
          <FaSave />
          <span>Сохранить</span>
        </BtnAdd>
      </RenderBox>
    </div>
  );
};

export default connect((state) => ({ terminal: state.handlePoints.config }))(RenderInputs);
