import React from 'react';
import { Link } from 'react-router-dom';
import { BoxButtonContent, ButtonContent } from '../../../../style/style';

const PoliceButton = ({
  el, style, handeleClick, name,
}) => (
  <button
    onClick={() => handeleClick(name)}
    onDragStart={() => false}
    to={el.link}
    key={el.link}
    style={style}
  >
    <ButtonContent>
      <BoxButtonContent>
        <p style={{ ...style.text, textAlign: style.textAlign }}>{el.text}</p>
      </BoxButtonContent>
    </ButtonContent>
  </button>
);

export default PoliceButton;
