import React, {
  useState, useEffect, Suspense, useCallback, useReducer, lazy,
} from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import { TerminalBox } from './style';
import TerminalEditor from './TerminalEditor/TerminalEditor';
import defaultTerminal from './defaultConfig';
import TerminalCtx from './terminalCtx';
import { ReactComponent as BackArr } from '../../../../../images/back.svg';
import recurse from '../../../../../utils/defaultConfigTerminal';

const Terminal = lazy(() => import('./Terminal'));

const TerminalStyle = ({
  deviceData, history, handleSave, id,
}) => {
  const [fonts, setFonts] = useState([]);
  const { config } = deviceData;
  const [element, dispatchElement] = useReducer((state, action) => action.value, 'head');

  const conf = recurse(defaultTerminal.style, config.style);

  const [data, dispatch] = useReducer((state, action) => {
    const {
      name, type, parentName, value, param, payload,
    } = action;
    switch (type) {
      case 'layout':
        return name === 'vertical'
          ? { ...state, head: { ...state.head, height: 1280, width: 1024 } }
          : { ...state, head: { ...state.head, height: 1080, width: 1920 } };
      case 'fonts':
        return { ...state, head: { ...state.head, fontFamily: value } };
      case 'delImage':
      case 'uploadImage':
        const arr = value.split('.');
        if (arr.length === 3) {
          const parent = arr[0];
          const extraParent = arr[1];
          const par = arr[2];
          return { ...state, [parent]: { ...state[parent], [extraParent]: { ...state[parent][extraParent], [par]: payload } } };
        }
        return { ...state, [arr[0]]: { ...state[arr[0]], [arr[1]]: payload } };
      case 'uploadImage2':
        return { ...state, bottomButton: { ...state.bottomButton, [value]: { ...state.bottomButton[value], background: payload } } };
      case 'text':
        return { ...state, [name]: { ...state[name], [param]: value } }
      case 'params':
        let newValue = !isNaN(+value) ? +value : value;
        if (param === 'boxShadow' && !value.match(/px/)) {
          const arrShadow = state[name].boxShadow.split(' ');
          arrShadow.splice(3, 1, value);
          newValue = arrShadow.join(' ');
        }
        return { ...state, [name]: { ...state[name], [param]: newValue } };
      case 'bigParams':
        const newV = !isNaN(+value) ? +value : value;
        return { ...state, [parentName]: { ...state[parentName], [name]: { ...state[parentName][name], [param]: newV } } };
      case 'viewType':
        return { ...state, [name]: { ...state[name], [param]: value >= 4 ? 1 : value } };
      default:
        return state;
    }
  }, conf);

  const saveConfig = useCallback((e) => {
    e.preventDefault();
    const sendData = config || defaultTerminal;
    sendData.style = data;
    handleSave(sendData);
    toast.success('Выполнено');
  }, [data, config, handleSave]);

  const ctx = {
    data,
    dispatch,
    element,
    dispatchElement,
    fonts,
    saveConfig,
  };

  useEffect(() => {
    axios({
      method: 'get',
      url: '/api/fonts',
    }).then((res) => setFonts(res.data.map((el) => ({ id: el.name, name: el.name }))));
  }, []);

  const previewConfig = { ...deviceData, config: { ...deviceData.config, style: data } };

  return (
    <TerminalCtx.Provider value={ctx}>
      <div className="title-box">
        <button className="title__btn-back" onClick={() => history.goBack()}>
          <BackArr /><span>Назад</span>
        </button>
        <h1 className="title">СТИЛИЗАЦИЯ РЕГИСТРАТОРА</h1>
      </div>
      <TerminalBox>
        <TerminalEditor />
        <Suspense fallback={<div />}>
          {data.head !== undefined && <Terminal onSave={saveConfig} id={id} config={previewConfig} />}
        </Suspense>
      </TerminalBox>
    </TerminalCtx.Provider>
  );
};

export default React.memo(TerminalStyle);
