import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import request from '../../../utils/request';
import { Title, ContentBox, PreloaderBox } from '../../../styles/Common';
import ParamsTable from './ParamsTable';

const Params = ({ preloader, showError }) => {
  const [groups, setGroups] = useState([]);
  const [paramList, setParamList] = useState(null);
  const [multiData, setMultiData] = useState([]);

  const fetchData = useCallback(async () => {
    preloader();
    try {
      const res = await axios({
        method: 'get',
        url: '/api/soundlist',
      });
      const newMultiData = [];
      newMultiData.push(res.data.sounds);
      newMultiData.push(res.data.variables);
      setMultiData(newMultiData);
      preloader();
    } catch (e) {
      showError(e.data.error);
      preloader();
    }
  }, [preloader, showError]);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const fetchParams = useCallback(async () => {
    try {
      const newParams = await request('/server/api/officeparams/', 'get');

      setParamList(newParams.data.data);

      const groupsArr = newParams.data.data.map((param) => param.group_name);
      const groupObj = {};
      for (let i = 0; i < groupsArr.length; i += 1) {
        const str = groupsArr[i];
        groupObj[str] = true;
      }
      const newGroup = Object.keys(groupObj);
      setGroups([...newGroup]);
    } catch (e) {
      showError(e.data.error);
    }
  }, [showError]);

  useEffect(() => {
    fetchParams();
  }, [fetchParams]);

  return (
    <>
      <Title>Параметры сервера</Title>
      <ContentBox>
        <PreloaderBox>
          {groups.length > 0 && (
          <ParamsTable
            paramList={paramList}
            groups={groups}
            showError={showError}
            setParamList={setParamList}
            preloader={preloader}
            multiData={multiData}
          />
          )}
        </PreloaderBox>
      </ContentBox>
    </>
  );
};

export default Params;
