import React, { useState, useEffect, useCallback } from 'react';
import moment from 'moment';
import { toast } from 'react-toastify';
import axios from 'axios';
import {
  ReportFiltersBox, ReportFiltersTitle, FilterElements, FilretButtonsBox,
} from '../../../styles/ReportFilters';
import { BtnAdd } from '../../../styles/Common';
import MainFilters from './FilterElements/MainFilters/MainFilters';
import DopFilters from './FilterElements/DopFilters/DopFilters';
import Orders from './FilterElements/Orders/Orders';
import request from '../../../utils/request';
import { getReportConfig } from '../../../store/appStore';

const ReportFilters = ({
  paramsCallback, showFilters, type,
}) => {
  const { startDate, endDate } = getReportConfig();
  const [filtersData, setFiltersData] = useState();
  const [groupingData, setGroupingData] = useState();
  const [filterParams, setFilterParams] = useState({
    operator: 'all',
    workPlace: 'all',
    queue: 'all',
    close_cnt: 'all',
    groupinType: 'wp_id',
    bunch_id: 'all',
    all_cnt: 'all',
    neyavka_cnt: 'all',
    avg_wait_time: 'all',
    total_wait_time: 'all',
    avg_proc_time: 'all',
    total_proc_time: 'all',
    total_work_time: 'all',
    avg_work_time: 'all',
    mark1: 'all',
    mark2: 'all',
    mark3: 'all',
    mark4: 'all',
    mark5: 'all',
    markcnt: 'all',
    share_cnt: 'all',
    solved_questions: 'all',
    create_data: 'all',
    close_data: 'all',
    tiket_number: 'all',
    status: 'all',
    prior: 'all',
    ticket_mark: 'all',
    dateType: 'day',
    totalproc: 'all',
    dateTypeDescription: 'день',
    startDate: startDate.format('DD.MM.YYYY HH:mm'),
    endDate: endDate.format('DD.MM.YYYY HH:mm'),
    historyDate: moment().format('DD.MM.YYYY HH:mm'),
    intervalReprotHistoryDate: 'all',
    orderArr: [],
  });

  // const clearFilters = () => {
  //   console.log('чистим все фильты === ');
  // };

  const openNotify = (type, message) => {
    if (type === 'success') {
      toast.success(`${message}`, 1000);
    } else {
      toast.error(`${message}`, 1000);
    }
  };


  const exportReport = async () => {
    const filters = {
      main: {
        ...filterParams,
        limit: 'all',
        offset: 'all',
      },
    };
    try {
      const token = window.localStorage.getItem('token');
      const url = '/api/reports/build';
      const res = await axios({
        method: 'post',
        url,
        data: {
          token,
          filters: { ...filters },
          type,
          exports: 'true',
        },
      });
      const message = 'Отчет подготавливается для скачивания и появиться в разделе "Архив"';
      if (res.data.answer.success) {
        openNotify('success', message);
      }
    } catch (err) {
      openNotify('error', err);
    }
  };

  const buildReport = () => {
    const filters = {
      main: {
        ...filterParams,
      },
    };
    console.log('filters === ', filters);

    paramsCallback(filters);
  };

  const fetchFiltersData = useCallback(async () => {
    const res = await axios.get('/api/queueobjects/', 'get');
    const groupingRes = await request('/server/api/bunch/', 'get');
    console.log('groupingRes === ', groupingRes);
    setGroupingData(groupingRes.data.data);
    setFiltersData(res.data);
  }, []);

  const changeFilter = useCallback((filter, value) => {
    console.log(value);

    if (value !== '') {
      const newFilters = filterParams;
      newFilters[filter] = value;
      setFilterParams(newFilters);
    }
  }, []);

  const changeOrder = (order, value) => {
    const newFilters = { ...filterParams };
    console.log('order', value);
    newFilters.orderArr = value;
    setFilterParams(newFilters);
  };

  useEffect(() => {
    fetchFiltersData();
  }, [fetchFiltersData]);


  return (
    <ReportFiltersBox>
      <ReportFiltersTitle>Параметры отчета</ReportFiltersTitle>
      {filtersData ? (
        <>
          <FilterElements>
            <MainFilters
              changeFilter={changeFilter}
              showFilters={showFilters.main}
              filtersData={filtersData}
              groupingData={groupingData}
            />
            {showFilters.others.length > 0 ? (
              <DopFilters
                changeFilter={changeFilter}
                showFilters={showFilters.others}
              />
            ) : (null)}
            {showFilters.orders.length > 0 ? (
              <Orders
                changeFilter={changeOrder}
                showFilters={showFilters.orders}
              />
            ) : (null) }
          </FilterElements>
          <FilretButtonsBox>
            <BtnAdd fix={135} onClick={buildReport}>Применить</BtnAdd>
            <BtnAdd fix={180} onClick={exportReport}>Экспортировать</BtnAdd>
            {/* <BtnAdd fix={90} onClick={clearFilters}>Сброс</BtnAdd> */}
          </FilretButtonsBox>
        </>
      ) : (<div> Загрузка... </div>)}

    </ReportFiltersBox>
  );
};
export default ReportFilters;
