import React, { useState } from 'react';
import paginationFactory from 'react-bootstrap-table2-paginator';
import cellEditFactory from 'react-bootstrap-table2-editor';
import Table from '../../../../Elements/Table/Table';
import Config from './TableConfig';
import Preloader from '../../../../Elements/Preloader/Preloader';
import DeleteWarning from '../../../../Elements/Modal/DeleteWarning';
import request from '../../../../../utils/request';
import { useToggle } from '../../../../../hooks/useToggle';
import { TableAbsolute } from '../../../../../styles/Table';

const ValuesTable = ({
  valuesList, fetchBook, setFieldUpdate, setFieldData, toggleOpen, write,
}) => {
  const [showDelWarn, setShowDelWarn] = useToggle(false);
  const [valueId, setValueId] = useState(false);

  const delValue = async () => {
    await request(`/server/api/dictionaryvalue/${valueId}/`, 'delete');
    fetchBook();
    setShowDelWarn();
    setValueId(false);
  };


  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const { id } = row;
    if (dataset.name === 'del') {
      setValueId(id);
      setShowDelWarn();
    } else if (dataset.name === 'update') {
      setFieldUpdate(true);
      setFieldData({
        id,
      });
      toggleOpen();
    }
  };

  const beforeSaveCell = async (oldValue, newValue, row, column) => {
    const { dataField } = column;
    row[dataField] = newValue;
    try {
      await request(`/server/api/dictionaryvalue/${row.id}/`, 'update', row);
    } catch (e) {
      alert('Ошибка обновления');
    }
  };

  const Setting = Config(write);
  const { columns } = Setting;
  return (
    <div style={{ position: 'relative' }}>
      <TableAbsolute>
        {valuesList === null
          ? <Preloader isOpen small /> : valuesList === undefined
            ? <div>Свойств нет</div> : (
              <Table
                keys="id"
                data={valuesList}
                columns={columns}
                handleIcon={handleTableIcon}
                cellEdit={cellEditFactory({
                  mode: 'click',
                  blurToSave: true,
                  beforeSaveCell,
                })}
                pagination={paginationFactory()}
              />
            ) }
        <DeleteWarning confirmDel={delValue} isOpen={showDelWarn} toggleOpen={setShowDelWarn} />
      </TableAbsolute>
    </div>

  );
};

export default React.memo(ValuesTable);
