import React from 'react';
import { TiketReoportStatusTemplate, DubleLineTemplate } from '../../../utils/Formater';
// import secret from '../../../../../../secret/secret.mp3';

const parseAudio = (audio) => {
  if (!audio) return <span>Записи нет</span>;
  const audioArr = audio.split(',');
  return audioArr.map((record) => {
    const audioPath = record.split('/');
    return <audio src={`/records/${audioPath[audioPath.length - 1]}`} controls />;
  });
};

export default function (getColumnSearchProps) {
  return {
    columns: [
      {
        dataIndex: '24',
        title: 'Дата обслуживания',
        render: (cell, row) => DubleLineTemplate('Начало:', row.create_data, 'Окончание:', row.close_data),
      },
      {
        dataIndex: 'tiket_number',
        title: '№ Талона',
        ...getColumnSearchProps('tiket_number'),
      },
      {
        dataIndex: 'user_fio',
        title: 'Оператор',
        ...getColumnSearchProps('user_fio'),
      },
      {
        dataIndex: 'workplace_name',
        title: 'Рабочее место',
      },
      {
        dataIndex: 'queue_name',
        title: 'Очередь',
      },
      {
        dataIndex: 'prior',
        title: 'Приоритет',
      },
      {
        dataIndex: '21',
        title: 'Длительность',
        render: (cell, row) => DubleLineTemplate('Ожидания:', row.total_wait_time, 'Oбслуживания:', row.total_proc_time),
      },
      {
        dataIndex: '11',
        title: 'Статус',
        render: (cell, row) => TiketReoportStatusTemplate(`${row.ticket_mark * 20}%`, row.status),
      },
      {
        dataIndex: 'audioRecord',
        title: 'Запись',
        render: (_, res) => {
          const { audio } = res;
          return (
            <div style={{
              display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center',
            }}
            >{parseAudio(audio)}
            </div>
          );
        },
      },
    ],
  };
}
