import React, { useState, useRef } from 'react';
import { FaChevronDown } from 'react-icons/fa';
import {
  Container, SelectDiv, SelectOptions, Option, CloseBlock,
} from './Style';
import findPosition from '../../../../utils/findPosition';

const Select = ({
  options = [], value, onChange, name, noDefault, m, width, maxHeight, readOnly, bg, disabled, maxContent, align,
}) => {
  const [active, setActive] = useState(false);
  const [bottom, setBottom] = useState(true);
  const sel = useRef(null);
  const box = useRef(null);
  const activeElement = options.find((el) => el.id === value);

  const Change = (name, id) => {
    const body = { target: { name, value: id } };
    onChange(body);
    setActive(!active);
  };

  return (
    <Container ref={box} align={align} m={m} width={width}>
      <SelectDiv
        data-test="clickItem"
        ref={sel}
        bg={bg}
        onClick={(e) => {
          findPosition(sel.current, setBottom);
          setActive(!active);
        }}
      >
        <span data-test="defaultValue">{activeElement ? activeElement.name : !noDefault ? options[0] ? options[0].name : 'Не выбрано' : 'Не выбрано'}</span>
        <FaChevronDown />
      </SelectDiv>
      {!disabled && active && options && !readOnly ? (
        <>
          <CloseBlock onClick={() => setActive(false)} />
          <SelectOptions data-test="selectedMenu" bottom={bottom} w={maxContent} maxHeight={maxHeight}>
            {!noDefault && <Option bg={bg} onClick={() => Change(name, '')}>Не выбрано</Option>}
            {options.map((el, index) => (
              <Option
                bg={bg}
                key={index}
                onClick={(e) => {
                  Change(name, el.id);
                }}
              >
                {el.name}
              </Option>
            ))}
          </SelectOptions>
        </>
      ) : null}
    </Container>
  );
};

export default React.memo(Select);
