import React from 'react';
import ColorPick from '../../../../../../utils/colorPicker';
import GradientPicker from '../../../../../../utils/gradient';

import { ColorBox } from '../style';

const color = ({
  element, dispatch, rusName, paramName, value, width,
}) => {
  const handleColor = ({ payload }) => dispatch({
    type: 'params',
    name: element,
    param: paramName,
    value: payload,
  });

  return (
    <ColorBox width={width}>
      <span dangerouslySetInnerHTML={{ __html: rusName }} />
      {/color/.test(paramName) || paramName === 'boxShadow' || paramName === 'borderColor'
        ? (
          <ColorPick
            handleColor={handleColor}
            name={paramName}
            color={value}
          />
        )
        : (
          <GradientPicker
            colorBg={value}
            handleGradient={handleColor}
            name="background"
          />
        )
    }
    </ColorBox>
  );
};

export default color;
