import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import { Title, PreloaderBox, BtnAdd } from '../../../styles/Common';
import TableConfig from './TableConfig';
import Preloader from '../../Elements/Preloader/Preloader';
import Table from '../../Elements/Table/Table';

const Archive = (props) => {
  const { preloader } = props;
  const [ReportParams, setReportParams] = useState();

  const getRepotData = useCallback(async () => {
    preloader();
    const rpRequest = await axios.get('/api/reports');
    const { data: reportsData } = rpRequest;
    console.log('reportsData === ', reportsData);
    const params = {
      items: reportsData,
      TableConfig: TableConfig(),
    };

    setReportParams((v) => ({ ...v, ...params }));
    preloader();
  }, [preloader]);


  const handleTableIcon = async (e, row) => {
    const { dataset } = e.target;
    const { id: fileId } = row;

    if (dataset.name === 'del') {
      const filterData = {
        id: fileId,
      };

      const token = window.localStorage.getItem('token');
      await axios({
        method: 'delete',
        url: `/api/reports/?token=${token}`,
        data: filterData,
      });
      getRepotData();
    }
  };

  useEffect(() => {
    getRepotData();
  }, [getRepotData]);
  console.log({ ReportParams });
  return (
    <div>
      <Title>Архив</Title>
      <BtnAdd onClick={getRepotData} add>
        {/* <Update /> */}
        #
        <span>Обновить</span>
      </BtnAdd>
      { ReportParams ? (
        <Table
          keys="id"
          data={ReportParams.items}
          columns={ReportParams.TableConfig.columns}
          handleIcon={handleTableIcon}
        />
      )
        : (
          <PreloaderBox>
            <Preloader isOpen small />
          </PreloaderBox>
        )}
    </div>
  );
};

export default Archive;
