import React, {
  useEffect, useRef, useCallback,
} from 'react';
import { Body } from '../../style/style';
import Request from '../../utils/request';
import { resetPre, setTextError } from '../../utils/actions';

const CanvasPre = ({
  style, data, config, dispatch, history, startScreen, errorUrl,
}) => {
  const { pin, service, date } = data;
  const time = useRef(null);

  const printTicket = useCallback(async (body) => {
    const { satelliteIp, satellitePort } = config;
    body.satelliteIp = satelliteIp;
    body.satellitePort = satellitePort;
    await Request('/proxySatellite/gate/print', 'no', body)
      .then((res) => {
        if (!res.data.success) {
          // eslint-disable-next-line no-throw-literal
          throw 'Ошибка печати. Проверьте принтер.';
        }
        time.current = setTimeout(() => {
          history.push(startScreen);
          dispatch(resetPre());
          clearTimeout(time.current);
        }, 10000);
      })
      .catch((res) => {
        history.push(errorUrl);
        dispatch(setTextError(res));
      });
  }, [startScreen, history, dispatch, config, errorUrl]);

  const ref = useRef(null);

  useEffect(() => {
    const ctx = ref.current.getContext('2d');
    ref.current.width = 500;
    ref.current.height = 500;
    ctx.clearRect(0, 0, ctx.width, ctx.height);
    ctx.fillStyle = '#fff';
    ctx.rect(0, 0, 500, 500);
    ctx.fill();
    ctx.textAlign = 'center';
    ctx.textBaseline = 'top';
    ctx.fillStyle = '#000';
    ctx.font = `bold 28px ${style.button.fontFamily || 'Courier New'}`;
    ctx.fillText('Ваш PIN-CODE', 250, 140);
    ctx.font = `bold 56px ${style.button.fontFamily || 'Courier New'}`;
    ctx.fillText(`${pin}`, 250, 200);
    ctx.font = `bold 28px ${style.button.fontFamily || 'Courier New'}`;
    ctx.fillText(service, 250, 280);
    ctx.font = `bold 18px ${style.button.fontFamily || 'Courier New'}`;
    ctx.fillText(date, 250, 320);
  }, [date, pin, service, style.button.fontFamily]);

  useEffect(() => {
    const base64 = ref.current.toDataURL();
    const body = { data: base64.split(',')[1] };
    printTicket(body);
  }, [printTicket]);

  return (
    <Body style={style.body}>
      <canvas ref={ref} style={{ width: 500, height: 500, position: 'relative' }} />
    </Body>
  );
};

export default CanvasPre;
