import React, { useState } from 'react';
import moment from 'moment';
import Sections from './Sections/Sections';
import QualityControlElements from './QualityControlElements/QualityControlElements';
import Params from './Params/Params';
import { Title, ReportBigBox } from '../../../styles/Common';

const QualityControl = (props) => {
  console.log(props);
  const { preloader } = props;
  const [activeSection, setActiveSection] = useState(1); // поменяй на 1 даун!
  const [settings, setSettings] = useState({
    startDate: moment().startOf('month').format('DD.MM.YYYY HH:mm'),
    endDate: moment().endOf('day').format('DD.MM.YYYY HH:mm'),
    questId: '',
  });

  console.log(settings);


  return (
    <ReportBigBox>
      <Title>Оценка качества</Title>
      <Sections
        activeSection={activeSection}
        switchSection={setActiveSection}
      />
      <Params
        activeSection={activeSection}
        params={{ settings, setSettings }}
      />
      <QualityControlElements
        activeSection={activeSection}
        settings={settings}
        preloader={preloader}
      />

    </ReportBigBox>
  );
};

export default QualityControl;
