import React from 'react';
import { CheckBox, InputCheck, Check } from '../../../styles/Common';


const CheckBoxComp = ({
  value, onChange, checked, name,
}) => (
  <CheckBox>
    <InputCheck
      type="checkbox"
      value={value}
      name={name}
      onChange={() => onChange({ target: { name, value: !value, type: 'checkbox' } })}
      checked={checked}
    />
    <Check />
  </CheckBox>
);

export default CheckBoxComp;
