import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { FaCheckSquare } from 'react-icons/fa';
import { TicketButtonBox } from '../../../styles/Common';
import IconFormatter from '../../../utils/iconFormatter';

function connectFormatter(i) {
  return <div>{i.map((item) => (typeof item === 'object') && <div key={item.q_id}>{item.name}</div>)}</div>;
}

function defaultFormatter(item) {
  return (
    <TicketButtonBox active={item}>
      <FaCheckSquare size="24px" />
    </TicketButtonBox>
  );
}

function headerFormatter(column, colIndex, { filterElement }) {
  return (
    <div className="filterBoxTable">
      { filterElement }
    </div>
  );
}


export default function () {
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Наименование',
        sort: true,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Наименование',
        }),
        headerFormatter,
        editable: false,
      },
      {
        dataField: 'select_by_default',
        text: 'По умолчанию',
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'По умолчанию',
        }),
        headerFormatter,
        formatter: defaultFormatter,
        editable: false,
        headerStyle: {
          width: 150,
        },
      },
      {
        dataField: 'connected_queues',
        text: 'Подключенные услуги',
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Подключенные услуги',
        }),
        headerFormatter,
        formatter: connectFormatter,
        editable: false,
      },
      {
        dataField: 'Settings',
        text: ' ',
        headerFormatter,
        formatter: () => IconFormatter({
          filter: false, write: true, params: false, subscribe: true,
        }),
        formatExtraData: {},
        editable: false,
        headerStyle: {
          width: 150,
        },
      },
    ],
  };
}
