import React, { useState, useCallback, useEffect } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import moment from 'moment';
import md5 from 'md5';
import { PreContainer } from './style';
import Calendar from './components/calendar';
import request from '../../../../utils/request';
// import Table from '../../../Elements/Table/Table';
// import TableConfig from './components/TableConfig';
import DeleteWarning from '../../../Elements/Modal/DeleteWarning';

const initData = {
  name: '',
  description: '',
  prolongremain: '',
  prolongdefpamode: '',
  sourcefirstday_id: '',
  sourcelastday_id: '',
  genpos: '',
};

const TableData = ({
  queue, showError, preloader, selectedQueue, setSelectedQueue, setData, data, calendar, setCalendar,
}) => {
  const [templateData, setTemplateData] = useState({ ...initData });
  const [fullCalendar, setFullCalendar] = useState();
  const selectedDay = data.dayId;
  const [startDate, setStartDate] = useState(moment().startOf('month'));
  const [endDate, setEndDate] = useState(moment().endOf('month'));
  const currentMonth = moment().format('MM');
  const [month, setMonth] = useState(currentMonth);
  const currentYear = moment().format('YYYY');
  const [year, setYear] = useState(currentYear);
  const [isEdit, setIsEdit] = useState(false);
  const [showDel, setDel] = useState(false);
  const [open, setOpen] = useState(false);
  const [openInfo, setOpenInfo] = useState(false);
  // const [deleteItem, setDelete] = useState({});
  const [isLoad, setIsLoad] = useState(false);
  const [clear, setClear] = useState(false);
  const optionsQueue = queue.map((el) => ({ id: el.qId, name: el.qFullName }));
  // const [templates, setTemplates] = useState([]);

  const setSelectedDay = (value) => {
    setData({ ...data, dayId: value });
  };

  // const fetchTemplates = useCallback(async () => {
  //   const d = await request('/server/api/dpatemplate/');
  //   setTemplates(d.data.data);
  // }, []);

  const fetchFullCalendar = useCallback(async () => {
    const d = await request('/server/api/dcalendar/', 'get', {});
    setFullCalendar(d.data.data);
  }, []);

  useEffect(() => {
    // fetchTemplates();
    fetchFullCalendar();
  }, [fetchFullCalendar, queue]);

  useEffect(() => {
    const newMonth = moment().set({ month: month - 1, year });
    setStartDate(moment(newMonth).startOf('month'));
    setEndDate(moment(newMonth).endOf('month'));
  }, [month, queue]);

  const onSelectedQueue = (value) => {
    setSelectedQueue(value);
    setData({ ...data, q_id: value, dayId: { ...data.dayId, id: undefined, data: [] } });
  };

  const fetchCalendar = useCallback(async (s) => {
    const sDay = s || selectedDay;
    setIsLoad(true);
    const d = {
      // qId: selectedQueue,
      dateFrom: moment(startDate).format('YYYY-MM-DD HH:mm:ss'),
      dateTo: moment(endDate).format('YYYY-MM-DD HH:mm:ss'),
    };
    const fetchData = await request('/server/api/dpafullcalendarslot/', 'get', d);
    const cal = {};
    fetchData.data.data.forEach((el) => {
      const date = +el.thedate.split(' ')[0].split('.')[2];
      if (cal[date]) {
        cal[date].push(el);
      } else {
        cal[date] = [el];
      }
    });

    setCalendar(cal);

    setData((v) => {
      const curDay = +v.dayId.day.split('.')[0];
      return curDay ? {
        ...v,
        dayId: {
          ...sDay,
          id: cal[curDay] ? cal[curDay][0].calendarday_id : null,
          data: cal[curDay] || [],
        },
      } : v;
    });
    setIsLoad(false);
  }, [startDate, endDate, setCalendar, setData]);

  useEffect(() => {
    if (selectedQueue) fetchCalendar();
  }, [selectedQueue, startDate, endDate, fetchCalendar]);

  useEffect(() => {
    // Надо отдельный запрос на слоты иначе провал - берет все записи - надо учесть при переписывании
    // if (openInfo) fetchCalendar();
  }, [openInfo, fetchCalendar]);

  const onDelete = async (id) => {
    try {
      await request(`/server/api/dpacalendarslot/${id}`, 'delete');
      toast.success('Выполнено.');
      setOpenInfo(false);
      fetchCalendar(selectedDay);
    } catch (e) {
      setOpenInfo(false);
      showError(e.data.error);
    }
  };

  const onClear = async (id) => {
    const clearBody = {
      calendarCellId: id,
    };
    try {
      await request(`/server/api/dpacalendarslot/${id}`, 'clear', clearBody);
      toast.success('Выполнено.');
      setOpenInfo(false);
      fetchCalendar(selectedDay);
    } catch (e) {
      setOpenInfo(false);
      showError(e.data.error);
    }
  };

  const onRecord = async (id) => {
    const d = {
      calendarCellId: id,
      clientEmail: 'admin@admin.panel',
      clientPhoneNumber: '83330007029',
      clientFIO: 'admin.panel',
    };

    const ordered = {};
    Object.keys(d).sort().forEach((key) => {
      ordered[key] = d[key];
    });

    let hashString = '';
    Object.keys(ordered).forEach((el) => hashString += d[el]);
    d.sign = md5(hashString);

    try {
      const res = await axios.post('/server/public/preorder/register', { data: d });
      if (res.data.error) throw res;
      toast.success('Вы записаны.');
      showError({ text: { end: '', rus: `Ваш пин-код: ${res.data.data.pinCode}` } });
      setOpenInfo(false);
      fetchCalendar(selectedDay);
    } catch (e) {
      setOpenInfo(false);
      showError(e.data.error);
    }
  };

  const onSave = async (isEdit) => {
    preloader();
    let firstId = '';
    let secondId = '';
    const momentFirst = moment(templateData.sourcefirstday_id).format('DD.MM.YYYY');
    const momentSecond = moment(templateData.sourcelastday_id).format('DD.MM.YYYY');
    try {
      fullCalendar.forEach((el) => {
        const elDate = moment(`${el.dayOfMonth}.${el.monthNumber}.${el.yearNumber}`, 'D.M.YYYY').format('DD.MM.YYYY');
        if (elDate === momentFirst) firstId = el.dayId;
        if (elDate === momentSecond) secondId = el.dayId;
      });
      const data = { ...templateData };
      data.sourcefirstday_id = firstId;
      data.sourcelastday_id = secondId;
      data.q_id = selectedQueue;
      const url = `/server/api/dpatemplate/${data.id}/`;
      delete data.id;
      await request(isEdit ? url : '/server/api/dpatemplate/', isEdit ? 'update' : 'add', data);
      preloader();
      setTemplateData({ ...initData });
      toast.success('Выполнено');
      // fetchTemplates();
      setIsEdit(false);
    } catch (e) {
      preloader();
      showError(e.data.error);
    }
  };

  const saveDay = async (copyDay) => {
    preloader();
    const body = {
      q_id: open.q_id,
      ...copyDay,
    };
    try {
      await request(`/server/api/dcalendar/${selectedDay.id}`, 'copy', body);
      preloader();
      toast.success('Выполнено');
      setData({ ...data, dayId: { id: undefined, data: [], day: '' } });
      fetchCalendar(selectedDay);
      setOpen(false);
    } catch (e) {
      preloader();
      showError(e.data.error);
    }
  };

  const clearDay = async () => {
    preloader();
    try {
      await request(`/server/api/dcalendar/${selectedDay.id}`, 'clear', { q_id: clear });
      toast.success('Выполнено.');
      setData({ ...data, setId: { id: undefined, data: [], day: '' } });
      setDel(false);
      fetchCalendar(selectedDay);
      preloader();
    } catch (e) {
      preloader();
      setDel(false);
      showError(e);
    }
  };

  // const handleIcon = (e, row) => {
  //   const { dataset } = e.target;
  //   const {
  //     name, description, prolongremain, prolongdefpamode, genpos, sourcefirstday_id, sourcelastday_id, id,
  //   } = row;
  //   if (dataset.name === 'del') {
  //     setDelete({ name: row.name, id: row.id });
  //     setDel(true);
  //   } else if (dataset.name === 'update') {
  //     setSelectedQueue(row.q_id);
  //     let s = '';
  //     let f = '';
  //     fullCalendar.forEach((el) => {
  //       if (el.dayId === sourcefirstday_id) s = new Date(moment(`${el.dayOfMonth}.${el.monthNumber}.${el.yearNumber}`, 'D.M.YYYY'));
  //       if (el.dayId === sourcelastday_id) f = new Date(moment(`${el.dayOfMonth}.${el.monthNumber}.${el.yearNumber}`, 'D.M.YYYY'));
  //     });
  //     const newDate = {
  //       name,
  //       description,
  //       prolongremain,
  //       prolongdefpamode,
  //       genpos,
  //       sourcefirstday_id: s,
  //       sourcelastday_id: f,
  //       id,
  //     };
  //     setTemplateData({ ...newDate });
  //
  //     setSelectedDay({ ...data.dayId, id: undefined, data: [] }, 'handleIcon');
  //     setIsEdit(true);
  //   }
  // };

  const copyDay = (value, q) => setOpen({ open: value, q_id: q });

  const isConstructor = () => {
    setTemplateData({ ...initData });
    setIsEdit(false);
    setSelectedDay({ ...data.dayId, id: undefined, data: [] }, 'isConstructor');
  };

  // const delService = async () => {
  //   preloader();
  //   try {
  //     await request(`/server/api/dpatemplate/${deleteItem.id}/`, 'delete');
  //     setDel(false);
  //     setIsEdit(false);
  //     setTemplateData({ ...initData });
  //     // fetchTemplates();
  //     preloader();
  //     toast.success('Выполнено');
  //   } catch (e) {
  //     preloader();
  //     showError(e);
  //   }
  // };

  const onDeleteDay = (q) => {
    setClear(q);
    setDel(true);
  };

  // const { columns } = TableConfig(fullCalendar, queue);
  return (
    <PreContainer>
      <Calendar
        optionsQueue={optionsQueue}
        queue={selectedQueue}
        setQueue={onSelectedQueue}
        month={month}
        year={year}
        open={open}
        showError={showError}
        copyDay={copyDay}
        setOpen={setOpen}
        setOpenInfo={setOpenInfo}
        openInfo={openInfo}
        onSave={onSave}
        saveDay={saveDay}
        onRecord={onRecord}
        setClear={onDeleteDay}
        onDelete={onDelete}
        onClear={onClear}
        currentMonth={currentMonth}
        setMonth={setMonth}
        setYear={setYear}
        calendar={calendar}
        setTemplateData={setTemplateData}
        setSelectedDay={setSelectedDay}
        selectedDay={selectedDay}
        selectedQueue={selectedQueue}
        templateData={templateData}
        isEdit={isEdit}
        setIsEdit={isConstructor}
        isLoad={isLoad}
        dayId={data.dayId}
      />
      {/* <h3>Список шаблонов</h3> */}
      {/* {fullCalendar && queue && <Table keys="id" columns={columns} handleIcon={handleIcon} data={templates} />} */}
      <DeleteWarning name="Выбранный день" confirmDel={clearDay} isOpen={showDel} toggleOpen={() => setDel(false)} />
    </PreContainer>
  );
};

export default TableData;
