import React, { useContext } from 'react';
import handleFz from '../../../../../../../../utils/handleFz';
import BoardCtx from '../../../BoardCtx';
import Timer from './header/Timer';

const Footer = ({ layout, text }) => {
  const { elWidth, setActiveEl } = useContext(BoardCtx);
  const { footer, clockString, header } = layout;
  const {
    fontSize1, order, background, color1, fontFamily, height, fontSize2, fontSize3, color2, logotype,
  } = footer;

  const styleFooter = {
    ...footer,
    background,
    color: color1,
    fontSize: handleFz(fontSize1, elWidth),
    order: !order ? 1 : 2,
    margin: order === 0 ? '0.5% 0 0' : '0',
    minHeight: +height,
    maxHeight: +height,
  };

  const timeFZ = handleFz(fontSize2, elWidth);
  const dateFZ = handleFz(fontSize3, elWidth);
  if (!header.runstring) {
    return <div
      className="boardFooterBox"
      onClick={() => setActiveEl('footer')}
      style={styleFooter}
    >
      <marquee className="footerString" style={{ fontFamily }}>
        {text.footer1}
        {clockString && <Timer styles={layout.footer} timeFZ={timeFZ} order={order} dateFZ={dateFZ} color={color2}/>}
      </marquee>
    </div>
  } else {
    return null
  }
};

export default Footer;
