import React, { useContext } from 'react';
import { StyleSettingsBox } from '../../../../../../../../styles/BoardStyle';
import Context from '../../../terminalCtx';
import HeadSettings from './Head';
import TextSettings from './Text';
import ButtonSettings from './Button';
import NavigationSettings from './Navigation';
import InputSettings from './Input';

const StyleSettings = () => {
  const { element, data, dispatch } = useContext(Context);

  return (
    <StyleSettingsBox element={element}>
      {element === 'head' && <HeadSettings data={data} dispatch={dispatch} />}
      {element === 'text' && <TextSettings data={data} dispatch={dispatch} />}
      {element === 'button' && <ButtonSettings data={data} dispatch={dispatch} />}
      {element === 'navigation' && <NavigationSettings data={data} dispatch={dispatch} />}
      {element === 'input' && <InputSettings data={data} dispatch={dispatch} />}
    </StyleSettingsBox>
  );
};

export default StyleSettings;
