import React from 'react';


function priceFormatter(column) {
  return (
    <div style={{
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      height: '96px',
    }}
    >
      <div> {column.text}</div>
    </div>
  );
}

const hcolFormatter = column => (
  <div style={{ display: 'flex', flexDirection: 'column' }}>
    <div style={{ fontSize: 13, textAlign: 'center' }}>{column.text}</div>
    <div style={{
      display: 'flex', justifyContent: 'space-between', marginTop: '10px', borderTop: '1px solid #fc4c02', padding: '7px 12px',
    }}
    >
      <div style={{
        textAlign: 'center',
        flex: '1',
        // padding: '10px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
      >количество оценок
      </div>
      <div style={{
        textAlign: 'center',
        flex: '1',
        // padding: '10px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
      >средняя
      </div>
      <div style={{
        textAlign: 'center',
        flex: '1',
        // padding: '10px',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
      >NPS
      </div>
    </div>
  </div>
);

const columnFormater = (cell, row, list) => (
  <div style={{
    display: 'flex', justifyContent: 'space-between', marginTop: '10px',
  }}
  // padding: '5px',
  >
    <div style={{ textAlign: 'center', flex: '1' }}>{row[list] ? row[list].cnt : '—'}</div>
    <div style={{ textAlign: 'center', flex: '1' }}>{row[list] ? row[list].avg : '—'}</div>
    <div style={{ textAlign: 'center', flex: '1' }}>{row[list] ? `${Number(row[list].nps).toFixed(2)}%` : '—'}</div>
  </div>
);

export default function (list) {
  const colObjects = [];
  list.forEach((li) => {
    colObjects.push({
      dataField: `${li[0]}.avg`,
      text: `${li[1]}`,
      sort: false,
      editable: false,
      headerFormatter: hcolFormatter,
      headerStyle: {
        width: 220,
      },
      formatter: (cell, row) => columnFormater(cell, row, li[0]),
    });
  });


  return {
    columns: [{
      dataField: 'allCnt',
      text: 'Количество оценок',
      sort: false,
      editable: false,
      headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      formatter: cell => <div style={{ textAlign: 'center' }}>{cell}</div>,
      headerStyle: {
        width: 203,
      },
    },
    ...colObjects,
    ],
  };
}
