import React, { useState, useRef } from 'react';
import {
  ButtonChange,
  AlignBox,
  InputMargin,
  InputMarginBox,
} from '../../../../../../styles/BoardStyle';
import findPosition from '../../../../../../utils/findPosition';
import { ColorBox, Box } from '../style';

const Margin = ({
  data, dispatch, element, name, title,
}) => {
  const [bottom, setBottom] = useState(false);
  const [open, setOpen] = useState(false);
  const {
    marginTop, marginLeft, marginBottom, marginRight,
  } = data;
  const {
    paddingTop, paddingLeft, paddingBottom, paddingRight,
  } = data;

  const box = useRef(null);
  const margin = name === 'margin';

  const onChange = ({ name, value }) => {
    if (!element.match(/\./)) {
      dispatch({
        type: 'params',
        name: element,
        param: name,
        value,
      });
    } else {
      const arr = element.split('.');
      dispatch({
        type: 'bigParams',
        parentName: arr[0],
        name: arr[1],
        param: name,
        value,
      });
    }
  };

  const toggle = () => {
    findPosition(box.current, setBottom);
    setOpen(!open);
  };

  return (
    <ColorBox width="140px">
      <span dangerouslySetInnerHTML={{ __html: title }} />
      <Box width="140px">
        <ButtonChange width="140px" onClick={toggle} active={open}>{open ? 'Закрыть' : 'Изменить'}</ButtonChange>
        <div ref={box} style={{ position: 'relative' }}>
          {open && (
          <AlignBox bottom={bottom} shadow absolute>
            <InputMarginBox>
              <div className="absoluteDescription"> Сверху </div>
              <InputMargin
                type="number"
                onChange={e => onChange({ name: margin ? 'marginTop' : 'paddingTop', value: Number(e.target.value) })}
                width="50%"
                m="0"
                value={margin ? marginTop : paddingTop}
              />
            </InputMarginBox>
            <InputMarginBox>
              <div className="absoluteDescription"> Справа </div>
              <InputMargin
                type="number"
                onChange={e => onChange({ name: margin ? 'marginRight' : 'paddingRight', value: Number(e.target.value) })}
                width="50%"
                m="0"
                value={margin ? marginRight : paddingRight}
              />
            </InputMarginBox>
            <InputMarginBox>
              <div className="absoluteDescription"> Снизу </div>
              <InputMargin
                type="number"
                onChange={e => onChange({ name: margin ? 'marginBottom' : 'paddingBottom', value: Number(e.target.value) })}
                width="50%"
                m="0"
                value={margin ? marginBottom : paddingBottom}
              />
            </InputMarginBox>
            <InputMarginBox>
              <div className="absoluteDescription"> Слева </div>
              <InputMargin
                type="number"
                onChange={e => onChange({ name: margin ? 'marginLeft' : 'paddingLeft', value: Number(e.target.value) })}
                width="50%"
                m="0"
                value={margin ? marginLeft : paddingLeft}
              />
            </InputMarginBox>
          </AlignBox>
          )
        }
        </div>
      </Box>
    </ColorBox>
  );
};

export default Margin;
