import React, { useState, useEffect, useCallback } from 'react';
import { toast } from 'react-toastify';
import { SelectBox } from '../style';
import CopyDay from './copyDay';
import InfoTime from './infoTime';
import SelectedDay from './selectedDay';
import request from '../../../../../utils/request';
import DeleteWarning from '../../../../Elements/Modal/DeleteWarning';

const Record = ({
  onRecord, selectedDay, onDelete, setClear, saveDay, open, setOpen, copyDay, setOpenInfo, openInfo, onClear, queue, selectedQueue, showError, isLoad,
}) => {
  const [time, setTime] = useState([]);
  const [showDel, setShowDel] = useState(false);
  const [sTime, setSTime] = useState(null);
  const [q_id, setQid] = useState(null);
  const list = {};

  selectedDay.data.forEach((el) => {
    const t = el.pastart_on.split(' ')[1];
    const it = list[`${t} ${el.q_id}`];
    if (!it) {
      list[`${t} ${el.q_id}`] = {
        length: 1,
        registered: el.registered,
        qName: queue.find((q) => q.id === el.q_id) ? queue.find((q) => q.id === el.q_id).name : '',
        q_id: el.q_id,
      };
    } else {
      list[`${t} ${el.q_id}`] = {
        q_id: el.q_id,
        qName: queue.find((q) => q.id === el.q_id) ? queue.find((q) => q.id === el.q_id).name : '',
        length: list[`${t} ${el.q_id}`].length + 1,
      };
    }
  });

  const find = useCallback((t, q) => {
    const reg = new RegExp(t);
    const times = selectedDay.data.filter((el) => reg.test(el.pastart_on) && el.q_id === q);
    setTime(times);
  }, [selectedDay.data]);

  const findRecord = (t, q) => {
    setSTime(t);
    setQid(q);
    find(t, q);
    setOpenInfo(true);
  };

  useEffect(() => find(sTime, q_id), [selectedDay, sTime, q_id, find]);

  const showClear = Object.keys(list).some((el) => !list[el].registered);

  const finList = {};

  Object.keys(list)
    .map((el) => ({
      time: el.split(' ')[0], registered: list[el].registered, length: list[el].length, q_id: list[el].q_id, qName: list[el].qName,
    }))
    .sort((a, b) => {
      const hA = +a.time.split(':')[0];
      const hB = +b.time.split(':')[0];
      const mA = +a.time.split(':')[1];
      const mB = +b.time.split(':')[1];
      if (hA === hB) {
        return mA > mB ? 1 : -1;
      }
      return hA > hB ? 1 : -1;
    })
    .forEach((item) => finList[item.q_id] = finList[item.q_id]
      ? [...finList[item.q_id], item]
      : [item]);

  const onSave = async (data, id) => {
    try {
      await request(`/server/api/dpacalendarslot/${id}`, 'update', data);
      toast.success('Выполнено');
    } catch (e) {
      showError(e.data.error);
    }
  };

  return (
    <>
      <SelectBox className="recordBig">
        <div className="titleConstructor">
          <p className="">Список слотов на выбранный день</p>
          {/* <p className="dis" onClick={setIsEdit}>Конструктор шаблона</p> */}
        </div>
        {Object.keys(finList).map((fin, index) => <SelectedDay key={index} selectedQueue={selectedQueue} list={finList[fin]} findRecord={findRecord} copyDay={copyDay} setClear={setClear} showClear={showClear} />)}
      </SelectBox>
      <CopyDay saveDay={saveDay} open={open.open} setOpen={setOpen} />
      <InfoTime
        isLoad={isLoad}
        onRecord={onRecord}
        onSave={onSave}
        onClear={onClear}
        setShowDel={setShowDel}
        onDelete={onDelete}
        time={time}
        open={openInfo}
        setOpen={setOpenInfo}
      />
      <DeleteWarning
        name="Выбранный слот"
        confirmDel={() => {
          onDelete(showDel);
          setShowDel(false);
        }}
        isOpen={showDel}
        toggleOpen={() => setShowDel(false)}
      />
    </>
  );
};

export default Record;
