import React from 'react';
import InputMask from 'react-input-mask';
import { InputGroup, InputGroupModif } from '../../style/style';

const Input = ({
  item, clickInput, id, value, style,
}) => {
  const renderType = (type = 0) => {
    switch (type) {
      case 1: {
        return (
          <InputGroup input={style}>
            {item.name && <label className="phoneLabel">{item.name}</label>}
            <InputMask
              className={`phonenumber ${id === 'phonenumber' && 'activeInput'}`}
              value={value}
              mask="+7 (999) 999 99 99"
              alwaysShowMask
              onClick={() => clickInput(id)}
            />
          </InputGroup>
        );
      }
      case 2: {
        return (
          <InputGroup input={style} lab={!item.name}>
            {item.name && <label>{item.name}</label>}
            <input
              type="text"
              readOnly
              onClick={() => clickInput(id)}
              value={value}
            />
          </InputGroup>
        );
      }
      case 3: {
        return (
          <InputGroupModif input={style}>
            {item.name && <label className="phoneLabel">{item.name}</label>}
            <InputMask
              className={`phonenumber ${id === 'phonenumber' && 'activeInput'}`}
              value={value}
              mask="99.99.9999"
              alwaysShowMask
              onClick={() => clickInput(id)}
            />
          </InputGroupModif>
        );
      }
      default: {
        return (
          <InputGroup name={item.name} input={style}>
            {item.name && <label>{item.name}:</label>}
            <input
              type="number"
              readOnly
              onClick={() => clickInput(id)}
              value={value}
            />
          </InputGroup>
        );
      }
    }
  };
  const type = item.type ? item.type : item.params.value === 'phone' ? 1 : item.params.value === 'text' ? 2 : 0;
  return (
    <div>
      {renderType(type)}
    </div>
  );
};

export default Input;
