import React, { useState } from 'react';
import { SwitchButton, ButtonBox } from '../../../../../styles/ReportMenu';
import { getReportConfig, updateReportConfig } from '../../../../../store/appStore';

const ReportButtons = () => {
  let activeButtonValue;

  const { reportType } = getReportConfig();

  switch (reportType) {
    case 'generalReport':
      activeButtonValue = 1;
      break;
    case 'ticketReport':
      activeButtonValue = 2;
      break;
    case 'operatorReport':
      activeButtonValue = 3;
      break;
    case 'queueReport':
      activeButtonValue = 4;
      break;
    case 'workPlaceReport':
      activeButtonValue = 5;
      break;
    case 'clockwiseReport':
      activeButtonValue = 6;
      break;
    case 'intervalReport':
      activeButtonValue = 7;
      break;
    default:
      break;
  }

  const [activeButton, setActiveButton] = useState(activeButtonValue);

  const ChangeReportType = (type) => {
    updateReportConfig({ reportType: type });
  };

  const setActive = (type) => {
    const active = type;
    setActiveButton(active);
  };

  return (
    <ButtonBox>
      <SwitchButton onClick={() => { ChangeReportType('generalReport'); setActive(1); }} activeStyle={activeButton === 1}>Сводный</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('ticketReport'); setActive(2); }} activeStyle={activeButton === 2}>По талонам</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('operatorReport'); setActive(3); }} activeStyle={activeButton === 3}>По операторам</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('queueReport'); setActive(4); }} activeStyle={activeButton === 4}>По очередям</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('workPlaceReport'); setActive(5); }} activeStyle={activeButton === 5}>По рабочим местам</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('groupReport'); setActive(6); }} activeStyle={activeButton === 6}>Групповой</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('clockwiseReport'); setActive(7); }} activeStyle={activeButton === 7}>Почасовой</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('intervalReport'); setActive(8); }} activeStyle={activeButton === 8}>За интервал</SwitchButton>
      <SwitchButton onClick={() => { ChangeReportType('dynamicsServiceTime'); setActive(9); }} activeStyle={activeButton === 9}>Динамика</SwitchButton>
    </ButtonBox>
  );
};

export default ReportButtons;
