import React, { useState } from 'react';
import request from '../../../../../utils/request';
import { Btn, BtnsBox } from '../../../../../styles/Modal';
import { BtnAdd } from '../../../../../styles/Common';
import Modal from '../../../../Elements/Modal/Modal';
import RenderInputs from './RenderInputs';

const NewService = ({
  fetchServices, toggleOpen, isOpen, setServiceData, serviceData,
  setServiceUpdate, serviceUpdate, initialData, preloader, showError, write,
}) => {
  const [isValid, setIsValid] = useState(false);

  const resetModal = () => {
    toggleOpen();
    setServiceData({ ...initialData });
    setServiceUpdate(false);
  };

  const addService = async () => {
    if (isValid) {
      preloader();
      try {
        await request('/server/api/queue/', 'add', { ...serviceData });
        fetchServices();
      } catch (err) {
        showError(err.data.error);
      }
      preloader();
      resetModal();
    }
  };

  const updateService = async () => {
    if (isValid) {
      const itemId = serviceData.id;
      delete serviceData.id;
      preloader();
      try {
        await request(`/server/api/queue/${itemId}/`, 'update', { ...serviceData });
        fetchServices();
      } catch (err) {
        showError(err.data.error);
      }
      preloader();
      resetModal();
    }
  };

  const handleInput = (e) => {
    const { name, value } = e.target;
    let validData;
    if (name === 'workWeek') {
      validData = { ...serviceData, [name]: { workWeekId: value } };
      setServiceData({ ...validData });
    } else {
      validData = { ...serviceData, [name]: value };
      setServiceData({ ...validData });
    }
    const {
      name: iV, fullname: pV, position: nV, prefix: tV,
    } = validData;
    if ((iV && pV && nV && tV) || serviceUpdate) {
      setIsValid(true);
    } else {
      setIsValid(false);
    }
  };

  return (
    <React.Fragment>
      {write && (
        <BtnAdd onClick={toggleOpen} add>
          <span>Добавить</span>
        </BtnAdd>
      )}
      <Modal title={serviceUpdate ? 'Редактирование услуги' : 'Добавление Услуги'} toggleOpen={resetModal} isOpen={isOpen}>
        <RenderInputs setServiceData={setServiceData} serviceUpdate={serviceUpdate} handleInput={handleInput} serviceData={serviceData} preloader={preloader} />
        {write && (
        <BtnsBox>
          <Btn
            margin="0 25px 0 0"
            valid={isValid}
            onClick={serviceUpdate ? updateService : addService}
          >
            Сохранить
          </Btn>
          <Btn onClick={resetModal} cancel>Отменить</Btn>
        </BtnsBox>
        )}
      </Modal>
    </React.Fragment>
  );
};

export default React.memo(NewService);
