import React, { useState, useEffect, useCallback } from 'react';
import moment from 'moment';
import { FaCheckCircle, FaTimesCircle } from 'react-icons/fa';
import { Title } from '../../../styles/Common';
import {
  LicenceBlock, LicenceBox, LicenceInfo, LicenceDate, LicenceItem, LicenceKey, LicenceValue, LicenceInfoBlock,
} from '../../../styles/About';
import Request from '../../../utils/request';
import Preloader from '../../Elements/Preloader/Preloader';
import LicenceUpdate from './LicenceUpdate';


const LicenceComponent = ({ preloader, showError }) => {
  const [licence, setLicence] = useState(null);

  const getLicence = useCallback(async () => {
    const licenceReq = await Request('/server/api/licence/');
    const { data: { data: licData } } = licenceReq;
    const currentLic = licData.find(lic => (lic.active));
    setLicence(currentLic);
  }, []);

  useEffect(() => {
    getLicence();
  }, [getLicence]);

  return (
    <LicenceBlock>
      <Preloader isOpen={false} small={false} />
      <Title>Лицензия</Title>

      <LicenceBox>
        {licence ? (
          <React.Fragment>
            <LicenceInfo>
              <LicenceDate>
                <div>Лицензия действительна {(!licence.validfrom && !licence.validto) && '(ДЕМО ЛИЦЕНЗИЯ)'}</div>
                <div>{(licence.validfrom) ? moment(licence.validfrom).format('DD.MM.YYYY') : '01.01.2019'} - {(licence.validto) ? moment(licence.validto).format('DD.MM.YYYY') : '31.12.2119'}</div>
              </LicenceDate>
              <LicenceUpdate preloader={preloader} showError={showError} />
              <LicenceInfoBlock>
                <div>
                  <h3>Параметры</h3>
                  {Object.keys(licence.licdescriptionjsn).filter(l => typeof licence.licdescriptionjsn[l] !== 'boolean').map(k => (
                    <LicenceItem key={k}>
                      <LicenceKey>{k}</LicenceKey>
                      <LicenceValue>{licence.licdescriptionjsn[k]}</LicenceValue>
                    </LicenceItem>
                  ))}
                </div>
                <div>
                  <h3>Модули</h3>
                  {Object.keys(licence.licdescriptionjsn).filter(l => typeof licence.licdescriptionjsn[l] === 'boolean').map(k => (
                    <LicenceItem key={k}>
                      <LicenceKey>{k}</LicenceKey>
                      <LicenceValue>{(licence.licdescriptionjsn[k]) ? <FaCheckCircle /> : <FaTimesCircle />}</LicenceValue>
                    </LicenceItem>
                  ))}
                </div>

              </LicenceInfoBlock>
            </LicenceInfo>
          </React.Fragment>
        ) : (
          <React.Fragment>
            Получение информации о лицензии...
          </React.Fragment>
        )}
      </LicenceBox>
    </LicenceBlock>
  );
};
export default LicenceComponent;
