import React, { useState, useEffect, useCallback } from 'react';
import request from '../../../../utils/request';
import InputSelect from '../../../Elements/Input/SelectInput/SelectInput';

import { BtnTable, ExtraField } from '../../../../styles/Common';
import { ReactComponent as Trash } from '../../../../images/del.svg';

const RenderCheckList = ({
  zoneData, setZoneData, preloader,
}) => {
  const [bookList, setBookList] = useState([]);
  const { params } = zoneData.config;

  const fetchBooks = useCallback(async () => {
    preloader();
    try {
      const books = await request('/server/api/dictionary/', 'get');
      const fields = await request('/server/api/ticketsfields/', 'get');
      const bookSelect = [];
      fields.data.data.forEach((field) => {
        books.data.data.forEach((book) => {
          if (field.type === 'select' && book.id === field.dict_id) {
            bookSelect.push(book);
          }
        });
      });
      setBookList(bookSelect);
    } catch (err) {
      console.log('err', err);
    }
    preloader();
  }, [preloader]);

  useEffect(() => {
    fetchBooks();
  }, [fetchBooks]);

  // const items = [
  //   {
  //     label: 'Выберите стандартное значение:', name: 'config', options: bookList, checked: checkParams,
  //   },
  // ];

  const options = bookList.map((el) => ({ id: el.id, name: el.name }));

  const onChange = ({ target }, name) => {
    if (name === 'fv') {
      params[target.name][name] = [target.value];
    } else {
      params[target.name][name] = target.value;
    }
    const data = { ...zoneData, config: { ...zoneData.config, params } };
    setZoneData(data);
  };

  const deleteItem = (index) => {
    params.splice(index, 1);
    setZoneData({ ...zoneData, config: { ...zoneData.config, params } });
  };

  const getSelect = (fid, fv, index) => {
    const ops = [];
    bookList.forEach((book) => book.id === fid && book.values.forEach((el) => ops.push({ id: el.id, name: el.name })));
    return (
      <>
        <p className="mt1">Значение</p>
        <InputSelect
          name={index}
          value={fv[0]}
          noDefault
          onChange={(value) => onChange(value, 'fv')}
          options={ops}
        />
      </>
    );
  };
  /* <CheckZones */
  /*  item={item} */
  /*  data={zoneData} */
  /*  setData={setZoneData} */
  /*  params={checkParams} */
  /* /> */

  return (
    params.map((item, index) => (
      <ExtraField key={index}>
        <p>Дополнительное поле</p>
        <InputSelect name={index} value={item.fid} noDefault onChange={(value) => onChange(value, 'fid')} options={options} />
        {item.fid && getSelect(item.fid, item.fv, index)}
        <BtnTable m="15px 0 0 0" onClick={() => deleteItem(index)} active><Trash /></BtnTable>
      </ExtraField>
    ))
  );
};
export default RenderCheckList;
