import React, { useEffect, useState } from 'react';
import useReactRouter from 'use-react-router/use-react-router';
import { HeaderLogoBox, HeaderLogo } from '../../../styles/HeaderStyles';
import logo from '../../../images/logo.png';
import {
  NavLinkBox, NavLink, SideBox, SideItembox, NavElems,
} from '../../../styles/NavMenu';
// import v from '../../../../package.json';
import navItemsList from './NavMenu/navItemsList';
import getLinks from '../../../utils/getLinks';

const SideNavigation = ({ setClick }) => {
  const [path, setPath] = useState('');
  // const [showVersion, setShowVersion] = useState(false);
  // const [isHover, setIsHover] = useState(false);
  const { location, history } = useReactRouter();

  useEffect(() => {
    setPath(location.pathname);
  }, [location.pathname]);

  // const showMenu = (menu) => {
  //   setIsHover(menu);
  // };

  const render = () => {
    const arrLink = getLinks();
    const arrSide = navItemsList.map((item) => {
      if (arrLink.every((el) => el !== item.link)) return null;
      const activeMenu = item.subItems.some((el) => el.link === path || path.indexOf(el.link) > -1) || item.link === path;
      return (
        <div
          key={item.text}
          // onMouseEnter={() => showMenu(item.text)}
          // onMouseLeave={() => showMenu(null)}
        >
          <NavLinkBox key={item.text} activeMenu={activeMenu}>
            <NavLink to={item.link} onClick={setClick}>
              <SideItembox>
                {item.icon}
                <span>{item.text}</span>
              </SideItembox>
            </NavLink>
          </NavLinkBox>
        </div>
      );
    });
    return arrSide;
  };
  return (
    <SideBox>
      <HeaderLogoBox>
        <HeaderLogo onClick={() => { history.push('/monitoring/'); }} src={logo} />
        {/* {showVersion && <VarsionBlock>{v.version}</VarsionBlock>} */}
      </HeaderLogoBox>
      <NavElems>
        {render()}
      </NavElems>
    </SideBox>
  );
};

export default SideNavigation;
