import React from 'react';
import { ReactComponent as Edit } from '../../../../../images/edit.svg';
import c from '../../../../../styles/Colors';
import sortCaret from '../../../../../utils/sortCaret';

function priceFormatter(column, colIndex, { filterElement, sortElement }, sort) {
  return (
    <div className={`filterBoxTable ${sort ? 'sort' : null}`}>
      {column.dataField === 'created_on'
      || column.dataField === 'answerdata[6]'
      || column.dataField === 'answerdata[7]'
      || column.dataField === 'answerdata[8]'
      || column.dataField === 'answerdata[9]'
      || column.dataField === 'answerdata[10]'
      || column.dataField === 'answerdata[qround]'
      || column.dataField === 'number'
      || column.dataField === 'fio' ? (null) : (<Edit style={{margin: '2px 10px 0 0'}}  fill={c.main} />) }
      { filterElement || column.text }
      { sortElement }
    </div>
  );
}

const hcolFormatter = column => <div style={{ fontSize: 12, textAlign: 'center' }}>{column.text}</div>;

export default function (list) {
  const colObjects = [];
  list.forEach((li) => {
    colObjects.push({
      dataField: `answerdata[${li[0]}]`,
      text: `${li[1]}`,
      sort: false,
      editable: false,
      headerFormatter: hcolFormatter,
      headerStyle: {
        width: 220,
      },
      formatter: cell => <div style={{ textAlign: 'center' }}>{cell || '—'}</div>,
    });
  });


  return {
    columns: [{
      dataField: 'created_on',
      text: 'Дата оценки',
      sort: false,
      editable: false,
      headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      headerStyle: {
        width: 203,
      },
    },
    ...colObjects,
    {
      dataField: 'answerdata[qround]',
      text: 'Средняя оценка',
      sort: false,
      editable: false,
      headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      headerStyle: {
        width: 190,
      },
    },
    {
      dataField: 'number',
      text: 'Номер талона',
      sortCaret,
      editable: false,
      sort: false,
      headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      headerStyle: {
        width: 190,
      },
    },
    {
      dataField: 'fio',
      text: 'ФИО сотрудника (который обслуживал клиента)',
      sortCaret,
      editable: false,
      sort: false,
      headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      headerStyle: {
        width: 230,
      },
    }],
  };
}
