import React from 'react';
import SelectInput from '../../../../../Elements/Input/SelectInput/SelectInput';
import { ColorBox } from '../style';

const font = ({
  value, dispatch, element, title, options, nameParams = 'fontFamily',
}) => {
  const big = nameParams.match(/\./);
  const onChange = (e) => {
    if (!big) {
      dispatch({
        type: 'params',
        name: element,
        param: nameParams,
        value: String(e.target.value),
      });
    } else {
      const arr = nameParams.split('.');
      dispatch({
        type: 'bigParams',
        parentName: element,
        name: arr[0],
        param: arr[1],
        value: String(e.target.value),
      });
    }
  };
  return (
    <ColorBox>
      <span dangerouslySetInnerHTML={{ __html: title }} />
      <SelectInput
        onChange={onChange}
        noDefault
        options={options}
        value={value}
      />
    </ColorBox>
  );
};

export default font;
