import React from 'react';
import moment from 'moment';
import c from '../styles/Colors';
import { BtnTable } from '../styles/Common';
import { ReactComponent as Edit } from '../images/edit.svg';
import { ReactComponent as False } from '../images/false.svg';
import { ReactComponent as True } from '../images/true.svg';

const dateFormat = (cell) => moment(cell, 'YYYY.MM.DD HH:mm:ss').format('DD.MM.YYYY HH:mm:ss');

function placeholder(column, colIndex, { filterElement }) {
  return (
    <div className="filterBoxTable">
      {column.dataField !== 'Settings' && <Edit style={{ margin: '2px 10px 0 0' }} fill={c.main} /> }
      { filterElement }
    </div>
  );
}

function checkFormatter(value) {
  return (
    <>
      {value.toString() === 'true'
        ? <BtnTable active><True /></BtnTable>
        : <BtnTable><False /></BtnTable>}
    </>
  );
}

export { dateFormat, placeholder, checkFormatter };
