import React, { useState, useEffect } from 'react';
import { FaCog, FaCheckDouble } from 'react-icons/fa';
import Styled from 'styled-components';
import {
  CheckLabel, List, LabelText, CheckBox, InputCheck, Check, BtnAdd, ButtonBox,
} from '../../../styles/Common';
import { useToggle } from '../../../hooks/useToggle';
import getLinks from '../../../utils/getLinks';

const Text = Styled.div`
  display: inline-block;
  margin-right: 10px;
  color: ${(p) => p.theme.text};
`;

const HeadBox = Styled.div`
  display: flex;
  margin-bottom: 15px;
  align-items: center;
  justify-content: space-between;
`;

const CheckBoxList = ({
  item, data, setData, only,
}) => {
  const { name, options, checked } = item;
  const [check, setCheck] = useState([...checked]);
  const [openList, setOpenList] = useToggle(true);
  const write = getLinks(true);

  const showList = () => {
    setOpenList();
  };

  useEffect(() => {
    setCheck([...checked]);
  }, [checked]);


  const handleCheckOnly = ({ target: { value } }) => {
    const val = Number(value);
    const checkArr = [...check];

    const checkIndex = checkArr.indexOf(val) >= 0;

    if (!checkIndex) {
      setData({ ...data, [name]: val });
      setCheck([val]);
    }
  };

  const handleCheck = (e) => {
    const numValue = Number(e.target.value);
    let checkArr = [];
    let newValues = [];
    checkArr = [...check];

    const checkIndex = checkArr.indexOf(numValue);
    if (checkIndex === -1) {
      newValues = [...checkArr, numValue];

      setCheck([...newValues]);

      setData({ ...data, [name]: newValues });
    } else {
      checkArr.splice(checkIndex, 1);
      setData({ ...data, [name]: checkArr });
      setCheck([...checkArr]);
    }
  };

  const checkAll = () => {
    const allOptions = options.map((optionItem) => optionItem.id);
    if (check.length === allOptions.length) {
      setCheck([]);
      setData({ ...data, [name]: [] });
    } else {
      setCheck([...allOptions]);
      setData({ ...data, [name]: allOptions });
    }
  };

  return (
    <>
      <HeadBox>
        <Text>{item.label}</Text>
        <ButtonBox>
          <BtnAdd onClick={showList} show add>
            <FaCog size="16px" />
            <span>{openList ? 'Закрыть' : 'Раскрыть'}</span>
          </BtnAdd>
          {write && openList && !only ? (
            <BtnAdd onClick={checkAll} width={150} show add>
              <FaCheckDouble size="16px" />
              <span>Отметить все</span>
            </BtnAdd>
          ) : null}
        </ButtonBox>
      </HeadBox>
      {openList && (
        <>
          <List>
            {options && options.map((option) => (
              <CheckLabel key={option.id}>
                <LabelText>{option.printname || option.name}</LabelText>
                <CheckBox>
                  <InputCheck
                    type="checkbox"
                    value={option.id}
                    name="connectedQueues"
                    onChange={write ? only ? handleCheckOnly : handleCheck : null}
                    checked={check.some((que) => Number(que) === option.id)}
                  />
                  <Check />
                </CheckBox>
              </CheckLabel>
            ))}
          </List>
        </>
      )}
    </>
  );
};

export default CheckBoxList;
