import React, { useState, useEffect } from 'react';
import CheckBoxList from '../../../Elements/Input/CheckBoxList';


const RenderCheckList = ({
  placeData, setPlaceData, queue, setIsValid,
}) => {
  const { connectedQueues } = placeData;
  const [checkedQueue, setCheckedQueue] = useState([]);

  const handleValidCheckList = (newPlaceData) => {
    setIsValid(true, newPlaceData);
    setPlaceData(newPlaceData);
  };

  useEffect(() => {
    setCheckedQueue([...connectedQueues]);
  }, [placeData, connectedQueues]);

  // useEffect(() => {}, [placeData]);

  const items = [
    {
      label: 'Услуги:', name: 'connectedQueues', options: queue, checked: checkedQueue,
    },
  ];

  return (
    items.map((item) => (
      <div style={{ marginTop: 30 }} key={item.name}>
        <CheckBoxList
          item={item}
          data={placeData}
          setData={handleValidCheckList}
        />
      </div>
    ))
  );
};
export default RenderCheckList;
