import React from 'react';
import { FaSync } from 'react-icons/fa';
import Modal from '../../../../Elements/Modal/Modal';
import { ReactComponent as Rec } from '../../../../../images/chekbox.svg';
import { ReactComponent as Trash } from '../../../../../images/bin.svg';
import Time from './time';
import Preloader from '../../../../Elements/Preloader/Preloader';

const Name = ({
  open, time, setOpen, onRecord, onClear, onSave, isLoad, setShowDel,
}) => (
  <Modal title="Информация по дню" isOpen={open} toggleOpen={() => setOpen(false)}>
    {!isLoad ? (
      <ul className="recordBox infoTimeBox">
        {time.map((el, index) => (
          <li key={index} className="recordItem">
            <Time onSave={onSave} el={el} />
            <div>
              <p className="time">{el.pastart_on.split(' ')[1]}</p>
              <div className="infoBtn">
                {!el.registered ? (
                  <>
                    <span onClick={() => onRecord(el.calendarCellId)}>
                      <Rec />
                    </span>
                    <span className="delete" onClick={() => setShowDel(el.calendarCellId)}>
                      <Trash />
                    </span>
                  </>
                )
                  : (
                    <span className="delete" onClick={() => onClear(el.calendarCellId)}>
                      <FaSync />
                    </span>
                  )}
              </div>
            </div>
          </li>
        ))}
      </ul>
    )
      : <Preloader isOpen={isLoad} small local />}
  </Modal>
);

export default Name;
