import React, { useState, useEffect, useCallback } from 'react';
import request from '../../../../utils/request';
import Input from '../../../Elements/Input/Input';
import RenderCheckList from './RenderCheckList';
import { InputDopBox } from '../../../../styles/Common';

const RenderInputs = ({
  placeData, setPlaceData, preloader, setIsValid, placeUpdate, zones,
}) => {
  const [queue, setQueue] = useState([]);
  const fetchQueue = useCallback(async () => {
    preloader();
    const queueList = await request('/server/api/queue/', 'get');
    setQueue([...queueList.data.data]);
    preloader();
  }, [preloader]);

  useEffect(() => {
    fetchQueue();
  }, [fetchQueue]);

  const handleInput = (e) => {
    const { name, value } = e.target;
    let validData = {};
    if (name === 'connectedZones') {
      if (value === '') {
        validData = { ...placeData, [name]: [] };
      } else {
        validData = { ...placeData, [name]: [Number(value)] };
      }
    } else {
      validData = { ...placeData, [name]: value };
    }
    const { name: iV, placename: pV, connectedZones: zV } = validData;
    setPlaceData({ ...validData });
    if (iV && pV && zV.length) {
      setIsValid(true, validData);
    } else {
      setIsValid(false, validData);
    }
  };

  const handleComplexPlace = (e) => {
    const { name, value } = e.target;
    const { placename: currentPlace } = placeData;
    let validData = {};
    const wnums = currentPlace.replace(/\D+/ig, ',').replace(/^,/, '').split(',');
    wnums[name.replace(/\D+/ig, '')] = value;

    console.log('wnums === ', wnums);

    const placenameData = wnums.filter(Boolean).join(',');
    console.log('placenameData === ', placenameData);
    validData = { ...placeData, placename: placenameData };
    const { name: iV, placename: pV, connectedZones: zV } = validData;
    console.log('pV === ', pV);
    setPlaceData({ ...validData });
    if (iV && pV && zV.length) {
      setIsValid(true, validData);
    } else {
      setIsValid(false, validData);
    }
  };

  const zonesOptions = (
    zones && zones.map((item) => ({ id: item.id, name: item.name }))
  );

  const {
    connectedZones,
    description,
    disabled,
    name,
    placename,
  } = placeData;

  const wnums = placename.replace(/\D+/ig, ',').replace(/^,/, '').split(',');

  const items = [
    {
      label: 'Наименование:', name: 'name', value: name || '', required: true,
    },
    {
      label: 'Параметр WNUM0:',
      name: 'wnum0',
      type: 'number',
      value: Number(wnums[0]),
      required: false,
      className: 'small',
      extraHandler: handleComplexPlace,
    },
    {
      label: 'Параметр WNUM1:',
      name: 'wnum1',
      type: 'number',
      value: Number(wnums[1]),
      required: false,
      className: 'small',
      extraHandler: handleComplexPlace,
    },
    {
      label: 'Параметр WNUM2:',
      name: 'wnum2',
      type: 'number',
      value: Number(wnums[2]),
      required: false,
      className: 'small',
      extraHandler: handleComplexPlace,
    },
    { label: 'Описание:', name: 'description', value: description || '' },
    {
      label: 'Подключенная зона:', noDefault: true, name: 'connectedZones', type: 'select', value: connectedZones[0] || undefined, options: zonesOptions, required: true,
    },
    {
      label: 'Отключен:', name: 'disabled', type: 'checkbox', value: disabled || false,
    },
  ];
  console.log('Number(wnums[1]) === ', Number(wnums[1]));
  console.log('items === ', items);

  return (
    <>
      <InputDopBox>
        {items.map((item) => (
          <Input
            m="0 0 10px"
            key={item.name}
            className={item.className}
            item={item}
            onChange={item.extraHandler || handleInput}
          />
        ))}
      </InputDopBox>
      <RenderCheckList
        setPlaceData={setPlaceData}
        setIsValid={setIsValid}
        queue={queue}
        placeData={placeData}
        placeUpdate={placeUpdate}
      />
    </>

  );
};
export default RenderInputs;
