import React from 'react';
import Timer from './Timer';
import News from './News';
import Video from './Video';
import Schedule from './Schedule';

const VideoNewsSchedule = ({
  inSoundAction, styles, text, handleFz, elWidth, setActiveEl,
}) => {
  const orderBox = styles.video.order === 1 ? 0 : 1;
  const orderVideo = styles.video.order === 1 ? 1 : 0;
  const orderSchedule = styles.news.order === 1 ? 1 : 0;
  return (
    <div className="boardVideoNewsScheduleBox">
      {(styles.tidings || styles.date) && (
      <div className="boardNewScheduleBox" style={{ order: orderBox, flexDirection: styles.news.flexDirection ? 'column' : 'row' }}>
        {styles.tidings && <News layout={styles} handleFz={handleFz} text={text} elWidth={elWidth} setActiveEl={setActiveEl} />}
        {styles.date && (
        <div
          className="boardTimerScheduleBox"
          style={{
            ...styles.time,
            flexDirection: styles.time.flexDirection ? 'column' : 'row',
            minHeight: styles.time.height2,
            maxHeight: styles.time.height2 === 'auto' ? '100%' : styles.time.height2,
            height: styles.time.height2,
            background: styles.time.background,
            borderRadius: styles.borderRadius ? '12px' : 0,
            border: `${styles.time.borderSize1}px solid ${styles.time.color3}`,
            order: orderSchedule,
          }}
        >
          <Timer styles={styles.time} handleFz={handleFz} elWidth={elWidth} setActiveEl={setActiveEl} />
          <Schedule styles={styles.time} direction={styles.time.flexDirection} handleFz={handleFz} text={text.time1} elWidth={elWidth} setActiveEl={setActiveEl} />
        </div>
        )}
      </div>
      )}
      {styles.vid && <Video order={orderVideo} inSoundAction={inSoundAction} layout={styles} text={text.video1} handleFz={handleFz} elWidth={elWidth} setActiveEl={setActiveEl} />}
    </div>
  );
};

export default VideoNewsSchedule;
