import React, { useState, useEffect, useCallback } from 'react';
import moment from 'moment';
import { FaSave } from 'react-icons/fa';
import request from '../../../utils/request';
import {
  Title, ContentBox, BtnAbsolute, PreloaderBox,
} from '../../../styles/Common';
import {
  HeaderAD, BodyAD, BodyAdColumn, BodyADLog, ReportBox, CustomDiv,
} from '../../../styles/ActiveDirectory';
import Preloader from '../../Elements/Preloader/Preloader';
import Input from '../../Elements/Input/Input';

const ActiveDirectory = ({ showError, preloader, edit }) => {
  const [dataList, setDataList] = useState(null);
  const [isOn, setIsOn] = useState(false);
  const [settings, setSettings] = useState(null);
  const [sync, setSync] = useState(null);
  const [newData, setNewData] = useState(null);

  const handleSettings = (data) => {
    const settingsArr = [
      { label: 'Наименование', name: 'name', value: data.name },
      { label: 'IP адрес или имя сервера AD', name: 'dc_serverfqdn', value: data.dc_serverfqdn },
      { label: 'FQDN домена', name: 'dc_fqdn', value: data.dc_fqdn },
      { label: 'Логин синхронизации с AD:', name: 'dc_username', value: data.dc_username },
      {
        label: 'Пароль:', name: 'dc_passwd', type: 'password', value: data.dc_passwd,
      },
      { label: 'Интервал синхронизации(сек):', name: 'timeout_sync', value: data.timeout_sync },
    ];
    setSettings(settingsArr);
  };

  const handleSync = (data) => {
    const syncArr = [
      { label: 'Администратор:', name: 'eq_root', value: data.eq_root },
      { label: 'Суперчитатель:', name: 'eq_manager', value: data.eq_manager },
      { label: 'Диспетчер:', name: 'eq_controller', value: data.eq_controller },
      { label: 'Оператор:', name: 'eq_operator', value: data.eq_operator },
      { label: 'Читатель отчетов:', name: 'eq_reporter', value: data.eq_reporter },
    ];
    setSync(syncArr);
  };

  const fetchData = useCallback(async () => {
    const res = await request('/server/api/activedirectory/', 'get');
    const { data, data: { enabled, dc_customrolesjson } } = res.data;
    setDataList(data);
    handleSettings(data);
    handleSync(dc_customrolesjson);
    setIsOn(enabled);
  }, []);

  const handleCheck = () => {
    setIsOn(!isOn);
    const newDataList = { ...dataList, enabled: !isOn };
    setNewData(newDataList);
  };

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const handleInput = (e) => {
    const { name, value } = e.target;
    let newDataList = {};
    if (name.indexOf('eq_') !== -1) {
      const syncData = { ...dataList.dc_customrolesjson, [name]: value };
      newDataList = { ...dataList, dc_customrolesjson: { ...syncData } };
      handleSync(syncData);
    } else {
      newDataList = { ...dataList, [name]: value };
      handleSettings(newDataList);
    }
    newDataList.enabled = isOn;
    setNewData(newDataList);
  };

  const isOnCheck = {
    label: 'Включена:', name: 'disabled', type: 'checkbox', value: isOn,
  };

  const handleSave = async () => {
    if (newData) {
      preloader();
      await request('/server/api/activedirectory/', 'update', newData);
      preloader();
    } else {
      showError('Изменений не было');
    }
  };

  return (
    <>
      <Title>НАСТРОЙКИ СИНХРОНИЗАЦИИ С ACTIVE DIRECTORY</Title>
      <ContentBox>
        <PreloaderBox>
          {dataList === null || settings === null || sync === null
            ? <Preloader isOpen small />
            : (
              <>
                <HeaderAD>
                  <BtnAbsolute left="0px" width={150} add show onClick={handleSave}>
                    <FaSave />
                    <span>Сохранить</span>
                  </BtnAbsolute>
                  <CustomDiv>
                    Время последней синхронизации:
                    <br />
                    {moment(dataList.last_sync, 'YYYY.MM.DD HH:mm:ss').format('DD.MM.YYYY - HH:mm:ss')}
                  </CustomDiv>
                  <Input m="0" item={isOnCheck} onChange={handleCheck} />
                </HeaderAD>
                <ReportBox>
                  <BodyAD>
                    <BodyAdColumn>
                      <p>Настройки</p>
                      {settings.map((item) => <Input key={item.name} item={item} type={item.type || 'text'} onChange={handleInput} edit={edit} />)}
                    </BodyAdColumn>
                    <BodyAdColumn>
                      <p>Настройки синхронизации с Active Directory</p>
                      {sync.map((item) => <Input key={item.name} item={item} onChange={handleInput} edit={edit} />)}
                    </BodyAdColumn>
                  </BodyAD>
                </ReportBox>
                <ReportBox next>
                  <BodyADLog>
                    <BodyAD>
                      <CustomDiv>
                        Создан: <CustomDiv>{dataList.created_by_username}</CustomDiv>
                      </CustomDiv>
                      <CustomDiv>
                        Дата создания: <CustomDiv>{moment(dataList.created_on, 'YYYY.MM.DD HH:mm:ss').format('DD.MM.YYYY - HH:mm:ss')}</CustomDiv>
                      </CustomDiv>
                      <CustomDiv>
                        Изменен: <CustomDiv>{dataList.modified_by_username}</CustomDiv>
                      </CustomDiv>
                      <CustomDiv>
                        Когда изменен: <CustomDiv>{moment(dataList.modified_on, 'YYYY.MM.DD HH:mm:ss').format('DD.MM.YYYY - HH:mm:ss')}</CustomDiv>
                      </CustomDiv>
                    </BodyAD>
                    <BodyAD mt>
                      <div style={{ width: '100%' }}>
                        Результат синхронизации:
                        <textarea name="" id="" cols="30" rows="10" value={dataList.last_sync_message} readOnly style={{ resize: 'none', width: '100%' }} />
                      </div>
                    </BodyAD>
                  </BodyADLog>
                </ReportBox>
              </>
            )}
        </PreloaderBox>
      </ContentBox>
    </>
  );
};
export default ActiveDirectory;
