import React from 'react';
import getLinks from '../../../utils/getLinks';
import { Btn, BtnsBox } from '../../../styles/Modal';

const ModalButtons = ({
  isValid, update, updateFunc, addFunc, reset, notCancel, isWrite
}) => {
  const write = isWrite || getLinks(true);
  if (write) {
    return (
      <BtnsBox>
        <Btn
          margin="0 25px 0 0"
          valid={isValid}
          onClick={isValid ? update ? updateFunc : addFunc : null}
        >
          Сохранить
        </Btn>
        {!notCancel && <Btn onClick={reset} cancel>Отменить</Btn>}
      </BtnsBox>
    );
  }
  return null;
};

export default React.memo(ModalButtons);
