import React from 'react';
import Color from './modules/color';

const Colors = ({ arr, dispatch, width }) => (
  <div className="elementBox">
    <p>Цвет</p>
    { arr.map((el, index) => (
      <Color
        width={width}
        element={el.parentName}
        value={el.value}
        dispatch={dispatch}
        rusName={el.title}
        paramName={el.paramName}
        key={index}
      />
    ))}
  </div>
);

export default Colors;
