import React, { useState, useEffect } from 'react';
import { FaRedo, FaSave } from 'react-icons/fa';
import {
  FieldBox,Text, BtnReset, BtnSave, AbsoluteBox,
} from '../../../../styles/ServiceList';
import SelectInput from '../../../Elements/Input/SelectInput/SelectInput';
import { Input } from '../../../../styles/Common';
import MultiSelect from '../../../Elements/MultiSelect/MultiSelect';


const Field = ({
  param, onSave, resetParam, write, multiData,
}) => {
  const [value, setValue] = useState(null);
  useEffect(() => {
    setValue(param.value);
  }, [param]);
  const reg = /\.|,/g;
  const regNumber = /\d/;

  const options = [
    { id: 0, name: 'Нет' },
    { id: 1, name: 'Да' },
  ];

  const handleMultiSelect = (newVal) => {
    const newParsed = newVal.map((item) => {
      const newItem = item.value;
      return newItem;
    });
    const joined = newParsed.join(' ');
    onSave(joined, param.id, param.param_type);
  };

  return (
    <FieldBox>
      <AbsoluteBox m={param.param_type === 'boolean'}>
        {write && param.reset && <BtnReset onClick={() => resetParam(param.id)}><FaRedo /></BtnReset>}
        {write && param.value !== value && <BtnSave m={param.param_type === 'boolean'} onClick={() => onSave(value, param.id, param.param_type)}><FaSave /></BtnSave>}
      </AbsoluteBox>
      <Text>{param.description}</Text>
      {param.param_type === 'soundstring' && value !== null
        ? <MultiSelect data={multiData} value={param.value} onSave={handleMultiSelect} />
        // <SelectRange plate top value={value.length > 0 ? value.split(' ') : []} onChange={val => setValue(val)} />
        : param.param_type === 'boolean'
          ? <SelectInput disabled={!write} options={options} noDefault value={Number(param.value)} onChange={e => onSave(e.target.value, param.id)} />
          : (
            <Input
              type="text"
              disabled={!write}
              step="1"
              min="0"
              value={value || ''}
              onKeyDown={e => e.key === 'Backspace' ? true : (param.param_type === 'number' && (reg.test(e.key) || !regNumber.test(e.key))) && e.preventDefault()}
              // onKeyDown={e => console.log(e.key !== 'Backspace', param.param_type === 'number', reg.test(e.key), !regNumber.test(e.key))}
              onChange={e => setValue(param.param_type === 'number' ? +e.target.value : e.target.value)}
            />
          )
      }
    </FieldBox>
  );
};
export default Field;
