import React, { useState } from 'react';
import { BtnAdd, LoginOperatorBox } from '../../styles/Common';
import SelectInput from '../Elements/Input/SelectInput/SelectInput';

const LoginPanel = (props) => {
  const { wplaces, rmoLogin } = props;

  const [selectedWplace, setSelectedWplace] = useState(null);

  const login = () => {
    rmoLogin(selectedWplace);
  };

  const options = wplaces.map(wp => ({ id: wp, name: wp }));
  const onChange = ({ target }) => setSelectedWplace(target.value);

  return (
    <LoginOperatorBox>
      <SelectInput m="0 30px 0 0" options={options} onChange={onChange} value={selectedWplace} name="login" multiple={false} />
      {/* <Select onChange={e => setSelectedWplace(e.target.value)}> */}
      {/* <option>Не выбрано</option> */}
      {/* {wplaces.map(wplace => <option key={wplace}>{wplace}</option>)} */}
      {/* </Select> */}
      <BtnAdd onClick={login} login show>
        Вход
      </BtnAdd>
    </LoginOperatorBox>
  );
};
export default LoginPanel;
