import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { BoxButtonContent, ButtonContent } from '../../style/style';
import changeStyle from '../../utils/changeStyle';
import history from '../../../../../../../../utils/customHistory';
import { setTicket } from '../../utils/actions';


const Buttons = ({
  buttons = [], style, isEdit, isStart,
}) => {
  const { headUrl, id } = useSelector((state) => state.config);
  const dispatch = useDispatch();

  const renderButtons = () => buttons.map((item) => {
    const styleButton = { ...changeStyle({ style, item }), ...style.bottom };
    const { value } = item.params;
    styleButton.textShadow = 'none';
    const { iconPosition, contentPosition } = styleButton;
    const fontSizeExtra = styleButton.fontSize / 2;
    const { iconTop } = styleButton;
    const text = item.name.replace(/\\\\/g, '</br>');
    const extraText = item.extraText ? item.extraText.replace(/\\\\/g, '</br>') : undefined;

    const onClick = () => {
      if (isEdit) return;
      if (isStart) {
        console.log(1);
        history.push(`${headUrl}/${id}/print`);
      } else if (value !== 0) {
        console.log(2);
        dispatch(setTicket({ name: 'q_id', value }));
        history.push(item.path);
      } else {
        console.log(3);
        history.push(item.path);
      }
    };

    return (
      <div
        onDragStart={() => false}
        onClick={onClick}
        style={styleButton}
      >
        <ButtonContent heightIcon={styleButton.heightIcon} img={item.icon} iconPosition={iconPosition} contentPosition={contentPosition} iconTop={iconTop}>
          {item.icon && (
          <img
            style={{ ...styleButton.icon }}
            src={item.icon && item.icon.replace(/url\(|"|\)/g, '')}
            alt=""
          />
          )}
          <BoxButtonContent img extraText={item.extraText}>
            <p
              style={{ ...styleButton.text, textAlign: styleButton.textAlign }}
              dangerouslySetInnerHTML={{ __html: text }}
            />
            {item.extraText && <span style={{ fontSize: fontSizeExtra, textAlign: styleButton.textAlign }} dangerouslySetInnerHTML={{ __html: extraText }} />}
          </BoxButtonContent>
        </ButtonContent>
      </div>
    );
  });

  const cStyle = {
    display: 'flex', alignItems: 'center', justifyContent: 'center', flexDirection: 'column',
  };

  return (
    <div style={cStyle}>
      {renderButtons()}
    </div>
  );
};

export default Buttons;
