import React from 'react';
import { AppealBox } from '../../../../../styles/Modal';

const ShowText = ({ item }) => {
  if (typeof item === 'string') return item;
  const data = JSON.parse(item);
  return (
    <AppealBox>
      <div className="item">
        <p>Филиал</p>
        <p>{data.branch}</p>
      </div>
      <div className="item">
        <p>Отделение</p>
        <p>{data.department}</p>
      </div>
      <div className="item">
        <p>Тема обслуживания</p>
        <p>{data.theme}</p>
      </div>
      <div className="item">
        <p>Лицевой счет</p>
        <p>{data.ls}</p>
      </div>
      <div className="item">
        <p>ФИО</p>
        <p>{data.fio}</p>
      </div>
      <div className="item">
        <p>Телефон</p>
        <p>{data.tel}</p>
      </div>
      <div className="item">
        <p>E-mail</p>
        <p>{data.email}</p>
      </div>
      <div className="item">
        <p>Адресс</p>
        <p>{data.city}, {data.street} {data.home}, кв{data.apartment} </p>
      </div>
      <div className="item center">
        <p>Сообщение</p>
      </div>
      <div className="item noBg">
        <p>{data.text} </p>
      </div>
    </AppealBox>
  );
};

export default ShowText;
