import React, { useState, useEffect, useCallback } from 'react';
import { ReactComponent as CloseIcon } from './Icons/close.svg';

const MultiSelectNew = ({
  data, selectedList, selectValuse, keys, defaultSelect,
}) => {
  const [selectedVlaus, setselectedVlaus] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [filtredData, setFiltredData] = useState([]);
  const [searchValue, setSearchValue] = useState('');

  useEffect(() => {
    if (defaultSelect) {
      setselectedVlaus(defaultSelect);
    } else {
      setselectedVlaus([]);
    }
  }, [data, defaultSelect]);

  const addSelectedItem = (element) => {
    selectedVlaus.push(element);
    setselectedVlaus(selectedVlaus);
    selectedList(selectedVlaus);
  };

  const deleteSectedItem = useCallback((index) => {
    selectedVlaus.splice(index, 1);
    setselectedVlaus(selectedVlaus);
    selectedList(selectedVlaus);
  }, [selectedVlaus, selectedList]);

  const deleteAllSectedItem = () => {
    setselectedVlaus([]);
    selectedList([]);
  };

  const closeDd = useCallback((e) => {
    e.stopPropagation();
    document.removeEventListener('mouseup', closeDd);
    setIsOpen(false);
    setSearchValue('');
  }, []);

  const closeDropdownList = useCallback(() => {
    document.addEventListener('mouseup', closeDd, false);
  }, [closeDd]);


  const renderSelectedValuse = useCallback(() => selectedVlaus.map((element, index) => (
    <div key={index} className="MultiSelect__Selected-Item">
      <div className="MultiSelect__Selected-Item-Text">{element[selectValuse]}</div>
      <div className="MultiSelect__Selected-Item-Close-Cross" onClick={() => deleteSectedItem(index)}>X</div>
    </div>
  )), [selectedVlaus, selectValuse, deleteSectedItem]);

  const renderDataValuse = () => {
    let currenArr = data;
    if (filtredData.length > 0) {
      currenArr = filtredData;
    }
    if (searchValue !== '' && filtredData.length === 0) { return (<div className="MultiSelect__Empty-Search"> Ничего не найдено </div>); }

    return (currenArr.map((element, index) => {
      let Item = <div key={index} onClick={() => { addSelectedItem(element); }} className="MultiSelect__Selected-Value">{element[selectValuse]}</div>;
      selectedVlaus.forEach((el, ind) => {
        if (el.id === element.id) {
          Item = <div key={ind + 10000} className="MultiSelect__Selected-Value__Done">{element[selectValuse]}</div>;
          return (Item);
        }
        return false;
      });
      return (Item);
    }));
  };

  useEffect(() => {
    renderSelectedValuse();
  }, [selectedVlaus, renderSelectedValuse]);

  useEffect(() => {
    const filtredDataValues = data.filter((el) => {
      if (el[selectValuse].indexOf(searchValue) !== -1) {
        return (el);
      }
      return false;
    });
    setFiltredData(filtredDataValues);
  }, [searchValue, data, selectValuse]);

  useEffect(() => { if (isOpen) closeDropdownList(); }, [isOpen, closeDropdownList]);

  const goTestFunc = () => {
    setIsOpen(true);
  };

  return (
    <div className="MultiSelect__Box" onClick={() => goTestFunc()}>
      <div className="MultiSelect__Main">
        <div className="MultiSelect__Values">
          <div className="MultiSelect__Selected-Item-List">
            {renderSelectedValuse()}
          </div>
          {selectedVlaus.length > 0 && (
          <div className="MultiSelect__Delete-All-Icon">
            <CloseIcon onClick={() => deleteAllSectedItem()} style={{ fill: 'red' }} />
          </div>
          ) }
        </div>
        <div className={isOpen ? 'MultiSelect__Content__Open' : 'MultiSelect__Content__Close'}>
          <input className="MultiSelect__Search" type="text" value={searchValue} onChange={(e) => setSearchValue(e.target.value)} placeholder="Введите для поиска" />
          <div className="MultiSelect__Search-Items">
            {renderDataValuse()}
          </div>
        </div>
      </div>
    </div>
  );
};

export default MultiSelectNew;
