import React, {
  useState, useEffect, useCallback, useRef,
} from 'react';
import moment from 'moment';
import axios from 'axios';
import { Title } from '../../../styles/Common';
import {
  AboutBox, AboutItem, AboutItemTitle, AboutItemInfo,
} from '../../../styles/About';
import request from '../../../utils/request';
import LicenceComponent from './LicenceComponent';
import Loader from '../../../utils/loader';
import UpdateBlock from './UpdateBlock';

const About = ({ preloader, showError }) => {
  const [info, setInfo] = useState({});
  const [time, setTime] = useState({});

  const intervalBox = useRef(null);

  const timer = (data) => {
    const { eqServerLoadedOn } = data;
    const currentTime = moment();
    const durat = moment.duration(currentTime.diff(moment(eqServerLoadedOn, 'YYYY.MM.DD HH:mm:ss')));
    const days = durat.get('days');
    const hours = durat.get('hours');
    const min = durat.get('minutes');
    const sec = durat.get('seconds');
    setTime({
      days, hours, min, sec,
    });
  };

  const fetchInfo = useCallback(async () => {
    const isLocalhost = /localhost/.test(window.location.host);
    const res = await request(isLocalhost ? 'https://192.168.3.234:3030/server/public/getVersions/' : '/server/public/getVersions/', 'post', {});
    const fVersions = await axios.get(isLocalhost ? 'https://192.168.3.234:3030/versions' : '/versions');
    const { data: { versions } = undefined } = fVersions;
    const { latest: fLatest = {}, current: fCurrent = {} } = versions;
    intervalBox.current = setInterval(() => timer(res.data.data), 1000);
    setInfo({ ...res.data.data, fLatest, fCurrent });
  }, []);


  useEffect(() => {
    fetchInfo();
    return () => {
      clearInterval(intervalBox.current);
    };
  }, [fetchInfo]);

  const { fLatest, fCurrent } = info;
  const {
    days, hours, min, sec,
  } = time;

  const arrData = [
    { title: 'Версия B-сервера', text: 'version_baq' },
    { title: 'Версия F-сервера', text: 'fCurrent', extra: 'server' },
    { title: 'Версия CL библиотек', text: 'version_cl' },
    { title: 'Версия базы данных', text: 'baqServerDbVersion' },
    { title: 'Последнее обновление БД', text: 'dbLastUpgradedOn', date: true },
  ];

  return (
    <>
      <LicenceComponent preloader={preloader} showError={showError} />
      <Title>О системе</Title>

      <>
        <AboutBox align="flex-start" width="1200px">
          {arrData.map((el) => (
            <AboutItem key={el.title}>
              <AboutItemTitle>
                {el.title}:
              </AboutItemTitle>
              { fLatest
                ? (
                  <AboutItemInfo>
                    {el.date
                      ? moment(info[el.text], 'YYYY.MM.DD HH:mm:ss').format('HH:mm:ss - DD.MM.YYYY')
                      : el.extra
                        ? info[el.text][el.extra]
                        : info[el.text]}
                  </AboutItemInfo>
                )
                : <Loader />}
            </AboutItem>
          ))}
          <AboutItem>
            <AboutItemTitle>
              Время работы В-сервера:
            </AboutItemTitle>
            { fLatest
              ? (
                <AboutItemInfo>
                  {days ? `${days} дн.` : ''}
                  {' '}
                  {hours ? `${hours} час.` : ''}
                  {' '}
                  {min ? `${min} мин.` : '0 мин.'}
                  {' '}
                  {sec && `${sec} сек.`}
                </AboutItemInfo>
              )
              : <Loader />}
          </AboutItem>
          <UpdateBlock
            title="Панель администрирования"
            cVersion={(fCurrent) && fCurrent.admin}
            lVersion={(fLatest) && fLatest.admin}
            loading={fCurrent && fLatest}
            type="admin"
          />
        </AboutBox>

        {/* <AboutBox> */}
        {/* </AboutBox> */}
      </>
    </>
  );
};

export default About;
