import React from 'react';
import Align from './modules/align';

const Aligns = ({ arr, dispatch }) => (
  <div className="elementBox">
    <p>Выравнивание</p>
    { arr.map((el, index) => (
      <Align
        element={el.parentName}
        value={el.value}
        dispatch={dispatch}
        title={el.title}
        paramName={el.paramName}
        key={index}
      />
    ))}
  </div>
);

export default Aligns;
