import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import { ReportTitleBox, TableTitle, TableTitleLeftItem } from '../../../../../styles/TableTitle';
import BackToRootReport from '../../utils/BackToRootReport';
import DataExportConfig from '../../utils/DataExport/DataExportConfig';
import { BtnAdd } from '../../../../../styles/Common';

const ReportTitle = (props) => {
  const {
    reportConfig, name, item, exportDisable,
  } = props;

  let ticket_id = '';
  let ticket_number = '';

  const { renderBackButton } = reportConfig;

  const dataExportConfig = DataExportConfig(props);

  const openNotify = (type, message) => {
    if (type === 'success') {
      toast.success(`${message}`, 1000);
    } else {
      toast.error(`${message}`, 1000);
    }
  };

  const runDataExport = async () => {
    console.log('dataExportConfig === ', dataExportConfig);

    const { option } = dataExportConfig;
    console.log('options === ', option);

    const {
      query, name, reportConfig: {
        endDate, startDate,
      },
    } = option;

    let { reportConfig: { reportType } } = option;
    console.log('name === ', name);


    if (name.indexOf('история талона') !== -1) {
      reportType = 'ticketHistoryReport';
      const newRegExp = "ht.ticket_id = '(.*)'";
      const newRegExp2 = 'история талона (.*)';
      const newStr = query.match(newRegExp);
      const newStr2 = name.match(newRegExp2);
      // eslint-disable-next-line prefer-destructuring
      ticket_number = newStr2[1];
      // eslint-disable-next-line prefer-destructuring
      ticket_id = newStr[1];
    }

    const filterData = {
      type: reportType,
      startData: startDate,
      endData: endDate,
    };

    if (query) filterData.sqlQuery = query;

    const filters = {
      main: {
        ticket_id,
        ticket_number,
      },
    };

    try {
      const token = window.localStorage.getItem('token');
      const url = '/api/reports/build';
      const res = await axios({
        method: 'post',
        url,
        data: {
          token,
          filters: { ...filters },
          type: reportType,
          exports: 'true',
        },
      });

      const { statusText } = res;
      if (statusText === 'OK') {
        const message = 'Отчет подготавливается для скачивания и появиться в разделе "Архив"';
        openNotify('success', message);
      }
    } catch (err) {
      openNotify('error', err);
    }
  };

  return (
    <React.Fragment>
      <ReportTitleBox reportTitile left={renderBackButton}>
        <TableTitleLeftItem>
          {renderBackButton
            ? (
              <BackToRootReport />
            ) : null}
          {item ? (
            `Всего записей: ${item}`
          ) : null}
        </TableTitleLeftItem>
        <TableTitle>{name}</TableTitle>
        <div>
          {item && exportDisable === undefined ? (
            <>
              <BtnAdd fix={180} onClick={runDataExport}>Экспортировать</BtnAdd>
              {/* <Dropdown
                config={dataExportConfig}
                clickHandler={runDataExport}
                replaceableTitle={false}
              /> */}
            </>
          ) : null}
        </div>
      </ReportTitleBox>
    </React.Fragment>
  );
};

function mapStateToProps(state) {
  return { reportConfig: state.reportConfig };
}

export default connect(mapStateToProps)(ReportTitle);
