import React from 'react';
import moment from 'moment';
import { placeholder } from '../../../utils/tableFormatters';

export default function (users) {
  function timeFormatter(cell) {
    const date = new Date(cell);
    return moment(date).format('DD.MM.YYYY HH:mm:ss');
  }

  function headerFilterFormatter(column, colIndex, { sortElement }) {
    return (
      <div className="filterBoxTable">
        <div>
          {column.text}
          {' '}
          {sortElement}
        </div>
        {/* { filterElement } */}
      </div>
    );
  }

  const actions = {
    0: 'Неизвестное действие',
    // 1: 'Авторизация',
    // 2: 'Отключение',
    3: 'Создание объекта на сервере',
    4: 'Удаление объекта на сервере',
    5: 'Изменение объекта на сервере',
    6: 'Изменение параметров сервера',
    7: 'Вход по веб',
    8: 'Выход по веб (или удаление сессии по окончанию времени жизни сессии)',
  };

  return {
    columns: [
      {
        dataField: 'created_on',
        text: 'Время',
        sort: true,
        formatter: timeFormatter,
        headerFormatter: headerFilterFormatter,
      },
      {
        dataField: 'created_by_id',
        text: 'Пользователь',
        sort: true,
        formatter: cell => users[cell],
      },
      {
        dataField: 'action',
        text: 'Выберите действие',
        editable: false,
        formatter: cell => actions[cell],
        // headerFormatter: formatters.placeholder,
        // filter: selectFilter({
        //   options: actions,
        //   placeholder: 'Выберите действие',
        // }),
      },
      {
        dataField: 'comment',
        text: 'Выберите комментайрий',
        searchable: false,
        editable: false,
        headerFormatter: placeholder,
        // filter: textFilter({
        //   className: 'customPlaceholder',
        //   placeholder: 'Выберите комментайрий',
        // }),
      },
    ],
  };
}
