import React, { useState } from 'react';
import { FaAngleUp } from 'react-icons/fa';
import request from '../../../../utils/request';
import { Input } from '../../../../styles/Common';
import {
  QuestionItem, Element, AnswersBox, Checkbox, AnswerItems, InputTextArea,
} from './style';
import AddButton from '../../../Elements/Button/AddButton';
import { ReactComponent as Edit } from '../../../../images/edit.svg';
import { ReactComponent as Bin } from '../../../../images/bin.svg';

const Question = ({
  parent, item, setItem, update, getData,
}) => {
  const [open, setOpen] = useState(false);


  const changeAnswers = (index, active, value) => {
    const newConnect = [...parent.connectedQuestions];
    const element = newConnect.find((el) => el.id === item.id);
    // console.log(element);
    // console.log(index);
    // console.log(value);
    switch (active) {
      case 1:
        element.answers[index].correct = !element.answers[index].correct;
        break;
      case 2:
        const elem = {
          correct: false,
          answerint: 'Ваш ответ',
          type: 2,
          id: Math.random(),
        };
        element.answers = [...element.answers, elem];
        break;
      case 3:
        element.answers.splice(index, 1);
        break;
      default:
        if (element.answers[index].answerint) {
          element.answers[index].answerint = value;
        } else {
          element.answers[index].answertxt = value;
        }
        break;
    }
    const newD = {
      ...parent,
      connectedQuestions: [...newConnect],
    };
    update(element, newD);
  };

  const changeQuestion = (value, mark, e) => {
    if (e) e.stopPropagation();
    const newConnect = [...parent.connectedQuestions];
    const element = newConnect.find((el) => el.id === item.id);
    if (mark) {
      element.operator_mark = value;
    } else {
      element.questiontext = value;
    }
    const newD = {
      ...parent,
      connectedQuestions: [...newConnect],
    };
    update(element, newD);
    setItem(newD);
  };

  const deleteQuestion = async (e) => {
    e.stopPropagation();
    await request(`/server/api/qcquestion/${item.id}/`, 'delete');
    getData();
  };


  return (
    <QuestionItem>
      <h4 onClick={() => setOpen(!open)}>
        <span>{item.questiontext}</span>
        {open
          ? <FaAngleUp />
          : <Edit className="dashed" />}
        <Checkbox active={item.operator_mark} onClick={(e) => changeQuestion(!item.operator_mark, true, e)} m="0 15px 0" />
        <Bin onClick={(e) => deleteQuestion(e)} />
      </h4>
      <AnswersBox open={open}>
        <label>
          <p>Текст вопроса:</p>
          <InputTextArea defaultValue={item.questiontext} onBlur={(e) => changeQuestion(e.target.value)} />
        </label>
        <AnswerItems>
          <p>Ответы</p>
          <ul className="answerBox">
            {item.answers.map((el, index) => (
              <Element key={el.id}>
                <Input defaultValue={el.type === 2 ? el.answerint : el.answertxt} onBlur={(e) => changeAnswers(index, 4, e.target.value)} />
                <Checkbox active={Boolean(el.correct)} onClick={() => changeAnswers(index, 1)} />
                <Bin onClick={() => changeAnswers(index, 3)} />
              </Element>
            ))}
          </ul>
          <AddButton toggle={() => changeAnswers(undefined, 2)} />
        </AnswerItems>
      </AnswersBox>

    </QuestionItem>
  );
};

export default Question;
