import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { ReactComponent as Edit } from '../../../images/edit.svg';
import c from '../../../styles/Colors';
import IconFormatter from '../../../utils/iconFormatter';

function priceFormatter(column, colIndex, { filterElement }) {
  return (
    <div className="filterBoxTable">
      {column.dataField !== 'Settings' && <Edit style={{ margin: '2px 10px 0 0' }} fill={c.main} />}
      {filterElement}
    </div>
  );
}

function typeFormatter(cell) {
  const obj = {
    multiselect: 'Множественный выбор',
    select: 'Одиночный выбор',
    text: 'Текстовое поле',
  };
  return obj[cell];
}


export default function Setting(write) {
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Имя',
        filter: textFilter({
          placeholder: 'Имя',
        }),
        editable: false,
        headerFormatter: priceFormatter,
      },
      {
        dataField: 'rus_name',
        text: 'Наименование',
        filter: textFilter({
          placeholder: 'Наименование',
        }),
        editable: false,
        headerFormatter: priceFormatter,
      },
      {
        dataField: 'type',
        text: 'Тип',
        editable: false,
        formatter: typeFormatter,
        sort: true,
      },
      {
        dataField: 'description',
        text: 'Описание',
        editable: false,
      },
      {
        dataField: 'created_on',
        text: 'Дата создания',
        filter: textFilter({
          placeholder: 'Дата создания',
        }),
        headerFormatter: priceFormatter,
        editable: false,
      },
      {
        dataField: 'modified_by_username',
        text: 'Изменен',
        editable: false,
      },
      {
        dataField: 'modified_on',
        text: 'Когда изменен',
        editable: false,
      },
      {
        dataField: 'Settings',
        text: '',
        headerStyle: {
          width: 110,
        },
        formatter: () => IconFormatter({ filter: false, write, params: false }),
        formatExtraData: {},
        editable: false,
      },
    ],
  };
}
