import React, { useState, useEffect } from 'react';
import {
  DropdownListBox, DropdownListHeader,
  DropdownListBody, DpordownListItem,
  DpordownListItemText, DpordownListItemIcon,
} from '../../../styles/Dropdown';

import { ReactComponent as DdArrow } from '../../../images/DdArrow.svg';

const Dropdown = ({ config, clickHandler, replaceableTitle }) => {
  const [showDropdownList, setShowDropdownList] = useState(false);
  const [event, setEvent] = useState(false);
  const { headerTitleText, items, option } = config;

  const [headerTitle, setHeaderTitle] = useState(headerTitleText);

  function onDropdownListItemClick(dropdownListItemName, options, key, name) {
    clickHandler(dropdownListItemName, options, key, name);
    if (replaceableTitle) setHeaderTitle(dropdownListItemName !== '' ? dropdownListItemName : name);
    setShowDropdownList(false);
  }

  function renderDropdownItems() {
    return items.map((element) => (
      <DpordownListItem key={element.key} onClick={() => onDropdownListItemClick(element.text, option, element.key, element.name)}>
        {element.icon ? (<DpordownListItemIcon>{element.icon}</DpordownListItemIcon>) : (null)}
        <DpordownListItemText>{element.text !== '' ? element.text : element.name}</DpordownListItemText>
      </DpordownListItem>
    ));
  }

  function openDropdownList() {
    setEvent(true);
    setShowDropdownList(true);
  }

  const closeDd = (e) => {
    e.stopPropagation();
    document.removeEventListener('mouseup', closeDd);
    setShowDropdownList(false);
    setEvent(false);
  };

  const closeDropdownList = () => {
    document.addEventListener('mouseup', closeDd, false);
  };

  useEffect(() => {
    if (showDropdownList) {
      closeDropdownList();
    }
  }, [event]);

  return (
    <DropdownListBox>
      <DropdownListHeader onMouseUp={openDropdownList}>
        <DpordownListItemText>{headerTitle}</DpordownListItemText>
        <DpordownListItemIcon isTitle active={showDropdownList}>{showDropdownList ? (<DdArrow />) : (<DdArrow />)}</DpordownListItemIcon>
      </DropdownListHeader>
      <DropdownListBody active={showDropdownList}>
        {renderDropdownItems()}
      </DropdownListBody>
    </DropdownListBox>
  );
};

export default Dropdown;
