import React, { useState, useEffect, useCallback } from 'react';
import RequestTable from './RequestTable';

const Request = ({ settings, preloader }) => {
  const barcodeRef = React.useRef(null);

  const [tableSettings, setTableSettings] = useState(null);

  const [params, setParams] = useState({
    page: 1,
    sizePerPage: 10,
    totalSize: null,
    sortOrder: 'desc',
    filters: 'none',
  });

  const onSizePerPageChange = useCallback((sizePerPageNumber, pageNumber) => {
    setParams((v) => ({
      ...v,
      sizePerPage: sizePerPageNumber,
      page: pageNumber,
    }));
  }, []);

  const onPageChange = useCallback((pageNumber, sizePerPageNumber) => {
    setParams((v) => ({
      ...v,
      sizePerPage: sizePerPageNumber,
      page: pageNumber,
    }));
  }, []);

  const onTableChange = useCallback((type, newState) => {
    let { sortOrder } = newState;
    const { page, sizePerPage, filters } = newState;
    if (params.sortOrder === sortOrder && sortOrder === 'desc') {
      sortOrder = 'asc';
    } else {
      sortOrder = 'desc';
    }
    setParams((v) => ({
      ...v,
      page,
      sizePerPage,
      sortOrder,
      filters,
    }));
  }, [params.sortOrder]);

  useEffect(() => {
    const pagination = {
      page: params.page,
      sizePerPage: params.sizePerPage,
      totalSize: params.totalSize,
      sortOrder: params.sortOrder,
      filters: params.filters,
      onPageChange,
      onSizePerPageChange,
      onTableChange,
      startDate: settings.startDate,
      endDate: settings.endDate,
    };
    setTableSettings(pagination);
  }, [params, settings, onPageChange, onSizePerPageChange, onTableChange]);

  // const fix = () => {
  //   const barcodeSvg = barcodeRef.current;
  //   html2canvas(barcodeSvg).then((canvas) => {
  //     document.body.appendChild(canvas);
  //   });

  //   // console.log('barcodeSvg === ', barcodeSvg);
  //   // const pdf = new jsPDF('p', 'pt', 'a4');
  //   // pdf.html(barcodeSvg, {
  //   //   callback(doc) {
  //   //     doc.save();
  //   //   },
  //   //   x: 10,
  //   //   y: 10,
  //   // });
  // };

  return (
    <>
      {/* <button onClick={fix}>fix</button> */}
      {tableSettings === null ? (null) : (
        <RequestTable
          pagination={tableSettings}
          preloader={preloader}
        />
      )}
    </>
  );
};


export default Request;
