const rowFormatter = (cell, row) => {
  const {
    less_2_min, less_4_min, less_6_min, less_8_min, less_10_min, less_12_min, less_14_min, less_16_min, less_18_min, less_20_min, more_20_min,
  } = row;
  return (
    <div style={{ display: 'flex', justifyContent: 'space-between' }} key={row.queue_name}>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_2_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_4_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_6_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_8_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_10_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_12_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_14_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_16_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_18_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {less_20_min}</div>
      <div style={{ width: '80px', display: 'flex', justifyContent: 'center' }}> {more_20_min}</div>
    </div>
  );
};

