const rowFormatter2 = (cell, row) => {
  const { all_cnt } = row;
  return (
    <div style={{ display: 'flex', justifyContent: 'center' }}>{all_cnt}</div>
  );
};

export default function (getColumnSearchProps) {
  return {
    columns: [
      {
        dataIndex: 'queue_name',
        title: 'Очередь',
        ...getColumnSearchProps('queue_name'),
      },
      {
        dataIndex: 'somekey',
        title: 'Кол-во талонов со временем обслуживания',
        render: (cell, row) => rowFormatter(cell, row),
        sorter: (a, b) => a.somekey - b.somekey,
      },
      {
        dataIndex: 'all_cnt',
        title: 'Всего клиентов обслужено',
        render: (cell, row) => rowFormatter2(cell, row),
        sorter: (a, b) => a.all_cnt - b.all_cnt,
      },
    ],
  };
}
