import React from 'react';
import { Provider } from 'react-redux';
import App from './App';
import store from './utils/store';
import './style/fonts.css';
import { TerminalTransform, FixedBox } from '../style';

const Terminal = ({
  id, config, onSave, match, screen,
}) => {
  let direction = false;
  let scale = 1;
  if (config) direction = config.config.style.head.width < config.config.style.head.height;
  const width = direction ? 500 : 800;
  if (config) scale = width / config.config.style.head.width;
  return (
    <FixedBox isEdit={Boolean(config)}>
      {config && !screen && <button className="btn btn_save" onClick={onSave}>Сохранить изменения</button>}
      <TerminalTransform isEdit={Boolean(config)} scale={scale}>
        <Provider store={store}>
          <App oldConfig={config} isEdit={id} screen={screen} id={id || match.params.id} />
        </Provider>
      </TerminalTransform>
    </FixedBox>
  );
};


export default React.memo(Terminal);
