import React, { useState } from 'react';
import request from '../../../../../utils/request';
import Modal from '../../../../Elements/Modal/Modal';
import RenderInputs from './RenderInputs';
import ModalButtons from '../../../../Elements/Modal/ModalButtons';
import AddButton from '../../../../Elements/Button/AddButton'

const NewQuestion = ({
  fetchQuests, toggleOpen, isOpen, setQuestData, questData,
  setQuestUpdate, questUpdate, initialData, preloader,
}) => {
  const [isValid, setIsValid] = useState(false);

  const resetModal = () => {
    toggleOpen();
    setQuestData({ ...initialData });
    setQuestUpdate(false);
  };

  const addQuest = async () => {
    if (isValid) {
      await request('/server/api/servedquestion/', 'add', { ...questData });
      fetchQuests(); resetModal();
    }
  };

  const updateQuest = async () => {
    if (isValid) {
      const itemId = questData.id;
      delete questData.id;
      await request(`/server/api/servedquestion/${itemId}/`, 'update', { ...questData });
      fetchQuests(); resetModal();
    }
  };

  return (
    <React.Fragment>
      <AddButton toggle={toggleOpen} />
      <Modal title={questUpdate ? 'Редактирование вопроса' : 'Добавление вопроса'} toggleOpen={resetModal} isOpen={isOpen}>
        <RenderInputs
          setIsValid={setIsValid}
          questData={questData}
          preloader={preloader}
          setQuestData={setQuestData}
        />
        <ModalButtons isValid={isValid} updateFunc={updateQuest} addFunc={addQuest} update={questUpdate} reset={resetModal} />
      </Modal>
    </React.Fragment>
  );
};

export default React.memo(NewQuestion);
