import React from 'react';
import SelectInput from '../../../../../Elements/Input/SelectInput/SelectInput';
import { ColorBox } from '../style';

const Order = ({
  element, dispatch, value, title
}) => (
  <ColorBox>
    <span dangerouslySetInnerHTML={{ __html: title }} />
    <SelectInput
      noDefault
      options={[
        { id: 0, name: 1 },
        { id: 1, name: 2 },
      ]}
      size="16px"
      onChange={e => dispatch({
        type: 'params',
        name: element,
        param: 'order',
        value: Number(e.target.value),
      })
    }
      value={value}
    />
  </ColorBox>
);

export default Order;
