function Timer(format = 1, raw = false) {
  const months = ['Января', 'Февраля', 'Марта', 'Апреля', 'Мая', 'Июня', 'Июля', 'Августа', 'Сентября', 'Октября', 'Ноября', 'Декабря'];
  const days = ['Воскресенье', 'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота'];

  const d = new Date();

  // eslint-disable-next-line no-mixed-operators
  const localeDate = new Date(d.getTime() - d.getTimezoneOffset() * 6e4);

  const date = localeDate.toISOString().replace(/(\d{4})-(\d{2})-(\d{2}).*/, '$3.$2.$1');
  const time = localeDate.toISOString().replace(/^.*T(\d{2}):(\d{2}).*/, '$1:$2');

  const year = localeDate.getFullYear();
  const month = localeDate.getMonth();
  const day = localeDate.getDay();

  if (raw) {
    return format ? { time, date } : {
      time, year, d, day: days[day], month: months[month],
    };
  }

  let result;
  if (format === 1) {
    result = `<span class="realtime">${time}</span><br/><span class="realdate">${date}</span>`;
  } else {
    result = `<span class="realtime">${time}</span><br/><span class="realdate">${days[day]}<br/>${d} ${months[month]} ${year}</span>`;
  }

  return result;
}

export default Timer;

export const runTimer = (id, format, doc, timer = Timer) => {
  if (!doc && !doc.getElementById) throw new Error('Wrong document was passed');

  const el = doc.getElementById(id);

  const run = () => {
    el.innerHTML = timer(format);
  };
  run();
  setInterval(run, 59000);
};
