import React, { useEffect, useCallback, useState } from 'react';
import { useSelector } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom';
import Request from '../../utils/request';
import Preload from '../preloader/preload';
import Canvas from './canvas';
import CanvasPre from './canvasPre';
import findError from '../../utils/findError';
import store from '../../utils/store';
// import history from '../../../../../../../../utils/customHistory';
import { resetTicket } from '../../utils/actions';
import { PrintBox } from '../../style/style';
import { resetScanPolicyStore } from '../../../../../../../../store/scanPolicy';

const { dispatch } = store;

const Print = ({ ...props }) => {
  const [data, setData] = useState({});
  const {
    ticket, config, preEntry, style,
  } = useSelector((state) => state);
  const { id } = useParams();
  const history = useHistory();
  console.log(id);
  const { errorUrl } = config;
  const { isEdit } = config;
  const { startScreen } = useSelector((state) => state.config);
  const sendTicket = useCallback(async () => {
    const tk = { ...ticket, dictionary: [...ticket.dictionary] };
    tk.dictionary.forEach((el) => {
      delete el.to;
      delete el.input;
    });
    tk.dictionary = tk.dictionary.concat(config.defaultParams);
    tk.zone_id = config.idZone;

    try {
      const res = await Request('/server/public/createTicket', 'no', { data: tk });
      const error = findError({
        res, history, dispatch, errorUrl,
      });
      if (!error) {
        setData(res.data);
      } else {
        dispatch(resetTicket());
      }
    } catch (e) {
      console.log(e);
    }
  }, [errorUrl, ticket, config.defaultParams, config.idZone]);

  const sendPin = useCallback(async () => {
    // const arrDictionary = [{fv: [], fid: id}]
    try {
      const res = await Request('/server/public/preorder/createTicketByPin', 'no', { data: ticket });
      const error = findError({
        res, history, dispatch, errorUrl,
      });
      if (!error) setData(res.data);
    } catch (e) {
      console.log(e);
    }
  }, [ticket, errorUrl]);

  useEffect(() => {
    if (isEdit) history.push(startScreen);
    if (preEntry.pin) {
      setData(preEntry);
    } else if (ticket.pinCode) {
      sendPin();
    } else {
      sendTicket();
    }
    return (() => {
      resetScanPolicyStore();
    });
  }, [isEdit, sendPin, sendTicket, ticket.pinCode, preEntry, startScreen]);

  useEffect(() => {
    if (Object.keys(data).length) {
      setTimeout(() => {
        history.push(startScreen);
      }, 7000);
    }
  }, [data]);

  const render = () => {
    if (data.pin) {
      return <CanvasPre dispatch={dispatch} errorUrl={errorUrl} {...props} config={config} data={data} />;
    }
    if (data.style) {
      return <Canvas dispatch={dispatch} errorUrl={errorUrl} {...props} config={config} data={data} />;
    }
    return <Preload print />;
  };
  return (
    <PrintBox style={{ background: style.body.background }}>
      {render()}
    </PrintBox>
  );
};


const compareProps = (prevProps, nextProps) => JSON.stringify(prevProps) === JSON.stringify(nextProps);

export default React.memo(Print, compareProps);
