import React from 'react'
import { textFilter } from 'react-bootstrap-table2-filter';
import { ReactComponent as Edit } from '../../../../images/edit.svg';
import c from '../../../../styles/Colors'
import IconFormatter from '../../../../utils/iconFormatter';
import moment from 'moment';

const priceFormatter = (column, colIndex, { filterElement }) => (
  <div className="filterBoxTable">
    {column.dataField !== 'Settings' && filterElement && <Edit style={{margin: '2px 10px 0 0'}}  fill={c.main} />}
    {filterElement || column.text}
  </div>
);

export default (users, write) => {
  return [
    {
      dataField: 'user_id',
      text: 'Пользователь',
      sort: false,
      headerFormatter: priceFormatter,
      filter: textFilter({
        className: 'customPlaceholder',
        placeholder: 'Пользователь',
      }),
      formatter: (cell) => users.some(el => el.id === cell) ? users.find(el => el.id === cell).username : '',
      editable: false,
    },
    {
      dataField: 'session',
      text: 'Пользователь',
      sort: false,
      headerFormatter: priceFormatter,
      filter: textFilter({
        className: 'customPlaceholder',
        placeholder: 'Сессия',
      }),
      editable: false,
    },
    {
      dataField: 'constant',
      text: 'Тип',
      sort: false,
      headerFormatter: priceFormatter,
      formatter: (cell) => cell ? 'Постоянная' : "Временная",
      editable: false,
    },
    {
      dataField: 'created_on',
      text: 'Дата создания',
      sort: false,
      headerFormatter: priceFormatter,
      formatter: (cell) => {
        return moment(cell, 'YYYY-MM-DDTHH:mm:ss').format('YYYY-MM-DD HH:mm:ss')
      },
      editable: false,
    },
    {
      dataField: 'Settings',
      text: '',
      headerStyle: {
        width: 50,
      },
      formatter: () => IconFormatter({ filter: false, write, update: false, params: false }),
      formatExtraData: {},
      editable: false,
    },
  ]
}
