import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { ReactComponent as Edit } from '../../../../images/edit.svg';
import c from '../../../../styles/Colors';
import iconFormatter from '../../../../utils/iconFormatter';
import sortCaret from '../../../../utils/sortCaret';

function priceFormatter(column, colIndex, { filterElement, sortElement }, sort) {
  return (
    <div className={`filterBoxTable ${sort ? 'sort' : null}`}>
      { column.dataField !== 'Settings' && <Edit style={{margin: '2px 10px 0 0'}}  fill={c.main} /> }
      { filterElement }
      { sortElement }
    </div>
  );
}


export default function (type) {
  return {
    columns: [
      {
        dataField: 'headTitle',
        text: 'Наименование',
        sort: true,
        sortCaret,
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
        filter: textFilter({
          placeholder: 'Наименование',
        }),
        editable: false,
      },
      {
        dataField: 'title',
        text: 'Заголовок',
        sort: true,
        sortCaret,
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
        filter: textFilter({
          placeholder: 'Заголовок',
        }),
        editable: false,
      },
      {
        dataField: 'description',
        text: 'Описание',
        headerFormatter: priceFormatter,
        filter: textFilter({
          placeholder: 'Описание',
        }),
        editable: false,
        formatter(cell) {
          return cell;
        },
      },
      {
        dataField: 'type',
        text: 'Тип',
        headerFormatter: priceFormatter,
        filter: textFilter({
          placeholder: 'Тип',
        }),
        filterValue: (cell, row) => type.find(el => el.id === cell).name,
        editable: false,
        formatter(cell) {
          return type.find(t => t.id === cell) ? type.find(t => t.id === cell).name : '';
        },
      },
      {
        dataField: 'Settings',
        text: ' ',
        isDummyField: true,
        headerFormatter: priceFormatter,
        formatter: () => iconFormatter({
          filter: false, write: true, params: false,
        }),
        formatExtraData: {},
        editable: false,
        headerStyle: {
          width: 100,
        },
      },
    ],
  };
}
