import React, {
  useState, useEffect, useCallback, useRef,
} from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import {
  LoginBox, LoginForm, InputBox, Btn, FormBox, Terminals, AlphaMat, Sueo,
} from '../../../styles/LoginStyles';
import InputLogin from './InputLogin';
import getToken from '../../../utils/getToken';
import { IconLogin, IconPass } from './icons';
import Logotype from '../../../images/logo.png';
import request from '../../../utils/request';
import OfficeParams from './officeParams';
import { useToggle } from '../../../hooks/useToggle';

//
import Modal from '../Modal/Modal';
import Portal from '../Modal/PortalModal';
// import { Btn } from '../../../styles/Modal';

const Login = ({ history }) => {
  const [userLogin, setUserLogin] = useState('');
  const [animationForm, setAnimationForm] = useState(0);
  const [userPasswd, setUserPassword] = useState('');
  const [office, setOffice] = useState([]);
  const [open, setOpen] = useState(false);
  const alphaM = useRef(null);
  const terminals = useRef(null);
  const sueo = useRef(null);

  //
  const [isOpen, toggleOpen] = useToggle(false);
  const resetModal = () => {
    toggleOpen();
  };
  //

  const fetchParams = useCallback(() => {
    if (!getToken()) return;
    const getParams = request('/server/api/officeinfo/');
    getParams
      .then((res) => {
        const paramsData = res.data.data;
        const isValid = paramsData.every((el) => Boolean(el.value));
        console.log('res === ', res);
        if (isValid) {
          setAnimationForm(1);
          history.push('/monitoring/');
        } else {
          setOffice(res.data.data);
          setOpen(true);
        }
      });
  }, [history]);


  useEffect(() => {
    fetchParams();
  }, [fetchParams]);

  const handleLogin = async (e) => {
    e.preventDefault();
    const userInfo = { userLogin, userPasswd };
    axios({ method: 'post', url: '/server/public/login', data: userInfo })
      .then((res) => {
        const { data } = res;
        console.log('res === ', res.data.userRoles);
        if (res.data.userRoles !== 'eq_operator') {
          if (res.data.error) {
            toast.error(res.data.error.text.rus);
          } else {
            window.localStorage.setItem('token', data.session);
            window.localStorage.setItem('fio', data.userFio);
            window.localStorage.setItem('login', data.userLogin);
            window.localStorage.setItem('role', data.userRoles);
            fetchParams();
          }
        } else {
          toggleOpen();
          console.log(' низя ');
        }
      })
      .catch((e) => {
        // toast.error(e.data.error);
      });
  };

  const handleInput = (e) => {
    const { name, value } = e.target;
    if (name === 'userLogin') {
      setUserLogin(value);
    } else {
      setUserPassword(value);
    }
  };

  const items = [
    {
      label: <IconLogin />, name: 'userLogin', value: userLogin || '',
    },
    {
      label: <IconPass />, name: 'userPasswd', type: 'password', value: userPasswd || '',
    },
  ];

  const mouseMove = (e) => {
    const mx = e.clientX;
    const wx = window.innerWidth;
    const x = (mx * 100) / wx / 100 - 0.5;
    const arr = [
      { el: alphaM.current, anim: 10 },
      { el: terminals.current, anim: 25 },
      { el: sueo.current, anim: 40 },
    ];

    arr.forEach((item) => { item.el.style.transform = `translate(calc(-50% - ${x * item.anim}px), -50%)`; });
  };

  const save = async () => {
    try {
      const sendData = {};
      office.forEach((el) => sendData[el.param] = el.value);
      await request('/server/api/officeinfo/', 'update', sendData);
      history.push('/monitoring/');
    } catch (e) {
      toast.error('Не удалось установить параметры офиса', 5000);
    }
  };

  const modalStyle = {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  };

  const isValid = office.length > 0 && office.every((el) => el.value);
  return (
    <LoginBox onMouseMove={mouseMove}>
      <FormBox className={animationForm ? 'login' : null}>
        <LoginForm onSubmit={handleLogin} className={animationForm ? 'animationForm' : ''}>
          <img src={Logotype} alt="" />
          <h1>Электронная очередь</h1>
          <h2>Вход</h2>
          <h3>Панель администрирования</h3>
          <InputBox>
            {items.map((item) => (
              <InputLogin
                key={item.name}
                item={item}
                onChange={handleInput}
              />
            ))}
          </InputBox>
          <Btn bg={userLogin && userPasswd} type="submit">Войти</Btn>
        </LoginForm>
        <Modal
          title=""
          isOpen={isOpen}
          toggleOpen={resetModal}
          style={modalStyle}
        >
          <h1>Вы не можете войти в админ панель</h1>
          <Btn
            cancel
            onClick={() => {
              toggleOpen();
            }}
          >Назад
          </Btn>
        </Modal>
      </FormBox>
      <Terminals className={animationForm ? 'login' : null} ref={terminals} />
      <AlphaMat className={animationForm ? 'login' : null} ref={alphaM} />
      <Sueo className={animationForm ? 'login' : null} ref={sueo} />
      <OfficeParams office={office} isValid={isValid} setOffice={setOffice} save={save} setOpen={setOpen} open={open} />
    </LoginBox>
  );
};

export default Login;
