import React from 'react';
import { BtnAdd } from '../../../styles/Common';
import getLinks from '../../../utils/getLinks';

const AddButton = ({ toggle, text, ...props }) => {
  const write = getLinks(true);
  if (write) {
    return (
      <BtnAdd onClick={toggle} {...props}> {text || 'Добавить'} </BtnAdd>
    );
  }

  return null;
};

export default React.memo(AddButton);
