import React from 'react';
import SelectInput from '../../../../../Elements/Input/SelectInput/SelectInput';
import { ColorBox } from '../style';


const changeView = ({ dispatch, value }) => {
  const options = [
    { id: 0, name: '00.00.00' },
    { id: 1, name: '00-00-00' },
    { id: 2, name: '00/00/00' },
  ];
  return (
    <div className="elementBox">
      <p>Варинаты написания даты</p>
      <ColorBox>
        <SelectInput
          options={options}
          onChange={e => dispatch({
            type: 'viewType',
            name: 'clock',
            param: 'viewType',
            value: Number(e.target.value),
          })}
          noDefault
          value={value}
        />
      </ColorBox>
    </div>

  );
};

export default changeView;
