import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import TerminalScreen from './TerminalScreen';
import BoardScreen from './BoardScreen';
import QualityScreen from './QualityScreen';
import NeyavkaScreen from './NeyavkaScreen';
import TicketProcess from './TicketProcess';
import PreentryScreen from './PreentryScreen';
import * as kagocelPreloader from '../kagocelpreloader.gif';
// import * as ringring from '../ringring.mp3';


const ScreensProvider = ({ currentZone, styleConfig, satelliteId }) => {
  // console.log('startScreen === ', startScreen);
  const { logoComp: { backgroundImage: logo }, header } = styleConfig;
  const [currentScreen, setCurrentScreen] = useState('terminal'); //  Надо проверку, если есть активный талон в локалстораже, то не терминал, а борда, а если талон есть но он закрытый, то оценка качества
  const [currentBoardStatus, setCurrentBoardStatus] = useState([]);
  const [showPreloader, setShowPreloader] = useState(true);

  const changeActiveScreen = (screen) => {
    setShowPreloader(false);
    setCurrentScreen(screen);
  };

  const getCurrentBoardStatus = async () => {
    const boardStatusRes = await axios.post('/server/public/getLastCallTicketEvents');
    if (boardStatusRes.statusText === 'OK') {
      const { data: { events } } = boardStatusRes;
      const TicketOnBoard = events.map((el) => ({ tiketNumber: el.eqEvent.number, wplace: el.eqEvent.wp_fname })).reverse();
      setCurrentBoardStatus(TicketOnBoard);
    }
  };


  const getTicket = async (params, preentry = false) => {
    setShowPreloader(true);
    // Тут создаем талон и пробрасываем его в отоборажение экрана талона
    const tData = {
      zone_id: currentZone.id,
      satellite_id: satelliteId,
      q_id: null,
    };
    if (preentry) {
      tData.pinCode = params.pinCode;
    } else {
      tData.q_id = params.value;
    }

    let reqUrl = '/server/public/createTicket';
    if (preentry) reqUrl = '/server/public/preorder/createTicketByPin';
    const ticketRes = await axios.post(reqUrl, { data: { ...tData } });
    const {
      error, status, ticket, ticketsBeforeYou,
    } = ticketRes.data;
    // Проверка ошибок - если ошибка вевести ошибку
    console.log('error === ', error);
    console.log('status === ', status);
    if (status !== 'failed') {
      window.localStorage.setItem('currentTicketId', ticket.id);
      window.localStorage.setItem('currentTicketNumber', ticket.number);
      window.localStorage.setItem('position', ticketsBeforeYou);
      await getCurrentBoardStatus();
      setShowPreloader(false);
      changeActiveScreen('board');
    } else {
      toast.error(error.text.rus);
      setShowPreloader(false);
    }
  };


  const checkTicketStatus = async (silent = false) => {
    const currentTicketId = localStorage.getItem('currentTicketId');
    if (!silent) setShowPreloader(true);
    const tiketStatusRes = await axios.post('/server/public/getDataForRestoreTicket', { idT: currentTicketId });
    if (tiketStatusRes.statusText === 'OK') {
      const {
        data: {
          info: { status }, body: {
            wpname, operatorfio, soundtext, position, placename,
          },
        },
      } = tiketStatusRes;

      const placedesc = [];
      soundtext.split(' ').forEach((el, index, arr) => {
        if (el.indexOf('WNUM') !== -1) {
          placedesc.push({ place: arr[index - 1], number: el });
        }
      });

      window.localStorage.setItem('placedesc', JSON.stringify(placedesc));
      window.localStorage.setItem('wpname', wpname);
      window.localStorage.setItem('operatorfio', operatorfio);
      window.localStorage.setItem('position', position);
      window.localStorage.setItem('placename', placename);
      console.log('status === ', status);
      switch (status) {
        case 1: // нас вызвали и ждут пока ты добежишь роняя кал
        case 2:
          changeActiveScreen('ticketProcess');
          break;
        case 3: // мы служим или отслужили
          changeActiveScreen('quality');
          break;
        case 4: // сам лох
        case 9:
          changeActiveScreen('neyavka');
          break;
        default:
          if (currentBoardStatus.length <= 0) await getCurrentBoardStatus();
          changeActiveScreen('board');
          break;
      }
    }
  };

  // case 0: // создали поставили в очередь ждем пердим
  // case 6: // Нас положили-отложили
  // case 7: // Нас вернули обратно в очередь
  //   await getCurrentBoardStatus();
  //   changeActiveScreen('board');
  //   break;


  useEffect(() => {
    if (localStorage.getItem('currentTicketId')) checkTicketStatus();

    setInterval(() => {
      if (localStorage.getItem('currentTicketId')) checkTicketStatus(true);
    }, 5000); // check every 5 seconds whether a minute has passed since last sync
  }, []);

  const getContent = () => {
    switch (currentScreen) {
      case 'terminal':
        return <TerminalScreen styleConfig={styleConfig} currentZone={currentZone} getTicket={getTicket} setShowPreloader={setShowPreloader} changeActiveScreen={changeActiveScreen} />;
      case 'board':
        return <BoardScreen setCurrentScreen={changeActiveScreen} currentBoardStatus={currentBoardStatus} styleConfig={styleConfig} checkTicketStatus={checkTicketStatus} />;
      case 'quality':
        return <QualityScreen setCurrentScreen={changeActiveScreen} />;
      case 'ticketProcess':
        return <TicketProcess setShowPreloader={setShowPreloader} />;
      case 'preentry':
        return <PreentryScreen getTicket={getTicket} styleConfig={styleConfig} currentZone={currentZone} />;
      case 'neyavka':
        return <NeyavkaScreen setCurrentScreen={changeActiveScreen} styleConfig={styleConfig} />;
      default:
        return 'Nothing';
    }
  };

  return (
    <div className="websTeminalScreen">
      <div className="websTeminalScreen__header" style={{ background: header.background }}>
        <div className="websTeminalScreen__logo"> <img src={logo} alt="" /> </div>
        <div className="websTeminalScreen__phone">Вебсервис</div>
      </div>
      {showPreloader && (<div className="videoPreloader">  <img src={kagocelPreloader} alt="preloader" />  </div>)}
      {getContent()}
    </div>
  );
};

export default ScreensProvider;
