import React, { useEffect, useState } from 'react';
import Input from '../../../Elements/Input/Input';
import MultiSelectNew from '../../../Elements/MultiSelectNew/MultiSelectNew';
import { Label } from '../../../../styles/Common';


const RenderInputs = ({
  fieldData, setFieldData, msData,
}) => {
  const [msOptions, setMsOptions] = useState({
    data: [],
    selectValuse: '',
    keys: '',
  });

  const handleInput = (e) => {
    const { name, value } = e.target;
    const validData = { ...fieldData, [name]: value };
    if (name === 'bunchType') {
      setFieldData({ ...validData, bunchObjectsIds: [] });
    } else {
      setFieldData({ ...validData });
    }
  };


  useEffect(() => {
    switch (fieldData.bunchType) {
      case ('none'):
        setMsOptions({ data: [], selectValuse: '', keys: '' });
        break;
      case ('wp_id'):
        setMsOptions({ data: msData.wplaces, selectValuse: 'name', keys: 'id' });
        break;
      case ('modified_by_id'):
        setMsOptions({ data: msData.users, selectValuse: 'fio', keys: 'id' });
        break;
      case ('q_id'):
        setMsOptions({ data: msData.queues, selectValuse: 'fullname', keys: 'id' });
        break;
      default:
        setMsOptions({ data: [], selectValuse: '', keys: '' });
        break;
    }
  }, [fieldData, msData.wplaces, msData.users, msData.queues]);

  const MultiSelectItemList = (list) => {
    const newobj = [];
    list.forEach((element) => {
      newobj.push(element);
    });
    setFieldData({ ...fieldData, bunchObjectsIds: newobj });
  };

  const typeOptions = () => [
    { name: 'Не выбрано', id: 'none' },
    { name: 'По рабочим местам', id: 'wp_id' },
    { name: 'По операторам', id: 'modified_by_id' },
    { name: 'По услугам', id: 'q_id' },
  ];

  const {
    name, description, bunchType, bunchObjectsIds,
  } = fieldData;

  const items = [
    {
      label: 'Имя:', name: 'name', value: name || '', required: true,
    },
    {
      label: 'Описание:', name: 'description', value: description || '', required: true,
    },
    {
      label: 'Тип:', name: 'bunchType', noDefault: true, type: 'select', value: bunchType || '', options: typeOptions(), required: true,
    },
  ];

  return (
    <>
      {items.map((item) => (
        <Input
          key={item.name}
          item={item}
          m="0 0 10px"
          onChange={handleInput}
        />
      ))}
      <div>
        <Label isRequired>Группируемые объекты:</Label>
        <MultiSelectNew
          data={msOptions.data}
          selectedList={MultiSelectItemList}
          selectValuse={msOptions.selectValuse}
          keys={msOptions.keys}
          defaultSelect={bunchObjectsIds}
        />
      </div>
    </>
  );
};
export default RenderInputs;
