import React from 'react';
import DatePickers from 'react-datepicker';
import moment from 'moment';
import { Box } from '../style';
import SelectInput from '../../../../Elements/Input/SelectInput/SelectInput';
import MultiTimePicker from './multiTimePicker';
import { Input } from '../../../../../styles/Common';
import { ColorBox } from '../../../../Control/Devices/DeviceStyle/Components/style';
import { Checkbox } from '../../Poll/style';

const Form = ({
  options, data, onData, setData,
}) => {
  console.log(moment(data.day || data.dayId.day, 'DD.MM.YYYY').toDate(), moment().toDate());
  return (
    <>
      <Box>
        <p>Выберите услугу</p>
        <SelectInput onChange={(e) => onData(e.target.value, 'q_id')} noDefault width="100%" value={data.q_id} options={options} />
      </Box>
      <Box>
        <p>Выберите день</p>
        <DatePickers
          selected={moment(data.day || data.dayId.day, 'DD.MM.YYYY').toDate()}
          minDate={moment().toDate()}
          onChange={(e) => onData(e, 'calendarday_id')}
          dateFormat="dd.MM.yyyy"
        />
      </Box>
      <Box row>
        <div className="interval">
          <p>Интервал времени (минуты)</p>
          <Input type="number" onChange={(e) => onData(e.target.value, 'interval')} value={data.interval} />
        </div>
        <div>
          <p>Кол-во слотов на одно время</p>
          <Input type="number" onChange={(e) => onData(e.target.value, 'amount')} value={data.amount} />
        </div>
      </Box>
      <Box row m="0 0 30px 0">
        <MultiTimePicker data={data} currentDay={moment().format('DD.MM.YYYY') === data.day} onChange={setData} />
      </Box>
      <ColorBox
        style={{ justifyContent: 'flex-start', marginTop: 15 }}
        onClick={() => setData({ ...data, replace: !data.replace })}
      >
        <Checkbox
          active={data.replace}
          style={{ marginRight: 5 }}
        />
        <span style={{ flex: 'none' }}>Перезаписывать существующие слоты</span>
      </ColorBox>
    </>
  );
};
export default Form;
