import React, { useContext } from 'react';
import Select from '../../../../../../Elements/Input/SelectInput/SelectInput';
import BoardCtx from '../../BoardCtx';
import UppercaseComponent from '../../../Components/Uppercase';
import { TemplateBox, Title } from '../../../TerminalStyle/style';
import { Input } from '../../../../../../../styles/Common';

const CommonSettings = () => {
  const { layout, dispatch, fonts } = useContext(BoardCtx);
  const optionsColumns = [{ id: 1, name: 1 }, { id: 2, name: 2 }, { id: 3, name: 3 }, { id: 4, name: 4 }];

  const arrCheckbox = [
    { title: 'Часы в шапке', value: layout.clock, paramName: 'clock' },
    { title: 'Часы в бег. строке', value: layout.clockString, paramName: 'clockString' },
    { title: 'Круглые углы', value: layout.borderRadius, paramName: 'borderRadius' },
    { title: 'По вертикали', value: layout.vertical, paramName: 'vertical' },
    { title: 'Окно вызова', value: layout.window, paramName: 'window' },
    { title: 'Видео', value: layout.vid, paramName: 'vid' },
    { title: 'Новости', value: layout.tidings, paramName: 'tidings' },
    { title: 'Окно даты', value: layout.date, paramName: 'date' },
  ];

  if (layout.vertical) arrCheckbox.splice(3, 1);

  return (
    <>
      <div className="common-settings-box">
        <div className="common-settings-size">
          <TemplateBox start width="260px">
            <Title>Шрифт</Title>
            <Select
              width="260px"
              m={0}
              noDefault
              options={fonts}
              value={layout.font}
              onChange={(e) => dispatch({ type: 'params', param: 'font', value: e.target.value })}
            />
          </TemplateBox>
          <TemplateBox start width="160px">
            <Title>Количество колонок</Title>
            <Select
              width="160px"
              m={0}
              noDefault
              options={optionsColumns}
              value={layout.cols}
              onChange={(e) => dispatch({ type: 'params', param: 'cols', value: e.target.value })}
            />
          </TemplateBox>
          <TemplateBox start width="210px">
            <Title>Ширина табло</Title>
            <Input
              width="210px"
              m={0}
              value={layout.width}
              onChange={(e) => dispatch({ type: 'params', param: 'width', value: e.target.value })}
            />
          </TemplateBox>
          <TemplateBox start width="210px">
            <Title>Высота табло</Title>
            <Input
              width="210px"
              m={0}
              value={layout.height}
              onChange={(e) => dispatch({ type: 'params', param: 'height', value: e.target.value })}
            />
          </TemplateBox>
          <TemplateBox start width="160px">
            <Title>Кол-во строк у колонки</Title>
            <Input
              width="210px"
              min='1'
              m={0}
              max="15"
              type="number"
              value={layout.lines}
              onChange={(e) => dispatch({ type: 'params', param: 'lines', value: +e.target.value > 15 ? 15 : e.target.value })}
            />
          </TemplateBox>
        </div>
        <div className="commonSettingsBox">
          <UppercaseComponent arr={arrCheckbox} noText dispatch={dispatch} />
        </div>
      </div>
    </>
  );
};

export default CommonSettings;
