import React, { useEffect, useCallback, useState } from 'react';
import useRouter from 'use-react-router';
import { toast } from 'react-toastify';
import request from '../../../../utils/request';
import BoardStyle from './BoardStyle/BoardStyle';
import TerminalStyle from './TerminalStyle/TerminalStyle';

const DeviceStyle = (props) => {
  const { preloader, showError } = props;
  const [deviceData, setDeviceData] = useState({});
  const { match, history } = useRouter();
  const { id } = match.params;

  const fetchDevice = useCallback(async () => {
    preloader();
    try {
      const res = await request(`/server/api/satellite/${id}/`, 'get');
      setDeviceData(res.data.data);
    } catch (e) {
      showError(e.data.error);
    }
    preloader();
  }, [showError, preloader, id]);

  const handleSave = async (config) => {
    let zoneId = '';
    if (deviceData.zone) {
      zoneId = deviceData.zone.zoneId;
    }
    delete deviceData.zone;
    const newDeviceData = { ...deviceData, zoneId, config: { ...deviceData.config, ...config } };
    preloader();
    try {
      await request(`/server/api/satellite/${id}`, 'update', newDeviceData);
      toast.success('Выполнено');
      fetchDevice();
    } catch (e) {
      showError(e.data.error);
    }
    preloader();
  };

  useEffect(() => {
    fetchDevice();
  }, [fetchDevice]);

  const { type } = deviceData;
  return (
    <>
      {type === 'board' || type === 'board-lottery' ? <BoardStyle history={history} config={deviceData.config} handleSave={handleSave} showError={showError} /> : null}
      {type === 'terminal' && <TerminalStyle history={history} deviceData={deviceData} id={match.params.id} handleSave={handleSave} />}
    </>
  );
};

export default DeviceStyle;
