import React from 'react';
import Config from './TableConfig';
import Preloader from '../../Elements/Preloader/Preloader';
import Table from '../../Elements/Table/Table';

const ActionLogTable = ({
  userList, actionList, pagination, onTableChange,
}) => {
  const users = {};
  if (userList) {
    userList.forEach((user) => {
      users[Number(user.id)] = user.username;
    });
  }
  const Setting = Config(users);

  const { columns } = Setting;

  return (
    <React.Fragment>
      {actionList === null || userList === null
        ? <Preloader isOpen small /> : actionList === undefined
          ? <div>Параметров нет</div> : (
            <Table
              data={actionList}
              pagination={pagination}
              columns={columns}
              keys="id"
              onTableChange={onTableChange}
              remote={{ pagination: true, filter: true, sort: true }}
              defaultSorted={[{
                dataField: 'created_on',
                order: 'desc',
              }]}
            />
          ) }
    </React.Fragment>
  );
};

export default React.memo(ActionLogTable);
