import React, { useEffect } from 'react';
import axios from 'axios';
import moment from 'moment';
import { PolicyContainer } from '../../../../style/style';
import PolicyBB from './policyBB';
import { updateScanPolicyStore } from '../../../../../../../../../../store/scanPolicy';

const PolicyScan = ({
  title, headUrl, terminalId, setList, setError, button, satelliteParams, startScreen,
}) => {
  const { satelliteIp, satellitePort } = satelliteParams;
  const getData = async () => {
    const response = await axios.get(`/proxySatellite/gate/cardreader?satelliteIp=${satelliteIp}&satellitePort=${satellitePort}`);

    return response.data;
  };

  useEffect(() => {
    const poll = setInterval(() => {
      getData()
        .then(async (res) => {
          if (res.done && res.oms) {
            console.log(res.oms);
            const { omsnum, birthdate } = res.oms;
            clearInterval(poll);
            const bDate = moment(birthdate, 'DDMMYYYY').format('YYYY-MM-DD');
            const { data: axData } = await axios.get(`/api/extGateway?act=get_external_precs&npolis=${omsnum}&bdate=${bDate}`);
            if (axData.status === 'ok') {
              const { data: rData } = axData;
              if (rData) {
                const { records: respData } = rData;
                console.log('records === ', respData);
                if (respData) {
                  updateScanPolicyStore({ activeScreen: 'list', records: respData });
                } else {
                  updateScanPolicyStore({ activeScreen: 'error', errorText: '' });
                }
              }
              // history.push(`${headUrl}/${terminalId}/scanPolicy/list`);
            } else {
              updateScanPolicyStore({ activeScreen: 'error', errorText: 'Мы сожалеем,<br/> данный полис не найден' });
            }
          } else if (res.done && !res.oms) {
            updateScanPolicyStore({ activeScreen: 'error', errorText: 'Мы сожалеем,<br/> данный полис не найден' });
          }
        });
    }, 1000);
    return () => clearInterval(poll);
  }, []);

  return (
    <PolicyContainer>
      <div className="policyBox">
        <h2 style={{ ...title, fontSize: title.fontSize * 0.8 }}>Пожалуйста,<br /> вставьте ваш полис<br /> в считыватель</h2>
      </div>
      <PolicyBB button={button} startScreen={startScreen} />
    </PolicyContainer>
  );
};

export default PolicyScan;
