import React, { useState } from 'react';
import Modal from '../../../Elements/Modal/Modal';
import request from '../../../../utils/request';
import RenderInputs from './RenderInputs';
import ModalButtons from '../../../Elements/Modal/ModalButtons';
import AddButton from '../../../Elements/Button/AddButton';

const NewField = ({
  isOpen, toggleOpen, fetchFields, fieldUpdate, setFieldData,
  fieldData, initialData, setFieldUpdate, preloader,
  bookUpdate, setBookUpdate, showError, write,
}) => {
  const [isValid, setIsValid] = useState(false);


  const resetModal = () => {
    setFieldData({ ...initialData });
    toggleOpen();
    setFieldUpdate(false);
    setIsValid(false);
    setBookUpdate(false);
  };

  const addField = async () => {
    if (isValid) {
      try {
        await request('/server/api/ticketsfields/', 'add', { ...fieldData });
        fetchFields();
        resetModal();
      } catch (e) {
        showError(e.data.error);
      }
    }
  };

  const updateField = async () => {
    if (isValid) {
      const itemId = fieldData.id;
      delete fieldData.id;
      await request(`/server/api/ticketsfields/${itemId}/`, 'update', { ...fieldData });
      fetchFields();
      resetModal();
    }
  };

  return (
    <>
      <AddButton toggle={toggleOpen} />
      <Modal title={fieldUpdate ? 'Редактирование поля' : 'Добавление поля'} isOpen={isOpen} toggleOpen={resetModal}>
        <RenderInputs
          fieldData={fieldData}
          setFieldData={setFieldData}
          setIsValid={setIsValid}
          preloader={preloader}
          write={write}
          fieldUpdate={fieldUpdate}
          bookUpdate={bookUpdate}
          showError={showError}
        />
        <ModalButtons isValid={isValid} update={fieldUpdate} addFunc={addField} updateFunc={updateField} reset={resetModal} />
      </Modal>
    </>
  );
};

export default React.memo(NewField);
