import React from 'react';
import { useSelector } from 'react-redux';
import { Container, BottomButton } from '../../style/style';
import { clearBeforeDataScene, resetTicket, setInfoScreen } from '../../utils/actions';
import store from '../../utils/store';
import history from '../../../../../../../../utils/customHistory';

const { dispatch } = store;

const BottomButtons = (props) => {
  const { scene, selectScreen, noBack } = props;
  const {
    style, headUrl, id, startScreen,
  } = useSelector((state) => ({ config: state.config, style: state.style.bottomButton, ...state.config }));
  const {
    position, top, left, styleShow, textShow, iconShow, contentPosition, iconPosition,
  } = style;


  const home = {
    id: 'home',
    icon: style.home.background,
    func: () => {
      history.push(startScreen);
      dispatch(resetTicket());
    },
    text: style.home.text,
    show: style.home.show,
  };
  const back = {
    id: 'back',
    icon: style.back.background,
    func: () => {
      dispatch(clearBeforeDataScene(scene.screenId));
      history.goBack();
    },
    text: style.back.text,
    show: style.back.show,
  };
  const info = {
    id: 'info',
    icon: style.info.background,
    func: () => {
      dispatch(setInfoScreen(scene.help));
      history.push(`${headUrl}/${id}/infoScene/`);
    },
    text: style.info.text,
    show: style.info.show,
  };

  const next = {
    id: 'next', func: () => history.push(scene.screenPath), name: style.ready.text, show: true,
  };

  const styleButton = { ...style, position: 'static' };
  const arrButtons = [back, home];
  if (noBack) arrButtons.splice(0, 1);
  if (scene.help) arrButtons.push(info);
  const reg = /multiScreen|inputScreen/;
  if (reg.test(scene.type)) arrButtons.push(next);

  if (!styleShow) {
    delete styleButton.background;
    delete styleButton.boxShadow;
    styleButton.borderWidth = 0;
  }
  return (
    <Container style={{
      position: !position ? 'absolute' : 'static', top, left, width: !position ? 'min-content' : '100%',
    }}
    >
      {arrButtons.map((el) => el.show ? (
      <BottomButton
        key={el.id}
        style={{
          ...styleButton,
          width: el.name
            ? 'min-content'
            : style.width,
          flexDirection: contentPosition ? 'column' : 'row',
        }}
        onClick={selectScreen
          ? () => { }
          : el.func}
      >
        {el.name
          ? <p>{el.name}</p>
          : (
            <>
              {Boolean(iconShow) && <img style={{ order: iconPosition ? 0 : 1, ...styleButton.icon }} src={el.icon} alt="" />}
              {Boolean(textShow) && <p style={{ order: !iconPosition ? 0 : 1 }}>{el.text}</p>}
            </>
          )}
      </BottomButton>
      ) : null)}
    </Container>
  );
};

export default BottomButtons;
