import React from 'react';
import {
  BoardVideoBox, VideoBoxTitle, VideoRemoveButton, VideosBox, VideoList,
} from '../../../../styles/Custom';
import { ReactComponent as Trash } from '../../../../images/bin.svg';
import { ReactComponent as Check } from '../../../../images/chekbox.svg';

const List = ({ videos = [], deleteVideo, setShow }) => {

  return (
    <VideoList>
      <p>Список загруженных видео</p>
      <VideosBox>
        {videos.map((el) => (
          <BoardVideoBox key={el.id}>
            <video
              src={el.url}
              width="100%"
              height="auto"
              id="videoBox"
              autoPlay={false}
              loop
              controls
            >
              <track default kind="captions" />
            </video>
            <VideoBoxTitle>
              <p>{el.name}</p>
              <VideoRemoveButton title="Отображение видео на табло" onClick={() => setShow(el.id)} show={el.show}>
                <Check />
              </VideoRemoveButton>
              <VideoRemoveButton onClick={() => deleteVideo(el.id)} show>
                <Trash />
              </VideoRemoveButton>
            </VideoBoxTitle>
          </BoardVideoBox>
        ))}
      </VideosBox>
    </VideoList>
  );
};

export default List;
