import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import SqlQuery from './SqlQuery';
import TableConfig from './TableConfig';
import Table from '../../../../../Elements/Table/Table';
import { PreloaderBox } from '../../../../../../styles/Common';
import Preloader from '../../../../../Elements/Preloader/Preloader';
import { QalityAvgMarkBox } from '../../../../../../styles/ReportTableColumns';

const SessionResultTable = ({ sessionDataId }) => {
  const [ReportParams, setReportParams] = useState();
  const { reportConfig } = useSelector((state) => state);


  useEffect(() => {
    SqlQuery(sessionDataId).then((resp) => {
      const { dataObjects, query, avgMark } = resp;
      const params = {
        items: dataObjects,
        query,
        TableConfig: TableConfig(),
        avgMark,
      };
      setReportParams(params);
    });
  }, [reportConfig]);

  return (
    <>
      { ReportParams !== undefined
        ? (
          <PreloaderBox>
            <QalityAvgMarkBox>
              <div style={{ marginRight: '10px' }}>Средняя оценка</div>
              <div style={{ fontWeight: '700' }}>{ReportParams.avgMark.toFixed(2)}</div>
            </QalityAvgMarkBox>
            <Table
              keys="0"
              data={ReportParams.items}
              columns={ReportParams.TableConfig.columns}
              disablePagination
            />
          </PreloaderBox>
        )
        : <Preloader isOpen small />}
    </>
  );
};

export default SessionResultTable;
