import React, { useState } from 'react';
import { FaPlus } from 'react-icons/fa';
import {
  FilterBox, SelectItem, InputBox, TextItem, ItemsBox,
} from '../../Queue/WorkPlaces/style';
import Select from '../Input/SelectInput/SelectInput';
import { BtnTable, Input } from '../../../styles/Common';
import { ReactComponent as Trash } from '../../../images/del.svg';

const FilterItem = ({
  item, dict, onChange, index, list, usedDict, removeFilter, user,
}) => {
  const [value, setValue] = useState('');
  let dictionary = {};
  const findSelect = (arr, it) => arr.some((el) => String(el) === String(it.id));
  const dopOptions = [];
  if (item.fid) {
    dictionary = dict.find((elem) => +elem.id === +item.fid);
    if (user) {
      item.fv.forEach((el) => dopOptions.push(el));
    } else {
      dictionary.values.forEach((elem) => dopOptions.push({ id: elem.id, name: elem.name }));
    }
  }
  let options = [];
  if (user) {
    dict.forEach((el) => options.push({ id: +el.id, name: el.name }));
  } else {
    list.forEach((el) => {
      for (let i = 0; i < dict.length; i += 1) {
        if (el.dict_id === dict[i].id) options.push({ id: dict[i].id, name: dict[i].name });
      }
    });
  }

  const addText = () => {
    onChange({ el: value, action: 1, index });
    setValue('');
  };

  options = options.filter((el) => !usedDict.some((elem) => +elem === +el.id) || +el.id === +item.fid);
  return (
    <FilterBox>
      <div>
        <Select
          m="0 0 10px 0"
          noDefault
          name="fid"
          value={item.fid && dict.find((elem) => +elem.id === +item.fid).id}
          options={options}
          onChange={({ target: { value, name } }) => onChange({ index, value, name })}
        />
        {!user && item.fid && dopOptions.map((element) => (
          <SelectItem
            key={element.id}
            onClick={() => onChange({
              index, value: element.id, name: 'fv',
            })}
            active={findSelect(item.fv, element)}
          >{element.name}
          </SelectItem>
        ))}
        <InputBox show={value.length > 0}>
          {user && <Input value={value} onChange={(e) => setValue(e.target.value)} style={{ minWidth: value.length > 0 ? 'calc(100% - 40px)' : '100%' }} placeholder="Введите текст" />}
          {value.length > 0 && <span onClick={addText}><FaPlus /></span>}
        </InputBox>
        <ItemsBox>
          {user && item.fid && dopOptions.map((el) => (
            <TextItem
              key={el}
              active={findSelect(item.fv, { id: el })}
            >
              <p>{el}</p>
              <span onClick={() => onChange({ el, index })}>×</span>
            </TextItem>
          ))}
        </ItemsBox>
      </div>
      <BtnTable m="15px 0 0" onClick={() => removeFilter(item)} data-name="del" active><Trash /></BtnTable>
    </FilterBox>
  );
};

export default React.memo(FilterItem);
