import React, { useEffect, useState, useCallback } from 'react';
import axios from 'axios';
import { Table } from 'antd';
import 'antd/dist/antd.css';
// import Table from '../Table/Table';
import IntervalTable from '../../Statistics/Reports/ReportList/ReportType/IntervalReport/IntervalTable';
import Preloader from '../Preloader/Preloader';

const PrepareTable = ({
  filters: { main }, keys, columns, type, url, expand,
}) => {
  console.log(keys, columns, type, url);
  const [tableParams, setTableParams] = useState();
  console.log(tableParams);
  const [showPreloader, setShowPreloader] = useState(true);


  const [pagination, setPagination] = useState({
    page: 1,
    sizePerPage: 10,
  });

  const onSizePerPageChange = useCallback((sizePerPageNumber, pageNumber) => {
    setPagination({
      ...pagination,
      sizePerPage: sizePerPageNumber,
      page: pageNumber,
    });
  }, [pagination]);

  const onPageChange = useCallback((pageNumber, sizePerPageNumber) => {
    setPagination({
      ...pagination,
      sizePerPage: sizePerPageNumber,
      page: pageNumber,
    });
  }, []);

  // const onTableChange = useCallback((type, newState) => {
  //   const { sortOrder } = newState;
  //   const { page, sizePerPage, filters } = newState;

  //   setPagination({
  //     ...pagination,
  //     page,
  //     sizePerPage,
  //     sortOrder,
  //     filters,
  //   });
  // }, []);

  const testFunc = async (page) => {
    const limit = pagination.sizePerPage;
    const offset = pagination.sizePerPage * (page - 1);

    console.log('page === ', page);
    console.log('limit === ', limit);
    console.log('offset === ', offset);
    const filters = {
      ...main, limit, offset,
    };

    const token = window.localStorage.getItem('token');
    setShowPreloader(true);
    const res = await axios({
      method: 'post',
      url,
      data: {
        token,
        filters: { main: { ...filters } },
        type,
      },
    });

    if (type === 'intervalReport') {
      const { data: { answer } } = res;
      const ww = { answer };
      setTableParams(ww);
    } else {
      const { data: { answer } } = res;
      const allItemCnt = answer[0] ? Number(answer[0].total_rows_count) : 0;
      const ww = {
        pagination: {
          current: page,
          pageSize: pagination.sizePerPage,
          total: allItemCnt,
          onChange: onPageChange,
          onShowSizeChange: onSizePerPageChange,
          // onTableChange,
          showSizeChanger: true,
        },
        items: [...answer],
      };
      setTableParams(ww);
    }
    setShowPreloader(false);
  };

  useEffect(() => { testFunc(1); }, [main]);
  useEffect(() => { testFunc(pagination.page); }, [pagination]);


  const buildItervalTable = () => (
    <>
      {tableParams.answer.myData.length !== 0 ? (
        <IntervalTable
          data={tableParams.answer}
        />
      ) : (<div className="itervalReprotBox">По Вашему запросу ничего не найдено</div>)}
    </>
  );


  return tableParams ? type === 'intervalReport' ? (buildItervalTable())
    : showPreloader ? (<Preloader isOpen={showPreloader} />) : (
      <Table
        keys={keys}
        dataSource={tableParams.items}
        columns={columns}
        size="small"
        pagination={tableParams.pagination}
      />
    )
    : (null);
};

export default PrepareTable;
