import React from 'react';
import {
  AccordionBox, AccordionTitle, AccordionContent, AccordionTitleText, AccordionTitleIcon,
} from '../../../styles/Accordion';
import { ReactComponent as RightArrow } from '../../../images/right-arrow.svg';
import { useToggle } from '../../../hooks/useToggle';

const Accordion = ({ title, content, letOpen }) => {
  const [isOpen, setIsOpen] = useToggle(!!letOpen);

  const openFilter = () => {
    setIsOpen(true);
  };

  return (
    <AccordionBox>
      <AccordionTitle onClick={openFilter}>
        <AccordionTitleText>
          {title}
        </AccordionTitleText>
        <AccordionTitleIcon isOpen={isOpen}>
          <RightArrow />
        </AccordionTitleIcon>
      </AccordionTitle>
      <AccordionContent isOpen={isOpen}>{content}</AccordionContent>
    </AccordionBox>
  );
};

export default Accordion;
