import React, { useContext } from 'react';
import Timer from './Timer';
import handleFz from '../../../../../../../../../utils/handleFz';
import BoardCtx from '../../../../BoardCtx';


const Header = ({ layout, text }) => {
  const {
    setActiveEl, elWidth,
  } = useContext(BoardCtx);
  const {
    header, footer, logo, clockString,
  } = layout;
  const { logotype } = footer;
  const {
    background, order, fontSize1, color1, vertical, fontWeight1, height, color2, fontSize2, fontSize3, fontFamily, positionBottom,
    colorHeaderMain, colorHeaderSub, fontSizeHeaderMain, fontSizeHeaderSub,
  } = header;

  const headerBg = {
    background,
  };
  if (layout.images.headerBg) {
    headerBg.background = `url(${layout.images.headerBg})`;
  }

  const timeFZ = handleFz(fontSize2, elWidth);
  const dateFZ = handleFz(fontSize3, elWidth);

  const styleHeaderText = {
    fontSize: handleFz(fontSize1, elWidth),
    color: color1,
    order: order ? 1 : 0,
    margin: layout.clock ? order ? '0 0 0 40px' : '0 40px 0 0' : 0,
    fontFamily,
    fontWeight: fontWeight1,
    textAlign: order ? 'left' : 'right',
  };
  const styleHeaderMain = {
    fontSize: handleFz(fontSizeHeaderMain, elWidth),
    color: colorHeaderMain,
    fontFamily,
  };
  const styleHeaderSub = {
    fontSize: handleFz(fontSizeHeaderSub, elWidth),
    color: colorHeaderSub,
    fontFamily,
  };
  const styleHeader = {
    ...layout.header,
    ...headerBg,
    margin: vertical ? '0 0 10px 0' : '0',
    order: positionBottom ? 2 : 0,
    minHeight: height,
    maxHeight: height,
  };
  return (
    <>
      {layout.header && (
      <div
        className="boardHeaderBox"
        style={styleHeader}
        onClick={() => setActiveEl('header')}
      >
        <div className="boardHeaderLogo" style={{ order: order ? 1 : 0, height: '100%' }}>
          {!logotype && <img src={layout.images.headerLogo} alt="" style={logo} />}
        </div>
        <div style={{
          display: 'flex', flex: '1 1', justifyContent: 'center', alignItems: 'center', flexDirection: 'column',
        }}
        >
          <p style={styleHeaderMain}>{text.headerMain}</p>
          <p style={styleHeaderSub}>{text.headerSub}</p>
        </div>
        <div className="boardHeaderTextBox">
          <div className="boardHeaderTextTimerBox">
            <p
              className="boardHeaderText"
              style={styleHeaderText}
            >
              {text.header1}
            </p>
            {layout.clock && <Timer styles={layout.header} timeFZ={timeFZ} order={order} dateFZ={dateFZ} color={color2} />}
          </div>

          {Boolean(header.runstring) && (
            <marquee
              className="footerString"
              style={{
                ...footer, fontFamily, fontSize: footer.fontSize1, color: footer.color1,
              }}
            >
              {text.footer1}
              {clockString && <Timer styles={footer} timeFZ={footer.fontSize2} order={footer.order} dateFZ={footer.fontSize3} color={footer.color2} />}
            </marquee>
          )}
        </div>
      </div>
      )}
    </>
  );
};

export default Header;
