import React, { useState } from 'react';
import Table from '../../../../Elements/Table/Table';
import Config from './TableConfig';
import DeleteWarning from '../../../../Elements/Modal/DeleteWarning';
import { useToggle } from '../../../../../hooks/useToggle';
import DeleteItem from '../../../../../utils/deleteItem';

const IntervalsTable = ({
  intervalList, setIntervalUpdate, setIntervalData, toggleOpen, preloader, fetchIntervals, write, showError,
}) => {
  const [intervalId, setIntervalId] = useState(false);
  const [intervalName, setIntervalName] = useState(false);
  const [showDelWarn, setShowDelWarn] = useToggle(false);

  const delInterval = async () => {
    await DeleteItem({
      preloader, url: `/server/api/dworkinterval/${intervalId}/`, fetchData: fetchIntervals, setShowWarn: setShowDelWarn, setId: setIntervalId, showError,
    });
  };

  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const {
      id, name, status, timebegin, timeend, description,
    } = row;
    if (dataset.name === 'del') {
      setIntervalId(id);
      setIntervalName(name);
      setShowDelWarn();
    } else if (dataset.name === 'update') {
      setIntervalUpdate(true);
      setIntervalData({
        id, name, status, timebegin, timeend, description,
      });
      toggleOpen();
    }
  };
  const Setting = Config(write);
  const { columns } = Setting;

  return (
    <>
      {intervalList.data.length > 0 && <Table keys="id" data={intervalList.data} handleIcon={handleTableIcon} columns={columns} />}
      <DeleteWarning name={intervalName} confirmDel={delInterval} isOpen={showDelWarn} toggleOpen={setShowDelWarn} />
    </>
  );
};

export default React.memo(IntervalsTable);
