import React, {
  useState, useEffect, useRef, useCallback,
} from 'react';
import useRouter from 'use-react-router';
import { toast } from 'react-toastify';
import { Input } from '../../../../styles/Common';
import Question from './question';
import AddButton from '../../../Elements/Button/AddButton';
import request from '../../../../utils/request';
import { Box, TitleBox } from './style';
import { ReactComponent as Checkbox } from '../../../../images/chekbox.svg';
import { ReactComponent as Edit } from '../../../../images/edit.svg';

const CardEdit = ({ getData, update, showError }) => {
  const [value, setValue] = useState('');
  const [item, setItem] = useState({});
  const [edit, setEdit] = useState(false);
  const [name, setName] = useState(false);
  const input = useRef(null);
  const { match } = useRouter();


  const getItem = useCallback(async () => {
    const { data } = await request(`/server/api/qcsurvey/${match.params.ticketId}`, 'get');
    console.log(data.data);
    setItem(data.data);
    setName(data.data.surveyname);
  }, [match]);

  useEffect(() => {
    getItem();
  }, [getItem]);

  const addQestion = async () => {
    const newQuest = {
      questiontext: value,
      operator_mark: false,
      correctAnswers: [],
    };
    try {
      const d = await request('/server/api/qcquestion/', 'add', newQuest);
      const dataUpdate = {
        surveyname: item.surveyname,
        connectedQuestions: [...item.connectedQuestions.map((el) => el.id), d.data.id],
      };
      await request(`/server/api/qcsurvey/${item.id}/`, 'update', dataUpdate);

      const newItem = {
        ...item,
        id: item.id,
        connectedQuestions: [
          ...item.connectedQuestions,
          {
            answers: [],
            id: d.data.id,
            questiontext: value,
          },
        ],
      };
      setItem(newItem);
    } catch (e) {
      showError(e.data.error);
    }
  };

  const changeName = async () => {
    const data = {
      surveyname: name,
      connectedQuestions: [...item.connectedQuestions.map((el) => el.id)],
    };
    try {
      await request(`/server/api/qcsurvey/${item.id}/`, 'update', data);
      toast.success('Выполнено');
      setEdit(false);
    } catch (e) {
      showError(e.data.error);
    }
  };

  return (
    <div style={{ width: '100%' }}>
      <TitleBox>
        <h3>Название опроса:</h3>
        <Input
          style={{ maxWidth: edit ? '100%' : '0px', padding: edit ? '10px 15px' : '0px' }}
          ref={input}
          type="text"
          onChange={(e) => setName(e.target.value)}
          defaultValue={item.surveyname}
          onFocus={() => setEdit(true)}
        />
        {!edit && <p>{name || item.surveyname}</p>}
        {edit ? <Checkbox onClick={changeName} /> : (
          <Edit onClick={() => {
            setEdit(true);
            input.current.focus();
          }}
          />
        )}
      </TitleBox>

      <Box>
        <h4>Добавить новый вопрос</h4>
        <Input value={value} onChange={(e) => setValue(e.target.value)} placeholder="Название вопроса" />
        <AddButton toggle={() => (value ? addQestion() : setValue(''))} />
      </Box>
      <h3 style={{ textAlign: 'center' }}>Список вопросов</h3>
      <div>
        {item.connectedQuestions && item.connectedQuestions.map((el) => <Question getData={getData} update={update} parent={item} setItem={setItem} key={el.id} item={el} />)}
      </div>
    </div>
  );
};

export default CardEdit;
