import React, { useState, useEffect, useCallback } from 'react';
import Queue from './Queue/QueueDoughnut';
import ReportTitle from '../../../../ReportTitle';
import AvgWaitTimeQuery from './AvgWaitTime/SqlQuery';
import AvgProcTimeQuery from './AvgProcTime/SqlQuery';
import QueueQuery from './Queue/SqlQuery';
import { PreloaderBox } from '../../../../../../../../../styles/Common';
import { ReportBox } from '../../../../../../../../../styles/ReportTableColumns';
import Preloader from '../../../../../../../../Elements/Preloader/Preloader';
import appStore from '../../../../../../../../../store/appStore';


const DoughnutsReport = () => {
  const [dataParanms, setDataParanms] = useState();

  const startDateMoment = appStore.useState((s) => s.reportConfig.startDate);
  const endDateMoment = appStore.useState((s) => s.reportConfig.endDate);
  const dateFormat = 'YYYY-MM-DD HH:mm:ss';
  const startDate = startDateMoment.format(dateFormat);
  const endDate = endDateMoment.format(dateFormat);

  const fetchMonitoringData = useCallback(async () => {
    const QueueData = await QueueQuery(startDate, endDate);
    const AvgProcTimeData = await AvgProcTimeQuery(startDate, endDate);
    const AvgWaitTimeData = await AvgWaitTimeQuery(startDate, endDate);


    const data = {
      Queue: QueueData.dataObjects,
      AvgProcTime: AvgProcTimeData.dataObjects,
      AvgWaitTime: AvgWaitTimeData.dataObjects,
    };

    setDataParanms(data);
  }, [startDate, endDate]);


  useEffect(() => {
    fetchMonitoringData();
  }, [startDate, endDate, fetchMonitoringData]);

  //   <AvgProcTime
  //   data={dataParanms.AvgProcTime}
  // />
  // <AvgWaitTime
  //   data={dataParanms.AvgWaitTime}
  // />

  return (
    <ReportBox>
      <ReportTitle name="Распределение талонов по очередям" />
      { dataParanms ? (
        <>
          <Queue
            data={dataParanms.Queue}
          />
        </>
      ) : (
        <PreloaderBox>
          <Preloader isOpen small />
        </PreloaderBox>
      )}
    </ReportBox>
  );
};

// export default DoughnutsReport;

export default DoughnutsReport;
