import React from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment';
import 'moment/locale/ru';
import ruDate from 'date-fns/locale/ru';
import { isAfter } from 'date-fns';
import {
  ReportRangeItem, FilterBox, FilterBoxTitle, FilterItemBox,
} from '../../../../../styles/ReportFilters';
import appStore from '../../../../../store/appStore';

const ReportRange = ({ changeFilter }) => {
  const reportConfig = appStore.useState((s) => s.reportConfig);

  const startDate = moment(appStore.useState((s) => s.reportConfig.startDate), 'dd.MM.yyyy HH:mm');
  const endDate = moment(appStore.useState((s) => s.reportConfig.endDate), 'dd.MM.yyyy HH:mm');

  const dpConfig = {
    selectsStart: true,
    showTimeSelect: true,
    timeFormat: 'HH:mm',
    timeIntervals: 15,
    locale: ruDate,
    timeCaption: 'Время',
    maxDate: new Date(),
    dateFormat: 'dd.MM.yyyy HH:mm',
  };

  const handleDataChange = ({ sDate, eDate }) => {
    console.log('sDate =', sDate);
    console.log('eDate =', eDate);

    const defaultStartDate = startDate;
    const defaultEndDate = endDate;
    const newStartDate = sDate || defaultStartDate.toDate();
    let newEndDate = eDate || defaultEndDate.toDate();

    newEndDate = isAfter(newStartDate, newEndDate) ? newStartDate : newEndDate;
    const time = moment(newStartDate);
    const endTime = moment(newEndDate);
    const dateFormat = 'DD.MM.YYYY HH:mm';

    appStore.update((s) => {
      s.reportConfig = {
        ...reportConfig,
        startDate: time,
        endDate: endTime,
      };
    });

    changeFilter('startDate', time.format(dateFormat));
    changeFilter('endDate', endTime.format(dateFormat));
  };

  return (
    <FilterBox>
      <FilterBoxTitle>Период</FilterBoxTitle>
      <FilterItemBox range>
        <ReportRangeItem>
          <div style={{ marginRight: '8px' }}>С</div>
          <DatePicker
            {...dpConfig}
            selectsStart
            selected={startDate.toDate()}
            onChange={(date) => handleDataChange({ sDate: date })}
          />
        </ReportRangeItem>
        <ReportRangeItem left>
          <div style={{ marginRight: '8px' }}>По</div>
          <DatePicker
            {...dpConfig}
            selectsEnd
            selected={endDate.toDate()}
            minDate={startDate.toDate()}
            onChange={(date) => handleDataChange({ eDate: date })}
          />
        </ReportRangeItem>
      </FilterItemBox>
    </FilterBox>
  );
};

export default ReportRange;
