import React from 'react';
import ReactDOM from 'react-dom';
import { PreloaderBackground, ModalBox } from '../../../styles/Modal';
import { Animate } from '../../Control/Devices/DeviceStyle/TerminalStyle/Terminal/scenes/preloader/preload';


const PortalPreloader = ({ className }) => {
  const modalMarkup = (
    <PreloaderBackground className={className}>
      <ModalBox>
        <Animate />
      </ModalBox>
    </PreloaderBackground>
  );

  return ReactDOM.createPortal(modalMarkup, document.body);
};

export default PortalPreloader;
