import React, { useState } from 'react';
import { FaChevronDown, FaChevronUp } from 'react-icons/fa';
import { ReactComponent as Eye } from '../../../../../images/eye.svg';

const SelectedDay = ({
  list, findRecord, copyDay, setClear, showClear, selectedQueue,
}) => {
  const isQ = selectedQueue === list[0].q_id;
  const [open, openList] = useState(isQ);
  return (
    <div className="selectedDay" style={{ order: isQ ? 1 : 2 }}>
      <p onClick={() => openList((l) => !l)}>
        {list[0].qName}
        <span>{open ? <FaChevronUp /> : <FaChevronDown />}</span>
      </p>
      <div className={`flex recordBox ${open ? 'openlist' : ''}`}>
        {list.map((el, index) => (
          <li key={index} className="recordItem">
            <div className="amount">{el.length}</div>
            <p>{el.time}</p>
            <>
              <span onClick={() => findRecord(el.time, el.q_id)}>
                <Eye />
              </span>
            </>
          </li>
        ))}
        <div className="calendarButtonBox">
          <li className="recordItem clear green" onClick={() => copyDay(true, list[0].q_id)}>Копировать день</li>
          {showClear && <li className="recordItem clear" onClick={() => setClear(list[0].q_id)}>Очистить день</li>}
        </div>
      </div>
    </div>
  );
};

export default SelectedDay;
