import React, { useState, useEffect } from 'react';
import timer from '../../../../../../../../../utils/timer';

const Timer = ({
  styles, handleFz, order, elWidth, dateFZ, timeFZ,
}) => {
  const [timeConfig, setTimeConfig] = useState(timer(1, true));

  useEffect(() => {
    const timerMin = setInterval(() => {
      const newTime = timer(1, true);
      setTimeConfig(newTime);
    }, 59000);

    return () => clearInterval(timerMin);
  }, []);

  const {
    format, time, date, d, day, month, year,
  } = timeConfig;
  const formattedDate = format ? (
    <>
      {day}
      <br />
      {`${d} ${month} ${year}`}
    </>
  ) : (<>{date}</>);

  const {
    fontFamily, color2,
  } = styles;
  return (
    <div className="boardTimerBox" style={{  alignItems: order ? 'flex-start' : 'flex-end' }}>
      <p
        style={{
          fontSize: `${timeFZ}px`,
          fontFamily,
          // lineHeight: `${handleFz(fontSize2, elWidth) - handleFz(5, elWidth)}px`,
          color: color2,
          margin: 0,
        }}
      >
        {time}
      </p>
      <p style={{ fontSize: `${dateFZ}px`, fontFamily, color: color2, margin: 0 }}>
        {formattedDate}
      </p>
    </div>
  );
};

export default Timer;
