import React, {
  useEffect, useRef, useCallback, useState,
} from 'react';
import {
  Body, ErrorText,
} from '../../style/style';
import Request from '../../utils/request';
import { resetTicket } from '../../utils/actions';
import findError from '../../utils/findError';
import getElements from './getElements';

const Canvas = ({
  style, data, config, dispatch, history, startScreen, errorUrl,
}) => {
  const [error, setError] = useState(null);
  const [finishDraw, setFinishDraw] = useState(false);
  const { number } = data.ticket;
  const styleTicket = JSON.parse(data.style);
  const time = useRef(null);

  const printTicket = useCallback((d) => {
    const { satelliteIp, satellitePort } = config;
    d.satelliteIp = satelliteIp;
    d.satellitePort = satellitePort;
    Request('/proxySatellite/gate/print', 'no', d)
      .then((res) => {
        if (!res.data.success) {
          setError('Ошибка печати. Проверьте принтер.');
        }
        time.current = setTimeout(() => {
          history.push(startScreen);
          dispatch(resetTicket());
          clearTimeout(time.current);
        }, 10000);
      })
      .catch((res) => {
        findError({
          res: res.response, history, dispatch, errorUrl,
        });
      });
  }, [startScreen, history, dispatch, config, errorUrl]);

  const ref = useRef(null);

  useEffect(() => {
    const ctx = ref.current.getContext('2d');
    ref.current.width = 500;
    ref.current.height = 500;
    ctx.clearRect(0, 0, ctx.width, ctx.height);
    ctx.fillStyle = '#fff';
    ctx.rect(0, 0, 500, 500);
    ctx.fill();
    ctx.textBaseline = 'top';
    getElements({
      ctx, style: styleTicket, data, setFinishDraw, ref, number,
    });
  }, [data, number, styleTicket]);

  useEffect(() => {
    if (finishDraw) {
      setTimeout(() => {
        const base64 = ref.current.toDataURL();
        const body = { data: base64.split(',')[1] };
        printTicket(body);
      }, 100);
    }
  }, [finishDraw, printTicket, ref.current]);

  return (
    <Body style={style.body}>
      <canvas ref={ref} style={{ width: 500, height: 500, position: 'relative' }} />
      {error && <ErrorText>{error}</ErrorText>}
    </Body>
  );
};

export default Canvas;
