import React, { useEffect } from 'react';
import moment from 'moment';
import { toast } from 'react-toastify';
import Form from './components/create';
import MultiForm from './components/multiForm';
import AddButton from '../../../Elements/Button/AddButton';
import Modal from '../../../Elements/Modal/Modal';
import ModalButtons from '../../../Elements/Modal/ModalButtons';
import request from '../../../../utils/request';

const Create = ({
  isOpen, toggleOpen, isUpdate, fetch, options, update, data, setData, showError, preloader,
}) => {
  const getDayId = async (value) => {
    const dd = +moment(value, 'DD.MM.YYYY').format('DD');
    const mm = +moment(value, 'DD.MM.YYYY').format('MM');
    const d = await request('/server/api/dcalendar/', 'get', {}, `year=${moment(value, 'DD.MM.YYYY').format('YYYY')}`);
    return d.data.data.find((el) => el.dayOfMonth === dd && el.monthNumber === mm).dayId;
  };

  const onData = async (value, params) => {
    if (params === 'calendarday_id') {
      const day = await getDayId(value);
      setData({ ...data, [params]: day, day: moment(value, 'YYYY.MM.DD').format('DD.MM.YYYY') });
    } else {
      setData({ ...data, [params]: value });
    }
  };

  const addMulti = async () => {
    preloader();
    const calendarday_id = await getDayId(data.day || data.dayId.day);

    const {
      amount, interval, approved, startTime, endTime, q_id, replace,
    } = data;

    const body = {
      q_id,
      amount,
      step: +interval,
      replace,
      approved,
      pastart_interval: `${moment(data.day || data.dayId.day, 'DD.MM.YYYY').format('YYYY.MM.DD')} ${startTime}`,
      paend_interval: `${moment(data.day || data.dayId.day, 'DD.MM.YYYY').format('YYYY.MM.DD')} ${endTime}`,
    };
    try {
      await request(`/server/api/dcalendar/${calendarday_id}`, 'populate', body);
      toast.success('Выполнено');
      preloader();
      toggleOpen((e) => !e);
      fetch();
    } catch (e) {
      preloader();
      showError(e.data.error);
    }
  };

  const add = async () => {
    preloader();
    const calendarday_id = await getDayId(data.day || data.dayId.day);
    const body = {
      q_id: data.q_id,
      pastart_on: `${moment(data.day || data.dayId.day, 'DD:MM:YYYY').format('YYYY:MM:DD')} ${data.time}`,
      calendarday_id: data.calendarday_id || calendarday_id,
      approved: data.approved,
    };
    try {
      await request('/server/api/dpacalendarslot/', 'add', body);
      toast.success('Выполнено');
      preloader();
      toggleOpen((e) => !e);
      fetch();
    } catch (e) {
      preloader();
      showError(e.data.error);
    }
  };

  useEffect(() => {
    setData((v) => ({ ...v, q_id: options[0] ? options[0].id : '' }));
  }, [options, setData]);

  // console.log(`${moment(data.day || data.dayId.day, 'DD:MM:YYYY').format('YYYY:MM:DD')} ${data.time}`, );
  return (
    <div className="createBox">
      <AddButton toggle={() => toggleOpen(2)} text="Добавить слот" />
      <AddButton toggle={() => toggleOpen(1)} text="Множественное добавление" />
      <Modal title={isUpdate ? 'Редактирование записи' : isOpen === 2 ? 'Добавление записи' : 'Добавление записей'} toggleOpen={() => toggleOpen(false)} isOpen={isOpen}>
        {isOpen === 2 && <Form data={data} onData={onData} setData={setData} add={add} options={options} />}
        {isOpen === 1 && <MultiForm data={data} onData={onData} setData={setData} add={add} options={options} />}
        <ModalButtons isValid={data.q_id} updateFunc={update} addFunc={isOpen === 1 ? addMulti : add} update={isUpdate} reset={() => toggleOpen(false)} />
      </Modal>
    </div>
  );
};

export default Create;
