import React from 'react';
import { ColorBox, Checkbox } from '../style';

const uppercase = ({
  value, element, dispatch, title, nameParams = '',
}) => {
  const big = nameParams.match(/\./);

  const onChange = (e) => {
    if (!big) {
      dispatch({
        type: 'params',
        name: element,
        param: nameParams,
        value: !value,
      });
    } else {
      const arr = nameParams.split('.');
      dispatch({
        type: 'bigParams',
        parentName: element,
        name: arr[0],
        param: arr[1],
        value: !value,
      });
    }
  };

  return (
    <ColorBox>
      <Checkbox
        onClick={onChange}
        active={value}
      />
      <span style={{ textAlign: 'left' }} dangerouslySetInnerHTML={{ __html: title }} />
    </ColorBox>
  );
};


export default uppercase;
