import React, { useContext } from 'react';
import BoardCtx from '../BoardCtx';
import Layout from './Layout/Layout';
import { FixedBox, TerminalTransform } from '../../TerminalStyle/style';
import history from '../../../../../../utils/customHistory';

const BoardPreview = ({ handleConfig }) => {
  const { layout } = useContext(BoardCtx);
  const direction = +layout.width < +layout.height;
  const width = direction ? 400 : 800;
  const scale = width / layout.width;
  const isEdit = history.location.pathname.indexOf('devices') >= 0;
  return (
    // <FixedBox dir={direction} isEdit={isEdit}>
    <div style={{
      transform: 'scale(0.4166666666666667)',
      transformOrigin: 'top left',
    }}
    >
      <button className="btn btn_save" onClick={handleConfig}>Сохранить изменения</button>
      <TerminalTransform isEdit={isEdit} scale={scale}>
        {layout.header && (<Layout />)}
      </TerminalTransform>
    </div>
    // </FixedBox>
  );
};

export default React.memo(BoardPreview);
