import React, { useState } from 'react';
import { Btn, BtnsBox } from '../../../../../../styles/Modal';
import { BtnAdd } from '../../../../../../styles/Common';
import Modal from '../../../../../Elements/Modal/Modal';
import request from '../../../../../../utils/request';
import RenderInputs from './RenderInputs';

const NewWeek = ({
  isOpen, toggleOpen, fetchData, weekUpdate,
  weekData, setWeekData, setWeekUpdate, preloader, dayList, initialData, write,
}) => {
  const [isValid, setIsValid] = useState(false);

  const resetModal = () => {
    setWeekData({ ...initialData });
    toggleOpen();
    setIsValid('');
    setWeekUpdate(false);
  };

  const addWeek = async () => {
    if (isValid) {
      preloader();
      await request('/server/api/dworkweek/', 'add', weekData);
      fetchData();
      resetModal();
      preloader();
    }
  };

  const updateWeek = async () => {
    if (isValid) {
      const itemId = weekData.id;
      delete weekData.id;
      preloader();
      await request(`/server/api/dworkweek/${itemId}/`, 'update', { ...weekData });
      fetchData();
      resetModal();
      preloader();
    }
  };

  const setData = (...props) => {
    setWeekData(...props);
    setIsValid(true);
  };

  return (
    <React.Fragment>
      {write && (
      <BtnAdd onClick={toggleOpen} add>
        <span>Добавить</span>
      </BtnAdd>
      )
      }
      <Modal title={weekUpdate ? 'Редактирование недели' : 'Добавление недели'} isOpen={isOpen} toggleOpen={resetModal}>
        <RenderInputs
          weekUpdate={weekUpdate}
          weekData={weekData}
          dayList={dayList}
          write={write}
          setWeekData={setData}
        />
        {write && (
        <BtnsBox>
          <Btn
            margin="0 25px 0 0"
            valid={isValid}
            onClick={weekUpdate ? updateWeek : addWeek}
          >
            Сохранить
          </Btn>
          <Btn onClick={resetModal} cancel>Отменить</Btn>
        </BtnsBox>
        )
        }
      </Modal>
    </React.Fragment>
  );
};

export default React.memo(NewWeek);
