import React, {
  useState, useCallback,
} from 'react';
import {
  Switch, Redirect,
} from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import routesList from './routesList';
import Preloader from '../views/Elements/Preloader/Preloader';
import ErrorModal from '../views/Elements/Modal/ErrorModal';
import { useToggle } from '../hooks/useToggle';
import getLinks from '../utils/getLinks';
import PrivateRoute from './PrivateRoute';


const Routes = () => {
  const [isOpen, toggleOpen] = useToggle(false);
  const [isError, setIsError] = useToggle(false);
  const [errorText, setErrorText] = useState('');

  const showError = useCallback((text) => {
    setIsError();
    setErrorText(text);
  }, [setIsError]);

  const links = getLinks();

  const routes = () => {
    const doneRoutes = routesList.map((route) => (
      <PrivateRoute
        exact
        key={route.name}
        path={route.name}
        route={route}
        preloader={toggleOpen}
        showError={showError}
        write={getLinks(true)}
      />
    ));
    return (doneRoutes.length > 0) ? doneRoutes : <Redirect to="/login/" />;
  };
  return (
    <>
      <Switch>
        {routes()}
        <Redirect from="/" to={links[0] || 'login'} strict exact />
        <Redirect from="*" to="/monitoring" />
      </Switch>
      <Preloader isOpen={isOpen} />
      <ToastContainer />
      <ErrorModal
        isError={isError}
        errorText={errorText}
        setIsError={setIsError}
        showError={showError}
      />
    </>
  );
};

export default Routes;
