import { DubleLineTemplate, GeneralReportMarkTemplate } from '../../../../../../utils/Formater';

export default function (getColumnSearchProps) {
  return {
    columns: [
      {
        dataIndex: '2',
        title: 'Оператор',
        ...getColumnSearchProps('2'),
      },
      {
        dataIndex: '9',
        title: 'Рабочее время',
        sorter: (a, b) => {
          return a[9] - b[9];
        },
        sortDirections: ['descend', 'ascend'],
        render: (cell, row) => DubleLineTemplate('Время обслуживания талонов:', row[6], 'Рабочих дней:', row[4]),
      },
      {
        dataIndex: '3',
        title: 'Талонов обработано',
        render: (cell, row) => DubleLineTemplate('Всего:', row[3], 'В день:', row[5]),
        sorter: (a, b) => {
          return b[3] - a[3];
        },
        sortDirections: ['descend', 'ascend'],
      },
      {
        dataIndex: '8',
        title: 'Оценка',
        sorter: (a, b) => {
          return b[8] - a[8];
        },
        sortDirections: ['descend', 'ascend'],
        render: (cell, row) => GeneralReportMarkTemplate(`${row[7] * 20}%`, row[8]),
      },
    ],
  };
}
