import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from './home/home';
import bottomButtons from '../components/bottomButtons/bottomButtons';
import history from '../../../../../../../utils/customHistory';

import { Main as Routes } from './routes';

export default function Scene(props) {
  const {
    config, style, params, selectScreen, isEdit, startScreen, headUrl,
  } = props;
  // const notStartScreen = startScreen === '';
  const inputScene = [];
  const routes = config.layoutData.map((scene) => { // перебор сцен из конфига
    if (scene.type === 'inputScreen') {
      scene.buttons.forEach((button) => {
        inputScene.push(<Route
          exact
          key={button.id}
          history={history}
          path={`${headUrl}/${scene.screenId}/:extraId`}
          component={(route) => (
            <Home
              {...route}
              {...props}
              headUrl={headUrl}
              isEdit={isEdit}
              scene={scene}
              startScreen={startScreen}
            />
          )}
        />);
      });
    }
    const path = scene.screenId;
    return (
      <Route
        exact
        key={scene.screenId}
        history={history}
        path={`${headUrl}/:id/${path}`}
        component={(route) => (
          <Home
            {...route}
            {...props}
            path={`${headUrl}/:id/${path}`}
            scene={scene}
            isEdit={isEdit}
            headUrl={headUrl}
            startScreen={startScreen}
          />
        )}
      />
    );
  });
  const extraRoutes = Routes.map((router) => (
    <Route
      path={`${headUrl}${router.name}`}
      key={router.name}
      component={(route) => (
        <router.component
          exact
          {...route}
          scene={{}}
          selectScreen={selectScreen}
          isEdit={isEdit}
          config={config}
          history={history}
          startScreen={startScreen}
          style={style}
        >
          {router.buttons ? bottomButtons({
            ...props, scene: {}, history, startScreen, params, style: style.bottomButton, noBack: router.noBack, headUrl,
          }) : null}
        </router.component>
      )}
    />
  ));
  if (selectScreen) {
    return (
      <Home
        path={null}
        scene={selectScreen}
        style={style}
        selectScreen={selectScreen}
        startScreen={startScreen}
      />
    );
  }
  const finishRoutes = routes.concat(extraRoutes, inputScene);

  return (
    <Switch>
      {finishRoutes}
    </Switch>
  );
}
