import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import { Tabs } from 'react-web-tools';
import Modal from '../../../Elements/Modal/Modal';
import Config from './Config';
import Settings from './Settings';
import request from '../../../../utils/request';
import AddButton from '../../../Elements/Button/AddButton';
import ModalButtons from '../../../Elements/Modal/ModalButtons';

const NewDevice = ({
  isOpen, toggleOpen, fetchDevices, deviceUpdate, setDeviceData,
  deviceData, initialData, setDeviceUpdate, preloader, showError, history,
}) => {
  const [isValid, setIsValid] = useState(false);
  const [display, setDisplay] = useState(0);

  useEffect(() => {
    const {
      ipaddr: iV, port: pV, name: nV, type: tV,
    } = deviceData;
    if (iV && pV && nV && tV) {
      setIsValid(true);
    }
  }, [deviceData, setIsValid]);
  const fetchDevice = useCallback(async () => {
    if (!deviceData.id) {
      return undefined;
    }
    preloader();
    try {
      const res = await request(`/server/api/satellite/${deviceData.id}/`, 'get');
      res.data.data.zoneId = res.data.data.zone ? res.data.data.zone.zoneId : null;
      setDeviceData(res.data.data);
    } catch (e) {
      showError(e.data.error);
    }
    preloader();
  }, [deviceData.id, preloader, showError, setDeviceData]);

  useEffect(() => {
    fetchDevice();
  }, [fetchDevice]);

  const resetModal = () => {
    setDeviceData({ ...initialData });
    toggleOpen();
    setDeviceUpdate(false);
    setIsValid(false);
  };

  const sendConfig = async (id) => {
    const { origin } = window.location;
    const {
      type, ipaddr, port, config, uuid,
    } = deviceData;
    let urlPage = `${origin}/satellite/${type}/?id=${id}`;
    if (type === 'board' || type === 'terminal') {
      urlPage = `${origin}/${type}/${id}/`;
    }
    let sUri = `/proxySatellite/gate/window/${uuid}`;
    if (!uuid) {
      sUri = '/proxySatellite/gate/config'; // old satellite
    }
    try {
      await axios({
        method: 'post',
        url: sUri,
        data: {
          urlPage,
          satelliteIp: ipaddr,
          satellitePort: port,
          alwaysOnTop: config.alwaysOnTop,
        },
      });
    } catch (e) {
      showError(e.data.error);
    }
  };

  const addDevice = async () => {
    const newDevice = { ...deviceData };
    if (isValid) {
      preloader();
      try {
        if (newDevice.type === 'tablet') {
          const res = await axios({
            method: 'post',
            url: '/server/public/surveys',
            data: {},
          });
          newDevice.survey_id = res.data.data[0].id;
        }
        const resp = await request('/server/api/satellite/', 'add', { ...newDevice });
        await sendConfig(resp.data.data.newCreatedId);
        history.push(`/devices/${resp.data.data.newCreatedId}/1`);
      } catch (e) {
        showError(e.data.error);
      }
      preloader();
    }
  };

  const updateDevice = async () => {
    if (isValid) {
      const itemId = deviceData.id;
      delete deviceData.id;
      preloader();
      const res = await request(`/server/api/satellite/${itemId}/`, 'update', { ...deviceData });
      sendConfig(res.data.data.updatedId);
      fetchDevices();
      resetModal();
      preloader();
    }
  };

  return (
    <>
      <AddButton toggle={toggleOpen} />
      <Modal title={deviceUpdate ? 'Редактирование устройства' : 'Добавление устройства'} isOpen={isOpen} toggleOpen={resetModal}>
        <Tabs>
          <Settings
            name="Основные настройки"
            setDeviceData={setDeviceData}
            deviceData={deviceData}
            setIsValid={setIsValid}
            preloader={preloader}
            showError={showError}
            display={display}
            setDisplay={setDisplay}
          />
          {(deviceData.config.style || deviceData.config.layout) && <Config setIsValid={setIsValid} data={deviceData} setData={setDeviceData} name="Конфигурация" />}
        </Tabs>
        <ModalButtons
          isValid={isValid}
          update={deviceUpdate}
          updateFunc={updateDevice}
          addFunc={addDevice}
          reset={resetModal}
        />
      </Modal>
    </>
  );
};

export default React.memo(NewDevice);
