import React from 'react';
import Order from './modules/order';

const MarginComponent = ({ arr, dispatch }) => {
  return (
    <div className="elementBox">
      <p>Порядок размещения</p>
      {arr.map(el => (
        <Order
          element={el.parentName}
          dispatch={dispatch}
          title={el.title}
          value={el.value}
          key={el.title}
        />
      ))}
    </div>
  );
};

export default MarginComponent;
