import React, { useState, useEffect, useRef } from 'react'
import {
  ButtonChange,
  AlignBox,
  InputMargin,
  InputMarginBox,
} from '../../../../../../styles/BoardStyle';
import FindPosition from '../../../../../../utils/findPosition';
import { Box, ColorBox } from '../style';

const BoxShadow = ({
  value, dispatch, element, title,
}) => {
  const [open, setOpen] = useState(false);
  const [bottom, setBottom] = useState(false);
  const [shadow, setShadow] = useState({});
  const box = useRef(null);

  useEffect(() => {
    let arr = value.split(' ');
    arr = arr.map(el => el.replace('px', ''));
    setShadow({
      color: arr[3],
      left: arr[0],
      top: arr[1],
      blur: arr[2],
    });
  }, [value]);

  const onChange = ({ index, v }) => {
    const sh = { ...shadow, [index]: v };
    const newShadow = `${sh.left}px ${sh.top}px ${sh.blur}px ${sh.color}`;
    dispatch({
      type: 'params',
      name: element,
      param: 'boxShadow',
      value: newShadow,
    });
  };

  const toggle = () => {
    FindPosition(box.current, setBottom);
    setOpen(!open);
  };

  return (
    <ColorBox>
      <span dangerouslySetInnerHTML={{ __html: title }} />
      <Box width="160px">
        <ButtonChange width="160px" onClick={toggle} active={open}>{open ? 'Закрыть' : 'Изменить'}</ButtonChange>
        <div ref={box} style={{ position: 'relative' }}>
          {open && (
          <AlignBox bottom={bottom} width="160px" shadow absolute>
            <InputMarginBox>
              <div className="absoluteDescription"> Слева </div>
              <InputMargin
                type="number"
                onChange={e => onChange({ index: 'left', v: Number(e.target.value) })}
                width="160px"
                m="0"
                value={shadow.left}
              />
            </InputMarginBox>
            <InputMarginBox>
              <div className="absoluteDescription"> Сверху </div>
              <InputMargin
                type="number"
                onChange={e => onChange({ index: 'top', v: Number(e.target.value) })}
                width="160px"
                m="0"
                value={shadow.top}
              />
            </InputMarginBox>
            <InputMarginBox>
              <div className="absoluteDescription"> Размытие </div>
              <InputMargin
                type="number"
                onChange={e => onChange({ index: 'blur', v: Number(e.target.value) })}
                width="160px"
                m="0"
                value={shadow.blur}
              />
            </InputMarginBox>
          </AlignBox>
          )
          }
        </div>
      </Box>
    </ColorBox>
  );
};

export default BoxShadow;
