import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { BodyContainer } from '../../style/style';
import RenderButtons from './renderButtons';
import BottomButtons from '../../components/bottomButtons/bottomButtons';
import Information from '../information';
import redirectNotButtons from '../../utils/redirectNotButtons';
import TwoScreen from '../../components/twoScreen';
import HiddenBtn from '../hiddenBtn';


const Home = ({ ...props }) => {
  const {
    scene, style, match, startScreen, path, history, selectScreen, isEdit,
  } = props;
  const { title, extraTitle, general } = style;
  const { twoScreen } = general;

  const layout = scene.type === 'inputScreen' ? 1 : scene.type === 'multiScreen' ? 2 : 0;
  const info = scene.type === 'infoScreen';

  // useEffect(() => {
  //   redirectNotButtons(scene.buttons, history, headUrl, id, !twoScreen && scene.bottomBlock && scene.bottomBlock.buttons.length === 0);
  // }, []);

  const b = { ...style.body };
  if (!b.backgroundImage) {
    delete b.backgroundImage;
  }
  let extraId = 0;
  if (match) extraId = match.params.extraId;

  let reg = false;
  if (startScreen) {
    reg = !new RegExp(startScreen.split('/')[3]).test(path);
  }
  const isStartNotButtons = scene.type === 'startScreen' && scene.buttons.length === 0;
  const isStartNotButtonsBottomBlock = scene.type === 'startScreen' && scene.bottomBlock && scene.bottomBlock.buttons.length === 0;
  console.log({ twoScreen });
  return (
    <BodyContainer style={b} isStartNotButtons={isStartNotButtons || isStartNotButtonsBottomBlock}>
      <>
        <div style={title}>{scene.title}</div>
        <div style={extraTitle}>{scene.description}</div>
        <HiddenBtn />
        { twoScreen
          ? <TwoScreen selectScreen={selectScreen} isEdit={isEdit} match={match} history={history} index={layout} {...props} />
          : info
            ? <Information selectScreen={selectScreen} history={history} scene={scene} style={style} />
            : !isStartNotButtons && <RenderButtons selectScreen={selectScreen} match={match} history={history} index={layout} {...props} />}

        {/* Рендер нижних кнопок */}
        {!twoScreen && !extraId && reg ? <BottomButtons selectScreen={selectScreen} scene={scene} info={info} {...props} /> : null}
      </>
    </BodyContainer>
  );
};

export default React.memo(Home);
