import React, { useContext } from 'react';
import { BoardElementsBox } from '../../../../../../../styles/BoardStyle';
import BoardCtx from '../../BoardCtx';

const BoardElements = () => {
  const { setActiveEl, activeEl } = useContext(BoardCtx);
  const elemenets = [
    { id: 'header', name: 'Шапка' },
    { id: 'columns', name: 'Колонки' },
    { id: 'big', name: 'Окно вызова' },
    { id: 'time', name: 'Окно даты' },
    { id: 'video', name: 'Окно видео' },
    { id: 'news', name: 'Окно Новостей' },
    { id: 'footer', name: 'Бегущая строка' },
  ];

  const renderBtns = () => elemenets.map((element) => {
    let active = false;
    if (activeEl === element.id) {
      active = true;
    }

    return (
      <div
        className={active ? 'tab-header active' : 'tab-header'}
        onClick={() => setActiveEl(element.id)}
        key={element.id}
      >
        {element.name}
      </div>
    );
  });

  return (
    <BoardElementsBox>
      {renderBtns()}
    </BoardElementsBox>
  );
};
export default BoardElements;
