export default function () {
  function status(cell) {
    let text;
    switch (cell) {
      case 0: text = 'Рабочее место не занято';
        break;
      case 1: text = 'Оператор залогинен';
        break;
      case 2: text = 'Оператор вызвал талон';
        break;
      case 3: text = 'Обслуживает талон';
        break;
      case 4: text = 'Пауза';
        break;
      case 5: text = 'Диспетчер';
        break;
      case 6: text = 'Подготовка к отключению';
        break;
      case 7: text = 'Отключено';
        break;
      default: break;
    }
    return text;
  }

  const statusType = {
    0: 'Рабочее место не занято',
    1: 'Оператор залогинен',
    2: 'Оператор вызвал талон',
    3: 'Обслуживает талон',
    4: 'Пауза',
    5: 'Диспетчер',
    6: 'Подготовка к отключению',
    7: 'Отключено',
  };

  return {
    columns: [
      {
        dataIndex: 'wplaceName',
        title: 'Наименование',
      },
      {
        dataIndex: 'userLogin',
        title: 'Логин',
      },
      {
        dataIndex: 'userFIO',
        title: 'ФИО',
      },
      {
        dataIndex: 'ticketNumber',
        title: 'Номер талона',
      },
      {
        dataIndex: 'status',
        title: 'Статус',
        render: status,
        filters: [
          {
            text: 'Рабочее место не занято',
            value: 0,
          },
          {
            text: 'Оператор залогинен',
            value: 1,
          },
          {
            text: 'В процессе обработки',
            value: 2,
          },
          {
            text: 'Обслуживает талон',
            value: 3,
          },
          {
            text: 'Пауза',
            value: 4,
          },
          {
            text: 'Диспетчер',
            value: 5,
          },
          {
            text: 'Подготовка к отключению',
            value: 6,
          },
          {
            text: 'Отключено',
            value: 7,
          },
        ],
        onFilter: (value, record) => record.status === value,
      },
    ],
  };
}
