import Request from '../../../../../../utils/request';

async function doQuest(reportType, setWorkplaceList) {
  const query = 'select id, placename, "name" from eq.twplace';

  const res = await Request('/server/api/genreportfromquery/', 'get', {
    querySql: `${query}`,
  });

  const answer = res.data.data;
  const items = [{ key: 'all', name: 'Все', text: 'Все' }];

  answer.forEach((e) => {
    items.push({ key: e[0], name: e[1], text: e[2] });
  });

  const conf = {
    headerTitleText: 'Все',
    items,
  };

  setWorkplaceList(conf);
}

export default doQuest;
