import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { getReportConfig } from '../../../../../../store/actions/reports';
import QueueListSqlQuerys from './QueueListSqlQuerys';
import OperatorListSqlQuery from './OperatorListSqlQuery';
import WorkplaceQslQuery from './WorkplaceSqlQuery';
import Dropdown from '../../../../../Elements/Dropdown/Dropdown';
import { FilterBox, FilterSelection } from '../../../../../../styles/ReportMenu';


const ReportFilters = ({ reportType, dispatch }) => {
  const [queueList, setQueueList] = useState({});
  const [operatorList, setOperatorList] = useState({});
  const [workplaceList, setWorkplaceList] = useState({});
  const { reportConfig } = useSelector((state) => state);

  useEffect(() => {
    if (reportType === 'clockwiseReport') {
      QueueListSqlQuerys(reportConfig, setQueueList);
      OperatorListSqlQuery(reportConfig, setOperatorList);
      WorkplaceQslQuery(reportConfig, setWorkplaceList);
    }
  }, [reportType, reportConfig]);


  const changeQueuFilter = async (e, options, key) => {
    dispatch(getReportConfig(
      {
        ...reportConfig,
        filterts: {
          ...reportConfig.filterts,
          queue: key,
        },
      },
    ));
  };

  const changeOperetorFilter = async (e, options, key) => {
    dispatch(getReportConfig(
      {
        ...reportConfig,
        filterts: {
          ...reportConfig.filterts,
          operator: key,
        },
      },
    ));
  };

  const changeWorkplaceFilter = async (e, options, key) => {
    dispatch(getReportConfig(
      {
        ...reportConfig,
        filterts: {
          ...reportConfig.filterts,
          workplace: key,
        },
      },
    ));
  };

  return (
    <>
      {reportType === 'clockwiseReport' ? (
        <div style={{ display: 'flex', justifyContent: 'space-around', flex: '1' }}>
          {queueList.items ? (
            <FilterBox>
              <div>Очередь</div>
              <FilterSelection>
                <Dropdown
                  config={queueList}
                  clickHandler={changeQueuFilter}
                  replaceableTitle
                />
              </FilterSelection>
            </FilterBox>
          ) : (null)}
          {operatorList.items ? (
            <FilterBox>
              <div>Оператор</div>
              <FilterSelection>
                <Dropdown
                  config={operatorList}
                  clickHandler={changeOperetorFilter}
                  replaceableTitle
                />
              </FilterSelection>
            </FilterBox>
          ) : (null)}
          {workplaceList.items ? (
            <FilterBox>
              <div>Рабочие место</div>
              <FilterSelection>
                <Dropdown
                  config={workplaceList}
                  clickHandler={changeWorkplaceFilter}
                  replaceableTitle
                />
              </FilterSelection>
            </FilterBox>
          ) : (null)}
        </div>
      )
        : (null)}
    </>
  );
};

export default ReportFilters;
