import React from 'react';
import FontSize from './modules/fontSize';

const fontSizes = ({ arr, dispatch, width }) => (
  <div className="elementBox">
    <p>Размер шрифта</p>
    {arr.map((el, index) => (
      <FontSize
        element={el.parentName}
        width={width}
        value={el.value}
        dispatch={dispatch}
        rusName={el.title}
        paramName={el.paramName}
        key={index}
      />
    ))}
  </div>
);

export default fontSizes;
