import React from 'react';
import { Link } from 'react-router-dom';


const Tabs = ({ match, children, lvl = 1 }) => {
  const arr = match.url.split('/');
  const active = arr.splice(0, lvl + 2).join('/');
  const Buttons = children.map((child) => <Link key={child.props.link} className={`tabHeader ${active === child.props.link ? 'activeTab' : ''}`} to={child.props.link} active={child.props.link}>{child.props.name}</Link>);
  const item = children.find((el) => active === el.props.link);

  return (
    <div className="tabsBox">
      <div className="tabBorder">
        <div className="tabHeaderBox">{Buttons}</div>
      </div>
      {item}
    </div>
  );
};

export default Tabs;
