import React from 'react';
import { ConfigExportBox } from '../../../../styles/Common';
import ImportExportConfig from '../../../../utils/importExportConfig';
import recurse from '../../../../utils/defaultConfigTerminal';
import defaultTerminal from '../DeviceStyle/TerminalStyle/defaultConfig';

const Config = ({ data, setData, setIsValid }) => {
  let url;
  if (data) {
    let blobStyle = new Blob([JSON.stringify(data.config)], { type: 'application/json' });
    if (data.config.style) {
      const conf = recurse(defaultTerminal.style, data.config.style);
      data.config.style = conf
      blobStyle = new Blob([JSON.stringify(data.config)], { type: 'application/json' });
    }
    url = URL.createObjectURL(blobStyle);
  }
  const uploadFile = (d) => {
    setData({ ...data, config: d });
    setIsValid(true);
  };

  return (
    <ConfigExportBox>
      <ImportExportConfig data={data} name={`${data.name}-${data.type}.json`} setData={uploadFile} url={url} />
    </ConfigExportBox>
  );
};

export default Config;
