import React from 'react';
import { useHistory } from 'react-router-dom';
import scanPolicyStore, { resetScanPolicyStore } from '../../../../../../../../../../store/scanPolicy';
import { BoxButtonContent, ButtonContent } from '../../../../style/style';
import changeStyle from '../../../../utils/changeStyle';

const PolicyBB = ({
  button, startScreen, isInner, Authorization,
}) => {
  const history = useHistory();
  const buttons = [
    { click: () => resetScanPolicyStore(), title: 'Назад' },
    {
      click: () => {
        resetScanPolicyStore();
        history.push(startScreen);
      },
      title: 'На Главный экран',
    },
  ];

  const nextButton = {
    click: () => {
      Authorization();
    },
    title: 'Далее',
  };
  if (isInner) buttons.splice(1, 1, nextButton);

  const styleButton = changeStyle({ style: button, item: { size: 'sm' } });
  return (
    <div className="policyBottomButtons">
      {buttons.map((el) => (
        <div
          onDragStart={() => false}
          style={{ ...styleButton, minWidth: 'max-content' }}
          key={el.title}
          onClick={el.click}
        >
          <ButtonContent>
            <BoxButtonContent>
              <p style={{ ...styleButton.text, textAlign: styleButton.textAlign }}>{el.title}</p>
            </BoxButtonContent>
          </ButtonContent>
        </div>
      ))}
    </div>
  );
};

export default PolicyBB;
