import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import './style.css';


const HiddenBtn = () => {
  const history = useHistory();
  const allParams = useParams();
  const { id } = allParams;
  // const
  const [btns, setBtns] = useState({
    btn1: false,
    btn2: false,
    btn3: false,
  });
  console.log(btns);
  const btnFn = (e) => {
    const curBtn = e.currentTarget.name;
    console.log(curBtn);
    setBtns({
      ...btns,
      [curBtn]: true,
    });
  };
  console.log('kekq');
  useEffect(() => {
    const { btn1, btn2, btn3 } = btns;
    if (btn1 && btn2 && btn3) {
      console.log('Можем переключать экран');
      // history
      history.push(`/terminal/${id}/service`);
    }
  }, [btns]);
  return (
    <>
      <button id="btn1" name="btn1" onClick={(e) => btnFn(e)}>1 кнопка</button>
      <button id="btn2" name="btn2" onClick={(e) => btnFn(e)}>2 кнопка</button>
      <button id="btn3" name="btn3" onClick={(e) => btnFn(e)}>3 кнопка</button>
    </>
  );
};

export default HiddenBtn;
