import React from 'react';
import styled from 'styled-components';

const Input = styled.input`
    font-family: Roboto;
    font-weight: 100;
    margin-bottom: 0;
    display: flex;
    align-items: center;
    //min-width: 260px;
    border: 1px solid rgba(0,0,0,.3);
    border-left: none;
    outline: none;
    padding: 10px 15px 10px 15px;
    background-color: #fafafa;
    border-radius: 0 3px 3px 0;
    order: 1;
    width: 100%;
    @media(max-width: 1366px) {
      height: 35px;
    }
    + label {
      background: #FC4C02;
    }
    &:valid + label {
      background: #4dbd74;
    }
`;

const InputBox = styled.div`
    display: flex;
    position: relative;
    margin-bottom: 25px;
`;
const Label = styled.label`
    line-height: 0.8;
    order: 0;
    top: 0;
    height: 45px;
    display: flex;
    align-items: center;
    transition: 0.1s ease-out;
    border-radius: 3px 0 0 3px;
    max-width: 45px;
    padding: 0 12px;
    left: 0;
    font-weight: 400;
    svg {
      height: 30px;
      width: 30px;
      path  {
        fill: white;
      }
    }
    @media(max-width: 1366px) {
      height: 35px;
      width: 35px;
    }
`;

const InputLogin = ({ item, onChange }) => {
  const {
    value, name, label, type,
  } = item;

  return (
    <InputBox noMargin={item.name === 'userPasswd'}>
      <Input
        required
        autocomplite
        id={name}
        name={name}
        type={type || 'text'}
        value={value}
        onChange={onChange}
        style={{ color: '#454545' }}
      />
      <Label htmlFor={name}>{label}</Label>
    </InputBox>
  );
};

export default InputLogin;
