import React from 'react';
import Margin from './modules/Margin';

const MarginComponent = ({ arr, dispatch }) => (
  <div className="elementBox">
    <p>Внешние отступы</p>
    {arr.map(el => (
      <Margin
        name="margin"
        element={el.parentName}
        dispatch={dispatch}
        data={el.data}
        title={el.title}
        key={el.title}
      />
    ))}
  </div>
);

export default MarginComponent;
