import React, { useState } from 'react';
import Table from '../../../../Elements/Table/Table';
import Config from './TableConfig';
import DeleteWarning from '../../../../Elements/Modal/DeleteWarning';
import { useToggle } from '../../../../../hooks/useToggle';
import DeleteItem from '../../../../../utils/deleteItem';

const DayTable = ({
  dayList, setDayUpdate, setDayData, toggleOpen, preloader, fetchDay, intervalList, showError, write,
}) => {
  const [dayId, setDayId] = useState(false);
  const [dayName, setDayName] = useState(false);
  const [showDelWarn, setShowDelWarn] = useToggle(false);

  const delDay = async () => {
    await DeleteItem({
      preloader, url: `/server/api/dworkday/${dayId}/`, fetchData: fetchDay, setShowWarn: setShowDelWarn, setId: setDayId, showError,
    });
  };

  const handleTableIcon = (e, row) => {
    const { dataset } = e.target;
    const {
      id, name, description, workIntervals,
    } = row;
    if (dataset.name === 'del') {
      setDayId(id);
      setDayName(name);
      setShowDelWarn();
    } else if (dataset.name === 'update') {
      setDayUpdate(true);
      setDayData({
        id, name, description, workIntervals,
      });
      toggleOpen();
    }
  };

  const Setting = Config(intervalList, write);
  const { columns } = Setting;
  return (
    <>
      {dayList.data.length > 0 && <Table keys="id" data={dayList.data} handleIcon={handleTableIcon} columns={columns} />}
      <DeleteWarning name={dayName} confirmDel={delDay} isOpen={showDelWarn} toggleOpen={setShowDelWarn} />
    </>
  );
};

export default React.memo(DayTable);
