import React from 'react';
import SelectInput from '../../../../../Elements/Input/SelectInput/SelectInput';
import { ColorBox } from '../style';

const position = ({
  element, value, dispatch, title,
}) => {
  const options = [
    { id: 'top-right', name: 'Шапка, справа' },
    { id: 'top-left', name: 'Шапка, слева' },
    { id: 'bottom-right', name: 'Подвал, справа' },
    { id: 'bottom-left', name: 'Подвал, слева' },
  ];
  return (
    <ColorBox>
      <span dangerouslySetInnerHTML={{ __html: title }} />
      <SelectInput
        noDefault
        width="180px"
        value={value}
        onChange={e => dispatch({
          type: 'params',
          name: element,
          param: 'position',
          value: e.target.value,
        })
        }
        options={options}
      />
    </ColorBox>
  );
};

export default position;
