import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { dateFormat, placeholder } from '../../../../../utils/tableFormatters';
import iconFormatter from '../../../../../utils/iconFormatter';

function IntervalFormatter(row, intervals) {
  const { data } = intervals;
  const week = {
    'Пн:': 'monday_id',
    'Вт:': 'tuesday_id',
    'Ср:': 'wednesday_id',
    'Чт:': 'thursday_id',
    'Пт:': 'friday_id',
    'Сб:': 'saturday_id',
    'Вс:': 'sunday_id',
  };

  return (
    <div className="intervalBox">
      {Object.keys(week).map((el, index) => {
        const ele = data.find((elem) => elem.id === row[week[el]]);
        return (
          <div className="connectIntervals" key={index}>
            {el}
            <span>{ele ? ele.name : 'Не задано'}</span>
          </div>
        );
      })}
    </div>
  );
}

export default function (intervals, write) {
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Наименование',
        headerFormatter: placeholder,
        editable: false,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Наименование',
        }),
      },
      {
        dataField: 'description',
        text: 'Описание',
        headerFormatter: placeholder,
        editable: false,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Описание',
        }),
      },
      {
        dataField: 'modified_by_username',
        text: 'Изменен',
        headerFormatter: placeholder,
        sort: true,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Изменен',
        }),
        editable: false,
      },
      {
        dataField: 'modified_on',
        text: 'Когда изменен',
        editable: false,
        formatter: dateFormat,
      },
      {
        dataField: 'connectIntervals',
        text: 'Подключенные дни',
        editable: false,
        headerStyle: {
          width: 450,
        },
        formatter: (el, cell) => IntervalFormatter(cell, intervals),
      },
      {
        dataField: 'Settings',
        isDummyField: true,
        text: '',
        headerStyle: {
          width: 100,
        },
        formatter: () => iconFormatter({ filter: false, write, params: false }),
        editable: false,
      },
    ],
  };
}
