
import Request from '../../../../../../../utils/request';

export default async function (ticketId) {
  const query = `select * from (select ht.ticket_id,
  to_char(ht.modified_on, 'DD.MM.YYYY HH24:MI:SS') as close_data,
  to_char(ht.created_on, 'DD.MM.YYYY HH24:MI:SS') as create_data, ht.number as ticket_number,
  qt.fullname as queue_name, ut.username as operator_name, ut.fio as operator_fio,
  zt.\"name\" as zone_name, ht.prior, ht.totalwait * interval '1 sec' as wait_time,` // eslint-disable-line
  + `ht.totaldecisionwait * interval '1 sec' as decision_time, ht.totalproc * interval '1 sec' as proc_time,
  ht.wp_id, coalesce (ht.wp_id, 0) as new_wp_id, ht.status, ht.procforsecs * interval '1 sec' as current_wait_time,
  ht.\"comment\" as ticket_comment from eq.ttickethistory ht, eq.tqueue qt, eq.tuser ut, ` // eslint-disable-line
  + `eq.tzone zt where ht.modified_by_id = ut.id and ht.q_id = qt.id and ht.zone_id = zt.id
  and ht.ticket_id = '${ticketId}') mt left join (select id, \"name\" from eq.twplace) wp ` // eslint-disable-line
  + 'on mt.new_wp_id = wp.id order by mt.close_data::timestamp ';

  const dataObjects = [];
  const res = await Request('/server/api/genreportfromquery/', 'get', {
    querySql: `${query}`,
  });

  const answer = res.data.data;
  answer.forEach((v) => {
    if (v[2] !== null) dataObjects.push({ ...v });
  });

  return { dataObjects, query };
}
