import React from 'react';

const Big = ({
  styles, handleFz, elWidth, setActiveEl, text, edit, ticketsList,
}) => {
  const {
    big, cols, vertical, borderRadius,
  } = styles;
  const {
    fontSize1, fontSize2, color1, background, color2, borderSize1, color3, fontFamily1, fontFamily2, height,
  } = big;

  const win = edit ? ['окно 1'] : ticketsList.length ? ticketsList[0].wNum : [];

  const renderText = (value) => {
    if (+value === 0) {
      return '';
    }
    return value;
  };

  return (
    <div
      className="boardBigBox"
      style={{
        ...big,
        background,
        minHeight: (!vertical || height === 'auto') ? '0' : height,
        maxHeight: (!vertical || height === 'auto') ? '100%' : height,
        height: (!vertical || height === 'auto') ? 'auto' : height,
        flex: 1,
        borderRadius: borderRadius ? '12px' : 0,
        border: `${borderSize1}px solid ${color3}`,
      }}
    >
      <div className="boardBigClientBox">
        <p
          className="boardBigTitle"
          style={{
            fontSize: handleFz(fontSize1, elWidth),
            fontFamily: fontFamily1,
            color: color1,
          }}
          onClick={() => { setActiveEl('bigTitle'); }}
        >
          {text.columns1}
        </p>
        <p
          style={{
            fontSize: handleFz(fontSize2, elWidth),
            color: color2,
            fontFamily: fontFamily2,
          }}
          onClick={() => setActiveEl('big')}
        >
          {edit ? 'ЖЖЖ 9999' : ticketsList.length ? ticketsList[0].number : null}
        </p>
      </div>
      {cols >= 2 && (
      <div className="boardBigWinBox">
        <p
          className="boardBigTitle"
          style={{
            fontSize: handleFz(fontSize1, elWidth),
            color: color1,
            fontFamily: fontFamily1,
          }}
          onClick={() => { setActiveEl('bigTitle'); }}
        >
          {text.columns2}
        </p>
        <p
          style={{
            fontFamily: fontFamily2,
            fontSize: handleFz(fontSize2, elWidth),
            color: color2,
          }}
          onClick={() => setActiveEl('big')}
        >
          {renderText(win[0])}
        </p>
      </div>
      )}
      {cols > 2 && (
      <div className="boardBigWinBox">
        <p className="boardBigTitle" style={{ fontSize: handleFz(fontSize1, elWidth), color: color1, fontFamily: fontFamily1 }} onClick={() => { setActiveEl('bigTitle'); }}>
          {text.columns3}
        </p>
        <p
          style={{
            fontSize: handleFz(fontSize2, elWidth), color: color2, fontFamily: fontFamily2,
          }}
          onClick={() => setActiveEl('big')}
        >
          {renderText(win[1])}
        </p>
      </div>
      )}
      {cols > 3 && (
      <div className="boardBigWinBox">
        <p className="boardBigTitle" style={{ fontSize: handleFz(fontSize1, elWidth), color: color1, fontFamily: fontFamily1 }} onClick={() => { setActiveEl('bigTitle'); }}>
          {text.columns4}
        </p>
        <p
          style={{
            fontSize: handleFz(fontSize2, elWidth), color: color2, fontFamily: fontFamily2,
          }}
          onClick={() => setActiveEl('big')}
        >
          {renderText(win[2])}
        </p>
      </div>
      )}
    </div>
  );
};

export default Big;
