import React, { useState, useEffect, useCallback } from 'react';
import moment from 'moment';
import request from '../../../../utils/request';
import { PreEntryBox } from './style';
import Calendar from './calendarBox';
import Create from './create';

const PreEntry = ({ write, preloader, showError }) => {
  const [options, setOptions] = useState([]);
  const [open, setOpen] = useState(false);
  const [queue, setQueue] = useState([]);
  const [calendar, setCalendar] = useState();

  const cHour = +moment().format('HH');
  const initState = {
    time: [cHour, Math.ceil(Number(moment().format('mm')) / 5) * 5],
    q_id: '',
    interval: 15,
    amount: 5,
    approved: true,
    replace: false,
    startTime: [cHour, Math.ceil(Number(moment().format('mm')) / 5) * 5],
    endTime: [cHour + 1, 0],
    dayId: { id: undefined, data: [], day: moment().format('DD.MM.YYYY') },
  };
  const [dataPre, setData] = useState(initState);

  const setSelectedQueue = (value) => setData({ ...dataPre, q_id: value });

  const fetch = useCallback(async () => {
    preloader();
    const { data: { data } } = await request('/server/public/preorder/queues/', 'get');
    setQueue(data);
    preloader();
    setOptions(data.map((el) => ({ id: el.qId, name: el.qFullName })));
  }, [preloader]);

  useEffect(() => {
    try {
      fetch();
    } catch (e) {
      showError(e.data.error);
      preloader();
    }
  }, [fetch, preloader, showError]);

  const update = () => {};
  const resetModal = () => {};

  const toggleOpen = (value) => {
    setOpen(value);
    setData({ ...initState, q_id: dataPre.q_id, dayId: dataPre.dayId });
  };

  return (
    <PreEntryBox>
      <Create
        data={dataPre}
        calendar={calendar}
        options={options}
        showError={showError}
        setData={setData}
        fetch={fetch}
        resetModal={resetModal}
        update={update}
        isUpdate={false}
        toggleOpen={toggleOpen}
        isOpen={open}
        write={write}
        preloader={preloader}
      />
      <Calendar
        data={dataPre}
        calendar={calendar}
        setCalendar={setCalendar}
        dayId={dataPre.dayId}
        selectedQueue={dataPre.q_id}
        setSelectedQueue={setSelectedQueue}
        preloader={preloader}
        showError={showError}
        queue={queue}
        fetch={fetch}
        setData={setData}
        setOpenParent={toggleOpen}
        write={write}
      />
    </PreEntryBox>
  );
};

export default PreEntry;
