import React, { useState, useEffect } from 'react';
import timer from '../../../../../../../../../utils/timer';

const Timer = ({
  styles, handleFz, elWidth, setActiveEl,
}) => {
  const [timeConfig, setTimeConfig] = useState(timer(1, true));
  const order = styles.order === 1 ? 0 : 1;

  useEffect(() => {
    const timerMin = setInterval(() => {
      const newTime = timer(1, true);
      setTimeConfig(newTime);
    }, 59000);

    return () => clearInterval(timerMin);
  }, []);

  const {
    format, time, date, d, day, month, year,
  } = timeConfig;

  const formattedDate = format ? (
    <>
      {day}
      <br />
      {`${d} ${month} ${year}`}
    </>
  ) : (<>{date}</>);

  const {
    fontSize1, color1, fontSize2, fontFamily1, height1, alignItems, bold,
  } = styles;

  const timeStyle = {
    order,
    maxHeight: height1 === 'auto' ? '100%' : height1,
    minHeight: height1 === '100%' ? 0 : height1,
    alignItems: alignItems === 'left' ? 'flex-start' : alignItems === 'right' ? 'flex-end' : 'center',
  };

  return (
    <div className="boardTimerBox" style={timeStyle} onClick={() => setActiveEl('time')}>
      <p
        style={{
          fontSize: handleFz(fontSize1, elWidth),
          fontFamily: fontFamily1,
          fontWeight: bold,
          // lineHeight: `${handleFz(fontSize1, elWidth) - handleFz(5, elWidth)}px`,
          margin: 0,
          color: color1,
        }}
      >
        {time}
      </p>
      <p style={{
        fontSize: handleFz(fontSize2, elWidth),
        fontFamily: fontFamily1,
        color: color1,
        margin: 0,
      }}
      >
        {formattedDate}
      </p>
    </div>
  );
};

export default Timer;
