import React, { useState } from 'react';
import Axios from 'axios';
import { PolicyContainer } from '../../../../style/style';
import Input from '../../../../components/input/input';
import Keyboard from '../../../../components/input/keyboard';
import PolicyBB from './policyBB';
import scanPolicyStore, { updateScanPolicyStore } from '../../../../../../../../../../store/scanPolicy';

const PolicyInner = ({
  input, terminalId, headUrl, button, startScreen,
}) => {
  const [data, setData] = useState({ number: '', date: '' });
  const scanPolicy = scanPolicyStore.useState((s) => s);
  console.log(scanPolicy);
  const Authorization = async () => {
    console.log('12312341241241245672712 === ', 12312312);
    try {
      const { number, date } = data;
      const year = date.substring(4, 8);
      const month = date.substring(2, 4);
      const day = date.substring(0, 2);
      const { data: axData } = await Axios.get(`/api/extGateway?act=get_external_precs&npolis=${number}&bdate=${year}-${month}-${day}`);
      console.log('axData === ', axData);
      if (axData.status === 'ok') {
        const { data: rData } = axData;
        if (rData) {
          const { records: respData } = rData;
          console.log('records === ', respData);
          if (respData) {
            updateScanPolicyStore({ activeScreen: 'list', records: respData });
          } else {
            updateScanPolicyStore({ activeScreen: 'error', errorText: '' });
          }
        }
      } else {
        updateScanPolicyStore({ activeScreen: 'error', errorText: 'Мы сожалеем,<br/> данный полис не найден' });
      }
      console.log(headUrl, terminalId, data);
    } catch (e) {
      console.log(e);
    }
  };

  const [sInput, setInput] = useState({ type: 'number', value: '' });

  const inputs = [
    {
      name: 'Номер полиса', param: 'number', type: 2, typeKey: 'number',
    },
    {
      name: 'Дата', param: 'date', type: 3, typeKey: 'number',
    },
  ];

  const setValue = (value) => {
    setData((v) => ({ ...v, [sInput.param]: value }));
  };

  const onClickInput = (param, type) => {
    setInput({ param, type });
  };

  return (
    <PolicyContainer>
      <div className="policyBox">
        {inputs.map((el) => <Input key={el.param} item={el} style={input} value={data[el.param]} id={el.param} type={el.type} clickInput={(v) => onClickInput(v, el.typeKey)} />)}
        <Keyboard
          styleButton={button}
          login={50}
          id={sInput.type}
          width={button.width}
          setValue={setValue}
          value={data[sInput.param]}
          polisy
        />
      </div>
      <PolicyBB startScreen={startScreen} button={button} isInner Authorization={Authorization} />
    </PolicyContainer>
  );
};

export default PolicyInner;
