import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { BtnTable } from '../../../../styles/Common';
import { ReactComponent as Trash } from '../../../../images/del.svg';
import { ReactComponent as Config } from '../../../../images/config.svg';
import { ReactComponent as Edit } from '../../../../images/edit.svg';
import c from '../../../../styles/Colors';

function priceFormatterDef(column, colIndex, { sortElement, filterElement }) {
  return (
    <div className="filterBoxTable deff">
      <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
        { column.text }
        { sortElement }
      </div>
      { filterElement }
    </div>
  );
}

function priceFormatter(column, colIndex, { sortElement, filterElement }) {
  return (
    <div className="filterBoxTable">
      {column.dataField !== 'Settings' && <Edit style={{margin: '2px 10px 0 0'}}  fill={c.main} /> }
      { filterElement }
    </div>
  );
}
function queueFormatter(cell, queues) {
  const newArray = [];
  cell.forEach(el => queues.forEach(q => q.id === el && newArray.push(q.name)));
  return (
    <div className="filterBoxTable">
      {newArray.join(', ')}
    </div>
  );
}


export default function (queueList, write) {
  function iconFormatter() {
    return (
      <div>
        <BtnTable data-name="update"><Config /></BtnTable>
        {write && <BtnTable data-name="del" active><Trash /></BtnTable>}
      </div>
    );
  }
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Наименование',
        // sort: true,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Наименование',
        }),
        editable: false,
        headerFormatter: priceFormatter,
      },
      {
        dataField: 'displayname',
        text: 'Вопрос/Услуга',
        editable: false,
        headerFormatter: priceFormatterDef,
      },
      {
        dataField: 'description',
        text: 'Описание',
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Описание',
        }),
        editable: false,
        headerFormatter: priceFormatter,
      },
      {
        dataField: 'connectedQueues',
        text: 'Очереди',
        editable: false,
        headerFormatter: priceFormatter,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Очереди',
        }),
        formatter: cell => queueFormatter(cell, queueList),
      },
      {
        dataField: 'Settings',
        text: ' ',
        headerFormatter: priceFormatter,
        formatter: () => iconFormatter(write),
        formatExtraData: {},
        editable: false,
        headerStyle: {
          width: 110,
        },
      },
    ],
  };
}
