import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import c from '../../../../../styles/Colors';
import { BtnTable } from '../../../../../styles/Common';
import { ReactComponent as Edit } from '../../../../../images/edit.svg';
import { ReactComponent as Video } from '../../../../../images/videoPlayer.svg';
import { ReactComponent as Comment } from '../../../../../images/commentText.svg';
import { ReactComponent as Microphone } from '../../../../../images/microphone.svg';
import sortCaret from '../../../../../utils/sortCaret';

function priceFormatter(column, colIndex, { filterElement, sortElement }, sort) {
  return (
    <div className={`filterBoxTable ${sort ? 'sort' : null}`}>
      {column.dataField === '00123' || column.dataField === '6' || column.dataField === '8' ? (null) : (<Edit style={{margin: '2px 10px 0 0'}}  fill={c.main} />) }
      { filterElement || column.text }
      { sortElement }
    </div>
  );
}

function talonIdFormatter(cell) {
  if (cell === null) cell = 'Без талона';
  return (
    <div>{cell}</div>
  );
}

function testnameFormatter(cell) {
  if (cell === null) cell = 'Обращение';
  return (
    <div>{cell}</div>
  );
}

function operatorTemlate(cell, row) {
  const { 12: fio, 11: username } = row;

  let value = '';
  if (row[1] === null) {
    value = 'Не привязан';
  } else if (cell === null) {
    value = fio;
  } else {
    value = username;
  }
  return (
    <div>{value}</div>
  );
}


function informationTemplate(cell, row) {
  return (
    <div style={{ display: 'flex' }}>
      {row[4] !== '' ? (
        <div style={{ marginLeft: '5px' }}>
          <BtnTable data-name="micro" type="button"><Microphone /></BtnTable>
        </div>
      )
        : null}
      {row[5] !== '' ? (
        <div style={{ marginLeft: '5px' }}>
          <BtnTable data-name="video" type="button"><Video /></BtnTable>
        </div>
      ) : null}
      {row[8] !== '' ? (
        <div style={{ marginLeft: '5px' }}>
          <BtnTable data-name="comment" type="button"><Comment /></BtnTable>
        </div>
      ) : null}
    </div>
  );
}

function photoFormatter(cell) {
  const regEx = /data:image\/([a-zA-Z]*);base64,([^"]*)/g;
  if (cell && regEx.test(cell)) {
    return (
      <img src={cell} data-name="photo" style={{ cursor: 'pointer', height: 50 }} alt="customer" />
    );
  }
  return 'Нет фотографии';
}

export default function (handleTableIcon) {
  return {
    columns: [
      {
        dataField: '7',
        text: 'Дата прохождения',
        sort: true,
        editable: false,
        sortCaret,
        headerStyle: {
          width: 210,
        },
        filter: textFilter({
          placeholder: 'Дата прохождения',
        }),
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      }, {
        dataField: '9',
        text: 'Номер талона',
        sort: false,
        editable: false,
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
        filter: textFilter({
          placeholder: 'Номер талона',
        }),
        headerStyle: {
          width: 300,
        },
        formatter: cell => talonIdFormatter(cell),
      },
      {
        dataField: '11',
        text: 'Оператор',
        sort: false,
        editable: false,
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
        filter: textFilter({
          placeholder: 'Оператор',
        }),
        headerStyle: {
          width: 320,
        },
        formatter: (cell, row) => operatorTemlate(cell, row),
      },
      {
        dataField: '3',
        text: 'Название опроса',
        sort: false,
        editable: false,
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
        filter: textFilter({
          placeholder: 'Название опроса',
        }),
        formatter: cell => testnameFormatter(cell),
      },
      {
        dataField: '6',
        text: 'Фотография',
        sort: false,
        editable: false,
        formatter: cell => photoFormatter(cell),
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      },
      {
        dataField: '00123',
        text: 'Дополнительная информация',
        isDummyField: true,
        sort: false,
        editable: false,
        formatter: (cell, row) => informationTemplate(cell, row, handleTableIcon),
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      },
    ],
  };
}
