import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import moment from 'moment';
import 'moment/locale/ru';
import ruDate from 'date-fns/locale/ru';
import Dropdown from '../../../Dropdown/Dropdown';
import getDropdownConfig from './DropdownConfig';
import { TestDiv, TestDiv2, TestDiv3 } from '../../../../../styles/ReportFilters';
import 'react-datepicker/dist/react-datepicker.css';


const AllFilter = ({
  changeFilter, filld, title, smallTitle,
}) => {
  const defaultFilterData = {
    comparator: 'none',
    value: '',
    from: '',
    before: '',
  };
  const [filterData, setFilterData] = useState(defaultFilterData);
  const [renderInput, setRenderInput] = useState('none');
  const DropdownConfig = getDropdownConfig();

  const changeFilterMark = (e, options, key, name) => {
    if (key === 'none') {
      setFilterData(defaultFilterData);
    } else {
      setFilterData({ ...filterData, comparator: name });
    }
    setRenderInput(key);
  };

  const handleInput = (q, w) => {
    const { eDate } = q;
    const dateFormat = 'DD.MM.YYYY HH:mm';
    const time = moment(eDate);
    const value = time.format(dateFormat);

    if (w === 'rangeBefore') {
      setFilterData({ ...filterData, before: value });
    } else if (w === 'rangeFrom') {
      setFilterData({ ...filterData, from: value });
    } else {
      setFilterData({ ...filterData, value });
    }
  };


  const dpConfig = {
    showTimeSelect: true,
    timeFormat: 'HH:mm',
    timeIntervals: 5,
    locale: ruDate,
    timeCaption: 'Время',
    dateFormat: 'dd.MM.yyyy HH:mm',
  };

  useEffect(() => {
    let where = '';
    switch (filterData.comparator) {
      case 'range':
        if (filterData.before && filterData.from !== '') {
          where = `> '${filterData.from}'::timestamp  and ${filld}::timestamp < '${filterData.before}'::timestamp`;
        } else {
          where = 'all';
        }
        break;
      case 'none':
        where = 'all';
        break;
      default:
        if (filterData.value !== '') {
          where = `${filterData.comparator} '${filterData.value}'::timestamp`;
        } else {
          where = 'all';
        }
        break;
    }
    changeFilter(filld, where);
  }, [filterData, changeFilter, filld]);

  const items = [
    { name: 'allCnt', type: 'noneDescription', value: filterData.value },
  ];

  const DuobleItems = [
    { name: 'rangeFrom', type: 'noneDescription', value: filterData.from },
    { name: 'rangeBefore', type: 'noneDescription', value: filterData.before },
  ];

  const renderFilterInput = () => {
    switch (renderInput) {
      case 'none':
        return <div />;
      case 'range':
        return DuobleItems.map((item, indx) => (
          <React.Fragment>
            <div
              key={indx}
              style={{
                marginLeft: '10px', display: 'flex', justifyContent: 'center',
              }}
            >
              <DatePicker
                {...dpConfig}
                selected={item.value ? moment(`${item.value}`, 'DD.MM.YYYY HH:mm').toDate() : item.value}
                onChange={date => handleInput({ eDate: date }, item.name)}
              />
            </div>
            {item.name === 'rangeFrom' ? <div style={{ display: 'flex', alignItems: 'center', marginLeft: '10px' }}>И</div> : (null)}
          </React.Fragment>
        ));
      default:
        return items.map((item, indx) => (
          <div
            key={indx}
            style={{
              marginLeft: '10px', display: 'flex', justifyContent: 'center',
            }}
          >
            <DatePicker
              {...dpConfig}
              selected={item.value ? moment(`${item.value}`, 'DD.MM.YYYY HH:mm').toDate() : item.value}
              onChange={date => handleInput({ eDate: date }, item.name)}
            />
          </div>
        ));
    }
  };

  return (
    <TestDiv2>
      <TestDiv>
        {smallTitle ? (<div style={{ fontSize: '12px' }}>{title}</div>) : (<div> {title} </div>)}
        <div style={{ marginLeft: '10px' }}>
          <Dropdown
            config={DropdownConfig}
            clickHandler={changeFilterMark}
            replaceableTitle
          />
        </div>
      </TestDiv>
      <TestDiv3 time="true">{renderFilterInput()}</TestDiv3>
    </TestDiv2>

  );
};

export default AllFilter;
