import React from 'react';
import DataPickers from './DatePickers/DatePickers';
import ReportButtons from './ReportButtons/ReportButtons';

const NavigationMenu = (props) => {
  const { reportType } = props;

  return (
    <div style={{ marginLeft: 7, marginBottom: 50 }}>
      <ReportButtons />
      {reportType === 'ticketReport'
      || reportType === 'clockwiseReport'
      || reportType === 'operatorReport'
      || reportType === 'queueReport'
      || reportType === 'groupReport'
      || reportType === 'workPlaceReport'
      || reportType === 'intervalReport'
      || reportType === 'dynamicsServiceTime' ? (null)
        : (
          <DataPickers
            reportType={reportType}
          />
        )
      }
    </div>
  );
};

export default NavigationMenu;
