import React from 'react';
import DatePicker from 'react-datepicker';
import ruDate from 'date-fns/locale/ru';
import { AddFontBox } from '../../../../styles/Common';

const Upload = ({
  setData, data, valid, onSave,
}) => {
  const { date, title, text } = data;

  const dpConfig = {
    selectsStart: true,
    locale: ruDate,
    timeCaption: 'Время',
    dateFormat: 'dd.MM.yyyy',
  };

  const handleDataChange = ({ sDate }) => {
    setData({ ...data, date: { ...data.date, value: sDate } });
  };


  return (
    <AddFontBox enctype="multipart/form-data" arr>
      <label>
        <p>{title.title}</p>
        <input
          name="name"
          value={title.value}
          type="text"
          onChange={(e) => setData({ ...data, title: { ...data.title, value: e.target.value } })}
        />
      </label>

      <label>
        <p>{text.title}</p>
        <textarea onChange={(e) => setData({ ...data, text: { ...data.text, value: e.target.value } })}>
          {text.value}
        </textarea>
      </label>
      <div style={{ minHeight: '40px', marginBottom: '40px' }}>
        <p>{date.title}</p>
        <DatePicker
          {...dpConfig}
          selectsStart
          selected={date.value}
          onChange={(date) => handleDataChange({ sDate: date })}
        />
        {/* <input
          name="name"
          value={date.value}
          type="text"
          onChange={(e) => setData({ ...data, date: { ...data.date, value: e.target.value } })}
        /> */}
      </div>
      {valid && <div className="save" onClick={onSave}>Сохранить</div>}
    </AddFontBox>
  );
};

export default Upload;
