import React from 'react';
import GetCheckbox from '../../components/checkbox/getCheckbox';
import GetButtons from '../../components/buttons/getButtons';
import GetInput from '../../components/input/getInput';

import {
  BigButtonsContainer, StyleButton, InputButton, InputButtonContainer,
} from '../../style/style';

const RenderButtons = ({
  index, startScreen, scene, history, style, match, selectScreen,
}) => {
  const {
    bottomButton, button, checkbox,
  } = style;

  const buttons = () => {
    switch (index) {
      case 2:
        return (
          <BigButtonsContainer>  {/* Рендер мультивыбор */}
            <StyleButton width={checkbox.widthContent} start={startScreen} btns={scene.buttons}>
              <GetCheckbox selectScreen={selectScreen} scene={scene} />
            </StyleButton>
          </BigButtonsContainer>
        );
      case 1:
        return (
          <InputButtonContainer> {/* Рендер ввода дополнительной информации */}
            <InputButton start={startScreen} input btns={scene.buttons}>
              <GetInput selectScreen={selectScreen} scene={scene} match={match} styleBottom={bottomButton} history={history} styleButton={button} />
            </InputButton>
          </InputButtonContainer>
        );
      default:
        return (
          <BigButtonsContainer style={{ alignItems: button.alignItems }}> {/* Рендер кнопок */}
            <StyleButton width={button.widthContent} style={{ justifyContent: button.justifyContent }} start={startScreen} btns={scene.buttons}>
              <GetButtons selectScreen={selectScreen} scene={scene} style={button} history={history} />
            </StyleButton>
          </BigButtonsContainer>
        );
    }
  };

  return buttons();
};

export default RenderButtons;
