import React, { useState } from 'react';
import { Btn, BtnsBox } from '../../../../../../styles/Modal';
import { BtnAdd } from '../../../../../../styles/Common';
import Modal from '../../../../../Elements/Modal/Modal';
import request from '../../../../../../utils/request';
import RenderInputs from './RenderInputs';

const NewDay = ({
  isOpen, toggleOpen, fetchData, dayUpdate,
  dayData, setDayData, setDayUpdate, preloader, intervalList, defaultWeekList, write,
}) => {
  const [isValid, setIsValid] = useState(false);

  const resetModal = () => {
    setDayData({ ...dayData });
    toggleOpen();
    setIsValid('');
    setDayUpdate(false);
  };

  const addDay = async () => {
    if (isValid) {
      preloader();
      await request('/server/api/dworkday/', 'add', dayData);
      fetchData();
      resetModal();
      preloader();
    }
  };

  const updateDay = async () => {
    if (isValid) {
      const itemId = dayData.id;
      delete dayData.id;
      preloader();
      await request(`/server/api/dworkday/${itemId}/`, 'update', { ...dayData });
      fetchData();
      resetModal();
      preloader();
    }
  };

  return (
    <React.Fragment>
      {write && (
      <BtnAdd onClick={toggleOpen} add>
        <span>Добавить</span>
      </BtnAdd>
      )
      }
      <Modal title={dayUpdate ? 'Редактирование дня' : 'Добавление дня'} isOpen={isOpen} toggleOpen={resetModal}>
        <RenderInputs
          dayUpdate={dayUpdate}
          defaultWeekList={defaultWeekList}
          dayData={dayData}
          intervalList={intervalList}
          setDayData={setDayData}
          setIsValid={setIsValid}
          write={write}
        />
        {write && (
        <BtnsBox>
          <Btn
            margin="0 25px 0 0"
            valid={isValid}
            onClick={dayUpdate ? updateDay : addDay}
          >
            Сохранить
          </Btn>
          <Btn onClick={resetModal} cancel>Отменить</Btn>
        </BtnsBox>
        )
        }
      </Modal>
    </React.Fragment>
  );
};

export default React.memo(NewDay);
