export default function (connectedWplaces) {
  const wPlaces = connectedWplaces.filter(pl => pl !== '').map((place) => {
    return place.match(/\d+/)[0];
  });
  wPlaces.sort(function (a, b) {
    return a - b
  });

  let out = [];
  let i, j;
  let isNull = 0;
  for (i = 0; i < wPlaces.length; i++) { //упаковка в диапазоны
    if (Number(wPlaces[i]) === 0) isNull++;
    if (!isNaN(wPlaces[i]) && isNull < 2) { //чтобы не было +0 -0 и т.п. яваскриптовых заморочек
      out.push(Number(wPlaces[i]));
      for (j = i + 1; j < wPlaces.length; j++) {
        if (Number(wPlaces[j - 1]) + 1 !== Number(wPlaces[j])) break;
      }
      j--;
      if (i === j) { //одно число
        out.push(',');
      }
      else if (i + 1 === j) { //два числа
        out.push(',', Number(wPlaces[j]), ',');
        i++;
      }
      else { //диапазон
        out.push('-', Number(wPlaces[j]), ',');
        i += (j - i);
      }
    }
  }
  out.pop();
  return out.join('');
}
