import React from 'react';
import {
  FaAlignLeft, FaAlignCenter, FaAlignRight,
} from 'react-icons/fa';
import {
  AlignBox,
  AlignItem,
} from '../../../../../../styles/BoardStyle';
import { ColorBox } from '../style';

const align = ({
  value, title, element, dispatch, paramName = 'textAlign',
}) => (
  <ColorBox>
    <span dangerouslySetInnerHTML={{ __html: title }} />
    <AlignBox shadow>
      <AlignItem
        onClick={() => dispatch({
          type: 'params',
          name: element,
          param: paramName,
          value: 'left',
        })}
        active={value === 'left'}
      >
        <FaAlignLeft />
      </AlignItem>
      <AlignItem
        onClick={() => dispatch({
          type: 'params',
          name: element,
          param: paramName,
          value: 'center',
        })}
        active={value === 'center'}
      >
        <FaAlignCenter />
      </AlignItem>
      <AlignItem
        onClick={() => dispatch({
          type: 'params',
          name: element,
          param: paramName,
          value: 'right',
        })}
        active={value === 'right'}
      >
        <FaAlignRight />
      </AlignItem>
    </AlignBox>
  </ColorBox>
);

export default align;
