import React, { useReducer } from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import { ThemeProvider } from 'styled-components';
import { ToastContainer } from 'react-toastify';
import axios from 'axios';
import history from './utils/customHistory';
import reducers from './store/reducers';
import createLogger from './utils/logger';
import './fonts.scss';
import colorDark from './styles/ColorDark';
import colorLight from './styles/Colors';
import ThemeCtx from './utils/themeCtx';
import { GlobalBox, GlobalStyle } from './styles/Common';
import Layout from './hoc/Layout';
import getParamsFromUrl from './utils/getParamsFromUrl';

import Terminal from './views/Control/Devices/DeviceStyle/TerminalStyle/Terminal';
import Board from './views/Control/Devices/DeviceStyle/BoardStyle/BoardPreview/Layout/Layout';
import Login from './views/Elements/Login/Login';
import PreEntry from './views/Control/Devices/DeviceStyle/PreEntry/App';
import Webservice from './views/Webservice/Webservice';

createLogger(false);
const createStoreWithMiddleware = applyMiddleware()(createStore);

const App = () => {
  const theme = window.localStorage.getItem('theme');
  const [color, dispatch] = useReducer((state, action) => action, theme === 'dark' ? colorDark : colorLight);

  // const url = window.location.href;

  const urlParams = getParamsFromUrl(window.location.href);

  const { token } = urlParams;
  if (token) {
    const chekToken = async () => {
      const res = await axios.post(`/server/api/license/?action=get&token=${token}`);
      console.log('res === ', res);

      if (res.statusText === 'OK') {
        console.log('res === ', res);
        window.localStorage.setItem('token', token);
        window.localStorage.setItem('fio', 'Пользователь ЕСМ');
        window.localStorage.setItem('login', 'ESM');
        window.localStorage.setItem('role', 'eq_root');
      }
    };
    chekToken();
  }

  const ctx = { color, dispatch };
  return (
    <Provider store={createStoreWithMiddleware(reducers)}>
      <ThemeProvider theme={color}>
        <GlobalBox role={window.localStorage.role}>
          <GlobalStyle />
          <ToastContainer
            autoClose={3000}
          />
          <ThemeCtx.Provider value={ctx}>
            <Router history={history}>
              <Switch>
                <Route
                  exact
                  history={history}
                  path="/login/"
                  render={(rest) => (
                    <Login
                      {...rest}
                    />
                  )}
                />
                <Route
                  path="/terminal/:id/"
                  component={Terminal}
                />
                <Route
                  path="/terminal/:id/:screenId"
                  component={Terminal}
                />
                <Route
                  exact
                  path="/board/:id/"
                  component={Board}
                />
                <Route
                  path="/preentry"
                  component={PreEntry}
                />
                <Route
                  path="/webservice"
                  component={Webservice}
                />
                <Route
                  path="/"
                  component={Layout}
                />
              </Switch>
            </Router>
          </ThemeCtx.Provider>
        </GlobalBox>
      </ThemeProvider>
    </Provider>
  );
};

ReactDOM.render(
  <App />,
  document.getElementById('root'),
);
