import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { BtnTable } from '../../../../../styles/Common';
import { ReactComponent as False } from '../../../../../images/false.svg';
import { ReactComponent as True } from '../../../../../images/true.svg';
import { dateFormat, placeholder } from '../../../../../utils/tableFormatters';
import iconFormatter from '../../../../../utils/iconFormatter';

function statusFormatter(value) {
  switch (value) {
    case 1:
      return <BtnTable bg="accent" hNone><False fill="white" /></BtnTable>;
    default:
      return <BtnTable bg="accent" hNone><True fill="white" /></BtnTable>;
  }
}

export default function (write) {
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Наименование',
        headerFormatter: placeholder,
        editable: false,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Наименование',
        }),
      },
      {
        dataField: 'description',
        text: 'Описание',
        headerFormatter: placeholder,
        editable: false,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Описание',
        }),
      },
      {
        dataField: 'status',
        text: 'Статус',
        sort: true,
        editable: false,
        formatter: statusFormatter,
      },
      {
        dataField: 'modified_by_username',
        text: 'Изменен',
        headerFormatter: placeholder,
        sort: true,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Изменен',
        }),
        editable: false,
      },
      {
        dataField: 'modified_on',
        text: 'Когда изменен',
        editable: false,
        formatter: dateFormat,
      },
      {
        dataField: 'timebegin',
        text: 'Начало',
        editable: false,
        headerStyle: {
          width: 100,
        },
        formatter: (cell) => dateFormat(cell).split(' ')[1].split(':').splice(0, 2).join(':'),
      },
      {
        dataField: 'timeend',
        text: 'Конец',
        editable: false,
        formatter: (cell) => dateFormat(cell).split(' ')[1].split(':').splice(0, 2).join(':'),
        headerStyle: {
          width: 100,
        },
      },
      {
        dataField: 'Settings',
        isDummyField: true,
        text: '',
        headerStyle: {
          width: 100,
        },
        formatter: () => iconFormatter({ filter: false, write, params: false }),
        editable: false,
      },
    ],
  };
}
