import React, { useState } from 'react';
import { SketchPicker } from 'react-color';
import { ColorBtn, CloseBox } from '../styles/BoardStyle';
import findPosition from './findPosition';

const ColorPicker = ({ color, handleColor, name }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [bottom, setBottom] = useState(false);

  const popover = bottom => ({
    position: 'absolute',
    zIndex: '2',
    top: bottom ? 'auto' : 0,
    bottom: bottom ? 0 : 'auto',
  });
  return (
    <div className="color-picker-box">
      <ColorBtn
        color={color}
        onClick={(e) => {
          e.preventDefault();
          findPosition(e.target, setBottom);
          setIsOpen(true);
        }}
      />
      {isOpen && (
      <div key={123} style={popover(bottom)}>
        <CloseBox onClick={(e) => { setIsOpen(false); }} />
        <SketchPicker
          onChange={(newColor) => {
            const { rgb } = newColor;
            const col = `rgba(${rgb.r},${rgb.g},${rgb.b},${rgb.a})`;
            handleColor({ type: 'element', value: name, payload: col });
          }}
          color={color}
        />
      </div>
      )}
    </div>
  );
};

export default ColorPicker;
