import React from 'react';
import axios from 'axios';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import { ReportTitleBox, TableTitle, TableTitleLeftItem } from '../../../../styles/TableTitle';
import Dropdown from '../../../Elements/Dropdown/Dropdown';
import DataExportConfig from '../../Reports/utils/DataExport/DataExportConfig';

const ReportTitle = (props) => {
  const {
    ReportParams, name, type, pagination,
  } = props;
  console.log('type =', type);
  const dataExportConfig = DataExportConfig(props);

  const openNotify = (type, message) => {
    if (type === 'success') {
      toast.success(`${message}`, 1000);
    } else {
      toast.error(`${message}`, 1000);
    }
  };

  const runDataExport = async (e) => {
    console.log('ReportParams === ', ReportParams);
    const token = window.localStorage.getItem('token');
    console.log(e);
    if (e === 'Excel') {
      let exportTableName;

      switch (ReportParams.TebleType) {
        default: break;
        case 'QuestionTable':
          exportTableName = 'questionTable';
          break;
        case 'RequestTable':
          exportTableName = 'requestReport';
          break;
        case 'DetailedReport':
          exportTableName = 'detailedReport';
          break;
        case 'ConsReport':
          exportTableName = 'consReport';
          break;
      }

      const exportTableParams = {
        type: exportTableName,
        token,
        sqlQuery: ReportParams.query,
        filters: { startDate: pagination.startDate, endDate: pagination.endDate },
        items: ReportParams.items,
        TableConfig: ReportParams.TableConfig.columns,
        needToExport: true,
      };


      try {
        console.log('exportTableParams === ', exportTableParams);
        const answer = await axios({
          method: 'post',
          url: `/api/reports/?token=${token}`,
          data: exportTableParams,
        });
        console.log('answer =', answer);
        const { statusText } = answer;
        if (statusText === 'OK') {
          const message = 'Отчет подготавливается для скачивания и появиться в разделе "Архив"';
          openNotify('success', message);
        }
      } catch (err) {
        openNotify('error', err);
      }
    } else if (e === 'pdf') {
      alert('В работе');
    } else {
      console.log('this is a test area');
    }
  };

  return (
    <ReportTitleBox reportTitile>
      <TableTitleLeftItem />
      <div style={{ display: 'flex', justifyContent: 'center', flex: 1 }}>
        <TableTitle>{name}</TableTitle>
      </div>
      {type === 'RequestTable'
      && (
      <div>
        {ReportParams && (
        <Dropdown
          config={dataExportConfig}
          clickHandler={runDataExport}
          replaceableTitle={false}
        />
        )}
      </div>
      )}
    </ReportTitleBox>
  );
};

function mapStateToProps(state) {
  return { reportConfig: state.reportConfig };
}

export default connect(mapStateToProps)(ReportTitle);
