import React, { useState, useEffect, useCallback } from 'react';
import request from '../../../utils/request';
import { Title, ContentBox, PreloaderBox } from '../../../styles/Common';
import ActionLogTable from './ActionLogTable';
import Filter from './Filter';

const ActionLog = ({ preloader }) => {
  const defaultTableCofig = {
    page: 1,
    sizePerPage: 10,
    sortField: 'created_on',
    sortOrder: 'desc',
    totalSize: 0,
  };
  const [tableParams, setTableParams] = useState(defaultTableCofig);
  const [userList, setUserList] = useState(null);
  const [actionList, setActionList] = useState(null);
  const [filters, setFilters] = useState(null);

  const fetchUsers = useCallback(async () => {
    const res = await request('/server/api/user/', 'get');
    setUserList(res.data.data);
  }, []);

  const fetchActions = useCallback(async () => {
    const offset = tableParams.sizePerPage * (tableParams.page - 1);
    const sortObject = { field: tableParams.sortField, dir: tableParams.sortOrder };
    preloader();
    const res = await request('/server/api/useraction/', 'get', { }, '', tableParams.sizePerPage, offset, sortObject, filters || []);
    preloader();
    setActionList(res.data.data);
    setTableParams((v) => ({ ...v, totalSize: res.data.total }));
  }, [filters, preloader, tableParams.page, tableParams.sizePerPage, tableParams.sortField, tableParams.sortOrder]);

  const onSizePerPageChange = (sizePerPageNumber, pageNumber) => {
    setTableParams({
      ...tableParams,
      sizePerPage: sizePerPageNumber,
      page: pageNumber,
    });
  };

  const onPageChange = (pageNumber, sizePerPageNumber) => {
    setTableParams({
      ...tableParams,
      sizePerPage: sizePerPageNumber,
      page: pageNumber,
    });
  };

  const onTableChange = (type, newState) => {
    let {
      page, sizePerPage, sortField, sortOrder, filters: filters1,
    } = newState;
    if (tableParams.sortOrder === sortOrder && sortOrder === 'desc') {
      sortOrder = 'asc';
    } else {
      sortOrder = 'desc';
    }
    setTableParams({
      ...tableParams,
      page,
      sizePerPage,
      sortField,
      sortOrder,
      filters: filters1,
    });
  };

  useEffect(() => {
    fetchUsers();
  }, [fetchUsers]);

  useEffect(() => {
    if (filters) fetchActions();
  }, [filters, fetchActions]);

  useEffect(() => {
    fetchActions();
  }, [tableParams.page, tableParams.sizePerPage, tableParams.sortField, tableParams.sortOrder, fetchActions]);

  const handleFilters = (filterList) => {
    setFilters(filterList);
    setTableParams({ ...tableParams, page: 1 });
  };

  const pagination = {
    page: tableParams.page,
    sizePerPage: tableParams.sizePerPage,
    totalSize: tableParams.totalSize,
    onPageChange,
    onSizePerPageChange,
  };

  const users = {};
  if (userList) {
    userList.forEach((user) => {
      users[Number(user.id)] = user.username;
    });
  }

  return (
    <>
      <Title>Лог действий</Title>
      <ContentBox>
        <Filter userList={userList} handleFilters={handleFilters} />
        <PreloaderBox>
          <ActionLogTable
            userList={userList}
            actionList={actionList}
            setUserList={setUserList}
            preloader={preloader}
            pagination={pagination}
            onTableChange={onTableChange}
          />
        </PreloaderBox>
      </ContentBox>
    </>
  );
};

export default ActionLog;
