import React, { useState, useEffect, useCallback } from 'react';
import { toast } from 'react-toastify';
import { Title, ContentBox, PreloaderBox } from '../../../styles/Common';
import AddButtons from '../../Elements/Button/AddButton';
import SelectInput from '../../Elements/Input/SelectInput/SelectInput';
import request from '../../../utils/request';
import { Box } from './style';
import Table from './table';
import Preloader from '../../Elements/Preloader/Preloader';

const Session = ({ ...props }) => {
  const [sessions, setSessions] = useState([]);
  const [users, setUsers] = useState([]);
  const [id, setId] = useState(false);

  const getData = useCallback(async () => {
    const { data: { data } } = await request('/server/api/usersession/');
    const fusers = await request('/server/api/user/');

    setSessions([...data]);
    setUsers([...fusers.data.data]);

  }, []);

  const addSessuion = async () => {
    const d = {
      user_id: id,
      sessioncomment: 'Constant session',
      config: {},
    };
    try {
      await request('/server/api/usersession/', 'add', d);
      toast.success('Выполнено');
    } catch (e) {
      toast.error(e);
    }
    getData();
  };

  useEffect(() => { getData(); }, [getData]);

  const options = users.map((el) => ({ name: el.username, id: el.id }));

  return (
    <div>
      <Title>Сессии</Title>
      <ContentBox>
        <Box>
          <p>Создание постоянной сессии для пользователя</p>
          <div>
            <SelectInput onChange={(e) => setId(e.target.value)} m="0" value={id || (users[0] ? users[0].id : '')} noDefault options={options} />
            <AddButtons toggle={addSessuion} />
          </div>
        </Box>
        <PreloaderBox>
          {sessions.length === 0 && users.length === 0
            ? <Preloader isOpen small />
            : <Table users={users} {...props} fetchData={getData} data={sessions} />}
        </PreloaderBox>
      </ContentBox>
    </div>
  );
};

export default Session;
