import React, { useEffect } from 'react';
import Input from '../../../Elements/Input/Input';
import RenderCheckList from './RenderCheckList';
import { BtnAdd } from '../../../../styles/Common';


const RenderInputs = ({
  zoneData, setZoneData, preloader, setIsValid, zoneUpdate,
}) => {
  useEffect(() => {
    const { name } = zoneData;
    if (zoneUpdate && name) {
      setIsValid(true);
    }
  }, [setIsValid, zoneData, zoneUpdate]);

  const handleInput = (e) => {
    const { name, value } = e.target;
    const validData = { ...zoneData, [name]: value };
    const {
      name: iV, longname: pV,
    } = validData;
    setZoneData({ ...validData });
    if (iV && pV) {
      setIsValid(true);
    } else {
      setIsValid(false);
    }
  };

  const {
    name, longname, description,
  } = zoneData;

  const items = [
    {
      label: 'Имя:', name: 'name', value: name || '', required: true,
    },
    {
      label: 'Наименование:', name: 'longname', value: longname || '', required: true,
    },
    { label: 'Описание:', name: 'description', value: description || '' },
  ];

  const addParams = () => {
    const { params } = zoneData.config;
    params.push({ fid: '', fv: [] });
    setZoneData({ ...zoneData, config: { ...zoneData.config, params } });
  };

  return (
    <>
      {items.map((item) => (
        <Input
          key={item.name}
          item={item}
          onChange={handleInput}
        />
      ))}

      <BtnAdd onClick={addParams} add>
        <span>Добавить</span>
      </BtnAdd>
      <RenderCheckList
        setZoneData={setZoneData}
        zoneData={zoneData}
        zoneUpdate={zoneUpdate}
        preloader={preloader}
      />
    </>

  );
};
export default RenderInputs;
