import React, { useState } from 'react';
import { Title, Input } from '../../../../styles/Common';
import { ReactComponent as Trash } from '../../../../images/bin.svg';
import { ReactComponent as Edit } from '../../../../images/edit.svg';
import {
  Card, CardTitle, CardBody, Item, ButtonBox, Box, Container,
} from './style';
import AddButton from '../../../Elements/Button/AddButton';

const CardList = ({ data, onEdit, onClick }) => {
  const [value, setValue] = useState('');

  return (
    <div style={{ width: '100%' }}>
      <Title>Опросы</Title>
      <Box>
        <p>Создание опроса: </p>
        <Input value={value} onChange={(e) => setValue(e.target.value)} placeholder="Название опроса" />
        <AddButton toggle={() => {
          if (value) {
            onClick({
              surveyname: value,
              connectedQuestions: [],
            });
          }
          setValue('');
        }}
        />
      </Box>
      <Container>
        <Card className="head">
          <div className="titleQuestion">Наименование</div>
          <div className="question">Вопросы</div>
        </Card>
        {data.map((el) => (
          <Card key={el.id}>
            <CardTitle>{el.surveyname}</CardTitle>
            <CardBody>
              <ul>
                {el.connectedQuestions.map((answ, index) => <Item key={index}>{answ.questiontext}</Item>)}
              </ul>
            </CardBody>
            <ButtonBox>
              <Edit onClick={() => onEdit(el.id)} />
              <Trash onClick={() => onClick({ id: el.id, name: el.surveyname }, 1)} />
            </ButtonBox>
          </Card>
        ))}
      </Container>
    </div>
  );
};

export default CardList;
