import React, { useState, useEffect, useCallback } from 'react';
import request from '../../../../utils/request';
import Input from '../../../Elements/Input/Input';
import RenderCheckList from './RenderCheckList';


const RenderInputs = ({
  userData, setUserData, preloader, setIsValid, userUpdate, roles,
}) => {
  const [queue, setQueue] = useState([]);
  const [pwd, setPwd] = useState('');
  const [pwd2, setPwd2] = useState('');

  const fetchQueue = useCallback(async () => {
    console.log(1);
    preloader();
    const queueList = await request('/server/api/queue/', 'get');
    setQueue([...queueList.data.data]);
    preloader();
  }, [preloader]);

  useEffect(() => {
    fetchQueue();
  }, [fetchQueue]);

  useEffect(() => {
    const { username } = userData;
    if (userUpdate && username && pwd === pwd2) {
      setIsValid(true);
    }
  }, [, pwd, pwd2, setIsValid, userData, userUpdate]);


  const handleInput = (e) => {
    const { name, value, type } = e.target;
    let validData = { ...userData };
    let repeatPwd = pwd2;
    let currentPwd = pwd;
    if (name === 'pwd2') {
      repeatPwd = value;
      setPwd2(value);
    } else if (name === 'pwd') {
      currentPwd = value;
      setPwd(value);
      setUserData({ ...validData, [name]: value });
    } else {
      if (type === 'checkbox') {
        validData = { ...userData, [name]: JSON.parse(value) }; // convert string to boolean
      } else {
        validData = { ...userData, [name]: value };
      }
      setUserData({ ...validData });
    }
    const { username, fio } = validData;
    if (username && currentPwd && currentPwd === repeatPwd && fio.trim()) { // valid for add user
      setIsValid(true);
    } else if (userUpdate && username && currentPwd === repeatPwd) { // valid for update user
      setIsValid(true);
    } else {
      setIsValid(false);
    }
  };

  const {
    username, fio, hidden, blocked,
  } = userData;

  const items = [
    {
      label: 'Логин:', name: 'username', value: username || '', required: true,
    },
    {
      label: 'Пароль:', name: 'pwd', value: pwd || '', required: true, type: 'password',
    },
    {
      label: 'Повторите пароль:', name: 'pwd2', value: pwd2 || '', required: true, type: 'password',
    },
    {
      label: 'ФИО:', name: 'fio', value: fio || '', required: true,
    },
    {
      label: 'Запрет работы на АРМ:', name: 'hidden', type: 'checkbox', value: hidden || false,
    },
    {
      label: 'Запрет входа через Панель администрирования:', name: 'blocked', type: 'checkbox', value: blocked || false,
    },
  ];

  return (
    <>
      {items.map((item) => (
        <Input
          key={item.name}
          item={item}
          onChange={handleInput}
        />
      ))}
      <RenderCheckList
        setUserData={setUserData}
        queue={queue}
        roles={roles}
        userData={userData}
        userUpdate={userUpdate}
      />
      {pwd !== pwd2 && <p style={{ color: 'red', textAlign: 'center' }}>Пароли не совпадают</p>}
    </>

  );
};
export default RenderInputs;
