import React from 'react';
import Request from './Request/Request';
import ConsolidatedReport from './ConsolidatedReport/ConsolidatedReport';
import DetailedReport from './DetailedReport/DetailedReport';
import NpsReport from './NpsReport/NpsReport';
import MarkReport from './MarkReport/MarkReport';

const QualityControlElements = ({ activeSection, settings, preloader }) => {
  switch (activeSection) {
    case 1:
      return (
        <Request
          settings={settings}
          preloader={preloader}
        />
      );
    case 2:
      return (
        <ConsolidatedReport
          settings={settings}
          preloader={preloader}
          activeSection={activeSection}
        />
      );
    case 3:
      return (
        <DetailedReport
          settings={settings}
          preloader={preloader}
          activeSection={activeSection}
        />
      );
    case 4:
      return (
        <NpsReport
          settings={settings}
          preloader={preloader}
          activeSection={activeSection}
        />
      );
    case 5:
      return (
        <MarkReport
          settings={settings}
          preloader={preloader}
          activeSection={activeSection}
        />
      );
    default:
      return (null);
  }
};

export default QualityControlElements;
