import React, { useRef } from 'react';
import { AddFontBox } from '../../../styles/Common';
import checkFileExtension from '../../../utils/checkFileExtension';

const UploadFont = ({
  save, isVideo, title, setTitle, showError, fileExtension,
}) => {
  const form = useRef(null);
console.log('title === ', title);
  const upload = (e, isDrop) => {
    console.log('upload func work === ');

    let file;
    if (isDrop) {
      const dt = e.dataTransfer;
      const { files } = dt;
      [file] = files;
    } else {
      [file] = e.target.files;
    }

    if (checkFileExtension(file.name, fileExtension)) {
      const { elements } = form.current;
      console.log('elements === ', elements);
      const formData = new FormData();
      formData.append('name', elements.name.value);
      formData.append(isVideo ? 'video' : 'font', file);
      save(formData);
    } else {
      showError('Данный формат файла не поддерживается');
    }
  };


  const clearInputValue = (e) => {
    e.target.value = '';
  };

  const handleDrop = (e, value) => {
    console.log('handleDrop func work');
    e.preventDefault();
    e.stopPropagation();
    upload(e, value);
  };

  const handleActive = (e) => {
    e.preventDefault();
  };


  return (
    <AddFontBox enctype="multipart/form-data" ref={form}>
      <label>
        <p>Введите название {isVideo ? 'видео' : 'шрифта'}</p>
        <input
          name="name"
          value={title}
          type="text"
          onChange={(e) => setTitle(e.target.value)}
        />
      </label>

      {title ? (
        <div className="upload-item_content file">
          <div className="upload-label-box">
            <div>
              <label
                htmlFor="input"
                onDragEnter={(e) => handleActive(e)}
                onDragOver={(e) => handleActive(e)}
                onDragLeave={(e) => handleActive(e)}
                onDrop={(e) => handleDrop(e, true)}
                className="upload-label"
              >
                <span>Перетащите {isVideo ? 'видео' : 'шрифт'} сюда</span>
                <span>Или нажмите здесь для загрузки  {isVideo ? 'видео' : 'шрифта'}</span>
              </label>
              <input
                type="file"
                style={{ display: 'none' }}
                onClick={(e) => clearInputValue(e)}
                id="input"
                onChange={(e) => handleDrop(e)}
              />
            </div>
          </div>
        </div>
      ) : (
        <div className="upload-item_content file">
          <div className="upload-label-box">
            <div>
              <label
                htmlFor="input"
                className="upload-label"
              >
                <span style={{ margin: 0 }}>Для загрузки {isVideo ? 'видео' : 'шрифта'} введите название</span>
              </label>
            </div>
          </div>
        </div>
      )}
    </AddFontBox>
  );
};

export default UploadFont;
