import React, { useState, useEffect } from 'react';
import useReactRouter from 'use-react-router';
import {
  NavIcon, NavSubLink, MenuText, NavSubLinkBox, NavSubBox,
} from '../../../../styles/NavMenu';

import navItemsList from './navItemsList';
import getLinks from '../../../../utils/getLinks';

const NavMenuItems = () => {
  const [path, setPath] = useState('');
  const { location } = useReactRouter();

  const arrLink = getLinks();

  useEffect(() => {
    setPath(location.pathname);
  }, [location.pathname]);

  return navItemsList.map((item) => {
    let activeMenu = false;
    let subMenuArr = [];
    if (item.subItems.length) {
      subMenuArr = item.subItems.map((subItem, index) => {
        if (arrLink.every((el) => el !== subItem.link)) return null;
        let activeLink = false;
        if (path.indexOf(subItem.link) !== -1) {
          activeLink = true;
        }
        return (
          <NavSubLinkBox key={index}>
            <NavSubLink to={subItem.link}>
              <NavIcon activeMenu={activeLink}>
                {subItem.icon}
              </NavIcon>
              <MenuText activeMenu={activeLink}>
                {subItem.text}
              </MenuText>
            </NavSubLink>
          </NavSubLinkBox>
        );
      });
    } else if (item.link === path) {
      activeMenu = true;
    }
    item.subItems.forEach((sub) => {
      if (path.indexOf(sub.link) !== -1) {
        activeMenu = true;
      }
    });
    if (activeMenu) {
      return (
        <NavSubBox key={item.link} activeMenu={activeMenu}>
          {subMenuArr}
        </NavSubBox>
      );
    } else {
      return null
    }
  });
};

export default NavMenuItems;
