import React, { useState, useEffect, useCallback } from 'react';
import moment from 'moment';
import DatePicker from 'react-datepicker';
import { Title } from '../../../styles/Common';
import request from '../../../utils/request';

const Report = () => {
  const [queue, setQueue] = useState(undefined);
  const [calendar, setCalendar] = useState(undefined);
  const [date, setDate] = useState({
    start: moment().startOf('month'),
    end: moment().endOf('month'),
  });

  const fetchCalendar = useCallback(async (s) => {
    const { start, end } = date;
    const d = {
      // qId: selectedQueue,
      dateFrom: moment(start).format('YYYY-MM-DD HH:mm:ss'),
      dateTo: moment(end).format('YYYY-MM-DD HH:mm:ss'),
    };
    const fetchData = await request('/server/api/dpafullcalendarslot/', 'get', d);
    const cal = {};
    fetchData.data.data.forEach((el) => {
      const dateD = +el.thedate.split(' ')[0].split('.')[2];
      if (cal[dateD]) {
        cal[dateD].push(el);
      } else {
        cal[dateD] = [el];
      }
    });

    setCalendar(cal);

    // setData((v) => {
    //   const curDay = +v.dayId.day.split('.')[0];
    //   return curDay ? {
    //     ...v,
    //     dayId: {
    //       ...sDay,
    //       id: cal[curDay] ? cal[curDay][0].calendarday_id : null,
    //       data: cal[curDay] || [],
    //     },
    //   } : v;
    // });
    // setIsLoad(false);
  }, [date]);

  const fetch = useCallback(async () => {
    const { data: { data } } = await request('/server/public/preorder/queues/', 'get');
    setQueue(data);
  }, []);

  useEffect(() => {
    fetch();
    fetchCalendar();
  }, [fetch, fetchCalendar]);
  console.log({ calendar });
  return (
    <div>
      <Title>Предварительная запись</Title>
      <div className="preEntryReportFilter">
        <div>
          <p>Первый день шаблона <span>*</span></p>
          <DatePicker
            onChange={(e) => console.log(e)}
            selectsStart
            startDate={date.start}
            endDate={date.end}
            dateFormat="dd.MM.yyyy"
          />
        </div>
        <div>
          <p>Последний день шаблона <span>*</span></p>
          <DatePicker
            onChange={(e) => console.log(e)}
            selectsEnd
            startDate={date.start}
            endDate={date.end}
            dateFormat="dd.MM.yyyy"
          />
        </div>
      </div>
    </div>
  );
};

export default Report;
