import React, { useState } from 'react';
import moment from 'moment';
import DatePickers from 'react-datepicker';
import Modal from '../../../../Elements/Modal/Modal';
import ModalButtons from '../../../../Elements/Modal/ModalButtons';
import { Checkbox } from '../../Poll/style';
import { ColorBox } from '../../../../Control/Devices/DeviceStyle/Components/style';

const CopyDay = ({ setOpen, open, saveDay }) => {
  const dateNow = moment();

  const [data, setData] = useState({
    replace: false,
    pastart_interval: dateNow.toDate(),
    paend_interval: dateNow.add(1, 'day').toDate(),
  });

  const save = () => {
    saveDay({
      ...data,
      pastart_interval: moment(data.pastart_interval).format('YYYY.MM.DD'),
      paend_interval: moment(data.paend_interval).format('YYYY.MM.DD'),
    });
  };

  const isValid = Object.keys(data).every((el) => data[el] !== '');
  return (
    <Modal title="Копирование дня" isOpen={open} toggleOpen={() => setOpen(false)}>
      <p style={{ marginBottom: 15 }}>Интервал дат для копирования</p>
      <div className="flex datePickerRange">
        <span>c - </span>
        <DatePickers
          selected={data.pastart_interval}
          onChange={(e) => setData({ ...data, pastart_interval: e, paend_interval: moment(e).add(1, 'day').toDate() })}
          // onChange={(e) => console.log(moment(e).add())}
          selectsStart
          dateFormat="dd.MM.yyyy"
        />
        <span>по - </span>
        <DatePickers
          selected={data.paend_interval}
          endDate={data.paend_interval}
          selectsEnd
          minDate={moment(data.pastart_interval).add(1, 'day').toDate()}
          startDate={moment(data.pastart_interval).add(1, 'day').toDate()}
          onChange={(e) => setData({ ...data, paend_interval: e })}
          dateFormat="dd.MM.yyyy"
        />
      </div>
      <ColorBox
        style={{ justifyContent: 'flex-start', marginTop: 15 }}
        onClick={() => setData({ ...data, replace: !data.replace })}
      >
        <Checkbox
          active={data.replace}
          style={{ marginRight: 5 }}
        />
        <span style={{ flex: 'none' }}>Перезаписывать ячейки</span>
      </ColorBox>
      <ModalButtons update={false} addFunc={save} reset={() => setOpen(false)} isValid={isValid} />
    </Modal>
  );
};

export default CopyDay;
