import { setTextError } from './actions';
import Store from './store';

const { dispatch } = Store;

export default function (buttons, history, headUrl, id, isBottom) {
  if (buttons.length < 1 && isBottom) {
    history.push(`/${headUrl}/${id}/error`);
    dispatch(setTextError('Добавьте кнопки в Панели Администрирования'));
  }
}
