import React, {
  useState,
} from 'react';
import Table from '../../Elements/Table/Table';
import {
  EmptyList,
} from '../../../styles/Common';
import Config from './TableConfig';
import Preloader from '../../Elements/Preloader/Preloader';
import DeleteWarning from '../../Elements/Modal/DeleteWarning';
import {
  useToggle,
} from '../../../hooks/useToggle';
import DeleteItem from '../../../utils/deleteItem';

const DisplaysTable = ({
  displayList,
  setDisplayUpdate,
  setDisplayData,
  toggleOpen,
  preloader,
  fetchDisplays,
  showError,
  write,
}) => {
  const [displayId, setDisplayId] = useState(false);
  const [displayName, setDisplayName] = useState(false);
  const [showDelWarn, setShowDelWarn] = useToggle(false);

  const delDisplay = async () => {
    await DeleteItem({
      preloader,
      url: `/server/api/digitboard/${displayId}/`,
      fetchData: fetchDisplays,
      setShowWarn: setShowDelWarn,
      setId: setDisplayId,
      showError,
    });
  };

  const handleTableIcon = (e, row) => {
    const {
      dataset,
    } = e.target;
    const {
      id,
      name,
      address,
      number,
      enabled,
      type,
      config,
    } = row;
    if (dataset.name === 'del') {
      setDisplayId(id);
      setDisplayName(name);
      setShowDelWarn();
    } else if (dataset.name === 'update') {
      setDisplayUpdate(true);
      setDisplayData({
        id,
        name,
        address,
        number,
        enabled,
        type,
        config,
      });
      toggleOpen();
    }
  };

  const Setting = Config(write);
  const {
    columns,
  } = Setting;

  return (
    <> {
      displayList === null
        ? (
          <Preloader
            isOpen
            small
          />
        ) : displayList.length
          ? (
            <Table
              keys="id"
              data={
            displayList
          }
              handleIcon={
            handleTableIcon
          }
              columns={
            columns
          }
            />
          ) : <EmptyList> Внешних устройств нет </EmptyList>
    } <DeleteWarning
      confirmDel={
      delDisplay
    }
      name={
      displayName
    }
      isOpen={
      showDelWarn
    }
      toggleOpen={
      setShowDelWarn
    }
    />
    </>
  );
};

export default React.memo(DisplaysTable);
