import React, {
  useEffect, useCallback, useState, useRef,
} from 'react';
import { useSelector } from 'react-redux';
import { AppContainer, AbsoluteItem } from './style/style';
import { ReactComponent as Logo } from './images/logo.svg';
import { getConfig, getStyle } from './utils/getConfig';
import Preloader from './scenes/preloader/preload';
import Timer from './components/timer/timer';
import Scene from './scenes/scene';
import history from '../../../../../../utils/customHistory';
import { resetTicket } from './utils/actions';
import Store from './utils/store';
import { resetScanPolicyStore } from '../../../../../../store/scanPolicy';


const { dispatch } = Store;


const App = ({ screen, ...props }) => {
  const { oldConfig, isEdit, id } = props;
  const { style, config } = useSelector((state) => state);
  const { startScreen, headUrl } = config;
  const { errorText } = config;
  const [notZoneId, setNotZoneId] = useState(false);
  const timer = useRef(false);
  const {
    head, header, extraText, phone, clock, logoABT, logoComp, footer, time, headerMain, headerSub,
  } = style;

  const dragStart = (e) => e.preventDefault();


  const goToStart = useCallback(() => {
    if (oldConfig) return;
    clearTimeout(timer.current);
    if (startScreen) {
      timer.current = setTimeout(() => {
        const { pathname } = history.location;
        if (errorText !== 'Отсутствует соединение с сервером' && pathname !== startScreen) {
          history.push(startScreen);
          dispatch(resetTicket());
        }
        goToStart();
        resetScanPolicyStore();
      }, 2 * 60 * 1000);
    }
  }, [startScreen, errorText, oldConfig]);

  useEffect(() => {
    if (oldConfig) getStyle({ style: oldConfig.config.style, isEdit });
  }, [oldConfig, isEdit]);

  useEffect(() => {
    getConfig({ id, dataProps: oldConfig, setNotZoneId });
  }, [id]);

  useEffect(() => {
    document.body.addEventListener('dragstart', dragStart);
    document.body.addEventListener('click', goToStart);
    return () => {
      document.body.removeEventListener('dragstart', dragStart);
      document.body.removeEventListener('click', goToStart);
      clearTimeout(timer);
    };
  }, [goToStart]);

  useEffect(() => {
    goToStart();
  }, [errorText, startScreen, goToStart]);

  if (logoABT) delete logoABT.backgroundImage;
  const st = { backgroundSize: 'cover', backgroundRepeat: 'no-repeat' };
  if (head && config.config && !notZoneId) {
    return (
      <AppContainer
        width={style.head.width}
        height={style.head.height}
        style={{ fontFamily: style.head.fontFamily, backgroundImage: `url(${style.head.backgroundImage})` }}
      >
        <div style={{
          ...header, st, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center',
        }}
        >
          <p style={{ fontSize: headerMain.fontSize, color: headerMain.color }}>{headerMain.text}</p>
          <p style={{ fontSize: headerSub.fontSize, color: headerSub.color }}>{headerSub.text}</p>
        </div>

        <AbsoluteItem data-name="text" style={extraText}>
          <div style={{ order: Number(!phone.order) }}>{extraText.text}</div>
          <div style={{ ...phone, order: phone.order }}>{phone.text}</div>
        </AbsoluteItem>

        <AbsoluteItem data-name="clock" style={clock}>
          <Timer clock={clock} styleTime={time} viewType={style.clock.viewType} />
        </AbsoluteItem>

        <AbsoluteItem data-name="logoAbt" style={logoABT} logo><Logo /></AbsoluteItem>
        {logoComp.backgroundImage && (
        <AbsoluteItem data-name="logoComp" style={logoComp}>
          <img src={logoComp.backgroundImage} alt="" />
        </AbsoluteItem>
        )}

        {headUrl && (
        <Scene
          config={config.config}
          satelliteParams={{ satelliteId: config.satelliteId, satellitePort: config.satellitePort }}
          startScreen={startScreen}
          headUrl={headUrl}
          selectScreen={screen}
          isEdit={isEdit}
          id={id}
          params={config.defaultParams}
          style={style}
        />
        )}
        <div style={{ ...footer, st }} />
      </AppContainer>
    );
  }
  return <Preloader notZoneId={notZoneId} />;
};

export default App;
