import React, { useState, useEffect, useRef } from 'react';
import {
  Table, Input, Button, Space,
} from 'antd';
import 'antd/dist/antd.css';
import Highlighter from 'react-highlight-words';
import { SearchOutlined } from '@ant-design/icons';
import SqlQuery from './SqlQuery';
import TableConfig from './TableConfig';
import ReportTitle from '../../../../ReportTitle';
import Preloader from '../../../../../../../../Elements/Preloader/Preloader';
import { PreloaderBox } from '../../../../../../../../../styles/Common';
import appStore from '../../../../../../../../../store/appStore';

const Operator = () => {
  const [ReportParams, setReportParams] = useState();
  const startDateMoment = appStore.useState((s) => s.reportConfig.startDate);
  const endDateMoment = appStore.useState((s) => s.reportConfig.endDate);
  const dateFormat = 'YYYY-MM-DD HH:mm:ss';
  const startDate = startDateMoment.format(dateFormat);
  const endDate = endDateMoment.format(dateFormat);

  const [searchText, setSearchText] = useState();
  const [searchedColumn, setSearchedColumn] = useState();
  const input = useRef(null);

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters) => {
    clearFilters();
    setSearchText('');
  };

  const getColumnSearchProps = (dataIndex) => ({
    filterDropdown: ({
      setSelectedKeys, selectedKeys, confirm, clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={input}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={(e) => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered) => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) => (record[dataIndex]
      ? record[dataIndex].toString().toLowerCase().includes(value.toLowerCase())
      : ''),
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        setTimeout(() => input.current.select(), 100);
      }
    },
    render: (text) => (searchedColumn === dataIndex ? (
      <Highlighter
        highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
        searchWords={[searchText]}
        autoEscape
        textToHighlight={text ? text.toString() : ''}
      />
    ) : (
      text
    )),
  });

  useEffect(() => {
    SqlQuery(startDate, endDate).then((resp) => {
      const { dataObjects, question } = resp;
      const params = {
        items: dataObjects,
        question,
        TableConfig: TableConfig(getColumnSearchProps),
      };
      setReportParams(params);
    });
  }, [endDate, startDate]);

  return (
    <div>
      <ReportTitle
        item={ReportParams ? ReportParams.items.length : null}
        name="операторы"
        exportDisable
      />
      { ReportParams
        ? (
          <Table
            keys={ReportParams.items.forEach((item, index) => item[index])}
            dataSource={ReportParams.items}
            columns={ReportParams.TableConfig.columns}
            size="small"
          />
        )
        : (

          <PreloaderBox>
            <Preloader isOpen small />
          </PreloaderBox>

        )}
    </div>
  );
};

export default Operator;
