import React from 'react';
import { textFilter } from 'react-bootstrap-table2-filter';
import { BtnTable } from '../../../styles/Common';
import { ReactComponent as True } from '../../../images/true.svg';
import { ReactComponent as False } from '../../../images/false.svg';
import { placeholder } from '../../../utils/tableFormatters';
import iconFormatter from '../../../utils/iconFormatter';

function checkFormatter(value) {
  return (
    <React.Fragment>
      {value
        ? <BtnTable active bg="accent" hNone><True /></BtnTable>
        : <BtnTable bg="gray" hNone><False /></BtnTable>
      }
    </React.Fragment>
  );
}

export default function (write) {
  return {
    columns: [
      {
        dataField: 'name',
        text: 'Наименование',
        headerFormatter: placeholder,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Введите Наименование',
        }),
        editable: false,

      },
      {
        dataField: 'address',
        text: 'IP адрес',
        headerFormatter: placeholder,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Введите IP адрес',
        }),
        editable: false,
        formatter(cell) {
          return cell.split(':')[0];
        },
      },
      {
        dataField: 'port',
        text: 'Порт',
        headerFormatter: placeholder,
        filter: textFilter({
          className: 'customPlaceholder',
          placeholder: 'Введите Порт',
        }),
        editable: false,
        formatter(cell, row) {
          return row.address.split(':')[1];
        },
      },
      {
        dataField: 'number',
        text: 'Номер',
        editable: false,
      },
      {
        dataField: 'created_by_username',
        text: 'Создан',
        sort: true,
        editable: false,
      },
      {
        dataField: 'created_on',
        text: 'Дата создания',
        sort: true,
        editable: false,
      },
      {
        dataField: 'enabled',
        text: 'Включен',
        sort: true,
        editable: false,
        formatter: checkFormatter,
      },
      {
        dataField: 'Settings',
        isDummyField: true,
        text: ' ',
        formatter: () => iconFormatter({ write }),
        formatExtraData: {},
        editable: false,
        headerStyle: {
          width: 100,
        },
      },
    ],
  };
}
