import React, { useState } from 'react';
import DatePicker, { registerLocale } from 'react-datepicker';
import moment from 'moment';
import ruDate from 'date-fns/locale/ru';
import { isAfter } from 'date-fns';
import {
  FilterBox, FilterBody, FilterFooter, FilterItem, FilterBtn,
} from '../../../styles/ActionLog';
import ComboBox from '../../Elements/Input/ComboBox';
import { Input } from '../../../styles/Common';

import { useToggle } from '../../../hooks/useToggle';

registerLocale('ru-RU', ruDate);

const Filter = ({ handleFilters, userList }) => {
  const [filterList, setFilterList] = useState([]);
  const [dates, setDates] = useState({ start: null, end: null });
  const [clearFilters, setClearFilters] = useToggle(false);
  const [comment, setComment] = useState('');

  const actions = {
    0: 'Неизвестное действие',
    // 1: 'Авторизация',
    // 2: 'Отключение',
    3: 'Создание объекта на сервере',
    4: 'Удаление объекта на сервере',
    5: 'Изменение объекта на сервере',
    6: 'Изменение параметров сервера',
    7: 'Вход по веб',
    8: 'Выход по веб (или удаление сессии по окончанию времени жизни сессии)',
  };

  const users = {};
  if (userList) {
    userList.forEach((user) => {
      users[Number(user.id)] = user.username;
    });
  }

  const clear = () => {
    setDates({ start: null, end: null });
    handleFilters([]);
    setComment('')
    setClearFilters();
  };


  const changeFilters = (changedFilters) => {
    const oldFilters = filterList.filter(ar => !changedFilters.find(rm => (rm.column === ar.column && ar.comparator === rm.comparator)));
    const newFilters = [].concat(oldFilters, changedFilters.filter(f => f.value !== null));
    setFilterList(newFilters);
  };

  const handleCombo = (value, column) => {
    changeFilters([{
      column,
      value,
      comparator: '=',
    }]);
  };

  const dateChange = ({ startDate, endDate }) => {
    let start = null;
    let end = null;
    const { start: defaultStartDate, end: defaultEndDate } = dates;
    start = startDate || defaultStartDate;
    end = endDate || defaultEndDate;
    end = isAfter(start, end) ? start : end;
    setDates({ start, end });
    const filterDate = [];
    if (start)filterDate.push({ column: 'created_on::DATE', value: moment(start).startOf('day').format('YYYY-MM-DD'), comparator: '>=' });
    if (end) filterDate.push({ column: 'created_on::DATE', value: moment(end).endOf('day').format('YYYY-MM-DD'), comparator: '<=' });
    changeFilters(filterDate);
  };

  const handleComment = (e) => {
    const { value } = e.target;
    setComment(value);
    changeFilters([{ column: 'comment', value: (value !== '') ? `%${value}%` : null, comparator: 'LIKE' }]);
  };

  const dpConfig = {
    selectsStart: true,
    showTimeSelect: false,
    className: 'dateInput',
    dateFormat: 'dd.MM.yyyy',
    placeholderText: 'Выберите дату',
  };

  const { start, end } = dates;
  return (
    <FilterBox>
      <h2>
        Фильтрация
      </h2>
      <FilterBody>
        <FilterItem>
          <div>Дата начала:</div>
          <DatePicker
            {...dpConfig}
            selected={start}
            selectsStart
            startDate={start}
            endDate={end}
            onChange={date => dateChange({ startDate: date })}
          />
        </FilterItem>
        <FilterItem>
          <div>Дата окончания:</div>
          <DatePicker
            {...dpConfig}
            selected={end}
            selectsEnd
            startDate={start}
            endDate={end}
            onChange={date => dateChange({ endDate: date })}
          />
        </FilterItem>
        {users
        && (
        <FilterItem>
          <div>Пользователи</div>
          <ComboBox list={users} setData={handleCombo} column="created_by_id" clear={clearFilters} placeholder="Выберите пользователя" />
        </FilterItem>
        )}
        {actions
        && (
        <FilterItem>
          <div>Действия</div>
          <ComboBox list={actions} setData={handleCombo} column="action" clear={clearFilters} placeholder="Выберите действие" />
        </FilterItem>
        )}
        <FilterItem>
          <div>Комментарий</div>
          <Input style={{ fontSize: 12 }} type="text" onChange={handleComment} value={comment} placeholder="Введите комментарий" />
        </FilterItem>
      </FilterBody>
      <FilterFooter>
        <FilterBtn mgr="10px" bg="green" onClick={() => handleFilters(filterList)}>Применить</FilterBtn>
        <FilterBtn active onClick={clear}>Сбросить</FilterBtn>
      </FilterFooter>
    </FilterBox>
  );
};

export default Filter;
