import React, { useEffect, useState } from 'react';
import { FaRedo, FaSave } from 'react-icons/fa';
import {
  FieldBox, Text, BtnReset, AbsoluteBox, BtnSave,
} from '../../../../styles/ServiceList';
import { Input } from '../../../../styles/Common';
import SelectInput from '../../../Elements/Input/SelectInput/SelectInput';


const Field = ({
  param, onSave, resetParam, write,
}) => {
  const [value, setValue] = useState(null);
  useEffect(() => {
    setValue(param.value);
  }, [param]);
  const reg = /\.|,/g;
  const regNumber = /\d/;
  const boolOptions = [
    { id: 1, name: 'Да' },
    { id: 0, name: 'Нет' },
  ];

  const renderFieldType = () => {
    const { param_type } = param;
    switch (param_type) {
      case 'boolean':
        return (
          <SelectInput
            disabled={!write}
            options={boolOptions}
            onChange={(e) => onSave(e.target.value, param.id, param.param_type)}
            value={Number(param.value)}
            noDefault
            name={param.value === '0' ? 'нет' : 'да'}
            multiple={false}
          />
        );
      case 'number':
        return (
          <Input
            disabled={!write}
            type="number"
            step="1"
            min="0"
            value={Number(value)}
            onKeyDown={(e) => (reg.test(e.key) || !regNumber.test(e.key))}
            onChange={(e) => setValue(Number(e.target.value))}
          />
        );
      default:
        return (
          <Input
            disabled={!write}
            type="text"
            value={value || ''}
            onKeyDown={(e) => e.preventDefault()}
            onChange={(e) => setValue(e.target.value)}
          />
        );
    }
  };

  return (
    <FieldBox>
      <AbsoluteBox m={param.param_type === 'boolean'}>
        {write && param.reset && <BtnReset onClick={() => resetParam(param.id)}><FaRedo /></BtnReset>}
        {write && param.value !== value && <BtnSave m={param.param_type === 'boolean'} onClick={() => onSave(value, param.id, param.param_type)}><FaSave /></BtnSave>}
      </AbsoluteBox>
      <Text>{param.description}</Text>
      {renderFieldType()}
    </FieldBox>
  );
};

export default Field;
