function priceFormatter(column, colIndex, { filterElement, sortElement }, sort) {
  return (
    <div className={`filterBoxTable ${sort ? 'sort' : null}`}>
      { !filterElement || column.dataField === '9' || column.dataField === '3' || column.dataField === '8' ? (null) : (<Edit style={{ margin: '2px 10px 0 0' }} fill={c.main} />) }
      { filterElement || column.text }
      { sortElement }
    </div>
  );
}

function checkFormatter(value, revers) {
  return (
    <React.Fragment>
      {value
        ? <BtnTable bg="accent" hNone>{revers ? <False fill="white" /> : <True fill="white" />}</BtnTable>
        : <BtnTable bg="gray" hNone>{revers ? <True fill="white" /> : <False fill="white" />}</BtnTable>
      }
    </React.Fragment>
  );
}

export default function Setting(zones, write, placeList) {
  function zoneFormatter(value) {
    const zone = zones.find(zoneItem => zoneItem.id === value[0]);
    return (
      <div>{zone ? zone.name : ''}</div>
    );
  }

  function zoneFilter(value, data) {
    const reg = new RegExp(`${value}`);
    const zoneList = zones.filter(el => reg.test(el.name));
    return data.filter(zone => zoneList.some(item => item.id === zone.connectedZones[0]));
  }

  return {
    columns: [
      {
        dataField: 'id',
        text: 'ID',
        editable: false,
        sort: true,
        sortCaret,
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
      },
      {
        dataField: 'name',
        text: 'Наименовение',
        headerFormatter: priceFormatter,
        filter: textFilter({
          placeholder: 'Наименование',
        }),
        editable: false,
      },
      {
        dataField: 'placename',
        text: 'Обозначение',
        headerFormatter: priceFormatter,
        filter: textFilter({
          placeholder: 'Обозначение',
        }),
        editable: false,
      },
      {
        dataField: 'connectedZones',
        text: 'Подключенная зона',
        editable: false,
        headerFormatter: priceFormatter,
        filter: textFilter({
          placeholder: 'Подключенная зона',
          onFilter: zoneFilter,
        }),
        formatter: zoneFormatter,

      },
      {
        dataField: 'disabled',
        text: 'Статус',
        sort: true,
        sortCaret,
        headerFormatter: (column, colIndex, { filterElement, sortElement }) => priceFormatter(column, colIndex, { filterElement, sortElement }, true),
        editable: false,
        formatter: checkFormatter,
      },
      {
        dataField: 'Settings',
        text: '',
        headerStyle: {
          width: 140,
        },
        formatter: () => IconFormatter({ filter: true, write, params: true }),
        formatExtraData: {},
        editable: false,
      },
    ],
  };
}
