import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import defaultFilters from '../../../../../Elements/ReportFilters/utils/defaultFilters';
import TableConfig from './TableConfig';
import PrepareTable from '../../../../../Elements/Tablev2/PrepareTable';
import ReportFilters from '../../../../../Elements/ReportFilters/ReportFilters';

const ClockwiseTable = (props) => {
  const [queryParams, setQueryParams] = useState();
  const { preloader } = props;

  const preparQueryParams = async (filters) => {
    const columns = TableConfig(); // Колонки для таблиц
    const params = {
      columns: columns.columns,
      filters: filters || defaultFilters(),
      url: '/api/reports/build',
      type: 'clockwiseReport',
    };
    setQueryParams(params);
  };

  useEffect(() => {
    preparQueryParams();
  }, [props]);


  return (
    <React.Fragment>
      { queryParams ? (
        <React.Fragment>
          <ReportFilters
            showFilters={{
              main: ['reportRange', 'operators', 'workplaces', 'queues'],
              others: [],
              orders: ['ndate', 'all_cnt'],
            }}
            paramsCallback={preparQueryParams}
            type="clockwiseReport"
          />
          <PrepareTable
            keys="ndate"
            type="OperatorTable"
            {...queryParams}
            preloader={preloader}
          />
        </React.Fragment>
      )
        : (null)
      }
    </React.Fragment>
  );
};

function mapStateToProps(state) {
  return { reportConfig: state.reportConfig };
}

export default connect(mapStateToProps)(ClockwiseTable);
