import React, {
  useState, useReducer, useEffect,
} from 'react';
import axios from 'axios';
import defaultConfig from './defaultConfig';
import BoardCtx from './BoardCtx';
import BoardPreview from './BoardPreview/BoardPreview';
import { ReactComponent as BackArr } from '../../../../../images/back.svg';
import Editor from './BoardEditor/Editor/Editor';
import recureseBoardConfig from '../../../../../utils/recurseBoardConfig';

const BoardStyle = ({
  config, handleSave, history, showError,
}) => {
  const [activeEl, setActiveEl] = useState('header');
  const [text, dispatchText] = useReducer((state, action) => ({ ...state, [action.param]: action.value }), config.text);
  const [fonts, setFonts] = useState([]);

  useEffect(() => {
    axios({
      method: 'get',
      url: '/api/fonts',
    }).then((res) => setFonts(res.data.map((el) => ({ id: el.name, name: el.name }))));
  }, []);

  const newConfig = recureseBoardConfig(config, defaultConfig);

  const [layout, dispatch] = useReducer((state, action) => {
    const {
      name, param, type, value,
    } = action;
    switch (type) {
      case 'params':
        const newState = { ...state };
        if (param === 'vertical') newState.vid = false;
        if (name) return { ...newState, [name]: { ...state[name], [param]: !isNaN(value) ? +value : value } };
        return { ...newState, [param]: value };
      case 'fetchStyle':
        return { ...state, ...action.payload };
      case 'name':
        return { ...state, name: action.payload };
      case 'vertical':
        return { ...state, vertical: action.payload };
      case 'video':
        return { ...state, vid: action.payload };
      case 'clock':
        return { ...state, clock: action.payload };
      case 'borderRadius':
        return { ...state, borderRadius: action.payload };
      case 'cols':
        return { ...state, cols: action.payload };
      case 'font':
        return { ...state, font: action.payload };
      case 'element':
        return { ...state, [activeEl]: { ...state[activeEl], [action.value]: action.payload } };
      case 'uploadImage':
        return { ...state, images: { ...state.images, [action.value]: action.payload } };
      case 'delImage':
        return { ...state, images: { ...state.images, [action.value]: '' } };
      case 'background':
        return { ...state, background: action.payload };
      case 'delBackground':
        return { ...state, background: '' };
      default:
        return state;
    }
  }, { ...newConfig.layout });

  const handleConfig = () => {
    handleSave({ layout, text });
  };

  const ctx = {
    dispatch,
    layout,
    activeEl,
    setActiveEl,
    text,
    dispatchText,
    fonts,
    handleConfig,
    showError,
    elWidth: 720,
  };

  return (
    <>
      <BoardCtx.Provider value={ctx}>
        <div className="title-box">
          <button className="title__btn-back" onClick={() => history.goBack()}>
            <BackArr /><span>Назад</span>
          </button>
          <h1 className="title">Стилизация табло</h1>
        </div>
        <div className="device-content">
          <Editor />
          <BoardPreview handleConfig={handleConfig} />
        </div>
      </BoardCtx.Provider>
    </>
  );
};

export default BoardStyle;
