import React, { useEffect } from 'react';
import { FaAlignCenter } from 'react-icons/fa';
import { Input } from '../../../../../styles/Common';
import InputSelect from '../../../../Elements/Input/SelectInput/SelectInput';
import {
  AlingItem, DontImg, InputFile, SpanSelect, Label,
} from '../../style';

export default ({
  align, height, img, change, type, data, barcode, changeImage, fonts,
}) => {
  let timeout;

  const optionsSize = [];

  for (let s = 14; s < 180; s += 2) {
    optionsSize.push({ id: s, name: s });
  }


  useEffect(() => clearTimeout(timeout));

  const changeImg = (event) => {
    if (event.target.files.length <= 0) return;
    const file = new FileReader();
    const typeFile = event.target.files[0].type;
    if (typeFile !== 'image/png' && typeFile !== 'image/jpeg') return;
    file.onload = (e) => {
      changeImage(e.target.result, 'logotype');
    };
    file.readAsDataURL(event.target.files[0]);
  };
  const options = fonts.map((el) => ({ id: el.name, name: el.name }));
  return (
    <>
      {!img && <InputSelect width="15%" noDefault options={options} value={data.fontFamily} onChange={(e) => change(type, e.target.value, 'fontFamily')} multiple={false} />}

      {!img && <InputSelect maxHeight="200px" noDefault width="100px" options={optionsSize} value={data.fontSize} name="fontSize" onChange={(e) => change(type, e.target.value, 'fontSize')} />}
      {img && <Input width="10%" data-name="height" defaultValue={height} onChange={(e) => change(type, e.target.value, 'height')} />}

      {!img && !barcode ? null : img && !barcode
        // ? <Input file width="30%" type="file" onChange={changeImg} />
        ? (
          <Label className="uploadBtnWrap" htmlFor="thefile">
            <InputFile type="file" id="thefile" onChange={changeImg} />
            <SpanSelect>Выбрать</SpanSelect>
            <span id="fileName">{!data.url && 'Изображение не выбрано'}</span>
          </Label>
        )
        : <DontImg />}

      <AlingItem active={align} onClick={() => change(type, !align, 'align')}>
        <FaAlignCenter />
      </AlingItem>
    </>
  );
};
