import React, { useState } from 'react';
import { FaSave } from 'react-icons/fa';

const Time = ({ el, onSave }) => {
  const [state, setState] = useState({
    clientfio: { v: el.clientfio, t: 'ФИО' },
    clientphone: { v: el.clientphone, t: 'Телефон' },
    clientmail: { v: el.clientmail, t: 'E-mail' },
  });

  const isChange = el.clientfio !== state.clientfio.v || el.clientphone !== state.clientphone.v || el.clientmail !== state.clientmail.v;
  return (
    <div className="timeMiniBox">
      {Object.keys(state).map((key) => (
        <div key={key} className="timeInputBox">
          <p>{state[key].t}</p>
          <input
            type="text"
            placeholder={state[key].v ? state[key].t : '--'}
            defaultValue={state[key].v}
            onChange={(e) => setState({ ...state, [key]: { ...state[key], v: e.target.value } })}
          />
        </div>
      ))}
      {isChange && (
      <div
        className="saveBtn"
        onClick={() => onSave({
          clientfio: state.clientfio.v,
          clientphone: state.clientphone.v,
          clientmail: state.clientmail.v,
          q_id: el.q_id,
          pastart_on: el.pastart_on,
          calendarday_id: el.calendarday_id,
          approved: true,
        }, el.calendarCellId)}
      >
        <FaSave />
      </div>
      )}
    </div>
  );
};

export default Time;
